import { useState } from 'react';

import { toucanMetaName } from '~/constants/html';

/**
 * useCrawlerConfig is hook that allows us to pull in config if toucan should run
 * its crawler. This is mostly done for some of our pages, and since we would like
 * those pages to dictate this, we essentially look for a meta tag.
 *
 * Here is what the meta tag looks like:
 * ```
 * <meta name="toucanbot" content="noindex" />
 * ```
 * This meta tag will stop the Toucan crawler from running.
 */
export const useCrawlerConfig = () => {
  const [isCrawlerEnabled] = useState(() => {
    const meta = document.querySelector(`meta[name="${toucanMetaName}"]`);
    if (!meta) {
      return true;
    }
    if ((meta.getAttribute('content') ?? '').includes('noindex')) {
      return false;
    }
    return true;
  });
  return { isCrawlerEnabled };
};
