import browser from 'webextension-polyfill';

import { IncreaseTranslationsSeenArgs } from '~/analytics/heartbeatManager';
import { toggleExtensionForPrintEventArgs } from '~/background/services/settings';
import { InlineTranslationLearningShortcutInfo } from '~/types/learningShortcut';

import { ContentMessageKeys } from './contentMessageKeys';

interface SignUpUserArgs {
  name: string;
  email: string;
  password: string;
  emailOptIn?: boolean;
}

export interface LogInUserArgs {
  email: string;
  password: string;
}

interface SaveUserLearningShortcutSettingArgs {
  learningShortcutId: string;
}

// Add additional arg types for useContentMessageProducerHook
export type ContentMessageProducerArgs =
  | IncreaseTranslationsSeenArgs
  | SignUpUserArgs
  | LogInUserArgs
  | SaveUserLearningShortcutSettingArgs;

export interface ContentMessage {
  contentMessageKey: ContentMessageKeys;
  args?: any;
}

/**
 * ContentMessageProducer class contains message producer methods.
 * When adding methods both the arguments and returns should be typed.
 * Methods should only send messages, all other business logic
 * should be kept out of these methods.
 *
 * As this class grows, we should group methods by domain(e.g. shortcuts, sidebar, heartbeat, etc.)
 *
 * Refer to this notion page for more detailed documentation.
 * https://www.notion.so/jointoucan/Extension-Messaging-b68625dcdf514943a508e99579e3a90b
 */
export class ContentMessageProducer {
  public static async sendMessage(contentMessage: ContentMessage) {
    const message = await browser.runtime.sendMessage(contentMessage);
    return message;
  }

  public static async getInlineTranslationLearningShortcutInfo(): Promise<InlineTranslationLearningShortcutInfo> {
    return ContentMessageProducer.sendMessage({
      contentMessageKey: ContentMessageKeys.getInlineTranslationLearningShortcutInfo,
    });
  }

  public static async logSeenLearningsCount(args: IncreaseTranslationsSeenArgs): Promise<void> {
    return ContentMessageProducer.sendMessage({ contentMessageKey: ContentMessageKeys.logSeenLearningsCount, args });
  }

  public static async signUpUser(args: SignUpUserArgs): Promise<{ data: any; error: any }> {
    return ContentMessageProducer.sendMessage({
      contentMessageKey: ContentMessageKeys.signUpUser,
      args,
    });
  }

  public static async loginUser(args: LogInUserArgs): Promise<{ data: any; error: any }> {
    return ContentMessageProducer.sendMessage({
      contentMessageKey: ContentMessageKeys.logInUser,
      args,
    });
  }

  public static async getLearningShortcuts(): Promise<{ data: unknown; error: any }> {
    return ContentMessageProducer.sendMessage({
      contentMessageKey: ContentMessageKeys.getLearningShortcuts,
    });
  }

  public static async saveUserLearningShortcutSetting(
    args: SaveUserLearningShortcutSettingArgs,
  ): Promise<{ data: any; error: any }> {
    return ContentMessageProducer.sendMessage({
      contentMessageKey: ContentMessageKeys.saveUserLearningShortcutSetting,
      args,
    });
  }

  public static async toggleExtensionForPrintEvent(
    args: toggleExtensionForPrintEventArgs,
  ): Promise<{ data: any; error: any }> {
    return ContentMessageProducer.sendMessage({
      contentMessageKey: ContentMessageKeys.toggleExtensionForPrintEvent,
      args,
    });
  }
}
