import { useCallback, useEffect, useRef } from 'react';

import {
  getHTMLElement,
  getNodeFromAddress,
  isHTMLElement,
  isToucanSpan,
  getParentElements,
  isTextNode,
} from '~/helpers/htmlHelpers';
import { filterNull, idleAsyncSeries, requestIdle } from '~/helpers/utilityHelpers';
import { useMutationObserver } from '~/hooks/useMutationObserver';

import { useHistoryChange } from '../useHistoryChange';

import { shouldCrawl } from './crawler';
import { hasNotBeenCrawled } from './excludedNodes';
import { useCrawler } from './useCrawler';
import { PartialDynamicCrawlerObserver, useDynamicCrawlerCache } from './useDynamicCrawlerCache';

/**
 * useDynamicCrawler is a crawler that will find dynamic content and crawl that content
 * allowing us to work on sites like Facebook. We previously had some hard coded implementation
 * of this, but this crawler is generalize to be able to handle. There is an effect that will
 * auto attach to the top level element or attach to a set of passed elements.
 * @param options.intersectionObserver the intersectionObserver passed to the internal crawler
 * @param options.hydrateTargets is an array of selectors that are marked as dynamic content
 */
export const useDynamicCrawler = ({
  intersectionObserver,
  hydrateTargets,
  onDynamicPage,
  excludedTags = [],
}: {
  intersectionObserver: IntersectionObserver;
  hydrateTargets?: { selector: string }[];
  onDynamicPage: () => void;
  excludedTags?: Array<string>;
}) => {
  const { crawlElement } = useCrawler({ crawlOnLoad: false, intersectionObserver, excludedTags });
  const { addElement, teardownObserver, observerCache } = useDynamicCrawlerCache();
  // Refs are used here because mutation function does not update
  const crawlCount = useRef<number>(0);
  const dynamicPageDetected = useRef<boolean>(false);
  const cancelIdleCallback = useRef<() => void>(() => {});
  const cancelSeries = useRef<() => void>(() => {});

  const mutationObserver = useMutationObserver({
    onMutation: mutations => {
      const addedNodes = mutations.flatMap(mutation => Array.from(mutation.addedNodes));

      // Process and re-crawl the new nodes.
      // This is done in a non-blocking way to allow user input and the host application
      // to run code while we attempt to do this all in the background when resources are
      // available.
      cancelIdleCallback.current = requestIdle(async () => {
        const addedElements = getParentElements(
          addedNodes
            .filter(node => !isTextNode(node))
            .filter(node => isHTMLElement(node) && !isToucanSpan(node))
            .map(node => node.parentElement)
            .filter(filterNull),
        );

        const { cancel } = idleAsyncSeries(
          addedElements.map(({ htmlElement }) => async () => {
            if (shouldCrawl(htmlElement) && hasNotBeenCrawled({ parentNode: htmlElement })) {
              crawlCount.current += 1;
              crawlElement(htmlElement);
              if (crawlCount.current > 50 && !dynamicPageDetected.current) {
                dynamicPageDetected.current = true;
                onDynamicPage();
              }
            }
          }),
        );

        cancelSeries.current = cancel;
      });
    },
  });

  // Teardown callbacks, and promises when the component is unmounted
  useEffect(
    () => () => {
      cancelIdleCallback.current();
      cancelSeries.current();
    },
    [],
  );

  /**
   * observeElement is a method that registers a element to be observed.
   * This observer will be added to find childList updates, and re-crawl updated nodes.
   * @param el a selector or a html element.
   */
  const observeElement = (el: string | HTMLElement) => {
    const element = getHTMLElement(el);
    if (element) {
      mutationObserver.observe(element, { subtree: true, childList: true });
      addElement({
        node: element,
        observer: mutationObserver,
      });
      return {
        node: element,
        observer: mutationObserver,
      };
    }
    return null;
  };

  // Run any preloaded observable targets
  useEffect(() => {
    if (hydrateTargets) {
      let observers: PartialDynamicCrawlerObserver[] = [];
      window.requestAnimationFrame(() => {
        observers = hydrateTargets.map(({ selector }) => observeElement(selector)).filter(filterNull);
      });
      return () => {
        observers.forEach(observer => {
          teardownObserver(observer);
        });
      };
    }
    observeElement(document.body);
    return () => {};
  }, []);

  useHistoryChange(
    useCallback(() => {
      observerCache.forEach(({ node, address, observer }) => {
        const el = getNodeFromAddress(address.split('.').map(num => parseInt(num, 10)));
        if (node === el) {
          // Do nothing node is still present
          return;
        }
        // remove orphaned nodes
        teardownObserver({ node, observer });
      });
    }, [observerCache]),
  );

  return {
    observeElement,
  };
};
