import { StorageKey } from '~/constants/storage';
import { useGetCurrentTabIdQuery } from '~/schema/background';

import { useStorage } from './useStorage';

export const useModalStorage = (key: StorageKey, defaultValue: boolean): [boolean, (value: boolean) => void] => {
  const { data } = useGetCurrentTabIdQuery();
  const currentTabId = data?.browser?.tab?.id ?? -1;

  const [value, setStorage] = useStorage<{ enabled: boolean; tabId: number }>(key, {
    enabled: defaultValue,
    tabId: -1,
  });

  const isEnabled = value.enabled && value.tabId === currentTabId;
  const setIsEnabledForCurrentTab = (enabled: boolean) => setStorage({ enabled, tabId: currentTabId });

  return [isEnabled, setIsEnabledForCurrentTab];
};
