/** @jsx jsx */
import React, { FC, useEffect, ChangeEvent, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, CircularProgress } from '@mui/material';
import { useForm } from 'react-hook-form';

import { ModalBaseProps, ModalType } from '~/components/ModalContainer/types';
import Header from '~/components/shared/Header';
import { Image } from '~/components/shared/Image';
import { Modal, ModalContent, ModalHeader } from '~/components/shared/Modal';
import { StorageKey } from '~/constants/storage';
import { getErrorId, resolveErrors } from '~/helpers/errors';
import { getSentenceWithTranslations } from '~/helpers/text';
import { useActiveTranslation } from '~/hooks/useActiveTranslation';
import { useDictionary } from '~/hooks/useDictionary';
import { useHideTranslations } from '~/hooks/useHideTranslations';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useTranslatedNodesById } from '~/hooks/useTranslatedNodes';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useAddTranslationInaccuraciesMutation, useSendErrorReportEmailMutation } from '~/schema/background';
import { TranslationInaccuracyError } from '~/schema/server';

import { ErrorMessage } from './shared/ErrorMessage';

const { Typography, Radio, RadioGroup, TextField, Button } = ToucanComponents;

enum ReportError {
  Misspelling = 'misspelling',
  Grammar = 'grammar',
  Context = 'context',
  Audio = 'audio problem',
  SomethingElse = 'something else',
}

interface ReportErrorInputs {
  errorReason: string;
  errorDetails: string;
}

interface ReportErrorModalProps extends ModalBaseProps {}

export const ReportErrorModal: FC<ReportErrorModalProps> = ({ onRequestClose, onRequestOpen, selectedModalType }) => {
  const { t } = useTranslation();
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    formState: { errors: formErrors },
  } = useForm<ReportErrorInputs>({
    defaultValues: {
      errorReason: '',
      errorDetails: '',
    },
  });
  const [didFormSubmit, setDidFormSubmit] = useState(false);
  const currentActiveTranslation = useActiveTranslation();
  const { translationId, targetPhrase } = useDictionary(currentActiveTranslation?.translationId ?? '') || {};
  const { targetLanguage } = useUserInfo();
  const { removeTranslationInaccuracy } = useHideTranslations();
  const [addTranslationInaccuracies] = useAddTranslationInaccuraciesMutation();
  const translatedNodesById = useTranslatedNodesById();
  const [sendErrorReportEmail, { loading, error }] = useSendErrorReportEmailMutation();
  const [isReportErrorModalOpenRequested, setIsReportErrorModalOpenRequested] = useModalStorage(
    StorageKey.IsReportErrorModalOpenRequested,
    false,
  );

  const errors = resolveErrors<Partial<ReportErrorInputs>>(formErrors, error);

  const closeModal = () => {
    onRequestClose(ModalType.ReportError);
    ContentMessengers.trackEvent({
      eventName: 'Report Error Modal Closed',
      properties: {},
    });
    setIsReportErrorModalOpenRequested(false);
    setDidFormSubmit(false);
  };

  const onSubmit = async ({ errorReason, errorDetails }: ReportErrorInputs) => {
    // We are already sending a request.
    if (loading) {
      return;
    }
    const isTranslationInaccuracy =
      errorReason === TranslationInaccuracyError.Context ||
      errorReason === TranslationInaccuracyError.Grammar ||
      errorReason === TranslationInaccuracyError.Misspelling;

    // Send data to Amplitude
    ContentMessengers.trackEvent({
      eventName: 'Error Report Submit Clicked',
      properties: {
        errorReason,
        URL: window.location.href,
        language: targetLanguage,
        translation: targetPhrase,
        wordRemoved: isTranslationInaccuracy,
      },
    });

    // Email Toucan with information
    await sendErrorReportEmail({
      errorReason,
      translation: targetPhrase ?? '',
      errorDetails,
      translationId: currentActiveTranslation?.translationId ?? '',
    });

    let childNodes = currentActiveTranslation?.node.parentNode?.childNodes;
    if (childNodes && childNodes.length <= 2) {
      // Go up two levels, this happens frequently in links
      childNodes = currentActiveTranslation?.node.parentNode?.parentNode?.childNodes;
    }

    if (isTranslationInaccuracy && childNodes && currentActiveTranslation) {
      const sentenceWithoutTranslation = getSentenceWithTranslations(
        childNodes,
        translatedNodesById,
        currentActiveTranslation?.id ?? '',
      );
      await removeTranslationInaccuracy(errorReason as TranslationInaccuracyError, currentActiveTranslation);
      await addTranslationInaccuracies({
        information: {
          sentenceWithoutTranslation,
          errorType: errorReason as TranslationInaccuracyError,
          translationId: translationId ?? '',
        },
      });
    }
    setDidFormSubmit(true);
  };

  // NOTE: Since the source of truth for this modal is based off of storage
  // we just respond to the changing of that state, this modal has a priority of 0
  // so it always overrides
  useEffect(() => {
    if (selectedModalType !== ModalType.ReportError && isReportErrorModalOpenRequested) {
      onRequestOpen(ModalType.ReportError);
      // Reset the state
      reset({
        errorReason: '',
        errorDetails: '',
      });
    }
  }, [selectedModalType, isReportErrorModalOpenRequested]);

  useEffect(() => {
    if (selectedModalType === ModalType.ReportError) {
      ContentMessengers.trackEvent({
        eventName: 'Report Error Modal Viewed',
        properties: {},
      });
    }
  }, [selectedModalType]);

  useEffect(() => {
    register('errorReason', { required: `${t('reportErrorSelectOneOfTheOptionsError')}` });
  }, []);

  return (
    <Modal
      css={css`
        text-align: center;
        align-items: center;
        width: 418px;
      `}
      onRequestClose={() => {}}
      isOpen={!!(selectedModalType === ModalType.ReportError) && isReportErrorModalOpenRequested}
    >
      <ModalHeader>
        <Header
          onRequestClose={closeModal}
          css={css`
            background-color: transparent;
          `}
          horizontalLineProps={{
            color: ToucanColors.gray[200],
          }}
          isMobileReferralShown={false}
          onMobileReferralClick={() => {}}
        />
      </ModalHeader>
      <ModalContent p={4}>
        {didFormSubmit ? (
          <Box display="flex" flexDirection="column" alignItems="center" textAlign="center">
            <Image src="/images/toucan-report-confirmation.svg" alt={t('reportErrorDetectiveToucan')} />
            <Box mt={2}>
              <Typography variant="md" font="secondary">
                {t('reportErrorToucanIsOnIt')}
              </Typography>
            </Box>
            <Box mt={2}>
              <Typography variant="sm" color={ToucanColors.gray[400]}>
                {t('reportErrorThanksForSendingYourReport')}
              </Typography>
            </Box>
            <Box mt={4}>
              <Button type="utility" size="medium" onClick={closeModal}>
                {t('reportErrorDone')}
              </Button>
            </Box>
          </Box>
        ) : (
          <Box textAlign="left">
            <Typography variant="md" font="secondary">
              {t('reportErrorReportAnError')}
            </Typography>
            <Box mt={2}>
              <Typography variant="sm" color={ToucanColors.gray[400]}>
                {t('reportErrorHelpMakeToucanEvenBetter')}
              </Typography>
            </Box>
            <Box mt={4}>
              <Typography variant="sm">{t('reportErrorPleaseSelectOption')}</Typography>
            </Box>
            {errors.errorReason && (
              <Box mt={2}>
                <Typography
                  variant="xs"
                  isBold
                  css={css`
                    color: ${ToucanColors.red[300]};
                  `}
                >
                  {errors.errorReason.message}
                </Typography>
              </Box>
            )}
            <Box component="form" mt={2}>
              <RadioGroup
                name="error-reason"
                data-testid="error-reason"
                onChange={(event: ChangeEvent<HTMLInputElement>) => {
                  setValue('errorReason', event.target.value, { shouldValidate: true });
                }}
              >
                <Radio value={ReportError.Misspelling} label={t('reportErrorTheresAWordMisspelled')} />
                <Radio value={ReportError.Grammar} label={t('reportErrorTheresAGrammaticalError')} />
                <Radio value={ReportError.Context} label={t('reportErrorTheTranslationDoesntMakeSense')} />
                <Radio value={ReportError.Audio} label={t('reportErrorTheAudioIsntWorking')} />
                <Radio value={ReportError.SomethingElse} label={t('reportErrorSomethingElseIsWrong')} />
              </RadioGroup>
              <Box mt={4}>
                <TextField
                  label={t('reportErrorWantToTellUsMore')}
                  helperText={t('reportErrorOptional')}
                  placeholder={t('reportErrorTheErrorWas')}
                  {...register('errorDetails')}
                />
              </Box>
              {errors?.message && (
                <Box mt={2}>
                  <ErrorMessage message={errors.message} errorId={getErrorId(error)} />
                </Box>
              )}
              <Box
                mt={2}
                css={css`
                  & svg {
                    color: ${ToucanColors.white};
                  }
                `}
              >
                <Button
                  data-testid="report-error-submit"
                  type="utility"
                  size="medium"
                  fullWidth
                  onClick={handleSubmit(onSubmit)}
                >
                  {loading ? <CircularProgress size="16px" /> : t('reportErrorReportError')}
                </Button>
              </Box>
            </Box>
          </Box>
        )}
      </ModalContent>
    </Modal>
  );
};
