/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { SnoozeDuration } from '~/schema/background';

const { ToucanIcon, useColorScheme } = ToucanComponents;

interface CloseButtonProps {
  isSecondaryMenuShown: boolean;
  setIsSecondaryMenuShown: (show: boolean) => void;
  onChangeReminderDate: (remind: SnoozeDuration) => Promise<void>;
}

export const CloseButton: FC<CloseButtonProps> = ({
  isSecondaryMenuShown,
  setIsSecondaryMenuShown,
  onChangeReminderDate,
}) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  const onHover = () => {
    if (isSecondaryMenuShown) {
      return;
    }
    setIsSecondaryMenuShown(true);
  };

  const snoozeReminder = async () => {
    await onChangeReminderDate(SnoozeDuration.OneHour);
  };

  return (
    <div
      onClick={snoozeReminder}
      onKeyDown={snoozeReminder}
      role="button"
      tabIndex={0}
      css={css`
        cursor: pointer;
        display: flex;
        align-self: center;
        margin-right: ${theme.spacing(2)};
      `}
      onMouseOver={onHover}
      onFocus={onHover}
    >
      <ToucanIcon
        icon="times"
        css={css`
          color: ${isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[200]};
          width: 16px;
          height: 16px;
          display: flex;
          justify-content: flex-start;
        `}
      />
    </div>
  );
};
