import React from 'react';

import { useCrawler } from '~/hooks/useCrawler';

/**
 * InitialPageCrawler is a component that is wrapped around the inline translation crawler hooks.
 * this allows use to reload the hook just by changing the key of the InlinePageCrawler.
 */
export const InitialPageCrawler: React.FC<{
  intersectionObserver: IntersectionObserver;
  excludedTags?: Array<string>;
}> = ({ intersectionObserver, excludedTags = [] }) => {
  useCrawler({ crawlOnLoad: true, intersectionObserver, excludedTags });
  return null;
};
