/** @jsx jsx */
import React, { FC, memo, useRef, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { isInViewport } from '~/helpers/domHelpers';

const { Typography } = ToucanComponents;

interface CalloutProps extends BoxProps {
  title: string;
  isOpen: boolean;
  isLeftSideCallout?: boolean;
}

enum CalloutPosition {
  Top = 'top',
  Left = 'left',
}

export const Callout: FC<CalloutProps> = memo(({ children, title, isOpen, isLeftSideCallout, ...boxProps }) => {
  const theme = useTheme();
  const ref = useRef<HTMLDivElement>(null);
  const [calloutPosition, setCalloutPosition] = React.useState<CalloutPosition>(CalloutPosition.Top);

  useEffect(() => {
    if (isOpen && ref.current) {
      // Move the callout to the top when the callout is not fully in the viewport
      setCalloutPosition(isLeftSideCallout && isInViewport(ref.current) ? CalloutPosition.Left : CalloutPosition.Top);
    }
  }, [ref.current, isLeftSideCallout, isOpen]);

  if (!isOpen) {
    return <React.Fragment>{children}</React.Fragment>;
  }

  return (
    <Box position="relative">
      {children}
      <Box
        position="absolute"
        left="50%"
        top={`-${theme.spacing(0.5)}`}
        display="flex"
        flexDirection={calloutPosition === CalloutPosition.Left ? 'row' : 'column'}
        justifyContent="center"
        alignItems="center"
        css={css`
          transform: ${calloutPosition === CalloutPosition.Left ? 'translate(-132%, 0%)' : 'translate(-50%, -100%)'};
        `}
        data-testid="callout"
        {...boxProps}
      >
        <Box
          ref={ref}
          display="flex"
          justifyContent="center"
          alignItems="center"
          py={0.25}
          px={1}
          borderRadius="25px"
          css={css`
            background-color: ${ToucanColors.yellow[100]};
            border: 1px solid ${ToucanColors.yellow[200]};
          `}
        >
          <Image
            src="images/sparkle.svg"
            alt="sparkle"
            css={css`
              height: 12px;
              width: 12px;
            `}
          />
          <Typography
            variant="xs"
            isBold
            css={css`
              margin-left: ${theme.spacing(1)};
              color: ${ToucanColors.yellow[500]};
              white-space: nowrap;
            `}
            data-testid="callout-title"
          >
            {title}
          </Typography>
        </Box>
        <Image
          src={calloutPosition === CalloutPosition.Left ? 'images/right-arrow.svg' : 'images/down-arrow.svg'}
          alt={calloutPosition === CalloutPosition.Left ? 'right arrow' : 'down arrow'}
          css={css`
            margin-top: ${calloutPosition === CalloutPosition.Left ? 0 : theme.spacing(0.25)};
            margin-left: ${calloutPosition === CalloutPosition.Left ? theme.spacing(0.25) : 0};
            height: 16px;
            width: 14px;
          `}
        />
      </Box>
    </Box>
  );
});
