import React, { FC, useMemo } from 'react';

import { css, keyframes } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { CircularProgress, alpha, useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { Language } from '~/constants/language';
import { useTranslation } from '~/libs/i18n';

import { SidebarSection } from '../../Sidebar/SidebarSection';

const { Typography, useColorScheme } = ToucanComponents;

interface TargetPhraseBreakdown {
  highlight: boolean;
  text: string;
  underline: boolean;
}

interface TranslationViewProps {
  targetPhrase: Array<TargetPhraseBreakdown>;
  sourcePhrase: string;
  highlightColor?: string;
  targetLanguage: Language;
}

const shimmer = keyframes`
  to {
    background-position: 25em 0;
  }
`;

export const TranslationView: FC<TranslationViewProps> = ({
  targetPhrase: targetPhraseSegments,
  sourcePhrase,
  highlightColor = ToucanColors.blue[200],
  targetLanguage,
}) => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const { t } = useTranslation();

  const targetPhrase = targetPhraseSegments.map(section => section.text).join('');

  const colors = useMemo(
    () => ({
      highlightedColor: alpha(highlightColor, isDarkMode ? 0.3 : 0.4),
      bgcolor: alpha(highlightColor, isDarkMode ? 0.15 : 0.5),
      hoverBgcolor: alpha(highlightColor, isDarkMode ? 0.3 : 1),
      shimmerColor: alpha(highlightColor, isDarkMode ? 0.15 : 0.3),
      shimmerHiddenColor: alpha(highlightColor, 0),
    }),
    [highlightColor, isDarkMode],
  );

  const isLoaded = targetPhraseSegments.length && targetPhraseSegments[0]?.text;

  return (
    <Box>
      <SidebarSection hasBottomLine display="flex" alignItems="center">
        <Box flex="1">
          <Typography
            font="secondary"
            isBold
            variant="md"
            color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}
          >
            {t('highlightToTranslate')}
          </Typography>
        </Box>
      </SidebarSection>
      <SidebarSection display="flex" bottomLineHeight={1}>
        <Box flex="0" pt={0.5} pr={1}>
          <Box pt={1} display="flex" alignItems="flex-start" mr={1}>
            {isLoaded ? (
              <AudioPlayer targetPhrase={targetPhrase} language={targetLanguage} showPause />
            ) : (
              <CircularProgress size={16} />
            )}
          </Box>
        </Box>
        <Box
          flex="1"
          css={css`
            & h4 {
              line-height: 1.6em;
              padding-bottom: ${theme.spacing(2)};
            }
          `}
        >
          <Typography variant="md" color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}>
            <Box
              component="span"
              position="relative"
              boxShadow={`0 0 0 3px ${colors.bgcolor}`}
              bgcolor={colors.bgcolor}
              css={
                isLoaded
                  ? undefined
                  : css`
                      background-image: linear-gradient(
                        90deg,
                        ${colors.shimmerHiddenColor} 0%,
                        ${colors.shimmerColor} 50%,
                        ${colors.shimmerHiddenColor} 70%
                      );
                      background-size: 100px;
                      background-position: -5em 0;
                      background-repeat: no-repeat;
                      box-decoration-break: clone;
                      animation: ${shimmer} 2s;
                      animation-iteration-count: infinite;
                      background-color: ${colors.bgcolor};
                      color: transparent;
                    `
              }
            >
              {isLoaded
                ? targetPhraseSegments.map(({ text, underline }, i) => (
                    <Box
                      key={`${text}-${i}`}
                      component="span"
                      position="relative"
                      css={css`
                        transition: box-shadow 0.2s ease-in-out;
                      `}
                      boxShadow={underline ? `0 3px 0 0 ${highlightColor}` : `0 0 0 0 ${highlightColor}`}
                    >
                      {text}
                    </Box>
                  ))
                : sourcePhrase}
            </Box>
          </Typography>
          <Typography variant="sm" color={isDarkMode ? ToucanColors.gray[100] : ToucanColors.gray[500]}>
            {sourcePhrase}
          </Typography>
        </Box>
      </SidebarSection>
    </Box>
  );
};
