import { useEffect, useRef } from 'react';

import { PostMessageEvent } from '~/constants/events';
import { client } from '~/finch-client';
import { sendPostMessage } from '~/helpers/sendPostMessage';
import { usePostMessageListener } from '~/hooks/usePostMessageListener';
import { GetExtensionDataDocument, GetExtensionDataQuery, GetExtensionDataQueryVariables } from '~/schema/background';

/**
 * This component listens for messages sent from the Toucan website and replies
 * to them.
 */
export const MessageListener = () => {
  const hasSentReadyMessageRef = useRef(false);
  useEffect(() => {
    if (!hasSentReadyMessageRef.current) {
      sendPostMessage(PostMessageEvent.MessageListenerReady);
      hasSentReadyMessageRef.current = true;
    }
  });

  usePostMessageListener(async (event: MessageEvent) => {
    const { data: eventData } = event;
    if (!eventData || typeof eventData !== 'object') {
      return;
    }
    // Potential optimization: return early if browser is not Firefox
    if (eventData.type === PostMessageEvent.ExtensionInformationRequest) {
      const { data } = await client.query<GetExtensionDataQuery, GetExtensionDataQueryVariables>(
        GetExtensionDataDocument,
      );

      sendPostMessage(PostMessageEvent.ExtensionInformationResponse, data);
    }
  });
  return null;
};
