import { useEffect, useState } from 'react';

import { getPageLanguages, isLanguagePage } from '~/helpers/htmlHelpers';

import { usePrevious } from '../usePrevious';

const checkPageIsValid = (language: string) => {
  const pageLanguages = getPageLanguages();
  const rootLanguage = pageLanguages.find(tag => tag.root);
  return isLanguagePage(language, rootLanguage);
};

export const usePageLanguage = (language: string = 'en') => {
  const [isValidPage, setPageIsValid] = useState(() => checkPageIsValid(language));
  const prevLanguage = usePrevious(language);

  useEffect(() => {
    if (prevLanguage && prevLanguage !== language) {
      setPageIsValid(checkPageIsValid(language));
    }
  }, [language]);

  return {
    isValidPage,
  };
};
