import { toucanPrefix } from '~/constants/html';

import ContentMessengers from '../messengers/contentMessengers';

interface RootRenderPayload {
  root: ShadowRoot;
  head: HTMLElement;
  body: HTMLElement;
  modals: HTMLElement;
  jssElement: HTMLElement;
}

type RootElements = RootRenderPayload & { element: HTMLElement };

export const createRootElements = (elementId: string | HTMLElement, tagName: string = 'div'): RootElements | null => {
  const element = elementId instanceof HTMLElement ? elementId : document.createElement(tagName);
  if (typeof elementId === 'string') {
    element.id = elementId;
  }
  // NOTE: Instagram put a relative position on all elements
  if (tagName === 'div') {
    element.style.position = 'static';
  }
  if (element.shadowRoot) {
    const { shadowRoot } = element;
    const head = shadowRoot.querySelector(`.${toucanPrefix}-head`);
    const body = shadowRoot.querySelector(`.${toucanPrefix}-body`);
    const modals = shadowRoot.querySelector(`.${toucanPrefix}-modals`);
    if (
      !head ||
      !body ||
      !modals ||
      !(head instanceof HTMLElement) ||
      !(body instanceof HTMLElement) ||
      !(modals instanceof HTMLElement)
    ) {
      return null;
    }
    const [jssElement] = Array.from(head.children);
    if (!jssElement || !(jssElement instanceof HTMLElement)) {
      return null;
    }
    return {
      element,
      root: shadowRoot,
      head,
      body,
      modals,
      jssElement,
    };
  }
  const head = document.createElement(tagName);
  const jssElement = document.createElement(tagName);
  const body = document.createElement(tagName);
  const modals = document.createElement(tagName);

  head.classList.add(`${toucanPrefix}-head`);
  body.classList.add(`${toucanPrefix}-body`);
  modals.classList.add(`${toucanPrefix}-modals`);

  // Hide head element to avoid it taking up space
  head.style.display = 'none';

  element.attachShadow({ mode: 'open' });

  const root = element.shadowRoot as null | ShadowRoot;
  if (!root) {
    ContentMessengers.captureContentError(new Error('Unable to create shadow root.'));
    return null;
  }

  root.appendChild(head);
  root.appendChild(body);
  root.appendChild(modals);

  head.appendChild(jssElement);
  return { element, root, head, body, modals, jssElement };
};
