import { ValidDomain } from '~/constants/domain';
import { FeatureName } from '~/constants/features';
import { useFeatureEnabled } from '~/libs/features/client';

export const getExtraPracticeContainerWidth = ({ currentDomain }: { currentDomain: string }) => {
  if (currentDomain.includes(ValidDomain.Wikipedia)) {
    return '350px';
  }
  return '100%';
};

export const useIsExtraPracticeEnabledOnDomain = (domain: string) => {
  const isEnabledOnBBC = useFeatureEnabled(FeatureName.ExtraPracticeBBC);
  const isEnabledOnBing = useFeatureEnabled(FeatureName.ExtraPracticeBing);
  const isEnabledOnForbes = useFeatureEnabled(FeatureName.ExtraPracticeForbes);
  const isEnabledOnGoogle = useFeatureEnabled(FeatureName.ExtraPracticeGoogle);
  const isEnabledOnMedium = useFeatureEnabled(FeatureName.ExtraPracticeMedium);
  const isEnabledOnNYT = useFeatureEnabled(FeatureName.ExtraPracticeNYT);
  const isEnabledOnQuora = useFeatureEnabled(FeatureName.ExtraPracticeQuora);
  const isEnabledTheGuardian = useFeatureEnabled(FeatureName.ExtraPracticeTheGuardian);
  const isEnabledOnWikipedia = useFeatureEnabled(FeatureName.ExtraPracticeWikipedia);
  if (domain.includes(ValidDomain.BBC) || domain.includes(ValidDomain.BBCUK)) {
    return Boolean(isEnabledOnBBC);
  }
  if (domain.includes(ValidDomain.Bing)) {
    return Boolean(isEnabledOnBing);
  }
  if (domain.includes(ValidDomain.Forbes)) {
    return Boolean(isEnabledOnForbes);
  }
  if (domain.includes(ValidDomain.Google)) {
    return Boolean(isEnabledOnGoogle);
  }
  if (domain.includes(ValidDomain.Medium)) {
    return Boolean(isEnabledOnMedium);
  }
  if (domain.includes(ValidDomain.NewYorkTimes)) {
    return Boolean(isEnabledOnNYT);
  }
  if (domain.includes(ValidDomain.Quora)) {
    return Boolean(isEnabledOnQuora);
  }
  if (domain.includes(ValidDomain.TheGuardian)) {
    return Boolean(isEnabledTheGuardian);
  }
  if (domain.includes(ValidDomain.Wikipedia)) {
    return Boolean(isEnabledOnWikipedia);
  }
  return true;
};

export const filterDomain = (domain: string) => {
  const labels = domain.split('.');
  const lastTwoLabels = labels.slice(-2).join('.');
  // TODO: Add other eTLDs (e.g., .ws.us) as needed
  return lastTwoLabels === 'co.uk' ? labels.slice(-3).join('.') : lastTwoLabels;
};
