/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx, keyframes } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';

import { Box } from '../Box';

import { LearningBlurb } from './LearningBlurb';

const { useColorScheme } = ToucanComponents;

export const Placeholder: FC = () => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();

  const toggleFade = keyframes`
  from {
    background: ${isDarkMode ? ToucanColors.gray[400] : ToucanColors.blue[300]};
  }
  to {
    background: ${isDarkMode ? ToucanColors.gray[300] : ToucanColors.blue[200]};
  }
  `;

  return (
    <Box>
      <LearningBlurb>
        <Box display="flex">
          <Box
            borderRadius="25px"
            width="12px"
            height="12px"
            marginRight={1}
            css={css`
              animation: ${toggleFade} 900ms linear 300ms infinite;
            `}
          />
          <Box
            borderRadius="25px"
            width="12px"
            height="12px"
            marginRight={1}
            css={css`
              animation: ${toggleFade} 900ms linear 600ms infinite;
            `}
          />
          <Box
            borderRadius="25px"
            width="12px"
            height="12px"
            css={css`
              animation: ${toggleFade} 900ms linear 900ms infinite;
            `}
          />
        </Box>
      </LearningBlurb>
      <Skeleton
        variant="rectangular"
        css={css`
          height: 232px;
          border-radius: ${theme.spacing(1)};
          margin-top: ${theme.spacing(2)};
          &.MuiSkeleton-root {
            background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[100]};
          }
        `}
      />

      <Skeleton
        variant="rectangular"
        css={css`
          height: 48px;
          border-radius: 0px 0px ${theme.spacing(1)} ${theme.spacing(1)};
          margin: ${theme.spacing(2)} -${theme.spacing(2)} -${theme.spacing(2)} -${theme.spacing(2)};
          &.MuiSkeleton-root {
            background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[100]};
          }
        `}
      />
    </Box>
  );
};
