/** @jsx jsx */
import React, { FC, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';
import { motion, useMotionValue, animate } from 'framer-motion';

import { Image } from '~/components/shared/Image';
import { LottieAnimation } from '~/components/shared/LottieAnimation';
import { maximumZIndex } from '~/constants/styling';
import { useUserInfo } from '~/hooks/useUserInfo';

import { ToastProps } from './types';

const { Typography, ToucanIcon } = ToucanComponents;

export const Toast: FC<ToastProps & { onAttemptClose: () => void; id: string; yPosition: number }> = ({
  id,
  title,
  subtitle,
  body,
  onClick,
  image,
  className,
  yPosition,
  titleColor,
  subtitleColor,
  onAttemptClose = () => {},
  isAnimation = false,
}) => {
  const theme = useTheme();
  const { isDarkModeEnabled } = useUserInfo();

  const onAttemptCloseProxy = (
    e: React.MouseEvent<HTMLDivElement, MouseEvent> | React.KeyboardEvent<HTMLDivElement>,
  ) => {
    // Stop the propagation to avoid banner click
    e.stopPropagation();
    onAttemptClose();
  };
  const y = useMotionValue(yPosition);

  const outProps = { opacity: 0, x: -20 };

  useEffect(() => {
    if (y.get() !== yPosition) {
      animate(y, yPosition, { type: 'spring' });
    }
  }, [yPosition]);

  return (
    <motion.div
      data-testid="toast"
      key={id}
      style={{ y }}
      animate={{ opacity: 1, x: 0 }}
      exit={outProps}
      initial={outProps}
      role="button"
      tabIndex={0}
      aria-hidden="false"
      onClick={onClick}
      onKeyDown={onClick}
      className={className}
      css={css`
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 442px;
        width: 442px;
        min-width: 442px;
        cursor: pointer;
        position: fixed;
        z-index: ${maximumZIndex};
        left: ${theme.spacing(2)};
        bottom: ${theme.spacing(2)};
        filter: drop-shadow(0px 12px 36px rgba(22, 22, 22, 0.08));
      `}
    >
      <div
        css={css`
          background-color: ${isDarkModeEnabled ? ToucanColors.gray[500] : ToucanColors.white};
          border-radius: ${theme.spacing(1)};
          display: flex;
          align-items: center;
          padding: ${theme.spacing(2)};
          position: relative;
          width: 100%;
        `}
      >
        <div
          css={css`
            margin-right: ${theme.spacing(1)};
            width: ${theme.spacing(6)};
            height: ${theme.spacing(6)};
            position: relative;
          `}
        >
          {isAnimation ? (
            <LottieAnimation filePath={image} rendererSettings={{ preserveAspectRatio: 'xMidYMin slice' }} />
          ) : (
            <Image src={image} alt="Toast Icon" width={`${theme.spacing(6)}`} height={`${theme.spacing(6)}`} />
          )}
        </div>
        <Box display="flex" flexDirection="column" flex="1">
          {body || (
            <React.Fragment>
              <Typography
                isBold
                font="primary"
                variant="sm"
                css={css`
                  color: ${titleColor ?? isDarkModeEnabled ? ToucanColors.white : ToucanColors.gray[400]};
                `}
              >
                {title}
              </Typography>
              <Typography
                isBold
                font="primary"
                variant="sm"
                css={css`
                  color: ${subtitleColor ?? ToucanColors.teal[300]};
                `}
              >
                {subtitle}
              </Typography>
            </React.Fragment>
          )}
        </Box>
        <Box display="flex" alignContent="center" justifyContent="center" flex={0}>
          <div
            onClick={onAttemptCloseProxy}
            onKeyDown={onAttemptCloseProxy}
            role="button"
            tabIndex={0}
            css={css`
              display: inline-flex;
            `}
          >
            <ToucanIcon
              icon="times"
              css={css`
                color: ${isDarkModeEnabled ? ToucanColors.gray[300] : ToucanColors.gray[200]};
                width: ${theme.spacing(2)};
                height: ${theme.spacing(2)};
              `}
            />
          </div>
        </Box>
      </div>
    </motion.div>
  );
};
