/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';
import browser from 'webextension-polyfill';

import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { useTranslation } from '~/libs/i18n';
import { SnoozeDuration } from '~/schema/background';

import { CloseButton } from './CloseButton';

const { Typography, Button, useColorScheme } = ToucanComponents;

interface PauseNotificationProps {
  isReminderExpanded: boolean;
  setIsReminderExpanded: (expanded: boolean) => void;
  reloadPage: () => void;
  isSecondaryMenuShown: boolean;
  onChangeReminderDate: (remind: SnoozeDuration) => Promise<void>;
  onRemoveReminder: () => Promise<void>;
  setIsSecondaryMenuShown: (show: boolean) => void;
  onRemoveFromBanlist: () => Promise<void>;
}

const pauseContainerVariants = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: { delay: 0, ease: 'easeIn', duration: 0.3 },
  },
  visibleExpanded: {
    opacity: 1,
    transition: { delay: 0, ease: 'easeIn', duration: 0.3 },
  },
  close: {
    transition: { ease: 'easeOut', duration: 0.3 },
  },
};

const pauseInformationVariants = {
  hidden: {
    opacity: 0,
    width: '0',
  },
  visible: {
    opacity: 1,
    width: '100%',
    transition: { delay: 0.2, ease: 'easeIn', duration: 0.3 },
  },
  close: {
    opacity: 0,
    width: '0',
    transition: { ease: 'easeOut', duration: 0.3 },
  },
};

export const PausedNotification: FC<PauseNotificationProps> = ({
  isReminderExpanded,
  setIsReminderExpanded,
  reloadPage,
  isSecondaryMenuShown,
  onChangeReminderDate,
  setIsSecondaryMenuShown,
  onRemoveFromBanlist,
  onRemoveReminder,
}) => {
  const { currentDomain } = useCurrentDomain();
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const { t } = useTranslation();

  const onMouseEnter = () => {
    setIsReminderExpanded(true);
  };

  const onButtonClickToAllowDomain = async () => {
    // TODO: removing from banlist should auto remove reminder
    await onRemoveFromBanlist();
    await onRemoveReminder();
    reloadPage();
  };

  return (
    <AnimatePresence>
      <motion.div
        key="pause-container"
        initial="hidden"
        animate={isReminderExpanded ? 'visibleExpanded' : 'visible'}
        exit="close"
        variants={pauseContainerVariants}
        css={css`
          display: flex;
          align-items: flex-start;
          background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
          border-radius: 8px;
          display: flex;
          align-items: center;
        `}
        onMouseOver={onMouseEnter} // NOTE: cannot use `onMouseEnter` due to a known bug https://github.com/facebook/react/issues/10109
        onFocus={onMouseEnter}
      >
        <div
          key="pause-container"
          css={css`
            display: flex;
            align-items: center;
            padding-left: ${theme.spacing(3)};
            padding-right: ${theme.spacing(3)};
            padding-bottom: ${theme.spacing(2)};
            padding-top: ${theme.spacing(2)};
          `}
        >
          <img
            src={browser.runtime.getURL('/images/toucan-primary.svg')}
            alt="toucan-logo"
            css={css`
              margin-top: ${theme.spacing(1)};
              margin-bottom: ${theme.spacing(1)};
              width: 24px;
              height: 24px;
            `}
          />
          <AnimatePresence>
            {isReminderExpanded && (
              <motion.div
                key="pause-information"
                initial="hidden"
                animate="visible"
                exit="close"
                variants={pauseInformationVariants}
                css={css`
                  display: flex;
                `}
              >
                <div
                  key="pause-information"
                  css={css`
                    display: flex;
                    flex-direction: column;
                    margin-right: ${theme.spacing(4)};
                    align-items: flex-start;
                    padding-left: ${theme.spacing(3)};
                  `}
                >
                  <div>
                    <Typography
                      variant="xs"
                      isBold
                      css={css`
                        color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
                      `}
                    >
                      {t('toucanIsPaused')}
                    </Typography>
                  </div>
                  <Button
                    onClick={onButtonClickToAllowDomain}
                    type="text"
                    css={css`
                      font-size: 12px;
                      font-weight: 700;
                      color: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300]};
                      &:hover {
                        color: ${isDarkMode ? ToucanColors.teal[100] : ToucanColors.blue[200]};
                        text-decoration: underline;
                      }
                      &:active {
                        color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
                        text-decoration: underline;
                      }
                      &:visited {
                        color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
                      }
                    `}
                  >
                    {t('allowOnCurrentDomain', { domain: currentDomain })}
                  </Button>
                </div>
                <CloseButton
                  onChangeReminderDate={onChangeReminderDate}
                  isSecondaryMenuShown={isSecondaryMenuShown}
                  setIsSecondaryMenuShown={setIsSecondaryMenuShown}
                />
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      </motion.div>
    </AnimatePresence>
  );
};
