const MIN_WORD_LENGTH = 3;

export const isWord = (text: string) => {
  const trimmedText = text.trim();
  const words = trimmedText.trim().split(/\s/g);
  if (words.length === 1) {
    // Checks to see if there is not any letters in the selection.
    if (!trimmedText.match(/([A-Z])+/i)) {
      return false;
    }

    // Strings with only 1 or 2 chars are currently not determines correctly when
    // the text is expanded to hold the whole word. This helps with the issue since there
    // will be less 3 letter patterns then 1 or 2.
    // TODO: updated the selected index logic to accommodate this.
    if (trimmedText.length < MIN_WORD_LENGTH) {
      return false;
    }
  }

  return true;
};

export const isInWordLimit = (text: string) => {
  const words = text.split(/\s/g);
  if (words.length > 100) {
    return false;
  }
  return true;
};
