/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';

import { maximumZIndex } from '~/constants/styling';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { SnoozeDuration } from '~/schema/background';

import { HorizontalLine } from './HorizontalLine';

const { Button, useColorScheme } = ToucanComponents;

interface SecondaryMenuProps {
  isSecondaryMenuShown: boolean;
  reloadPage: () => void;
  onChangeReminderDate: (remind: SnoozeDuration) => Promise<void>;
  onRemoveReminder: () => Promise<void>;
}

const secondaryMenuVariants = {
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: { delay: 0, ease: 'easeIn', duration: 0.3 },
  },
  close: {
    opacity: 0,
    transition: { ease: 'easeOut', duration: 0.3 },
  },
};

export const SecondaryMenu: FC<SecondaryMenuProps> = ({
  reloadPage,
  onChangeReminderDate,
  onRemoveReminder,
  isSecondaryMenuShown,
}) => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const { t } = useTranslation();

  const onRemove = async () => {
    const eventName = 'Site Pause Notification - "Don\'t Show Again" Clicked';
    const properties = {};
    ContentMessengers.trackEvent({ eventName, properties });

    await onRemoveReminder();
    reloadPage();
  };

  const onChangeDate = async () => {
    await onChangeReminderDate(SnoozeDuration.Tomorrow);
  };

  return (
    <AnimatePresence>
      {isSecondaryMenuShown && (
        <motion.div
          key="secondary-menu"
          initial="hidden"
          animate="visible"
          exit="close"
          variants={secondaryMenuVariants}
          css={css`
            z-index: ${maximumZIndex};
            display: flex;
            justify-content: flex-end;
            margin-top: 5px;
          `}
        >
          <div
            key="secondary-menu"
            css={css`
              padding: ${theme.spacing(2)};
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
              border-radius: 8px;
              align-items: flex-start;
            `}
          >
            <Button
              onClick={onChangeDate}
              type="text"
              css={css`
                font-size: 12px;
                font-weight: 700;
                color: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300]};
                &:hover {
                  color: ${isDarkMode ? ToucanColors.teal[100] : ToucanColors.blue[200]};
                  text-decoration: underline;
                }
                &:active {
                  color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
                  text-decoration: underline;
                }
                &:visited {
                  color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
                }
                margin-bottom: ${theme.spacing(1)};
              `}
            >
              {t('dismissUntilTomorrow')}
            </Button>
            <HorizontalLine />
            <Button
              onClick={onRemove}
              type="text"
              css={css`
                font-size: 12px;
                font-weight: 700;
                color: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300]};
                &:hover {
                  color: ${isDarkMode ? ToucanColors.teal[100] : ToucanColors.blue[200]};
                  text-decoration: underline;
                }
                &:active {
                  color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
                  text-decoration: underline;
                }
                &:visited {
                  color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
                }
                margin-top: ${theme.spacing(1)};
              `}
            >
              {t('dontShowAgainOnThisSite')}
            </Button>
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  );
};
