import { useEffect } from 'react';

import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useToast } from '~/hooks/useToast';

const DATASET_LOADING_TOAST_ID = 'DATASET_LOADING_TOAST_ID';

export const DatasetLoadingIndicator = () => {
  const [isLoading] = useStorage(StorageKey.IsLoadingDataset, false);
  const { showToast, removeToast } = useToast();

  useEffect(() => {
    if (isLoading) {
      showToast({
        id: DATASET_LOADING_TOAST_ID,
        title: 'Dataset is loading',
        subtitle: 'Your extension is currently downloading a dataset.',
        image: 'images/toucan-circle.svg',
        onClick: () => {},
        timeout: 30000,
      });
    } else {
      removeToast(DATASET_LOADING_TOAST_ID);
    }
  }, [isLoading]);

  return null;
};
