import { useEffect } from 'react';

import { DensityPercentages, DensityValuesPrimitive } from '~/constants/settings';
import { StorageKey } from '~/constants/storage';
import { usePrevious } from '~/hooks/usePrevious';
import { useStorage } from '~/hooks/useStorage';
import { isActive, useTranslatedNodesOnPage, useTranslatedNodeTransitions } from '~/hooks/useTranslatedNodes';
import { shouldShowTranslation } from '~/hooks/useTranslationReplacement/helpers';
import { useUserInfo } from '~/hooks/useUserInfo';

export const DynamicDifficulty = () => {
  const { setIdle, setInactive } = useTranslatedNodeTransitions();
  const { difficultyTier, weightsByDifficulty } = useUserInfo();
  const [difficultyLevel] = useStorage<DensityValuesPrimitive>(StorageKey.DensityLevel, 1);
  const difficultyPercentage = DensityPercentages[difficultyLevel];
  const lastDifficultyPercentage = usePrevious(difficultyPercentage);
  const lastDifficultyTier = usePrevious(difficultyTier);
  const nodesOnPage = useTranslatedNodesOnPage();

  useEffect(() => {
    // Difficulty changed update the nodes on the page
    if (lastDifficultyPercentage !== difficultyPercentage || lastDifficultyTier !== difficultyTier) {
      nodesOnPage.forEach(node => {
        const isPreviouslyActive = isActive(node);
        const shouldBeActive = shouldShowTranslation(
          node.densityThreshold,
          weightsByDifficulty[node?.difficultyLevel ?? ''] ?? 0,
          difficultyPercentage,
        );
        // No need to update the node.
        if (isPreviouslyActive === shouldBeActive) {
          return;
        }
        if (shouldBeActive) {
          setIdle(node);
        } else {
          setInactive(node);
        }
      });
    }
  }, [difficultyPercentage, lastDifficultyPercentage, nodesOnPage, difficultyTier, lastDifficultyTier]);

  return null;
};
