import { atom } from 'jotai';

import { TranslateMappingMutation } from '~/schema/background';

import { addDictionaryEntriesAtom } from '../useDictionary/atoms';
import { Translations } from '../useDictionary/types';
import { addTranslatedNodesAtom } from '../useTranslatedNodes/atoms';
import { PendingTranslatedNode, TranslatedNodeSource } from '../useTranslatedNodes/types';

import { stampDomTarget } from './helpers';
import { DomTarget, DomTargets, PartialDomTargets } from './types';

/**
 * domTargetsByIdAtom is a map of all the dom targets that have been created.
 * Storing them in a map allows us to easily access them by id, and update by id without
 * having to iterate over the entire array all the time.
 */
export const domTargetsByIdAtom = atom<Record<string, DomTarget>>({});
/**
 * domTargetsByParentNodeAtom is a WeakMap of all the dom targets that have been created, and
 * allows doe easy lookup of the dom targets by their parent node. This is super useful for
 * times when we need to check if a certain node is already a dom target
 */
export const domTargetsByParentNodeAtom = atom<WeakMap<HTMLElement, DomTarget>>(get => {
  const domTargetsById = get(domTargetsByIdAtom);
  const cache = new WeakMap<HTMLElement, DomTarget>();
  Object.values(domTargetsById).forEach(domTarget => {
    cache.set(domTarget.parentNode, domTarget);
  });
  return cache;
});
/**
 * domTargetsAtom is a list of all the dom targets that have been created. This is essentially a selector
 * on the domTargetsByIdAtom, but in a more familiar form.
 */
export const domTargetsAtom = atom<DomTargets>(get => Object.values(get(domTargetsByIdAtom)));

/**
 * visibleDomTargetsAtom is a list of all the dom targets that are currently visible. This is used when
 * we are looking to process dom targets to see if they have translations.
 */
export const visibleDomTargetsAtom = atom<DomTargets>(get =>
  get(domTargetsAtom).filter(target =>
    typeof target?.intersectionRatio === 'undefined' ? false : target?.intersectionRatio > 0.1,
  ),
);

/**
 * translatableDomTargetsAtom is a list of all the dom targets that have a text mapping, which means there
 * is content that can be translated.
 */
export const translatableDomTargetsAtom = atom<DomTargets>(get =>
  get(visibleDomTargetsAtom).filter(target => !!target.textMappings),
);

/**
 * addDomTargetAtom is an atom that adds a dom target to the domTargetsByIdAtom map. It checks to see if there is already
 * a node with the same parent node.
 *
 * It is a write only atom.
 *
 * @returns the dom target that was added or the existing dom target if there was one
 */
export const addDomTargetsAtom = atom<unknown, PartialDomTargets>(null, (get, set, potentialDomTargets) => {
  const domTargetsByParentNode = get(domTargetsByParentNodeAtom);
  const domTargetsById = get(domTargetsByIdAtom);

  const updatedTargets = potentialDomTargets.map(target => {
    const matchingTarget = domTargetsByParentNode.get(target.parentNode);
    const domTarget = stampDomTarget({ ...matchingTarget, ...target });

    // Update information in new dom target info
    domTargetsById[domTarget.id] = stampDomTarget(domTarget);

    return domTarget;
  });

  set(domTargetsByIdAtom, { ...domTargetsById });

  return potentialDomTargets.map(target =>
    updatedTargets.find(domTarget => domTarget.parentNode === target.parentNode),
  );
});

export const removeDomTargetsAtom = atom<unknown, PartialDomTargets>(null, (get, set, removableDomTargets) => {
  const domTargetsById = get(domTargetsByIdAtom);

  removableDomTargets.forEach(removableDomTarget => {
    if (removableDomTarget.id) {
      delete domTargetsById[removableDomTarget.id];
    }
  });

  set(domTargetsByIdAtom, { ...domTargetsById });
});

export const clearDomTargetsAtom = atom<unknown, void>(null, (get, set) => {
  set(domTargetsByIdAtom, {});
});

/**
 * handleTranslationResponsesAtom is an atom that handles a bunch of translation responses.
 * from any updates that have translation responses.
 *
 * This will take a set of pending responses, wait for them, and then create other resources from like.
 * - Translated Nodes
 * - Dictionary Entries
 *
 * It will also attempt cleanup and DOM targets that are associated with a bad response.
 */
export const handleTranslationResponsesAtom = atom<
  unknown,
  {
    pendingDomTargetResponses: Array<
      Promise<{
        data?: TranslateMappingMutation | undefined;
        parentNode: HTMLElement;
      }>
    >;
  }
>(null, async (_get, set, { pendingDomTargetResponses }) => {
  const responses = await Promise.allSettled(pendingDomTargetResponses);
  const newTranslations: Translations = {};
  const newTranslatedNodes: Array<
    Pick<
      PendingTranslatedNode,
      | 'id'
      | 'address'
      | 'node'
      | 'translationId'
      | 'replacement'
      | 'targetId'
      | 'densityThreshold'
      | 'source'
      | 'replacementIndex'
      | 'targetPhrase'
      | 'meta'
      | 'targetLanguage'
      | 'difficultyLevel'
    >
  > = [];
  const domTargetsToRemove: PartialDomTargets = [];

  responses.forEach(response => {
    if (response.status === 'rejected') {
      return;
    }
    const { data, parentNode } = response.value;
    if (!data?.translateMapping) {
      domTargetsToRemove.push({ parentNode });
      return;
    }

    data.translateMapping.forEach(translatedNode => {
      const { replacement, translation, id, targetId, densityThreshold, source } = translatedNode;
      Object.assign(newTranslations, { [translation.translationId]: translation });
      newTranslatedNodes.push({
        id,
        replacement: replacement.sourcePhrase,
        address: replacement.address as string | undefined,
        translationId: replacement.translationId,
        targetId,
        densityThreshold,
        targetPhrase: translation.targetPhrase,
        targetLanguage: replacement.targetLanguage,
        source: (source as TranslatedNodeSource) ?? TranslatedNodeSource.Crawler,
        difficultyLevel: replacement.difficultyLevel ?? undefined,
        meta: {
          isSaved: translatedNode.replacement.isSavedWord ?? undefined,
          isActiveLearningShortcutPhrase: translatedNode.replacement.isActiveLearningShortcutPhrase ?? undefined,
        },
      });
    });
  });

  if (domTargetsToRemove.length) {
    set(removeDomTargetsAtom, domTargetsToRemove);
  }

  if (Object.keys(newTranslations).length) {
    set(addDictionaryEntriesAtom, newTranslations);
  }

  if (newTranslatedNodes.length) {
    set(addTranslatedNodesAtom, newTranslatedNodes);
  }
});

/**
 * updateDomTargetsAtom is an atom that updates the dom targets with the new data.
 *
 * If there is a pending response, it will wait for it by passing it to the handleTranslationResponsesAtom atom.
 */
export const updateDomTargetsAtom = atom<unknown, PartialDomTargets>(null, async (get, set, updatedDomTargets) => {
  const domTargetsById = get(domTargetsByIdAtom);
  const domTargetsByParentNode = get(domTargetsByParentNodeAtom);
  const pendingDomTargetResponses: Array<
    Promise<{
      data?: TranslateMappingMutation | undefined;
      parentNode: HTMLElement;
    }>
  > = [];

  updatedDomTargets.forEach(updatedDomTarget => {
    const existingDomTarget = domTargetsByParentNode.get(updatedDomTarget.parentNode);
    if (!existingDomTarget) {
      return;
    }

    if (updatedDomTarget.pendingResp) {
      pendingDomTargetResponses.push(
        updatedDomTarget.pendingResp.then(response => ({
          data: response.data,
          parentNode: updatedDomTarget.parentNode,
        })),
      );
    }

    domTargetsById[existingDomTarget.id] = {
      ...existingDomTarget,
      ...updatedDomTarget,
    };
  });

  set(domTargetsByIdAtom, { ...domTargetsById });

  if (!pendingDomTargetResponses.length) {
    return;
  }

  set(handleTranslationResponsesAtom, { pendingDomTargetResponses });
});
