import React, { useState, useCallback } from 'react';

import { FeatureName } from '~/constants/features';
import { isToucanSite } from '~/helpers/currentDomainHelpers';
import { useProcessVisibleNodes } from '~/hooks/useCrawler';
import { useCrawlerConfig } from '~/hooks/useCrawlerConfig';
import { useDomainEnabled } from '~/hooks/useDomainEnabledEffect';
import { useHistoryChange } from '~/hooks/useHistoryChange';
import { useTranslationReplacement } from '~/hooks/useTranslationReplacement';
import { Feature } from '~/libs/features/client';

import { DynamicContentCrawler } from './DynamicContentCrawler';
import { DynamicDifficulty } from './DynamicDifficulty';
import { InitialPageCrawler } from './InitialPageCrawler';
import { IntersectionObserver } from './IntersectionObserver';
import { OnboardingPageCrawler } from './OnboardingPageCrawler';

interface CrawlerProps {
  excludedTags?: Array<string>;
  currentDomain: string;
}

/**
 * Crawler component does not have any UI it is a purely behavior driven component
 * that interacts with the sites content. The crawler also will reload when the page url changes.
 *
 * Restrict inline translations from being usable on YouTube which is enabled due to contextless moments being present by short circuiting the process.
 */
export const Crawler = ({ excludedTags = [], currentDomain }: CrawlerProps) => {
  const [historyChanges, setHistoryChanges] = useState(0);
  const [enableGarbageCollection, setGarbageCollectionState] = useState(false);
  // TODO: check the path to determine whether the page is actually an
  // onboarding page
  const [isOnboardingPage] = useState(() => isToucanSite());
  const { isCrawlerEnabled } = useCrawlerConfig();
  const isDomainEnabled = useDomainEnabled();
  const isEnabled = isCrawlerEnabled && isDomainEnabled;

  useTranslationReplacement({
    historyChanges,
    enableGarbageCollection,
    excludedTags,
  });

  // Processes visible nodes for all crawlers.
  useProcessVisibleNodes({ excludedTags });

  /**
   * Monkey-patch pushState to allow use to listen to all push state changes.
   * update the amount of changes happened
   */
  useHistoryChange(
    useCallback(() => {
      setHistoryChanges(currentAmount => currentAmount + 1);
    }, []),
  );

  if (!isEnabled && !isOnboardingPage) {
    return null;
  }

  if (['youtube.com', 'subscribe.jointoucan.com'].includes(currentDomain)) {
    return null;
  }

  return isOnboardingPage ? (
    <OnboardingPageCrawler />
  ) : (
    <IntersectionObserver>
      {({ intersectionObserver }) => (
        <>
          <InitialPageCrawler
            intersectionObserver={intersectionObserver}
            key={historyChanges} // to refresh crawler on page change
            excludedTags={excludedTags}
          />
          <DynamicDifficulty />
          <Feature name={FeatureName.DynamicCrawler}>
            <DynamicContentCrawler
              intersectionObserver={intersectionObserver}
              onDynamicPage={() => {
                setGarbageCollectionState(true);
              }}
              excludedTags={excludedTags}
            />
          </Feature>
        </>
      )}
    </IntersectionObserver>
  );
};
