import React from 'react';

import { Box } from '~/components/Box';
import { HexagonalImage } from '~/components/shared/HexagonalImage';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';
import { QuizStatus } from '~/types/quiz';

interface BadgeProps {
  badgeImage: { badgeImageUrl: string; badgeImageAltText: string };
  status: QuizStatus;
}

export const Badge = ({ badgeImage, status }: BadgeProps) => {
  const badge = badgeImage.badgeImageUrl
    ? badgeImage.badgeImageUrl
    : browser.runtime.getURL('/images/learningShortcuts/extraPractice/no-badge.svg');
  const { t } = useTranslation();

  return (
    <Box display="flex">
      <Box zIndex={1}>
        <HexagonalImage imageUrl={badge} width={48} height={48} />
      </Box>
      {(status === QuizStatus.Pending || status === QuizStatus.Finished) && (
        <Box zIndex={0} position="absolute" ml={-3.2} mt={-0.2}>
          <Image
            src={`/images/learningShortcuts/extraPractice/toucan-side-${
              status === QuizStatus.Finished ? 'peek-party' : 'peek'
            }.svg`}
            width={53}
            height={56}
            alt={t('toucanLogo')}
          />
        </Box>
      )}
    </Box>
  );
};
