import React, { RefObject, useCallback, useRef, useState } from 'react';

import moment from 'moment';

import { Events } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { SupportedSourceLanguages } from '~/constants/language';
import { Pinchpoints } from '~/constants/pinchpoints';
import { ContentMessageProducer } from '~/contentMessageProducer';
import { ContentMessageProducerHookResult, useContentMessageProducer } from '~/hooks/useContentMessageProducer';
import { useDailyPracticeQuizUses } from '~/hooks/useDailyPracticeQuizUses';
import { useReferral } from '~/hooks/useReferral';
import { useTranslatedNodeTransitions } from '~/hooks/useTranslatedNodes';
import { TranslatedNodeSource, TranslatedNodeStatus } from '~/hooks/useTranslatedNodes/types';
import { useTranslationCard } from '~/hooks/useTranslationCard';
import { useUserInfo } from '~/hooks/useUserInfo';
import { isDisplayAlphabetSupported } from '~/libs/TextToSpeech';
import { useFeatureEnabled } from '~/libs/features/client';
import contentMessengers from '~/messengers/contentMessengers';
import {
  ChecklistType,
  useGetInlineTranslationHoverInfoQuery,
  useMarkSettingsOpenedMutation,
  useUpdatePracticeQuizItemMutation,
  useUpdateShortcutEmojiMutation,
} from '~/schema/background';
import { LanguageId } from '~/schema/server';
import { InlineTranslationLearningShortcutInfo } from '~/types/learningShortcut';
import { PanelName, SlideName } from '~/types/sidebar';

import { useSidebar } from '../../Sidebar/useSidebar';

import { AnimatedPoints } from './AnimatedPoints';
import { ExpandTranslation } from './ExpandTranslation';
import { InlineTranslationCard } from './InlineTranslationCard';
import { InlineTranslationCardLayout } from './InlineTranslationCardLayout';
import { hasUserTranslation } from './helpers';

export interface InlineTranslationCardInfo {
  translationId: string;
  translatedNodeId: string;
}

export interface InlineTranslationCardContainerProps {
  translationId: string;
  sourcePhrase: string;
  targetPhrase: string;
  targetLanguage: LanguageId;
  openSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
  isPremiumSubscribed: boolean;
  isPronunciationEnabled: boolean;
  domain: string;
  status: TranslatedNodeStatus;
  source: TranslatedNodeSource;
  translatedNodeId: string;
  node: HTMLSpanElement | undefined;
  isOpen: boolean;
  difficultyLevel?: string;
  openSidebar: ReturnType<typeof useSidebar>['openSidebar'];
  onClickRemoveWord: (params: InlineTranslationCardInfo) => void;
  onClickReportError: (params: InlineTranslationCardInfo) => void;
  onClickPause: (params: InlineTranslationCardInfo) => void;
  onQuizEnter: () => void;
  onQuizExit: () => void;
  setIdle: ReturnType<typeof useTranslatedNodeTransitions>['setIdle'];
  setFocused: ReturnType<typeof useTranslatedNodeTransitions>['setFocused'];
  requestIdle: ReturnType<typeof useTranslatedNodeTransitions>['requestIdle'];
  sourceLanguage: SupportedSourceLanguages;
  isActiveLearningShortcutPhrase: boolean;
  learningShortcutEmoji?: string;
  setPinned: ReturnType<typeof useTranslatedNodeTransitions>['setPinned'];
  requestHovered: ReturnType<typeof useTranslatedNodeTransitions>['requestHovered'];
}

/**
 * InlineTranslationCardContainer is the container component for the inline translation card. It has all the business logic needed to render the card,
 * and deal with all the interactions with the card. This can be used in Container form, or this form or can be use in the presentational component form.
 */
export const InlineTranslationCardContainer = ({
  translatedNodeId,
  translationId,
  sourcePhrase,
  targetPhrase,
  targetLanguage,
  openSubscribePage,
  isPremiumSubscribed,
  isPronunciationEnabled,
  domain,
  status,
  source,
  node,
  isOpen,
  onQuizEnter,
  onQuizExit,
  onClickRemoveWord,
  onClickReportError,
  onClickPause,
  openSidebar,
  setIdle,
  setFocused,
  requestIdle,
  sourceLanguage,
  difficultyLevel,
  learningShortcutEmoji,
  setPinned,
  requestHovered,
  isActiveLearningShortcutPhrase,
}: InlineTranslationCardContainerProps) => {
  const { data, loading, refetch } = useGetInlineTranslationHoverInfoQuery({
    variables: {
      id: translationId,
    },
    timeout: 10000, // onboarding language is slow
  });

  const {
    data: shortcutData,
    loading: isShortcutLoading,
    error: shortcutError,
  }: ContentMessageProducerHookResult & {
    data: InlineTranslationLearningShortcutInfo;
  } = useContentMessageProducer(ContentMessageProducer.getInlineTranslationLearningShortcutInfo, { count: 5 });

  const { numPracticeQuizUsesRemaining } = useDailyPracticeQuizUses();
  const [pointsRef, setPointsRef] = useState<RefObject<HTMLElement> | null>(null);
  const progressBarRef = useRef<HTMLDivElement>(null);
  const audioRef = useRef<HTMLDivElement>(null);
  const [hasCelebrated, setHasCelebrated] = useState(false);
  const isReferralEnabled = useFeatureEnabled(FeatureName.ReferralsV2);
  const { toucanRewardOfferedPerReferral, referralCTAText } = useReferral();
  const [isEmojiCustomizationMenuEnabled, setIsEmojiCustomizationMenuEnabled] = useState(false);
  const [updateShortcutEmoji] = useUpdateShortcutEmojiMutation();

  // Derived values
  const isKnown = data?.me?.translation?.isKnown;
  const isSaved = data?.me?.translation?.isSaved;
  const progressionScore = data?.me?.translation?.progressionScore;
  const practicedToday =
    !!isReferralEnabled &&
    !!data?.me?.translation?.lastPracticedAt &&
    moment().isSame(moment(data?.me?.translation?.lastPracticedAt), 'day');
  const lastSeenProgressionScore = data?.me?.translation?.lastSeenProgressionScore;
  const isLearned = progressionScore === 100;
  const hasNoMorePracticeQuizzes = !isPremiumSubscribed && numPracticeQuizUsesRemaining === 0;
  const pronunciation = data?.word?.additionalInfo?.pronunciation ?? undefined;
  const partOfSpeech = data?.word?.partOfSpeech ?? undefined;
  const textToSpeak = isDisplayAlphabetSupported(targetLanguage) ? targetPhrase : pronunciation;
  const calloutDataTypeIsPractice = data?.onboarding.callOuts.currentCallout?.itemType === ChecklistType.Practice;
  const showPracticeCallout = !!calloutDataTypeIsPractice && !isLearned;
  const showSettingsCallout = data?.onboarding.callOuts.currentCallout?.itemType === ChecklistType.Settings;
  const [markSettingsOpened] = useMarkSettingsOpenedMutation();
  const isNewlyLearned = progressionScore === 100 && lastSeenProgressionScore && lastSeenProgressionScore < 100;
  const isExpandTranslationFeatureEnabled = useFeatureEnabled(FeatureName.ExpandInlineTranslation);
  const [updatePracticeQuizItem] = useUpdatePracticeQuizItemMutation();
  const { isUserRegistered } = useUserInfo();

  // support for non-whitespace delimited languages is not available for expanded translations
  const unsupportedLanguage = [LanguageId.Ko, LanguageId.Hi];
  let isExpandTranslationEnabled = true;
  if (unsupportedLanguage.includes(sourceLanguage)) {
    isExpandTranslationEnabled = false;
  }

  const isExpansionEnabled =
    isExpandTranslationFeatureEnabled && isExpandTranslationEnabled && isNewlyLearned && hasCelebrated;

  // Callbacks
  const refreshTranslation = useCallback(() => refetch(), [refetch]);
  const translationCard = useTranslationCard({
    translationId,
    sourcePhrase,
    targetPhrase,
    isKnown,
    isSaved,
    refreshTranslation,
    openSubscribePage: async () => {
      await openSubscribePage({ pinchpoint: Pinchpoints.InlineTranslationPracticeQuizCta });
    },
    isPremiumSubscribed,
    targetLanguage,
    source: 'inline translation',
    onQuizEnter: () => {
      setPointsRef(progressBarRef);
      onQuizEnter();
      updatePracticeQuizItem({});
    },
    openSidebar,
    progressionScore,
  });

  const onFocus = useCallback(() => {
    setFocused({ id: translatedNodeId });
  }, [translatedNodeId]);

  const onBlur = useCallback(() => {
    if (isEmojiCustomizationMenuEnabled) {
      return;
    }
    onQuizExit?.();
    if (status === TranslatedNodeStatus.Pinned || status === TranslatedNodeStatus.Quiz) {
      return;
    }
    requestIdle({ id: translatedNodeId });
    if (isNewlyLearned) {
      setHasCelebrated(true);
    }
  }, [isNewlyLearned, isEmojiCustomizationMenuEnabled, status]);

  const forceCloseCard = useCallback(() => {
    setIdle({ id: translatedNodeId });
  }, [setIdle, translatedNodeId]);

  const onClickSaveWord = useCallback(async () => {
    await translationCard.onClickSaveWord({ openSavedWords: true });
    forceCloseCard();
  }, [translationCard.onClickSaveWord]);

  const onAudioPlayRequested = useCallback(
    async (...params: Parameters<typeof translationCard.onAudioPlayRequested>) => {
      setPointsRef(audioRef);
      return translationCard.onAudioPlayRequested(...params);
    },
    [translationCard.onAudioPlayRequested],
  );

  const onClickBook = useCallback(() => {
    openSidebar({
      panelName: PanelName.SavedWords,
      slideName: SlideName.SavedWords,
      hydration: { entryTranslationId: translationId },
    });

    forceCloseCard();
  }, [forceCloseCard, openSidebar, translationId]);

  const onOpenSettings = useCallback(async () => {
    openSidebar({
      panelName: PanelName.Settings,
      slideName: SlideName.MainSettings,
    });

    // We need to mark the settings opened before we can refetch the data to update the settings callout
    await markSettingsOpened({});
    refetch();

    contentMessengers.trackEvent({
      eventName: Events.SettingsOpened,
      properties: { source: 'inline translation hover', isUserRegistered },
    });
    setIdle({ id: translatedNodeId });
  }, [translatedNodeId]);

  const onRemoveWord = useCallback(() => {
    onClickRemoveWord({ translationId, translatedNodeId });
  }, [onClickRemoveWord, forceCloseCard, translationId, translatedNodeId]);

  const onReportError = useCallback(() => {
    onClickReportError({ translationId, translatedNodeId });
    forceCloseCard();
  }, [onClickReportError, forceCloseCard, translationId, translatedNodeId]);

  const onPause = useCallback(() => {
    onClickPause({ translationId, translatedNodeId });
    forceCloseCard();
  }, [onClickPause, forceCloseCard, translationId, translatedNodeId]);

  const exitQuiz = useCallback(async () => {
    onQuizExit();
    await refreshTranslation();
  }, [onQuizExit, refreshTranslation]);

  const onProgressBarAnimationComplete = useCallback(() => {
    if (isNewlyLearned) {
      contentMessengers.trackEvent({
        eventName: 'Word Progression Bar Filled',
        properties: {
          fromIKnowThis: isKnown,
        },
      });
      setIdle({ id: translatedNodeId });
      setHasCelebrated(true);
    }
  }, [onQuizExit, refreshTranslation, isNewlyLearned]);

  const onClickReferral = () => {
    contentMessengers.trackEvent({
      eventName: Events.ReferralCtaClicked,
      properties: {
        pinchpointLocation: Pinchpoints.InlineTranslationReferralCta,
      },
    });
    openSidebar({
      panelName: PanelName.Referral,
      slideName: SlideName.MainReferral,
    });
  };

  /**
   * Controls showing the customization menu and the pinning/unpinning of the translation card.
   */
  const onShortcutEmojiClick = () => {
    setIsEmojiCustomizationMenuEnabled(prevValue => !prevValue);
    if (!isEmojiCustomizationMenuEnabled) {
      setPinned({ id: translatedNodeId });
    } else {
      requestHovered({ id: translatedNodeId }, true);
    }
  };

  const onShortcutEmojiSelection = async (emoji: string) => {
    await updateShortcutEmoji({ indicator: emoji });
    requestHovered({ id: translatedNodeId }, true);
  };

  return (
    <>
      {isExpansionEnabled && <ExpandTranslation />}
      <AnimatedPoints
        progressionScore={progressionScore}
        pointsRef={pointsRef ?? progressBarRef}
        key={`${translationId}-points`}
      />
      <InlineTranslationCardLayout
        isOpen={isOpen}
        domain={domain}
        translationId={translationId}
        pronunciation={pronunciation}
        isPronunciationEnabled={isPronunciationEnabled && hasUserTranslation(source)}
        isQuizActive={status === TranslatedNodeStatus.Quiz}
        forceCloseCard={forceCloseCard}
        onQuizExit={exitQuiz}
        onFocus={onFocus}
        onBlur={onBlur}
        node={node}
        showReferral={practicedToday}
        onClickReferral={onClickReferral}
        toucanRewardOfferedPerReferral={toucanRewardOfferedPerReferral}
        referralCTAText={referralCTAText}
        isShortcutLoading={isShortcutLoading}
        shortcutError={shortcutError}
        shortcutData={shortcutData}
        isActiveLearningShortcutPhrase={isActiveLearningShortcutPhrase}
        hasNoMorePracticeQuizzes={hasNoMorePracticeQuizzes}
      >
        <InlineTranslationCard
          {...translationCard}
          targetPhrase={targetPhrase}
          targetLanguage={targetLanguage}
          sourcePhrase={sourcePhrase}
          partOfSpeech={partOfSpeech}
          textToSpeak={textToSpeak}
          difficultyLevel={difficultyLevel}
          isKnown={isKnown ?? false}
          isSaved={isSaved ?? false}
          isLearned={isLearned}
          hasUserTranslation={hasUserTranslation(source)}
          forceCloseCard={forceCloseCard}
          onOpenSettings={onOpenSettings}
          onClickBook={onClickBook}
          onClickRemoveWord={onRemoveWord}
          onClickReportError={onReportError}
          onClickPause={onPause}
          showPracticeCallout={showPracticeCallout}
          showSettingCallout={showSettingsCallout}
          progressionScore={progressionScore}
          lastSeenProgressionScore={lastSeenProgressionScore}
          onClickSaveWord={onClickSaveWord}
          onProgressBarAnimationComplete={onProgressBarAnimationComplete}
          onAudioPlayRequested={onAudioPlayRequested}
          audioRef={audioRef}
          progressBarRef={progressBarRef}
          loading={loading}
          hasCelebrated={hasCelebrated}
          learningShortcutEmoji={learningShortcutEmoji}
          onShortcutEmojiClick={onShortcutEmojiClick}
          isEmojiCustomizationMenuEnabled={isEmojiCustomizationMenuEnabled}
          setIsEmojiCustomizationMenuEnabled={setIsEmojiCustomizationMenuEnabled}
          onShortcutEmojiSelection={onShortcutEmojiSelection}
          isActiveLearningShortcutPhrase={isActiveLearningShortcutPhrase}
        />
      </InlineTranslationCardLayout>
    </>
  );
};
