import { TranslationKeys } from '~/libs/i18n/types';
import { DifficultyTier } from '~/schema/server';

/**
 * type check since we are passing from the client a string value for the difficulty tier but sending an enum value to the server.
 * @param difficultyTier string
 * @returns boolean
 */
export const isDifficultyTier = (difficultyTier: string | DifficultyTier): difficultyTier is DifficultyTier => {
  if (Object.values(DifficultyTier).includes(difficultyTier as DifficultyTier)) {
    return true;
  }
  return false;
};

/**
 * converts the string value of the difficulty tier passed in the dataset to the formatted version of the difficulty level.
 */
export const formatDifficulty = (
  difficultyString: string | undefined,
  t: (translation: TranslationKeys) => string,
): string | undefined => {
  switch (difficultyString) {
    case 'beg':
      return t('beginner');
    case 'int':
      return t('intermediate');
    case 'adv':
      return t('advanced');
    default:
  }
  return undefined;
};
