import { useCallback } from 'react';

import { v4 } from 'uuid';

import { isHTMLElement } from '~/helpers/htmlHelpers';
import { useAddDictionaryEntry } from '~/hooks/useDictionary';
import { Translations } from '~/hooks/useDictionary/types';
import { useAddTranslatedNodes } from '~/hooks/useTranslatedNodes';
import { TranslatedNodeStatus, TranslatedNodeSource, TranslatedNode } from '~/hooks/useTranslatedNodes/types';
import { DifficultyTier } from '~/schema/server';

export const OnboardingDifficultyTierMap: Record<DifficultyTier | 'all', number> = {
  [DifficultyTier.Beginner]: 10,
  [DifficultyTier.Intermediate]: 11,
  [DifficultyTier.Advanced]: 12,
  all: 13,
};

/**
 * This crawler will allow us to crawl toucan's revised onboarding page as it relates
 * to the tutorial. Data attributes for words to translate have been applied within the
 * website. We are crawling onboarding in order to repurpose our hover component.
 * To make this work, the word to translate must have the class name of `toucan-onboarding`.
 * Additionally, there are 3 data attributes each word must have in order to be translated:
 * `data-sourcephrase`, `data-targetphrase`, `data-partofspeech`, `data-translationid` (ie. uuid),
 * `data-id` (ie. sourcePhrase:uuid), and `data-densitythreshold` (ie. default to 1).
 */
export const useOnboardingPageCrawler = () => {
  const addTranslatedNodes = useAddTranslatedNodes();
  const addTranslations = useAddDictionaryEntry();

  const crawlPage = useCallback(
    ({ difficultyTier }: { difficultyTier?: DifficultyTier } = {}) => {
      const onboardingMatches = document.querySelectorAll('span.toucan-onboarding');

      const addNodes: TranslatedNode[] = [];
      const translations: Translations = {};
      Array.from(onboardingMatches).forEach(node => {
        if (!isHTMLElement(node)) {
          return;
        }

        const { dataset } = node;

        if (!dataset.sourcephrase || !dataset.targetphrase) {
          return;
        }

        const translationId = dataset.translationid || v4();
        const densityThreshold = dataset.densitythreshold ? parseFloat(dataset.densitythreshold) : 1;
        const isActive =
          !difficultyTier ||
          densityThreshold === OnboardingDifficultyTierMap[difficultyTier] ||
          densityThreshold === OnboardingDifficultyTierMap.all;
        const status = isActive ? TranslatedNodeStatus.Idle : TranslatedNodeStatus.Inactive;

        const onboardingNode = {
          id: v4(),
          node,
          translationId,
          // Modify source to indicate we have a dictionary connected onboarding node
          source: dataset.translationid ? TranslatedNodeSource.OnboardingDictionary : TranslatedNodeSource.Onboarding,
          replacement: dataset.sourcephrase,
          densityThreshold,
          status,
          // All properties below are none operational.
          targetId: '0',
          replacementIndex: 0,
          address: '0',
        };

        addNodes.push(onboardingNode);

        const translation = {
          [`${onboardingNode.translationId}`]: {
            translationId: onboardingNode.translationId,
            sourcePhrase: dataset.sourcephrase,
            targetPhrase: dataset.targetphrase,
            partOfSpeech: dataset.partofspeech,
          },
        };

        Object.assign(translations, translation);
      });
      addTranslatedNodes(addNodes);
      addTranslations(translations);
    },
    [addTranslatedNodes, addTranslations],
  );

  return {
    crawlPage,
  };
};
