/**
 * Audio Context support checking and api normalization
 */

export const audioContextSupport = 'AudioContext' in window;
export const { AudioContext } = window;

/**
 * Tiny audio is a base64 encoded audio file for testing audio support
 */
const tinyAudio =
  'data:audio/mpeg;base64,/+MYxAAAAANIAUAAAASEEB/jwOFM/0MM/90b/+RhST//w4NFwOjf///PZu////9lns5GFDv//l9GlUIEEIAAAgIg8Ir/JGq3/+MYxDsLIj5QMYcoAP0dv9HIjUcH//yYSg+CIbkGP//8w0bLVjUP///3Z0x5QCAv/yLjwtGKTEFNRTMuOTeqqqqqqqqqqqqq/+MYxEkNmdJkUYc4AKqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqq';

/**
 * canPlayAudioElement is a function that will resolve
 * true or false if an audio element can be played on the given host.
 */
export const canPlayAudio = async (): Promise<boolean> => {
  if (audioContextSupport) {
    return Promise.resolve(true);
  }

  return new Promise(resolve => {
    try {
      const audio = new Audio();
      audio.autoplay = true;
      audio.volume = 0;

      // If this event fires, then MP3s can be played
      audio.addEventListener('canplay', () => resolve(true), false);
      // If this is fired, then client can't play MP3s
      audio.addEventListener('error', () => resolve(false), false);
      audio.src = tinyAudio;
    } catch (e) {
      // If some error happened then we can play MP3s
      resolve(false);
    }
  });
};

export const base64ToUint8 = (base64: string) => Uint8Array.from(atob(base64), c => c.charCodeAt(0));
