import { ToucanColors } from '@jointoucan/toucan-design';

import { ValidDomain } from '~/constants/domain';
import { isHTMLElement } from '~/helpers/htmlHelpers';

enum BBCPageTypes {
  Article = 'Article',
  NewsStory = 'NewsStory',
  Reel = 'Reel',
  SportsStory = 'SportsStory',
  Other = 'Other',
}

const getBBCPageType = (pathname: string) => {
  const regularExpressions = {
    [BBCPageTypes.Article]: /^\/[\S]+\/article\//,
    [BBCPageTypes.NewsStory]: /^\/news\//,
    [BBCPageTypes.SportsStory]: /^\/sport\//,
    [BBCPageTypes.Reel]: /^\/reel\/video\//,
  };
  const match = Object.entries(regularExpressions).find(([, regEx]) => regEx.test(pathname));
  return match ? match[0] : BBCPageTypes.Other;
};

enum NYTPageTypes {
  Article = 'Article',
  Home = 'Home',
  Section = 'Section',
  Other = 'Other',
}

const getNYTPageType = (pathname: string) => {
  const sectionPageRegEx = /^\/section\//;
  const articlePageRegEx = /^\/\d{4}\/\d{2}\/\d{2}\//;
  if (pathname === '/') {
    return NYTPageTypes.Home;
  }
  if (sectionPageRegEx.test(pathname)) {
    return NYTPageTypes.Section;
  }
  if (articlePageRegEx.test(pathname)) {
    return NYTPageTypes.Article;
  }
  return NYTPageTypes.Other;
};

interface IsValidContextlessMomentUrlInput {
  currentDomain: string;
  pathname: string;
  hash: string;
  search: string;
}

/**
 * Checks if the current url is supported by Toucan
 * @param currentDomain string. The domain to check. For example: en.wikipedia.org, es.wikipedia.org, quora.com, physicsandphilosophy.quora.com.
 * @param pathname string. The path to check.
 * @param hash string. Currently only used for google.com. If the hash contains coordinates don't proceed as formatting will be incorrect. For example...
 * ...Toucan would NOT inject into:
 * https://www.google.com/search?tbs=lf:1,lf_ui:9&tbm=lcl&sxsrf=ALiCzsbW2mlEX4FPvWQjGnkqjSJMKP1pxw:1669677853379&q=coffee&rflfq=1&num=10&ved=2ahUKEwjS59vagtL7AhX4M0QIHX0sCx0QtgN6BAgmEAE#rlfi=hd:;si:;mv:[[34.1842527,-118.34877469999999],[34.1382633,-118.39897850000001]];tbs:lrf:!1m4!1u17!2m2!17m1!1e2!1m4!1u3!2m2!3m1!1e1!1m4!1u2!2m2!2m1!1e1!1m4!1u1!2m2!1m1!1e1!1m4!1u1!2m2!1m1!1e2!1m4!1u22!2m2!21m1!1e1!2m1!1e2!2m1!1e1!2m1!1e3!2m4!1e17!4m2!17m1!1e2!3sIAE,lf:1,lf_ui:9
 * ...Toucan would inject into:
 * https://www.google.com/search?q=coffee&oq=coffee&aqs=chrome.0.35i39i355j46i39j69i59l2j0i67j69i60l3.1405j0j7&sourceid=chrome&ie=UTF-8
 * @param search string. The parameters of the url that is not part of the hash.
 * @returns boolean
 */
export const isValidContextlessMomentUrl = ({
  currentDomain,
  pathname,
  hash,
  search,
}: IsValidContextlessMomentUrlInput) => {
  const isCurrentDomainValid = (Object.values(ValidDomain) as string[]).some(domain => currentDomain.includes(domain));
  if (!isCurrentDomainValid) {
    return false;
  }
  if (currentDomain.includes(ValidDomain.BBC)) {
    return getBBCPageType(pathname) !== BBCPageTypes.Other;
  }
  if (currentDomain.includes(ValidDomain.BBCUK)) {
    return pathname.startsWith('/sounds/play/');
  }
  if (currentDomain === ValidDomain.Google) {
    return pathname === '/search' && !hash.includes('mv:[[');
  }
  if (currentDomain.includes(ValidDomain.Medium)) {
    /**
     * Exlude all...
     * - settings pages (ie. medium.com/me/settings)
     * - subdomains of medium.com (ie. help.medium.com, blog.medium.com)
     */
    return currentDomain === ValidDomain.Medium && !pathname.includes('/me/settings');
  }
  if (currentDomain.includes(ValidDomain.NewYorkTimes)) {
    return getNYTPageType(pathname) !== NYTPageTypes.Other;
  }
  if (currentDomain.includes(ValidDomain.Wikipedia)) {
    /**
     * Wikipedia utilizes subdomains for different languages (ie. en.wikipedia.org, es.wikipedia.org),
     * so we need to check for the base domain and only inject into articles and not site pages like:
     * contact us, dontate, help, etc.
     */
    return !(pathname.includes('Main_Page') || pathname.includes(':') || search.includes(':'));
  }
  return true;
};

type HelpersByDomain = Record<
  string,
  {
    getParentElement: () => Node | HTMLElement | null;
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => void;
    getBackgroundColor: () => string;
    containerMargin?: string;
    containerMarginExtraPractice?: string;
  }
>;

export const urlAdapter: HelpersByDomain = {
  'bbc.co.uk': {
    getParentElement: () => document.querySelector('.sc-c-action-buttons') ?? document.querySelector('.sc-c-synopsis'),
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (!isHTMLElement(containerOnPage)) {
        return;
      }
      elementToAppend.style.setProperty('width', '290px');
      /**
       * If the page includes "Subscribe" and "Bookmark" buttons,
       * show EP or CLM below those buttons.
       * Otherwise, show EP or CLM to the right of the synopsis.
       */
      if (containerOnPage.className.includes('sc-c-action-buttons')) {
        // See https://stackoverflow.com/a/29733127
        const lineBreak = document.createElement('div');
        lineBreak.style.setProperty('width', '100%');
        elementToAppend.style.setProperty('margin-top', '24px');
        containerOnPage.appendChild(lineBreak);
        containerOnPage.appendChild(elementToAppend);
      } else {
        const parent = containerOnPage.parentElement;
        const grandparent = parent?.parentElement;
        grandparent?.style.setProperty('display', 'flex');
        grandparent?.style.setProperty('justify-content', 'space-between');
        grandparent?.appendChild(elementToAppend);
      }
    },
    getBackgroundColor: () => window.getComputedStyle(document.body).getPropertyValue('background-color'),
    // We want to set different margins on different types of pages.
    containerMargin: '0',
    containerMarginExtraPractice: '0',
  },
  'bbc.com': {
    getParentElement: () => {
      const pageType = getBBCPageType(window.location.pathname);
      switch (pageType) {
        case BBCPageTypes.NewsStory:
          return document.querySelector('#topStories-label');
        case BBCPageTypes.Article:
          return document.querySelector('aside.article__similar-articles .related-articles');
        case BBCPageTypes.Reel:
          return document.querySelector('[class*="video_pane_container"] [class*="summary"]');
        case BBCPageTypes.SportsStory:
          return document.querySelector('.component__title');
        default:
          return null;
      }
    },
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (!isHTMLElement(containerOnPage)) {
        return;
      }
      const pageType = getBBCPageType(window.location.pathname);
      switch (pageType) {
        case BBCPageTypes.NewsStory:
        case BBCPageTypes.SportsStory:
          elementToAppend.style.setProperty('margin', '0 0 1rem');
          containerOnPage.insertAdjacentElement('beforebegin', elementToAppend);
          break;
        case BBCPageTypes.Article:
          elementToAppend.style.setProperty('margin', '1rem 0 0');
          containerOnPage.insertAdjacentElement('beforebegin', elementToAppend);
          break;
        case BBCPageTypes.Reel: {
          const originalParent = containerOnPage.parentElement;
          if (!originalParent) {
            return;
          }
          const flexContainer = document.createElement('div');
          flexContainer.style.setProperty('display', 'flex');
          flexContainer.style.setProperty('justify-content', 'space-between');
          elementToAppend.style.setProperty('width', '290px');
          elementToAppend.style.setProperty('margin-bottom', '16px');
          containerOnPage.insertAdjacentElement('beforebegin', flexContainer);
          flexContainer.appendChild(containerOnPage);
          flexContainer.appendChild(elementToAppend);
          break;
        }
        default:
      }
    },
    getBackgroundColor: () =>
      getBBCPageType(window.location.pathname) === BBCPageTypes.Reel
        ? window.getComputedStyle(document.querySelector('#root') ?? document.body).getPropertyValue('background-color')
        : ToucanColors.white,
    // We want to set different margins on different types of pages.
    containerMargin: '0',
    containerMarginExtraPractice: '0',
  },
  'bing.com': {
    getParentElement: () => document.querySelector('#b_context'),
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (isHTMLElement(containerOnPage)) {
        const childNodeLength = containerOnPage?.childNodes.length;
        const element = document.createElement('li');
        element.setAttribute('style', 'margin: 0px -20px 27px;');
        if (childNodeLength > 0) {
          containerOnPage?.insertAdjacentElement('afterbegin', element)?.appendChild(elementToAppend);
        } else {
          containerOnPage?.appendChild(element).appendChild(elementToAppend);
        }
      }
    },
    // dark mode not available on bing.com
    getBackgroundColor: () => ToucanColors.white,
    containerMargin: '0px',
    containerMarginExtraPractice: '0px',
  },
  'forbes.com': {
    getParentElement: () => {
      const { pathname } = window.location;
      return document.querySelector(
        pathname.includes('/sites/')
          ? '.article-body.fs-article.fs-responsive-text.current-article .vestpocket'
          : '.channel__sidebar.channel__sidebar--pop-picks.pop-picks.pop-picks--pop-active > .pop-picks__nav',
      );
    },
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (isHTMLElement(containerOnPage)) {
        containerOnPage.insertAdjacentElement('beforebegin', elementToAppend);
      }
    },
    getBackgroundColor: () => {
      const backgroundColorContainer = document.querySelector('.main-content.main-content--universal-header');
      return backgroundColorContainer
        ? window.getComputedStyle(backgroundColorContainer).getPropertyValue('background-color')
        : ToucanColors.white;
    },
    containerMargin: '0px 0px 18px',
    containerMarginExtraPractice: '0px 0px 18px',
  },
  'google.com': {
    getParentElement: () => {
      const rightHandSide = document.querySelector('#rhs');
      if (rightHandSide) {
        return document.querySelector('#rhs > div');
      }
      return document.querySelector('#center_col');
    },
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (isHTMLElement(containerOnPage)) {
        if (containerOnPage.getAttribute('id') === 'center_col') {
          /**
           * Right hand rail does not exist on google.com therefore create one.
           */
          const newDiv = document.createElement('div');
          newDiv.setAttribute('style', 'margin-left: var(--rhs-margin); width: 369px;');
          const rightHandSide = containerOnPage.insertAdjacentElement('afterend', newDiv);
          rightHandSide?.appendChild(elementToAppend);
        } else {
          /**
           * Copy the width of the targeted page container since the sidebar width is set on its children as fixed widths
           */
          const containerOnPageWidth = window.getComputedStyle(containerOnPage).getPropertyValue('width');
          elementToAppend.style.setProperty('width', containerOnPageWidth);
          containerOnPage.insertAdjacentElement('afterend', elementToAppend);
        }
      }
    },
    getBackgroundColor: () => {
      const backgroundColorContainer = document.querySelector('.srp');
      return backgroundColorContainer
        ? window.getComputedStyle(backgroundColorContainer).getPropertyValue('background-color')
        : ToucanColors.white;
    },
    containerMargin: '-16px 0px 18px',
    containerMarginExtraPractice: '16px 0px',
  },
  'medium.com': {
    getParentElement: () => {
      /**
       * Medium doesn't have any ID's to target, so we use the XPath to find the container
       */
      const isUserLoggedOut = !!document.evaluate(
        '//a[text()="Sign In"]',
        document,
        null,
        XPathResult.FIRST_ORDERED_NODE_TYPE,
        null,
      ).singleNodeValue;

      const homepageContainerXPathBaseLoggedOut =
        '//*[@id="root"]/div/div[4]/div[3]/div/div/div/aside/div/div[1]/div[1]';
      const homepageContainerXPathBaseLoggedIn =
        '//*[@id="root"]/div/div[3]/div[2]/div/div/div/div/div/div[1]/div[2]/div[1]';
      const homepageContainerXPathBase = isUserLoggedOut
        ? homepageContainerXPathBaseLoggedOut
        : homepageContainerXPathBaseLoggedIn;
      const articleContainerXPathBase = '//*[@id="root"]/div/div[3]/div[2]/div/div/div/div/div/div/div[1]';

      return document.evaluate(
        window.location.pathname === '/' ? homepageContainerXPathBase : articleContainerXPathBase,
        document,
        null,
        XPathResult.FIRST_ORDERED_NODE_TYPE,
        null,
      ).singleNodeValue;
    },
    appendElement: (containerOnPage: Node, elementToAppend: HTMLElement) => {
      containerOnPage.parentNode?.appendChild(elementToAppend);
    },
    /**
     * dark mode not available on medium.com
     */
    getBackgroundColor: () => ToucanColors.white,
    containerMargin: '20px 0px 0px',
    containerMarginExtraPractice: '20px 0px 0px',
  },
  'nytimes.com': {
    getParentElement: () => {
      const pageType = getNYTPageType(window.location.pathname);
      switch (pageType) {
        case NYTPageTypes.Home:
          return document.querySelector('[data-hierarchy="feed"] [span="6"] [data-hierarchy="zone"]');
        case NYTPageTypes.Section: {
          const list = document.querySelector('#collection-highlights-container ol');
          if (list && [2, 3].includes(list.children.length)) {
            return list.children[1];
          }
          return null;
        }
        case NYTPageTypes.Article:
          return document.querySelector('.bottom-of-article');
        default:
          return null;
      }
    },
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (!isHTMLElement(containerOnPage)) {
        return;
      }
      const pageType = getNYTPageType(window.location.pathname);
      switch (pageType) {
        case NYTPageTypes.Home:
          elementToAppend.style.setProperty('margin', '0px 0px 20px');
          containerOnPage.insertAdjacentElement('afterbegin', elementToAppend);
          break;
        case NYTPageTypes.Section: {
          const firstChild = containerOnPage.children[0];
          if (firstChild.tagName === 'OL') {
            elementToAppend.style.setProperty('margin', `0px 0px 20px 30px`);
            containerOnPage.insertAdjacentElement('afterbegin', elementToAppend);
          } else {
            elementToAppend.style.setProperty('margin', `20px 0px 0px`);
            containerOnPage.insertAdjacentElement('beforeend', elementToAppend);
          }
          break;
        }
        case NYTPageTypes.Article: {
          const spacer = document.createElement('div');
          spacer.style.setProperty('max-width', '600px');
          spacer.style.setProperty('margin', '0px auto');
          spacer.appendChild(elementToAppend);
          containerOnPage.insertAdjacentElement('beforeend', spacer);
          break;
        }
        default:
      }
    },
    // dark mode not available on nytimes.com
    getBackgroundColor: () => ToucanColors.white,
    // We want to set different margins on different types of pages.
    containerMargin: '0px',
    containerMarginExtraPractice: '0px',
  },
  'quora.com': {
    getParentElement: () => {
      const nodeList = document.querySelectorAll('#mainContent + div');
      const { length } = nodeList;
      return length > 0 ? nodeList[length - 1] : null;
    },
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (!isHTMLElement(containerOnPage)) {
        return;
      }
      const filteredHost = window.location.host.replace('www.', '');
      const isOnSubdomain = filteredHost !== ValidDomain.Quora;
      elementToAppend.style.setProperty('margin', isOnSubdomain ? '14px 0 8px' : '0 0 8px');
      const relatedQuestions = containerOnPage.querySelector('.dom_annotate_related_questions');
      if (relatedQuestions) {
        relatedQuestions.insertAdjacentElement('afterend', elementToAppend);
      } else {
        containerOnPage.insertAdjacentElement('afterbegin', elementToAppend);
      }
    },
    getBackgroundColor: () => document.body.style.backgroundColor,
    // We want to set different margins on different types of pages.
    containerMargin: '0',
    containerMarginExtraPractice: '0',
  },
  'reddit.com': {
    getParentElement: () =>
      // Note: Reddit doesn't have any ID's or classes to target, so we use a test id to find the container
      // Display below "Top communities" or "About community" container in sidebar (first div in sidebar)
      document.querySelector("[data-testid$='-sidebar'] > div"),
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (isHTMLElement(containerOnPage)) {
        containerOnPage.insertAdjacentElement('afterend', elementToAppend);
      }
    },
    getBackgroundColor: () => {
      const backgroundColorContainer = document.querySelector('.ListingLayout-backgroundContainer');
      return backgroundColorContainer
        ? window.getComputedStyle(backgroundColorContainer, ':before').getPropertyValue('background-color')
        : ToucanColors.white;
    },
    containerMargin: '24px 0px 8px',
    containerMarginExtraPractice: '16px 0px 0px',
  },
  'theguardian.com': {
    getParentElement: () => document.querySelector('#top-right-ad-slot'),
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (isHTMLElement(containerOnPage)) {
        containerOnPage.insertAdjacentElement('afterend', elementToAppend);
      }
    },
    getBackgroundColor: () => {
      const backgroundColorContainer = document.querySelector('main article');
      return backgroundColorContainer
        ? window.getComputedStyle(backgroundColorContainer).getPropertyValue('background-color')
        : ToucanColors.white;
    },
    containerMargin: `24px ${
      (document.querySelector('#top-right-ad-slot')?.clientWidth ?? 0) > 300 ? '16px' : '0px'
    } 0px 0px`,
    containerMarginExtraPractice: `24px ${
      (document.querySelector('#top-right-ad-slot')?.clientWidth ?? 0) > 300 ? '16px' : '0px'
    } 0px 0px`,
  },
  'twitter.com': {
    getParentElement: () => {
      // Note: Twitter doesn't have any ID's or classes to target, so we use the XPath to find the container
      const twitterContainerXPathBase =
        '//*[@id="react-root"]/div/div/div[2]/main/div/div/div/div[2]/div/div[2]/div/div/div/div';

      // Display below "What's happening" container in sidebar
      const twitterHomeFeedContainerXPath = `${twitterContainerXPathBase}[3]`;

      // Display below "You might like" container in sidebar
      const twitterProfileFeedContainerXPath = `${twitterContainerXPathBase}[4]`;

      return document.evaluate(
        window.location.pathname === '/home' ? twitterHomeFeedContainerXPath : twitterProfileFeedContainerXPath,
        document,
        null,
        XPathResult.FIRST_ORDERED_NODE_TYPE,
        null,
      ).singleNodeValue;
    },
    appendElement: (containerOnPage: Node, elementToAppend: HTMLElement) => {
      containerOnPage?.parentNode?.insertBefore(elementToAppend, containerOnPage.nextSibling);
    },
    getBackgroundColor: () => document.body.style.backgroundColor,
    containerMargin: '9px 0px 24px',
    containerMarginExtraPractice: '0px 0px 24px',
  },
  'wikipedia.org': {
    getParentElement: () => {
      const content = document.querySelector('#mw-content-text > div');
      const contentChildren = content?.children;

      if (!contentChildren) {
        return null;
      }

      let element = document.querySelector('#contentSub');

      /**
       * Use Array.prototype.some to iterate over contentChildren until we find a right-hand table, right-hand thumb image, or h2.
       * But must convert contentChildren to an array first using Array.from().
       */
      Array.from(contentChildren).some(el => {
        if (el.tagName === 'TABLE' && el.classList.contains('infobox')) {
          /**
           * Right hand table.
           */
          element = document.querySelector('table.infobox');
          return true;
        }
        if (el.tagName === 'DIV' && el.classList.contains('thumb') && el.classList.contains('tright')) {
          /**
           * Right hand thumb image.
           */
          element = document.querySelector('.thumb.tright');
          return true;
        }
        if (el.tagName === 'H2') {
          /**
           * We don't want to add EP after the h2 heading.
           */
          return true;
        }
        return false;
      });
      return element;
    },
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      if (!isHTMLElement(containerOnPage)) {
        return;
      }
      elementToAppend.style.setProperty('float', 'right');
      elementToAppend.style.setProperty('clear', 'right');
      elementToAppend.style.setProperty('padding-left', '12px');
      if (containerOnPage.getAttribute('id') === 'contentSub') {
        containerOnPage.insertAdjacentElement('afterend', elementToAppend);
        return;
      }
      elementToAppend.style.setProperty('width', `${containerOnPage.clientWidth}px`);
      containerOnPage.insertAdjacentElement('afterend', elementToAppend);
    },
    getBackgroundColor: () => ToucanColors.white,
    containerMargin: '12px 0px 24px 0px',
    containerMarginExtraPractice: '12px 0px 24px 0px',
  },
  'youtube.com': {
    getParentElement: () => document.querySelector('#related'),
    appendElement: (containerOnPage: HTMLElement | Node, elementToAppend: HTMLElement) => {
      containerOnPage?.parentNode?.insertBefore(elementToAppend, containerOnPage);
    },
    getBackgroundColor: () => {
      const backgroundColorContainer = document.getElementById('content');
      return backgroundColorContainer
        ? window.getComputedStyle(backgroundColorContainer).getPropertyValue('--yt-spec-general-background-a').trim()
        : ToucanColors.white;
    },
    containerMargin: '0px 0px 24px',
    containerMarginExtraPractice: '0px 0px 24px',
  },
};
