import { useEffect, useState } from 'react';

import { ToucanColors } from '@jointoucan/toucan-design';

import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useToast } from '~/hooks/useToast';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { LanguageId } from '~/schema/server';

export const LanguageChangeDetector = () => {
  const { targetLanguage, targetLanguageVariant, sourceLanguage } = useUserInfo();
  const { t } = useTranslation();
  const { showToast } = useToast();
  const { languageInfo } = useLanguageInfo();
  const [originalLanguage, setOriginalLanguage] = useState<string | null>(null);
  const [originalLanguageVariant, setOriginalLanguageVariant] = useState<string | null | undefined>(null);

  const targetLanguageDefaultVariantNames: { [key: string]: string } = {
    arabic: t('abjadiyyah'),
    hebrew: t('ktavAshuri'),
    hindi: t('devanagari'),
    japanese: t('hiraganaKatakana'),
    korean: t('hangul'),
    chinese: t('simplifiedChinese'),
  };

  const targetLanguageAlternativeVariantNames: { [key: string]: string } = {
    kanji: t('kanji'),
    roman: t('phoneticSpelling'),
  };

  useEffect(() => {
    if ((targetLanguage && !originalLanguage) || targetLanguage !== originalLanguage) {
      setOriginalLanguage(targetLanguage);
    }

    if ((targetLanguageVariant && !originalLanguageVariant) || targetLanguageVariant !== originalLanguageVariant) {
      setOriginalLanguageVariant(targetLanguageVariant);
    }

    const targetLanguageInfo = Object.values(languageInfo).find(lang => lang.key === targetLanguage);

    const languageChanged = targetLanguage !== originalLanguage;
    const languageVariantChanged = targetLanguageVariant !== originalLanguageVariant;

    if (originalLanguage && targetLanguage && targetLanguageInfo && (languageChanged || languageVariantChanged)) {
      const lang =
        sourceLanguage === LanguageId.Es
          ? targetLanguageInfo.language.toLocaleLowerCase()
          : targetLanguageInfo.language;

      const title = languageChanged
        ? t('yourLanguageHasChangedToLang', { lang })
        : t('yourDisplayAlphabetHasChangedToVariant', {
            variant: targetLanguageVariant
              ? targetLanguageAlternativeVariantNames[targetLanguageVariant]
              : targetLanguageDefaultVariantNames[targetLanguage],
          });

      showToast({
        title,
        subtitle: t('tryRefreshingThePageToUpdate'),
        image: 'images/toucan-circle.svg',
        onClick: () => {},
        timeout: 10000,
        subtitleColor: ToucanColors.gray[300],
      });
    }
  }, [targetLanguage, originalLanguage, sourceLanguage, targetLanguageVariant, originalLanguageVariant]);

  return null;
};
