import { useMemo } from 'react';

import { useDeepCompareMemoize } from 'use-deep-compare-effect';

import { isSupportedSourceLanguage, Language, SupportedSourceLanguages } from '~/constants/language';
import { noneEmoji } from '~/constants/shortcuts';
import { StorageKey } from '~/constants/storage';
import { VoiceNumber } from '~/libs/TextToSpeech';
import { DifficultyTier, LanguageId, MeQuery } from '~/schema/server';

import { useStorage } from './useStorage';

const DifficultyToWeightValue: Record<DifficultyTier, string> = {
  [DifficultyTier.Beginner]: 'beg',
  [DifficultyTier.Intermediate]: 'int',
  [DifficultyTier.Advanced]: 'adv',
};

const getDefaultUserSupportedSourceLanguage = () => {
  const browserSourceLanguage = navigator.language.slice(0, 2);
  const isBrowserDefaultSupportedSourceLanguage = isSupportedSourceLanguage(browserSourceLanguage);
  return isBrowserDefaultSupportedSourceLanguage ? browserSourceLanguage : LanguageId.En;
};

export const useUserInfo = () => {
  const [cache, , isLoading] = useStorage<{ data?: MeQuery } | null>(StorageKey.Me, null);
  const data = cache?.data ?? {};
  /**
   * TODO: Fix the type assertion below. I see two options:
   * 1) Change `as Language` to `as Language | undefined`
   * If we go this route, we'll create errors in lots of files that use this
   * hook, and we'll have to update them.
   * 2) Make Language.Spanish the default value for targetLanguage so that
   * targetLanguage is always a Language.
   */
  const targetLanguage = data?.me?.selectedLanguage as Language;
  const isDarkModeEnabled = data?.me?.isDarkModeEnabled ?? false;
  const isContextlessLearningMomentEnabled = data?.me?.isContextlessLearningMomentEnabled ?? true;
  const isPremiumSubscribed = data?.me?.isPremiumSubscribed ?? false;
  const sourceLanguage =
    (data?.me?.sourceLanguage as SupportedSourceLanguages) ?? getDefaultUserSupportedSourceLanguage();
  const targetLanguageVariant = data?.me?.selectedLanguageVariant;
  const email = data?.me?.email;
  const id = data?.me?.id;
  const role = data?.me?.role;
  const isAlwaysTranslateSavedWordsEnabled = data?.me?.isAlwaysTranslateSavedWordsEnabled ?? false;
  const difficultyTier = data?.me?.difficultyTier ?? DifficultyTier.Beginner;
  const weights = data?.me?.difficultyWeights ?? [];
  const weightsByDifficulty = useMemo(
    () =>
      weights.reduce<Record<string, number>>((accum, weight) => {
        Object.assign(accum, { [DifficultyToWeightValue[weight.tier] ?? '']: weight.weight });
        return accum;
      }, {}),
    useDeepCompareMemoize([weights]),
  );
  const playbackRate = data?.me?.audioPreferences?.playbackRate ?? 1;
  const voiceNumber = (data?.me?.audioPreferences?.voiceNumber ?? 0) as VoiceNumber;
  const isUserRegistered = !!email;
  let learningShortcutEmoji: string | undefined;
  if (data?.me?.learningShortcut?.indicator === noneEmoji) {
    learningShortcutEmoji = undefined;
  } else {
    learningShortcutEmoji = data?.me?.learningShortcut?.indicator ?? '';
  }

  return {
    sourceLanguage,
    targetLanguage,
    targetLanguageVariant,
    isDarkModeEnabled,
    isContextlessLearningMomentEnabled,
    isPremiumSubscribed,
    id,
    email,
    role,
    isAlwaysTranslateSavedWordsEnabled,
    difficultyTier,
    weights,
    weightsByDifficulty,
    isLoading,
    playbackRate,
    voiceNumber,
    learningShortcutEmoji,
    isUserRegistered,
  };
};
