import { toucanPrefix } from '~/constants/html';

import { isHTMLElement } from './htmlHelpers';
import { getTextFromNodes, TextFromNodesReplaceMethod } from './text';

export const getFragment = (selection: Selection) => {
  const range = selection.getRangeAt(0).cloneRange();
  return range.cloneContents();
};

export const getParentElement = (selection: Selection) => {
  let parentElement: HTMLElement | null = null;
  if (selection.rangeCount) {
    const container = selection.getRangeAt(0).commonAncestorContainer;

    if (container && container.parentNode && isHTMLElement(container.parentNode)) {
      parentElement = container.parentNode;
    }
  }
  return parentElement;
};

export const getContentFromSelection = (selection: Selection) => {
  // Move selection a tiny bit to see if there is leading or trailing translations.
  const initialText = selection?.toString() ?? '';
  let extended: 'backward' | 'forward' | null = null;

  // NOTE: There be dragons here 
  // Selection modify is an non standard API. This API
  // only allows for the modification of selection to happen in one direction.
  // so right now if there is space in front that takes precedent over
  // the space at the end.
  if ('modify' in selection) {
    if (initialText.match(/^ /)) {
      selection.modify('extend', 'backward', 'character');
      extended = 'backward';
    } else if (initialText.match(/ $/)) {
      selection.modify('extend', 'forward', 'character');
      extended = 'forward';
    }
  }

  const range = selection.getRangeAt(0).cloneRange();
  const fragment = range.cloneContents();
  return { fragment, foundTranslations: Array.from(fragment.querySelectorAll(`[id^="${toucanPrefix}"]`)), extended };
};

export const getSelectionText = (selection: Selection, replace: TextFromNodesReplaceMethod = x => x) => {
  const contents = getContentFromSelection(selection);

  if (!contents || !contents.foundTranslations.length || !contents?.fragment.childNodes) {
    return null;
  }

  const text = getTextFromNodes(contents.fragment.childNodes, replace);

  return text && contents.extended ? text.replace(contents.extended === 'forward' ? / $/ : /^ /, '') : text;
};
