/** @jsx jsx */
import React, { FC, useRef, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { Language } from '~/constants/language';
import { useTextToSpeech } from '~/hooks/useTextToSpeech';
import { useUserInfo } from '~/hooks/useUserInfo';
import { TextToSpeechAudio } from '~/libs/TextToSpeech';

import { useQuizSize } from '../useQuizSize';

const { useColorScheme, Typography, ToucanIcon } = ToucanComponents;

interface UserSelectionType {
  isCorrectAnswer: boolean;
  isUserSelected: boolean;
}

interface ButtonProps {
  option: string;
  hasBeenClicked: boolean;
  userSelection: UserSelectionType | null;
  index: number;
  language: Language;
  setUserSelectedChoice: (value: string) => void;
}

export const Button: FC<ButtonProps> = ({
  option,
  hasBeenClicked,
  userSelection,
  index,
  language,
  setUserSelectedChoice,
}) => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const mounted = useRef(true);
  const { fetchTextToSpeech } = useTextToSpeech();
  const [audioFile, setAudioFile] = useState<TextToSpeechAudio>();
  const [isPlaying, setIsPlaying] = useState(false);
  const { inputPadding, fontVariant } = useQuizSize();
  const { playbackRate, voiceNumber } = useUserInfo();

  const isCorrectAnswer = userSelection?.isCorrectAnswer;
  const isUserSelected = userSelection?.isUserSelected;

  const userSelected = `
    border: 1px solid ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
  `;

  const userSelectionRight = `
    background-color: ${ToucanColors.green[300]};
    border: 1px solid ${ToucanColors.green[300]};
  `;

  const userSelectionWrong = `
    background-color: ${ToucanColors.gray[isDarkMode ? 400 : 200]};
  `;

  const beforeUserSelection = `
    &:hover {
      background-color: ${ToucanColors.gray[isDarkMode ? 600 : 100]};
    }
    &:active {
      border: 1px solid ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
      box-shadow: inset 0 0 0 1px ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
      background-color: transparent;
    }
  `;

  const lightModeColorMap = hasBeenClicked && isCorrectAnswer ? ToucanColors.white : ToucanColors.gray[500];

  const playAudioFile = (audioFileToPlay: TextToSpeechAudio) => {
    if (audioFileToPlay) {
      audioFileToPlay.play();

      if (!mounted.current) {
        return;
      }

      setIsPlaying(true);

      // eslint-disable-next-line no-param-reassign
      audioFileToPlay.onended = () => {
        if (!mounted.current) {
          return;
        }

        setIsPlaying(false);
      };
    }
  };

  const playSound = async () => {
    if (hasBeenClicked) {
      return;
    }
    setUserSelectedChoice(option);
    if (!isPlaying && !audioFile && mounted.current) {
      const resp = await fetchTextToSpeech({
        targetPhrase: option,
        language,
        playbackRate,
        voiceNumber,
      });
      if (!mounted.current || !resp) {
        return;
      }
      setAudioFile(resp);
      playAudioFile(resp);
    } else if (!isPlaying && audioFile) {
      playAudioFile(audioFile);
    }
  };

  return (
    <Box
      component="button"
      width="100%"
      ml={index === 0 ? 0 : 1}
      p={inputPadding}
      onClick={playSound}
      display="flex"
      alignItems="center"
      css={css`
        transition: background-color 0.15s ease-in-out;
        border-radius: 4px;
        border: 1px solid ${ToucanColors.gray[isDarkMode ? 400 : 200]};
        cursor: pointer;
        text-align: left;
        background-color: transparent;
        ${!hasBeenClicked && beforeUserSelection}
        ${hasBeenClicked && isCorrectAnswer && userSelectionRight}
        ${hasBeenClicked && !isCorrectAnswer && isUserSelected && userSelectionWrong}
        ${!hasBeenClicked && isUserSelected && userSelected}
      `}
    >
      <ToucanIcon
        css={css`
          color: ${hasBeenClicked && isCorrectAnswer ? ToucanColors.white : ToucanColors.blue[300]};
          margin-right: ${theme.spacing(0.5)};
        `}
        icon="volume-up"
      />
      <Typography
        variant={fontVariant}
        css={css`
          padding-left: ${theme.spacing(1)};
          color: ${isDarkMode ? ToucanColors.white : lightModeColorMap};
        `}
      >
        {index + 1}
      </Typography>
    </Box>
  );
};
