import { Aracari } from 'aracari';

import { getFragment, getParentElement } from '~/helpers/selectionHelpers';
import { getTextFromNodes } from '~/helpers/text';
import { PendingTranslatedNode } from '~/hooks/useTranslatedNodes/types';

/**
 * NonAlphaPattern and IsAlphaPattern are used to check if the selection is
 * for valid unicode letters, they use a newer regexp api.
 *
 * https://github.com/tc39/proposal-regexp-unicode-property-escapes
 *
 * This allows use to detect letters in all languages.
 */
const NonAlphaPattern = /[^\p{L}-]+/gu;
const IsAlphaPattern = /^[\p{L}-]$/gu;

/**
 * HighlightedSelection is a class the is a wrapper around the Selection
 * class, and allows for the management of the selection, in a way that helps with
 * the Highlight to Translate  feature.
 */
export class HighlightedSelection {
  selection: Selection;

  mapping: Aracari | null;

  getTranslatedNode: (id: string) => PendingTranslatedNode | undefined;

  constructor(selection: Selection, getTranslatedNode: (id: string) => PendingTranslatedNode | undefined) {
    this.selection = selection;
    this.mapping = this.parentElement ? new Aracari(this.parentElement) : null;
    this.getTranslatedNode = getTranslatedNode;
  }

  get textContent() {
    const childNodes = this.fragment?.childNodes;
    if (childNodes) {
      /**
       * This is now aware of existing translations on the page, so that the replace method may replace
       * the targetPhrases with sourcePhrases, so that way we are not sending other languages to our
       * server.
       *
       * It gets the translated node id by breaking apart the id in the dom `tou-index-translatedNodeId`
       */
      return getTextFromNodes(childNodes, (text, id) => {
        const translatedNode = this.getTranslatedNode(id);
        return translatedNode ? translatedNode.replacement : text;
      });
    }
    return '';
  }

  // NOTE: This bounding box will be set at the end of the highlight.
  get boundingRect() {
    try {
      const range = this.selection.getRangeAt(0).cloneRange();
      return range.getBoundingClientRect();
    } catch (e) {
      return null;
    }
  }

  get anchorText() {
    const { selection } = this;
    const node = selection.anchorNode;
    return node?.textContent ?? '';
  }

  get anchorAddress() {
    const { anchorText, mapping } = this;
    return mapping?.getAddressForText(anchorText) ?? '0';
  }

  // NOTE: If there is duplicate text in the node this is needed to be able to
  // replace the proper text
  get selectedIndex() {
    const { anchorOffset } = this.selection;
    const { anchorText, textContent } = this;
    const chunks = anchorText.split(textContent);
    if (chunks.length > 2) {
      let found = false;
      let cursor = 0;
      while (!found) {
        const text = chunks.slice(0, cursor + 1).join(textContent);
        if (text.length === anchorOffset) {
          found = true;
        } else {
          cursor += 1;
        }
        if (cursor > chunks.length) {
          throw new Error('Unable to determine textContext index in anchorText');
        }
      }
      return cursor;
    }
    return 0;
  }

  get fragment() {
    try {
      return getFragment(this.selection);
    } catch (e) {
      return null;
    }
  }

  get parentTextContent() {
    return this.parentElement?.textContent ?? '';
  }

  get parentElement() {
    return getParentElement(this.selection);
  }

  get delimiters() {
    const [preText, postText] = this.surroundingTextContent;
    const charBefore = preText.split('').pop() ?? '';
    const charAfter = postText.split('').shift() ?? '';
    return [charBefore, charAfter];
  }

  get surroundingTextContent() {
    const { parentTextContent, textContent } = this;
    // NOTE: We will need to handle a case > 3 after split.
    // this happens if the same word is in the same node
    const [preText = '', postText = ''] = parentTextContent.split(textContent);
    return [preText, postText];
  }

  get expandedTextContent() {
    const { hasFragmentSelected, textContent, surroundingTextContent } = this;
    if (hasFragmentSelected) {
      const [preText, postText] = surroundingTextContent;
      return `${preText.split(NonAlphaPattern).pop()}${textContent}${postText.split(NonAlphaPattern).shift()}`;
    }
    return this.textContent;
  }

  get hasFragmentSelected() {
    return this.delimiters.some(char => !!char.match(IsAlphaPattern));
  }

  clear() {
    this.selection.empty();
  }
}
