import omit from 'lodash/omit';

import { TranslationInfo } from '~/components/InlineTranslations/InlineTranslationCard/types';
import { PostMessageEvent } from '~/constants/events';
import { GetExtensionDataQuery } from '~/schema/background';

export const sendPostMessage = (
  messageType: PostMessageEvent,
  payload?:
    | GetExtensionDataQuery
    | {
        translationInfo?: TranslationInfo;
      },
) => {
  window.parent.postMessage(
    {
      type: messageType,
      translationInfo: payload && 'translationInfo' in payload ? omit(payload.translationInfo, 'node') : undefined,
      ...omit(payload, 'translationInfo'),
    },
    '*',
  );
};
