import React, { createContext, useContext, useState } from 'react';

import { browserVendor } from '~/configs/config';
import { FeatureName } from '~/constants/features';
import { defaultSupportedSettings } from '~/constants/settings';
import { defaultSupportedPanels, mobileSupportedPanels, mobileSupportedPanelsWithProgress } from '~/constants/sidebar';
import { useFeatureEnabled } from '~/libs/features/client';
import { useDoesCurrentLanguageVectorSupportShortcutsQuery } from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';
import { OverlayName, PanelName, SettingName, SidebarHydratedState, SlideDirection, SlideName } from '~/types/sidebar';

interface NavigationProps {
  panel?: PanelName;
  slide?: SlideName;
  slideDirection?: SlideDirection;
  overlay?: OverlayName | null;
  hydration?: SidebarHydratedState;
  shouldDisableClickAway?: boolean;
}

interface SidebarHistory {
  panel: PanelName;
  slide: SlideName;
  slideDirection: SlideDirection;
  overlay: OverlayName | null;
  hydration?: SidebarHydratedState;
  shouldDisableClickAway?: boolean;
}

interface SidebarContextProps {
  onNavigateSidebar: (navigationProps: NavigationProps) => void;
  onNavigateBack: () => void;
  supportedPanels: Array<PanelName>;
  supportedSettings: Array<SettingName>;
  history: SidebarHistory[];
  setDisableClickAway: (shouldDisableClickAway: boolean) => void;
  shouldDisableClickAway?: boolean;
}

interface SidebarProviderProps {
  panelName?: PanelName;
  slideName?: SlideName;
  overlayName?: OverlayName | null;
  supportedPanels?: Array<PanelName>;
  supportedSettings?: Array<SettingName>;
  children?: React.ReactNode;
  shouldDisableClickAway?: boolean;
}

const initialSlideHistory: SidebarHistory = {
  panel: PanelName.Settings,
  slide: SlideName.MainSettings,
  overlay: null,
  slideDirection: SlideDirection.Forward,
  hydration: undefined,
  shouldDisableClickAway: false,
};

export const SidebarContext = createContext<SidebarContextProps>({
  onNavigateSidebar: () => {},
  onNavigateBack: () => {},
  supportedPanels: defaultSupportedPanels,
  supportedSettings: defaultSupportedSettings,
  history: [initialSlideHistory],
  setDisableClickAway: () => {},
  shouldDisableClickAway: false,
});

export const useSidebarContext = () => {
  const { history, ...context } = useContext(SidebarContext);
  const {
    panel: activePanelName,
    slide: activeSlideName,
    slideDirection: activeSlideDirection,
    overlay: activeOverlayName,
    hydration,
    shouldDisableClickAway,
  } = history[history.length - 1];
  const lastNavigation = history[history.length - 2];
  return {
    ...context,
    activePanelName,
    activeSlideName,
    activeSlideDirection,
    activeOverlayName,
    hydration,
    lastNavigation,
    shouldDisableClickAway,
  };
};

// Essentially a map of possible navigation.
type ValidNavigation = Record<PanelName, Partial<Record<SlideName, (OverlayName | null)[]>>>;

const validNavigation: ValidNavigation = {
  [PanelName.Settings]: {
    [SlideName.MainSettings]: [OverlayName.Snooze],
    [SlideName.ChangeLanguage]: [],
    [SlideName.BlockedSites]: [],
    [SlideName.Authenticate]: [],
    [SlideName.AudioControls]: [],
    [SlideName.AccountDeletionConfirm]: [],
    [SlideName.AccountDeletionConfirmation]: [],
    [SlideName.ExperimentInformation]: [],
  },
  [PanelName.Notifications]: {
    [SlideName.MainNotifications]: [],
    [SlideName.OnboardingChecklist]: [],
  },
  [PanelName.Progress]: {
    [SlideName.ProgressOverview]: [OverlayName.MakingProgress],
    [SlideName.WordList]: [OverlayName.WordQuiz],
    [SlideName.LearningShortcut]: [],
    [SlideName.TopicWordList]: [],
    [SlideName.LearningShortcutTopic]: [],
  },
  [PanelName.SavedWords]: {
    [SlideName.SavedWords]: [OverlayName.PracticeQuiz, OverlayName.PostPracticeQuiz],
  },
  [PanelName.HighlightToTranslate]: {
    [SlideName.HighlightToTranslate]: [],
  },
  [PanelName.Referral]: {
    [SlideName.MainReferral]: [],
    [SlideName.EmailReferral]: [],
  },
};

/**
 * NOTE: When this component is called by popup.tsx we are feature flagging for FocusManagement (shortcuts) here because isMobileSafari and isShortcutsEnabled aren't passable as a prop from popup.tsx.
 * This is because SidebarProvider lives within an IIFE that is outside a component and therefore invoked without without the ability to pass the appropriate props. These two values are needed to determine
 * if the progress tab should be shown in the sidebar for mobile. At this time only the matrix en to es supports the progress tab for shortcuts. All other languages do not support the progress tab regardless
 * of shortcuts being unavailable.
 */
export const SidebarProvider = ({
  children,
  panelName = PanelName.Settings,
  slideName = SlideName.MainSettings,
  overlayName = null,
  supportedPanels = defaultSupportedPanels,
  supportedSettings = defaultSupportedSettings,
  shouldDisableClickAway = false,
}: SidebarProviderProps) => {
  const [history, setHistory] = useState<SidebarHistory[]>([
    {
      panel: panelName,
      slide: slideName,
      overlay: overlayName,
      slideDirection: SlideDirection.Forward,
      hydration: undefined,
      shouldDisableClickAway: false,
    },
  ]);
  const { data } = useDoesCurrentLanguageVectorSupportShortcutsQuery();
  const isShortcutsEnabled =
    useFeatureEnabled(FeatureName.Shortcuts) && data?.doesCurrentLanguageVectorSupportShortcuts;
  const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;
  const supportedPanelsForMobile = isShortcutsEnabled ? mobileSupportedPanelsWithProgress : mobileSupportedPanels;
  const currentShouldDisableClickAway = shouldDisableClickAway;

  const onNavigateSidebar = ({
    panel,
    slide,
    slideDirection = SlideDirection.Forward,
    overlay,
    hydration: passedHydration,
    shouldDisableClickAway: passedShouldDisableClickAway,
  }: NavigationProps) => {
    const activeNavigation = history[history.length - 1];
    const nextPanel = panel ?? activeNavigation.panel;
    let nextSlide = slide ?? activeNavigation.slide;
    let nextOverlay = overlay ?? null;

    if (!validNavigation[nextPanel][nextSlide]) {
      [nextSlide] = Object.keys(validNavigation[nextPanel]) as SlideName[];
    }

    const validOverlay = validNavigation?.[nextPanel]?.[nextSlide];
    if (nextOverlay !== null && !validOverlay?.includes(nextOverlay) && validOverlay) {
      nextOverlay = null;
    }

    setHistory(prevHistory => [
      ...prevHistory,
      {
        panel: nextPanel,
        slide: nextSlide,
        slideDirection,
        overlay: nextOverlay,
        hydration: passedHydration,
        shouldDisableClickAway: passedShouldDisableClickAway,
      },
    ]);
  };

  const onNavigateBack = () => {
    setHistory(prevHistory => {
      const historyWithLastRecordRemoved = prevHistory.slice(0, -1);
      return [...historyWithLastRecordRemoved];
    });
  };

  const setDisableClickAway = (shouldDisableClickAwaySetting: boolean) => {
    setHistory(prevHistory => {
      const recentHistory = prevHistory[0];
      const newHistory = {
        ...recentHistory,
        shouldDisableClickAway: shouldDisableClickAwaySetting,
      };
      return [...prevHistory, newHistory];
    });
  };

  return (
    <SidebarContext.Provider
      value={{
        onNavigateSidebar,
        onNavigateBack,
        supportedPanels: isMobileSafari ? supportedPanelsForMobile : supportedPanels,
        supportedSettings,
        history,
        shouldDisableClickAway: currentShouldDisableClickAway,
        setDisableClickAway,
      }}
    >
      {children}
    </SidebarContext.Provider>
  );
};
