import React from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';

const { Typography } = ToucanComponents;
interface ShortcutErrorProps {
  shortcutError?: Error;
}
export const ShortcutError = ({ shortcutError }: ShortcutErrorProps) => {
  const { t } = useTranslation();

  return (
    <Box
      mt={1}
      display="flex"
      flexDirection="column"
      alignItems="flex-start"
      bgcolor={ToucanColors.red[100]}
      borderRadius="4px"
      p={1}
    >
      <Typography variant="2xs" isBold>
        {t('birdGotLoose')}
      </Typography>
      {shortcutError?.message && (
        <Typography
          variant="2xs"
          css={css`
            margin-top: 2px;
          `}
        >
          {shortcutError?.message}
        </Typography>
      )}
    </Box>
  );
};
