import { useCallback, useMemo } from 'react';

import { useSidebar } from '~/components/Sidebar/useSidebar';
import { Events } from '~/constants/events';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import {
  useAddToWordListMutation,
  useMarkWordAsKnownMutation,
  useMarkWordAsUnknownMutation,
  useRecordTranslationAudioPlayMutation,
  useRemoveFromWordListMutation,
} from '~/schema/background';
import { LanguageId } from '~/schema/server';
import { PanelName, SlideName } from '~/types/sidebar';

import { useDailyPracticeQuizUses } from './useDailyPracticeQuizUses';
import { usePremiumExperiment } from './usePremiumExperiment';
import { useUpdateTranslatedNodeMetaByTranslatedId } from './useTranslatedNodes';

interface UseTranslationCardParams {
  translationId: string;
  targetLanguage: string | LanguageId;
  targetPhrase: string;
  sourcePhrase: string;
  isKnown?: boolean;
  isSaved?: boolean;
  progressionScore?: number;
  refreshTranslation?: (params: { translationId: string }) => Promise<void>;
  isPremiumSubscribed: boolean;
  openSubscribePage: () => void;
  source: 'sidebar' | 'inline translation' | 'saved-words';
  hideProgressButton?: boolean;
  openSidebar?: (...params: Parameters<ReturnType<typeof useSidebar>['openSidebar']>) => void;
  onQuizEnter?: (translationId: string) => void;
}

export const useTranslationCard = ({
  translationId,
  isKnown,
  refreshTranslation,
  targetLanguage,
  targetPhrase,
  sourcePhrase,
  isSaved,
  isPremiumSubscribed,
  source,
  hideProgressButton,
  onQuizEnter,
  openSidebar,
  progressionScore,
}: UseTranslationCardParams) => {
  const { t } = useTranslation();
  const [recordTranslationAudioPlay] = useRecordTranslationAudioPlayMutation();
  const [markWordAsKnown] = useMarkWordAsKnownMutation();
  const [markWordAsUnknown] = useMarkWordAsUnknownMutation();
  const [removeFromWordList] = useRemoveFromWordListMutation();
  const [addToWordList] = useAddToWordListMutation();
  const { numPracticeQuizUsesRemaining } = useDailyPracticeQuizUses();
  const updateMetaByTranslatedId = useUpdateTranslatedNodeMetaByTranslatedId();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const isInlinePracticeBehindPaywall =
    !isPremiumSubscribed &&
    getFeaturePaywallStatus(PremiumFeatureName.InlinePractice) === PremiumFeaturePaywallStatus.Paid;

  const openQuiz = useCallback(async () => onQuizEnter?.(translationId), [translationId]);

  const onAudioPlayRequested = useCallback(async () => {
    contentMessengers.trackAudioPronunciationClicked({ learningID: translationId, language: targetLanguage });
    await recordTranslationAudioPlay({ id: translationId });
    await refreshTranslation?.({ translationId });
  }, [targetPhrase, targetLanguage, refreshTranslation, recordTranslationAudioPlay]);

  const onClickCheckMark = useCallback(async () => {
    const eventName = '"I know this" clicked';
    const properties = {
      translation: targetPhrase,
      word: sourcePhrase,
      isKnown,
      source,
    };
    contentMessengers.trackEvent({ eventName, properties });

    if (isKnown) {
      await markWordAsUnknown({ id: translationId });
    } else {
      await markWordAsKnown({ id: translationId });
    }
    await refreshTranslation?.({ translationId });
  }, [isKnown, translationId, refreshTranslation, markWordAsKnown, markWordAsUnknown]);

  const onClickSaveWord = useCallback(
    async ({ openSavedWords }: { openSavedWords?: boolean } = {}) => {
      if (isSaved) {
        await removeFromWordList({ id: translationId });
      } else {
        await addToWordList({ id: translationId });
        if (openSavedWords) {
          openSidebar?.({
            panelName: PanelName.SavedWords,
            slideName: SlideName.SavedWords,
            hydration: { entryTranslationId: translationId },
          });
        }
      }

      updateMetaByTranslatedId({ translationId, meta: { isSaved: !isSaved } });

      const eventName = isSaved ? Events.RemovedSavedWord : Events.AddedSavedWord;
      const properties = {
        translation: targetPhrase,
        word: sourcePhrase,
        isSaved,
        source,
      };
      contentMessengers.trackEvent({
        eventName,
        properties,
        userOptions: {
          includePremiumSubscriptionStatus: true,
        },
      });
      await refreshTranslation?.({ translationId });
    },
    [isSaved, translationId, refreshTranslation, removeFromWordList, addToWordList, updateMetaByTranslatedId, source],
  );

  const showProgress = useCallback(() => {
    openSidebar?.({
      panelName: PanelName.Progress,
      slideName: SlideName.ProgressOverview,
    });
  }, [openSidebar]);

  let progressButtonImgSrc = 'images/pencil.svg';
  let progressButtonImgAlt = t('pencil');
  let progressButtonText = t('practice');
  let onProgressButtonClick: () => void = openQuiz;
  let showProgressButton = true;
  let progressButtonExtraText: string | undefined;

  if (!isPremiumSubscribed && numPracticeQuizUsesRemaining && !isInlinePracticeBehindPaywall) {
    progressButtonExtraText = t('numFreeLeft', { number: numPracticeQuizUsesRemaining });
  }

  if (progressionScore === 100 && !!openSidebar) {
    progressButtonImgSrc = 'images/sparkle.svg';
    progressButtonImgAlt = t('sparkle');
    onProgressButtonClick = showProgress;
    showProgressButton = true;
    progressButtonText = t('viewYourProgress');
    progressButtonExtraText = undefined;
  }

  if (hideProgressButton) {
    showProgressButton = false;
  }

  return useMemo(
    () => ({
      onAudioPlayRequested,
      onClickCheckMark,
      onClickSaveWord,
      progressButtonImgSrc,
      progressButtonImgAlt,
      progressButtonText,
      onProgressButtonClick,
      showProgressButton,
      progressButtonExtraText,
    }),
    [
      onAudioPlayRequested,
      onClickCheckMark,
      onClickSaveWord,
      progressButtonImgAlt,
      progressButtonImgSrc,
      showProgressButton,
      progressButtonText,
      onProgressButtonClick,
      progressButtonExtraText,
    ],
  );
};
