/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { useQuizSize } from '../useQuizSize';

const { useColorScheme, Typography } = ToucanComponents;

interface AccentButtonProps {
  onClick: () => void;
  option: string;
  index: number;
}

export const AccentButton: FC<AccentButtonProps> = ({ onClick, option, index }) => {
  const { isDarkMode } = useColorScheme();
  const { inputPadding, fontVariant } = useQuizSize();

  return (
    <Box
      component="button"
      width="100%"
      ml={index === 0 ? 0 : 1}
      py={inputPadding}
      onClick={onClick}
      display="flex"
      alignItems="center"
      justifyContent="center"
      css={css`
        transition: background-color 0.15s ease-in-out;
        border-radius: 4px;
        border: 1px solid ${ToucanColors.gray[isDarkMode ? 400 : 200]};
        cursor: pointer;
        text-align: left;
        background-color: transparent;
        &:hover {
          background-color: ${ToucanColors.gray[isDarkMode ? 600 : 100]};
        }
        &:active {
          border: 1px solid ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
          box-shadow: inset 0 0 0 1px ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
          background-color: transparent;
        }
      `}
    >
      <Typography
        variant={fontVariant}
        css={css`
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
        `}
      >
        {option}
      </Typography>
    </Box>
  );
};
