/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { useQuizSize } from '../useQuizSize';

const { useColorScheme, Typography } = ToucanComponents;

interface UserSelectionType {
  isCorrectAnswer: boolean;
  isUserSelected: boolean;
}

interface ButtonProps {
  onClick: () => void;
  option: string;
  hasBeenClicked: boolean;
  userSelection: UserSelectionType | null;
  index: number;
}

export const Button: FC<ButtonProps> = ({ onClick, option, hasBeenClicked, userSelection, index }) => {
  const { isDarkMode } = useColorScheme();
  const { inputPadding, fontVariant } = useQuizSize();

  const isCorrectAnswer = userSelection?.isCorrectAnswer;
  const isUserSelected = userSelection?.isUserSelected;

  const userSelectionRight = `
    background-color: ${ToucanColors.green[300]};
    border: 1px solid ${ToucanColors.green[300]};
  `;

  const userSelectionWrong = `
    background-color: ${ToucanColors.gray[isDarkMode ? 400 : 200]};
  `;

  const beforeUserSelection = `
    &:hover {
      background-color: ${ToucanColors.gray[isDarkMode ? 600 : 100]};
    }
    &:active {
      border: 1px solid ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
      box-shadow: inset 0 0 0 1px ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
      background-color: transparent;
    }
  `;

  const lightModeColorMap = hasBeenClicked && isCorrectAnswer ? ToucanColors.white : ToucanColors.gray[500];

  return (
    <Box
      component="button"
      width="100%"
      p={inputPadding}
      mt={index === 0 ? 0 : 1}
      onClick={onClick}
      css={css`
        transition: background-color 0.15s ease-in-out;
        border-radius: 4px;
        border: 1px solid ${ToucanColors.gray[isDarkMode ? 400 : 200]};
        cursor: pointer;
        text-align: left;
        background-color: transparent;
        ${!hasBeenClicked && beforeUserSelection}
        ${hasBeenClicked && isCorrectAnswer && userSelectionRight}
        ${hasBeenClicked && !isCorrectAnswer && isUserSelected && userSelectionWrong}
      `}
    >
      <Typography
        variant={fontVariant}
        css={css`
          color: ${isDarkMode ? ToucanColors.white : lightModeColorMap};
        `}
      >
        {option}
      </Typography>
    </Box>
  );
};
