import React, { FC, useState } from 'react';

import { Box } from '~/components/Box';
import { QuizSize, QuizSizeProvider } from '~/components/InlineTranslations/InlineQuiz/useQuizSize';
import { Events } from '~/constants/events';
import { Language } from '~/constants/language';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { useAdditionalInfo } from '~/hooks/useAdditionalInfo';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useSubscription } from '~/hooks/useSubscription';
import { useUpdateTranslatedNodeMetaByTranslatedId } from '~/hooks/useTranslatedNodes';
import contentMessengers from '~/messengers/contentMessengers';
import {
  useAddToWordListMutation,
  useMarkWordAsKnownMutation,
  useMarkWordAsUnknownMutation,
  useRemoveFromWordListMutation,
  useRecordTranslationAudioPlayMutation,
  ContextlessLearningType,
} from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { FlashCardBack } from './FlashCardBack';
import { FlashCardFront } from './FlashCardFront';

interface FlashCardContentProps {
  onCompleteQuestion: (quizStatus: QuizStatus.Pass | QuizStatus.Fail) => void;
  questionNumber: number;
  questionTargetPhrase: string;
  quizTranslationIds: string[];
  questionSourcePhrase: string;
  isNonLatin: boolean;
  languageKey: Language | undefined;
  isCLMIntroScreenDisabled: boolean;
  adhocEventTracking: ({ quizType }: { quizType: ContextlessLearningType }) => void;
  isPremiumSubscribed: boolean;
}

export const FlashCardContent: FC<FlashCardContentProps> = ({
  onCompleteQuestion,
  questionNumber,
  questionTargetPhrase,
  quizTranslationIds,
  questionSourcePhrase,
  isNonLatin,
  languageKey,
  isCLMIntroScreenDisabled,
  adhocEventTracking,
  isPremiumSubscribed,
}) => {
  const [isFlipped, setIsFlipped] = useState(false);
  const [markWordAsKnown] = useMarkWordAsKnownMutation();
  const [markWordAsUnknown] = useMarkWordAsUnknownMutation();
  const [removeFromWordList] = useRemoveFromWordListMutation();
  const [addToWordList] = useAddToWordListMutation();
  const updateMetaByTranslationId = useUpdateTranslatedNodeMetaByTranslatedId();
  const [recordTranslationAudioPlay] = useRecordTranslationAudioPlayMutation();
  const { openSubscribePage } = useSubscription();
  const { getFeaturePaywallStatus } = usePremiumExperiment();

  const isSaveWordPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.SaveWord) === PremiumFeaturePaywallStatus.Paid;
  const isIKnowThisPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.IKnowThisWord) === PremiumFeaturePaywallStatus.Paid;

  const onClickToFlip = () => {
    setIsFlipped(currentFlippedStatus => !currentFlippedStatus);
  };

  const translationId = quizTranslationIds[questionNumber - 1];
  const { isKnown, isFluent, refetch, pronunciation, progressionScore, wordList } = useAdditionalInfo(translationId);

  // if Wordlist exists, word is saved
  const isSaved = !!wordList;

  const onAudioPlayRequested = async () => {
    await recordTranslationAudioPlay({ id: translationId });
  };

  const onClickCheckmark = async () => {
    if (isFluent) {
      // Once the user is fluent they know the word, and cannot unlearn it.
      return;
    }

    const eventName = '"I know this" clicked';
    const properties = {
      translation: questionTargetPhrase,
      word: questionSourcePhrase,
      isKnown,
      source: 'sidebar',
    };
    contentMessengers.trackEvent({
      eventName,
      properties,
    });

    if (isKnown) {
      await markWordAsUnknown({ id: translationId });
    } else {
      await markWordAsKnown({ id: translationId });
    }
    await refetch();
  };

  const onClickSaveWord = async () => {
    if (isSaved) {
      await removeFromWordList({ id: translationId });
    } else {
      await addToWordList({ id: translationId });
    }

    updateMetaByTranslationId({ translationId, meta: { isSaved: !isSaved } });
    const eventName = isSaved ? Events.RemovedSavedWord : Events.AddedSavedWord;
    const properties = {
      translation: questionTargetPhrase,
      word: questionSourcePhrase,
      isSaved,
      source: 'flashcard',
    };
    contentMessengers.trackEvent({
      eventName,
      properties,
      userOptions: {
        includePremiumSubscriptionStatus: true,
      },
    });
    await refetch();
  };

  return (
    <QuizSizeProvider value={QuizSize.Medium}>
      <Box>
        {!isFlipped ? (
          <FlashCardFront
            questionTargetPhrase={questionTargetPhrase}
            onClickToFlip={onClickToFlip}
            isNonLatin={isNonLatin}
            pronunciation={pronunciation}
            languageKey={languageKey}
            onAudioPlayRequested={onAudioPlayRequested}
          />
        ) : (
          <FlashCardBack
            onCompleteQuestion={onCompleteQuestion}
            questionTargetPhrase={questionTargetPhrase}
            onClickToFlip={onClickToFlip}
            isNonLatin={isNonLatin}
            questionSourcePhrase={questionSourcePhrase}
            pronunciation={pronunciation}
            onClickCheckmark={onClickCheckmark}
            onClickSaveWord={onClickSaveWord}
            isSaved={isSaved}
            isKnown={isKnown}
            isFluent={isFluent}
            progressionScore={progressionScore}
            languageKey={languageKey}
            onAudioPlayRequested={onAudioPlayRequested}
            questionNumber={questionNumber}
            isCLMIntroScreenDisabled={isCLMIntroScreenDisabled}
            adhocEventTracking={adhocEventTracking}
            openSubscribePage={openSubscribePage}
            isSaveWordDisabled={isSaveWordPremiumFeature && !isPremiumSubscribed}
            isIKnowThisWordDisabled={isIKnowThisPremiumFeature && !isPremiumSubscribed}
          />
        )}
      </Box>
    </QuizSizeProvider>
  );
};
