import { CSSObject } from '@emotion/react';

import { toucanPrefix } from '~/constants/html';

const transitionOutClassName = `${toucanPrefix}-transition--out`;

const DEFAULT_TRANSITION = 200;

export const setupTransitionHelpers = (duration: number = DEFAULT_TRANSITION) => {
  const style = document.createElement('style');
  style.id = `${toucanPrefix}-transition-helpers`;
  document.head.appendChild(style);

  const nodeCSS = `
  .${toucanPrefix}-node {
    all: unset;
    will-change: opacity;
    transition-property: opacity;
    transition-duration: ${duration}ms;
    transition-delay: 0;
    transition-timing-function: ease-in-out;
    opacity: 1;
  }`;
  const transitionOut = `.${transitionOutClassName} { opacity: 0; }`;

  if (style.sheet) {
    style.sheet.insertRule(nodeCSS, 0);
    style.sheet.insertRule(transitionOut, 1);
  }
};

export const setStyles = (node: HTMLSpanElement, object: CSSObject) => {
  Object.assign(node.style, object);
};

export const fadeOutNode = (node: HTMLSpanElement): Promise<void> =>
  new Promise(resolve => {
    window.requestAnimationFrame(() => {
      setTimeout(() => {
        resolve();
      }, DEFAULT_TRANSITION);
      node.classList.add(transitionOutClassName);
    });
  });

export const fadeInNode = (node: HTMLSpanElement): Promise<void> =>
  new Promise(resolve => {
    setTimeout(() => {
      resolve();
    }, DEFAULT_TRANSITION);
    node.classList.remove(transitionOutClassName);
  });

export const isInViewport = (element: HTMLElement) => {
  const rect = element.getBoundingClientRect();
  return rect.top >= 0 && rect.left >= 0 && rect.bottom <= window.innerHeight && rect.right <= window.innerWidth;
};
