/** @jsx jsx */
import React, { useEffect, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';
import { AnimateSharedLayout } from 'framer-motion';

import { Events } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import {
  useChangeNewNotificationStateMutation,
  useDoesCurrentLanguageVectorSupportShortcutsQuery,
  useIsNewNotificationAvailableQuery,
} from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { MotionBox } from '../Box';

import { useSidebarContext } from './SidebarContext';
import { SidebarFooterTabs } from './SidebarFooterTabs';

const { Typography, useColorScheme } = ToucanComponents;

type PanelWithTab = Exclude<PanelName, PanelName.Referral | PanelName.HighlightToTranslate>;
const hasTab = (panel: PanelName): panel is PanelWithTab =>
  panel !== PanelName.Referral && panel !== PanelName.HighlightToTranslate;

export const PanelSelector = () => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const { activePanelName, onNavigateSidebar, supportedPanels } = useSidebarContext();
  const { data, refetch } = useIsNewNotificationAvailableQuery();
  const [changeNotificationState] = useChangeNewNotificationStateMutation();
  const isNewNotification = data?.settings?.isNewNotificationAvailable ?? false;
  const [hoveredPanelName, setHoveredPanelName] = useState<PanelName | null>(null);
  const [clickedPanelName, setClickedPanelName] = useState<PanelName | null>(null);
  const { sourceLanguage, targetLanguage } = useUserInfo();
  const { data: languageVectorData, refetch: refetchMatrixQuery } = useDoesCurrentLanguageVectorSupportShortcutsQuery();
  const isShortcutsEnabledForVector = !!(
    useFeatureEnabled(FeatureName.Shortcuts) && languageVectorData?.doesCurrentLanguageVectorSupportShortcuts
  );

  useEffect(() => {
    refetchMatrixQuery();
  }, [sourceLanguage, targetLanguage, refetchMatrixQuery]);

  const panelDisplayNameMap: Record<PanelWithTab, string> = {
    [PanelName.Progress]: isShortcutsEnabledForVector ? t('shortcuts') : t('progress'),
    [PanelName.SavedWords]: t('saved'),
    [PanelName.Notifications]: t('toucanTips'),
    [PanelName.Settings]: t('settings'),
  };

  const onButtonClick = async (name: PanelName) => {
    if (name === PanelName.Progress) {
      onNavigateSidebar({ panel: PanelName.Progress, slide: SlideName.ProgressOverview });
    }
    if (name === PanelName.Notifications) {
      if (isNewNotification) {
        await changeNotificationState({ updated: false });
        await refetch();
      }
      contentMessengers.trackEvent({ eventName: Events.ToucanTipsViewed });
      onNavigateSidebar({ panel: PanelName.Notifications, slide: SlideName.MainNotifications });
    }
    if (name === PanelName.Settings) {
      onNavigateSidebar({ panel: PanelName.Settings, slide: SlideName.MainSettings });
    }
    if (name === PanelName.SavedWords) {
      onNavigateSidebar({ panel: PanelName.SavedWords, slide: SlideName.SavedWords });
    }
  };

  const regularColor = ToucanColors.gray[isDarkMode ? 300 : 400];
  const selectedColor = isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300];
  const hoverColor = isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[200];
  const clickedColor = isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[400];

  return (
    <React.Fragment>
      <AnimateSharedLayout>
        {supportedPanels.map(panelName => {
          if (!hasTab(panelName)) {
            return null;
          }
          return (
            <Box
              key={panelName}
              onClick={() => onButtonClick(panelName)}
              display="flex"
              position="relative"
              flexDirection="column"
              justifyContent="center"
              alignItems="center"
              onMouseEnter={() => setHoveredPanelName(panelName)}
              onMouseLeave={() => setHoveredPanelName(null)}
              onMouseDown={() => setClickedPanelName(panelName)}
              onMouseUp={() => setClickedPanelName(null)}
              css={css`
                cursor: pointer;
                width: 115px;
                color: ${activePanelName === panelName ? selectedColor : regularColor};
                &:hover {
                  color: ${activePanelName === panelName ? selectedColor : hoverColor};
                }
                &:active {
                  color: ${activePanelName === panelName ? selectedColor : clickedColor};
                }
              `}
            >
              {activePanelName === panelName ? (
                <MotionBox
                  position="absolute"
                  top={0}
                  left={0}
                  layoutId="overline"
                  initial={false}
                  height="4px"
                  width="100%"
                  bgcolor={selectedColor}
                />
              ) : null}
              <SidebarFooterTabs
                tab={panelName}
                activePanelName={activePanelName}
                hoveredPanelName={hoveredPanelName}
                clickedPanelName={clickedPanelName}
                isNewNotification={isNewNotification}
              />
              <Typography
                variant="2xs"
                isBold
                css={css`
                  margin-top: ${theme.spacing(1)};
                  text-transform: capitalize;
                  color: inherit;
                  text-align: center;
                `}
              >
                {panelDisplayNameMap[panelName]}
              </Typography>
            </Box>
          );
        })}
      </AnimateSharedLayout>
    </React.Fragment>
  );
};
