/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { Language } from '~/constants/language';
import { useTranslation } from '~/libs/i18n';

const { Typography, useColorScheme } = ToucanComponents;

interface FlashCardFrontProps {
  questionTargetPhrase: string;
  onClickToFlip: () => void;
  isNonLatin: boolean;
  pronunciation: string | null;
  languageKey: Language | undefined;
  onAudioPlayRequested: () => void;
}

export const FlashCardFront: FC<FlashCardFrontProps> = ({
  questionTargetPhrase,
  onClickToFlip,
  isNonLatin,
  pronunciation,
  languageKey,
  onAudioPlayRequested,
}) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  return (
    <React.Fragment>
      <Box
        component="button"
        width="100%"
        mt={2}
        mb={2}
        onClick={onClickToFlip}
        alignItems="center"
        height="232px"
        display="flex"
        flexDirection="column"
        css={css`
          transition: background-color 0.15s ease-in-out;
          border-radius: ${theme.spacing(1)};
          border: 1px solid ${ToucanColors.gray[isDarkMode ? 600 : 200]};
          background-color: ${isDarkMode ? ToucanColors.teal[600] : 'transparent'};
          cursor: pointer;
        `}
        data-testid="flashcard-front"
      >
        <Box alignItems="center" display="flex" flexDirection="column" flex="1" justifyContent="center">
          {languageKey && (
            <AudioPlayer
              language={languageKey}
              isStacked
              targetPhrase={questionTargetPhrase}
              onPlay={onAudioPlayRequested}
            >
              <Typography
                variant="md"
                isBold
                color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}
                css={css`
                  margin-top: ${theme.spacing(1.5)};
                `}
              >
                {questionTargetPhrase}
              </Typography>
            </AudioPlayer>
          )}
          {isNonLatin && (
            <Typography variant="xs" color={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}>
              {pronunciation}
            </Typography>
          )}
        </Box>
        <Box p={2}>
          <Typography variant="2xs" color={isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[300]}>
            {t('clickToSeeTranslation')}
          </Typography>
        </Box>
      </Box>
    </React.Fragment>
  );
};
