/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { Pinchpoints } from '~/constants/pinchpoints';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';
import { QuizType } from '~/schema/background';

import { LottieAnimation } from '../../shared/LottieAnimation';

const { useColorScheme, Typography, Button } = ToucanComponents;

interface PremiumUpsellProps {
  onSetIdle: () => void;
  onNotNowClick: () => void;
  quizType: QuizType;
  isInlineTranslation: boolean;
  headline?: string;
  blurb?: string;
}

export const PremiumUpsell: FC<PremiumUpsellProps> = ({
  onSetIdle,
  quizType,
  isInlineTranslation,
  onNotNowClick,
  headline,
  blurb,
}) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();
  const linearGradient = isDarkMode
    ? `180deg, ${ToucanColors.blue[500]} 0%, ${ToucanColors.yellow[300]} 320.83%`
    : `180deg, ${ToucanColors.blue[100]} 0%, ${ToucanColors.yellow[100]} 147.92%`;
  const { openSubscribePage } = useSubscription();
  const pinchpoint = {
    [QuizType.Practice]: isInlineTranslation
      ? Pinchpoints.InlineTranslationPracticeQuizPrimaryUpsell
      : Pinchpoints.SidebarPracticeQuizPrimaryUpsell,
    [QuizType.MultiPractice]: Pinchpoints.SidebarPracticeQuizCta,
  };

  if (!targetLanguageInfo) {
    return null;
  }

  const onStartFreeTrialClick = async () => {
    openSubscribePage({
      pinchpoint: pinchpoint[quizType],
    });

    onSetIdle();
  };

  return (
    <Box
      data-testid="premium-upsell"
      height={isInlineTranslation ? '240px' : '100%'}
      mx={isInlineTranslation ? '-16px' : 0}
      borderRadius={isInlineTranslation ? '10px 10px 0 0' : 0}
      css={css`
        background: linear-gradient(${linearGradient});
      `}
    >
      <Box
        position="absolute"
        width="100%"
        bottom={0}
        {...(isInlineTranslation && {
          width: '240px',
          top: 0,
          bottom: 'none',
        })}
      >
        <LottieAnimation
          filePath={`/animations/inlineQuiz/upsellSkyToucan-${isDarkMode ? 'dark' : 'light'}.json`}
          loop={false}
          rendererSettings={{ preserveAspectRatio: 'xMidYMin slice' }}
          style={{ display: 'flex' }}
        />
      </Box>
      <Box display="flex" flexDirection="column" mx={2} alignItems="center">
        <Typography
          variant={isInlineTranslation ? 'sm' : 'md'}
          isBold
          css={css`
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
            margin-top: ${theme.spacing(isInlineTranslation ? 2 : 4)};
            text-align: center;
          `}
        >
          {headline || t('getUnlimitedPractice')}
        </Typography>
        <Typography
          variant={isInlineTranslation ? 'xs' : 'sm'}
          css={css`
            color: ${ToucanColors.gray[isDarkMode ? 100 : 500]};
            margin-top: ${theme.spacing(isInlineTranslation ? 0.5 : 1)};
            text-align: center;
          `}
        >
          {blurb || t('learnEvenFaster', { language: targetLanguageInfo.language })}
        </Typography>

        <Button
          size={isInlineTranslation ? 'small' : 'medium'}
          onClick={onStartFreeTrialClick}
          type="alert"
          css={css`
            margin-top: ${theme.spacing(isInlineTranslation ? 2 : 3)};
            width: ${isInlineTranslation ? 'inherit' : '100%'};
          `}
        >
          {t('startFreeTrialFirstLetterCapitalized')}
        </Button>
        <Typography
          variant={isInlineTranslation ? 'xs' : 'sm'}
          css={css`
            margin-top: ${theme.spacing(isInlineTranslation ? 1 : 2)};
          `}
        >
          <Button type="text" onClick={onNotNowClick}>
            {t('notNow')}
          </Button>
        </Typography>
      </Box>
    </Box>
  );
};
