import { useCallback, useEffect, useState, useMemo, useRef } from 'react';

import Configs from '~/configs/config';
import { checkSourceLanguageIsLatinBased } from '~/constants/language';
import { DensityPercentages, DensityValuesPrimitive } from '~/constants/settings';
import { StorageKey } from '~/constants/storage';
import { ContentMessageProducer } from '~/contentMessageProducer';
import { createToucanSpan, getExcludedSubNodes, isToucanSpan } from '~/helpers/htmlHelpers';
import { filterNull } from '~/helpers/utilityHelpers';
import { useUserInfo } from '~/hooks/useUserInfo';
import ContentMessengers from '~/messengers/contentMessengers';
import { useGetTranslationsSeenQuery, useIncrementTranslationViewedMutation } from '~/schema/background';

import { useCurrentDomain } from '../useCurrentDomain';
import { useDictionaryEntries } from '../useDictionary';
import { useClearDomTargets, useDomTargets, useRemoveDomTargets } from '../useDomTargets';
import { DomTarget } from '../useDomTargets/types';
import { useDomainEnabled } from '../useDomainEnabledEffect';
import { usePrevious } from '../usePrevious';
import { useStorage } from '../useStorage';
import {
  useErroredTranslatedNodes,
  useMountedTranslatedNodes,
  usePendingTranslatedNodes,
  useRemoveTranslatedNodes,
  useTranslatedNodesOnPage,
  useTranslatedNodeTransitions,
} from '../useTranslatedNodes';
import { createElementId } from '../useTranslatedNodes/helpers';
import {
  BuiltTranslatedNode,
  TranslatedNode,
  TranslatedNodeSource,
  TranslatedNodeStatus,
} from '../useTranslatedNodes/types';

import { shouldShowTranslation } from './helpers';

interface TranslationReplacementProps {
  historyChanges: number;
  enableGarbageCollection: boolean;
  excludedTags?: Array<string>;
}

const MAX_REPLACEMENTS = 10;

export const useTranslationReplacement = ({
  historyChanges,
  enableGarbageCollection,
  excludedTags = [],
}: TranslationReplacementProps) => {
  const { targets: domTargets } = useDomTargets();
  const removeDomTargets = useRemoveDomTargets();
  const clearDomTargets = useClearDomTargets();
  const translations = useDictionaryEntries();
  const { setBuilt, setErrored, setInactive, setIdle } = useTranslatedNodeTransitions();
  const removeTranslatedNodes = useRemoveTranslatedNodes();
  const nodesOnPage = useTranslatedNodesOnPage();
  const erroredTranslatedNodes = useErroredTranslatedNodes();
  const [difficultyLevel] = useStorage<DensityValuesPrimitive>(StorageKey.DensityLevel, 1);
  const [installTime] = useStorage<number>(StorageKey.InstallTime, +new Date());
  const { currentDomain } = useCurrentDomain();
  const [hasSeenFirstTranslation, setFirstTranslationSeen] = useStorage<boolean>(
    StorageKey.FirstTranslationSeen,
    false,
  );

  // Used to check if the first translation has been seen right away since the storage value doesn't update immediately
  const hasSeenFirstTranslationRef = useRef(false);
  const [visitedDomains] = useStorage<string[]>(StorageKey.DomainsBeforeFirstTranslation, []);
  const prevHistoryChanges = usePrevious(historyChanges);
  const { data } = useGetTranslationsSeenQuery();
  const [incrementTranslationSeen] = useIncrementTranslationViewedMutation();
  const [lastGarbageCollection, setLastGarbageCollection] = useState<number>(Date.now());
  const { sourceLanguage, weightsByDifficulty } = useUserInfo();
  const isSourceLanguageLatinBased = useMemo(() => checkSourceLanguageIsLatinBased(sourceLanguage), [sourceLanguage]);
  const isExtensionEnabled = useDomainEnabled();

  const difficultyPercentage = DensityPercentages[difficultyLevel];

  // TODO move to selectors
  const pendingNodes = usePendingTranslatedNodes();
  const mountedNodes = useMountedTranslatedNodes();

  const onTranslationSeen = useCallback(
    async (id: string, mountedNode: BuiltTranslatedNode) => {
      const { source, translationId } = mountedNode;
      const currentTranslation = translations[translationId]?.targetPhrase;
      // View translation for dictionary entries.
      if (currentTranslation && source === TranslatedNodeSource.Crawler) {
        await incrementTranslationSeen({
          translationId: mountedNode.translationId,
        });
      }

      const shouldTrackFirstSeen =
        !data?.translationsSeen.hasSeenTranslation && !hasSeenFirstTranslation && !hasSeenFirstTranslationRef.current;

      if (shouldTrackFirstSeen) {
        setFirstTranslationSeen(true);
        hasSeenFirstTranslationRef.current = true;
        const msSinceDownload = +new Date() - installTime;
        ContentMessengers.trackEvent({
          eventName: 'First Translation Seen',
          properties: {
            domainOfFirstTranslation: currentDomain,
            visitedDomains,
            minutesSinceDownload: Math.floor(msSinceDownload / 1000 / 60),
          },
        });
      }
    },
    [translations, currentDomain, installTime, visitedDomains],
  );

  useEffect(() => {
    // TODO: batch this
    pendingNodes.forEach(pendingNode => {
      setBuilt({ id: pendingNode.id, node: pendingNode.node }, createToucanSpan(pendingNode));
    });
  }, [pendingNodes]);

  // Do the replacement
  useEffect(() => {
    const usedDomTargets: DomTarget[] = [];
    const badTranslatedNodes: BuiltTranslatedNode[] = [];
    if (!isExtensionEnabled) {
      return () => {};
    }
    const replaceNodes = () => {
      mountedNodes.slice(0, MAX_REPLACEMENTS).forEach(mountedNode => {
        const target = domTargets.find(domTarget => domTarget.id === mountedNode.targetId);
        // Remove these nodes
        if (!target || !target.textMappings) {
          badTranslatedNodes.push(mountedNode);
          return;
        }

        // Cleanup domTargets after replacement
        if (!usedDomTargets.includes(target)) {
          usedDomTargets.push(target);
        }

        const { textMappings, parentNode } = target;
        const { node, replacement, id } = mountedNode;
        let useAddress = true;
        const addresses = textMappings.remap().getAddressesForText(replacement) ?? [];
        const excludedSubNodes = getExcludedSubNodes(textMappings, parentNode, excludedTags);
        // NOTE: crawler filters its address here, the highlight to translate feature does
        // not need that logic since the users intention is to replace
        const selectedAddress =
          mountedNode.source === TranslatedNodeSource.Crawler
            ? addresses
                .filter(address => {
                  // TODO: needs to find excluded child nodes
                  const isInExcludedSubNode = excludedSubNodes.some(excludedAddress =>
                    address.startsWith(`${excludedAddress}.`),
                  );
                  const addressNode = textMappings.getNodeByAddress(address);
                  if (!(addressNode.parentNode instanceof HTMLElement)) {
                    return false;
                  }
                  return !isInExcludedSubNode && !isToucanSpan(addressNode.parentNode);
                })
                .find(filterNull)
            : mountedNode.address;

        const attemptReplace = (): boolean => {
          try {
            const isSingleWord = isSourceLanguageLatinBased ? !replacement.includes(' ') : false;
            textMappings.replaceText(replacement, [node], {
              perserveWord: isSingleWord,
              at: useAddress ? selectedAddress : undefined,
              replacementIndex: mountedNode.replacementIndex,
            });
          } catch (e) {
            if (useAddress) {
              useAddress = false;
              return attemptReplace();
            }
            setErrored({ node, id });
            return false;
          }
          return true;
        };
        if (attemptReplace()) {
          // const isInDensity = mountedNode.densityThreshold >= difficultyPercentage;
          const shouldShow =
            shouldShowTranslation(
              mountedNode.densityThreshold,
              weightsByDifficulty[mountedNode?.difficultyLevel ?? ''] ?? 0,
              difficultyPercentage,
            ) && mountedNode.status !== TranslatedNodeStatus.Inactive;

          if (shouldShow) {
            setIdle({ id, node });
            onTranslationSeen(id, mountedNode);
          } else {
            setInactive({ id, node });
          }
        }
      });
      const serverTranslation = mountedNodes.filter(node => node.source === TranslatedNodeSource.Server);
      ContentMessengers.serverDrivenTranslationsSeenCount(serverTranslation.length);
      ContentMessageProducer.logSeenLearningsCount({ count: mountedNodes.length });
      if (usedDomTargets.length) {
        removeDomTargets(usedDomTargets);
      }
      if (badTranslatedNodes.length) {
        removeTranslatedNodes(badTranslatedNodes);
      }
    };
    const ts = window.requestAnimationFrame(replaceNodes);
    return () => window.cancelAnimationFrame(ts);
  }, [mountedNodes, onTranslationSeen, isExtensionEnabled]);

  // Cleans up errored nodes
  useEffect(() => {
    if (erroredTranslatedNodes.length) {
      removeTranslatedNodes(erroredTranslatedNodes);
    }
  }, [erroredTranslatedNodes]);

  useEffect(() => {
    if (historyChanges !== prevHistoryChanges) {
      const translatedNodesToRemove: Array<TranslatedNode> = [];
      nodesOnPage.forEach(node => {
        const element = node.node;
        const elementOnPage = (document.getElementById(createElementId(node)) ?? undefined) as
          | HTMLSpanElement
          | undefined;
        if (element === elementOnPage) {
          return;
        }
        translatedNodesToRemove.push(node);
      });
      // Cleanup targets
      if (historyChanges >= 1) {
        clearDomTargets();
      }
      if (translatedNodesToRemove.length) {
        removeTranslatedNodes(translatedNodesToRemove);
      }
    }
  }, [nodesOnPage, historyChanges, prevHistoryChanges, domTargets]);

  // Garbage collection
  useEffect(() => {
    if (enableGarbageCollection && Date.now() - lastGarbageCollection > 10000) {
      setLastGarbageCollection(Date.now());
      // Cleanup old targets
      if (domTargets.length > 100) {
        if (Configs.isDebugging) {
          // eslint-disable-next-line no-console
          console.info('Garbage collection happened for domTargets');
        }
        // These can get recreated pretty easily
        clearDomTargets();
      }
      if (nodesOnPage.length > 100) {
        if (Configs.isDebugging) {
          // eslint-disable-next-line no-console
          console.info('Garbage collection happened for nodesOnPage');
        }
        const translatedNodesToRemove: Array<TranslatedNode> = [];
        nodesOnPage.forEach(node => {
          const element = node.node;
          const elementOnPage = (document.getElementById(createElementId(node)) ?? undefined) as
            | HTMLSpanElement
            | undefined;
          if (element === elementOnPage) {
            return;
          }
          translatedNodesToRemove.push(node);
        });
        removeTranslatedNodes(translatedNodesToRemove);
      }
    }
  }, [domTargets, nodesOnPage, lastGarbageCollection, enableGarbageCollection]);
};
