/** @jsx jsx */
import React, { ReactNode } from 'react';

import { css, jsx } from '@emotion/react';

import { Box } from '~/components/Box';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useSubscription } from '~/hooks/useSubscription';

interface SubscriptionRedirectContainerProps {
  shouldRedirect: boolean;
  pinchpoint: Pinchpoints;
  children: ReactNode;
}

export const SubscriptionRedirectContainer = ({
  shouldRedirect,
  pinchpoint,
  children,
}: SubscriptionRedirectContainerProps) => {
  const { openSubscribePage } = useSubscription();

  if (!shouldRedirect) {
    return <React.Fragment>{children}</React.Fragment>;
  }

  return (
    <Box
      role="button"
      onClick={() => {
        openSubscribePage({
          pinchpoint,
        });
      }}
      css={css`
        cursor: pointer;
      `}
    >
      {children}
    </Box>
  );
};
