/** @jsx jsx */
import React, { useEffect, useRef } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';
import { AnimatePresence, motion } from 'framer-motion';

import { ModalBaseProps, ModalType } from '~/components/ModalContainer/types';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { LottieAnimation } from '~/components/shared/LottieAnimation';
import { browserVendor } from '~/configs/config';
import { Language } from '~/constants/language';
import { StorageKey } from '~/constants/storage';
import { maximumZIndex } from '~/constants/styling';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useSaveLearningShortcutCelebrationMutation } from '~/schema/background';
import { LearningShortcutSubject } from '~/schema/server';
import { BrowserVendors } from '~/types/browsers';
import { PanelName } from '~/types/sidebar';

import { ExtraPracticeProgressionType } from './Finish';

const { Typography, Button, ToucanIcon } = ToucanComponents;

export const CelebrationModal = ({ onRequestClose, onRequestOpen, selectedModalType }: ModalBaseProps) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;
  const { targetLanguageInfo } = useLanguageInfo();
  const currentTargetLanguage = targetLanguageInfo?.language ?? Language.Spanish;
  const [isShortcutsCelebrationModalOpenRequested, setIsShortcutsCelebrationModalOpenRequested] = useModalStorage(
    StorageKey.IsShortcutsCelebrationModalOpenRequested,
    false,
  );
  const hasBeenViewed = useRef(false);
  const isOpen = selectedModalType === ModalType.ShortcutsCelebration;
  const [extraPracticeProgression] = useStorage<ExtraPracticeProgressionType>(
    StorageKey.ShortcutsExtraPracticeProgression,
    {
      updatedProgressionScore: 0,
      nextLearningShortcutTopic: undefined,
      hasCompletedShortcut: false,
      hasCelebrated: false,
      celebrationTitle: undefined,
      subject: LearningShortcutSubject.Food,
    },
  );
  const heading = extraPracticeProgression.celebrationTitle?.replace(/{{[a-z\W]+}}/gi, currentTargetLanguage);
  const [saveLearningShortcutCelebration] = useSaveLearningShortcutCelebrationMutation();
  const [shortcutsExtraPractice] = useStorage(StorageKey.ShortcutsExtraPractice, {
    data: { data: { extraPractice: { learningShortcut: { id: '' } } } },
  });
  const { openSidebar } = useSidebar();

  useEffect(() => {
    if (isShortcutsCelebrationModalOpenRequested && !hasBeenViewed.current) {
      const hasModalOpened = onRequestOpen(ModalType.ShortcutsCelebration);

      ContentMessengers.trackEvent({
        eventName: 'Shortcuts Celebration Modal Viewed',
        properties: {},
      });
      hasBeenViewed.current = true;

      if (hasModalOpened) {
        saveLearningShortcutCelebration({
          input: { learningShortcutId: shortcutsExtraPractice.data.data.extraPractice?.learningShortcut.id },
        });
      }
    }
  }, [selectedModalType, isShortcutsCelebrationModalOpenRequested]);

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.removeProperty('overflow');
    }
  }, [isOpen]);

  const closeModal = async () => {
    setIsShortcutsCelebrationModalOpenRequested(false);
    onRequestClose(ModalType.ShortcutsCelebration);
  };

  const onNextShortcutClick = async () => {
    await closeModal();
    openSidebar({ panelName: PanelName.Progress });
  };

  return (
    <AnimatePresence>
      {isOpen ? (
        <motion.div
          data-testid="extra-practice-modal-fireworks"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0, ease: 'easeIn', duration: 0.3 }}
          exit={{ opacity: 0, transition: { ease: 'easeOut', duration: 0.3 } }}
        >
          <div
            tabIndex={-1}
            css={css`
              position: fixed;
              top: 0;
              left: 0;
              width: 100vw;
              height: 100vh;
              display: flex;
              align-items: center;
              justify-content: center;
              background-color: rgba(0, 0, 0, 0.8);
              z-index: ${maximumZIndex};
              ${isMobileSafari && `padding: ${theme.spacing(2)};`}
            `}
          >
            <Box
              role="button"
              zIndex={1}
              position="absolute"
              top={24}
              right={24}
              onClick={closeModal}
              sx={{ cursor: 'pointer' }}
            >
              <ToucanIcon icon="times" color={ToucanColors.white} width={24} height={24} />
            </Box>
            {!isMobileSafari && (
              <LottieAnimation
                filePath="/animations/learningShortcuts/extraPractice/fireworks-left-B.json"
                loop
                rendererSettings={{ preserveAspectRatio: 'xMaxYMid slice' }}
                style={{ display: 'flex', width: 'auto', height: '100vh' }}
              />
            )}
            <Box
              display="flex"
              flexDirection="column"
              justifyContent="center"
              alignItems="center"
              width={isMobileSafari ? '100%' : '387px'}
            >
              <LottieAnimation
                filePath={`/animations/learningShortcuts/extraPractice/toucanCelebration-${extraPracticeProgression.subject}.json`}
                loop={false}
                style={{ display: 'flex', width: 'auto', height: '265px' }}
              />
              <Typography
                variant={isMobileSafari ? 'md' : 'lg'}
                font="secondary"
                isBold
                color={ToucanColors.white}
                css={css`
                  text-align: center;
                  margin-top: ${theme.spacing(4)};
                `}
              >
                {heading}
              </Typography>
              <Typography
                variant="sm"
                color={ToucanColors.white}
                css={css`
                  text-align: center;
                  margin-top: ${theme.spacing(4)};
                `}
              >
                {t('readyForYourNextShortcut')}
              </Typography>
              <Button
                onClick={onNextShortcutClick}
                size="medium"
                css={css`
                  margin-top: ${theme.spacing(4)};
                  ${isMobileSafari && `width: 100%;`}
                `}
              >
                {t('browseShortcuts')}
              </Button>
              <Box onClick={closeModal} sx={{ cursor: 'pointer' }}>
                <Typography
                  variant="xs"
                  color={ToucanColors.white}
                  css={css`
                    text-align: center;
                    margin-top: ${theme.spacing(2)};
                  `}
                >
                  {t('noThanks')}
                </Typography>
              </Box>
            </Box>
            {!isMobileSafari && (
              <LottieAnimation
                filePath="/animations/learningShortcuts/extraPractice/fireworks-right-B.json"
                loop
                rendererSettings={{ preserveAspectRatio: 'xMinYMid slice' }}
                style={{ display: 'flex', width: 'auto', height: '100vh' }}
              />
            )}
          </div>
        </motion.div>
      ) : null}
    </AnimatePresence>
  );
};
