/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { QuizStatus } from '~/types/quiz';

import { useQuizSize } from '../useQuizSize';

const { useColorScheme, Typography } = ToucanComponents;

interface ButtonProps {
  onClick: () => void;
  option: string;
  active: boolean;
  status?: QuizStatus.Pass | QuizStatus.Fail;
  disabled: boolean;
}

export const Button: FC<ButtonProps> = ({ onClick, option, active, disabled, status }) => {
  const { isDarkMode } = useColorScheme();
  const { inputPadding, fontVariant } = useQuizSize();

  const correctButton = `
    background-color: ${ToucanColors.green[300]} !important;
    box-shadow: inset 0 0 0 1px ${ToucanColors.green[300]};
  `;

  const incorrectButton = `
    background-color: ${ToucanColors.gray[isDarkMode ? 400 : 200]} !important;
  `;

  const activeButton = ` 
    box-shadow: inset 0 0 0 2px ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
    background-color: transparent;
  `;

  const disabledButton = ` 
    opacity: 0.3;
  `;

  const isCorrect = status === QuizStatus.Pass;
  const isIncorrect = status === QuizStatus.Fail;
  const lightModeTextColor = active && isCorrect ? ToucanColors.white : ToucanColors.gray[500];

  return (
    <Box
      component="button"
      p={inputPadding}
      onClick={active || disabled ? undefined : onClick}
      css={css`
        transition: all 0.2s ease-in-out;
        border-radius: 4px;
        box-shadow: inset 0 0 0 1px ${ToucanColors.gray[isDarkMode ? 400 : 200]};
        cursor: pointer;
        text-align: left;
        background-color: transparent;
        border: 0;
        &:active {
          ${disabled ? '' : `background-color: ${ToucanColors.gray[isDarkMode ? 600 : 100]};`}
        }
        ${disabled && disabledButton}
        ${active && !status && !disabled && activeButton}
        ${active && isCorrect && correctButton}
        ${active && isIncorrect && incorrectButton}
      `}
    >
      <Typography
        variant={fontVariant}
        css={css`
          color: ${isDarkMode ? ToucanColors.white : lightModeTextColor};
        `}
      >
        {option}
      </Typography>
    </Box>
  );
};
