/** @jsx jsx */
import React, { forwardRef, PropsWithChildren } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme, alpha } from '@mui/material';

const { Card, useColorScheme, ToucanIcon, Tooltip } = ToucanComponents;

interface ChicletProps {
  className?: string;
  onClick?: () => void;
  onOpenSettings?: () => void;
  afterContent?: React.ReactNode;
}

export const Chiclet = forwardRef<HTMLDivElement, PropsWithChildren<ChicletProps>>(
  ({ children, className, onClick, onOpenSettings, afterContent }, ref) => {
    const { isDarkMode } = useColorScheme();
    const theme = useTheme();
    return (
      <Card
        className={className}
        css={css`
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
          background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
          border: 0;
          border-radius: ${theme.spacing(1)};
          padding: ${onClick ? 0 : theme.spacing(1)};
          cursor: ${onClick ? 'pointer' : 'normal'};
          flex-direction: column;
        `}
        ref={ref}
        hasPadding={false}
      >
        <Box display="flex" flexDirection="row">
          {onOpenSettings ? (
            <React.Fragment>
              <Box
                component="button"
                onClick={onOpenSettings}
                css={css`
                  cursor: pointer;
                  display: inline-flex;
                  align-items: center;
                  border: 0;
                  padding: ${theme.spacing(1)};
                  background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
                  border: 0;
                  border-radius: ${theme.spacing(1)} 0 0 ${theme.spacing(1)};
                  :focus {
                    box-shadow: inset 0 0 0 2px ${alpha(ToucanColors.blue[100], 0.3)};
                    outline: none;
                  }
                  max-width: ${parseInt(theme.spacing(2), 10) + 16}px;
                `}
              >
                <Tooltip title="Settings" width={84}>
                  <Box
                    css={css`
                      margin-top: 1.5px;
                      margin-bottom: 1.5px;
                      width: 16px;
                      height: 16px;
                    `}
                  >
                    <ToucanIcon
                      icon="cog"
                      css={css`
                        width: 16px;
                        height: 16px;
                        color: ${ToucanColors.gray[200]};
                        :hover {
                          color: ${ToucanColors.gray[100]};
                        }
                        :active {
                          color: ${ToucanColors.gray[300]};
                        }
                      `}
                    />
                  </Box>
                </Tooltip>
              </Box>
              <Box
                css={css`
                  width: 1px;
                  height: 35px;
                  background-color: ${isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[100]};
                `}
              />
            </React.Fragment>
          ) : null}
          {onClick ? (
            <Box
              component="button"
              onClick={onClick}
              css={css`
                cursor: pointer;
                border: 0;
                padding: ${theme.spacing(1)};
                background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
                border: 0;
                border-radius: 0 ${theme.spacing(1)} ${theme.spacing(1)} 0;
                :focus {
                  box-shadow: inset 0 0 0 2px ${alpha(ToucanColors.blue[100], 0.3)};
                  outline: none;
                }
              `}
            >
              {children}
            </Box>
          ) : (
            children
          )}
        </Box>
        {afterContent && (
          <Box
            css={css`
              padding: ${theme.spacing(1)};
              padding-top: 0;
            `}
          >
            {afterContent}
          </Box>
        )}
      </Card>
    );
  },
);
