import { atom } from 'jotai';

import { DensityPercentages, DensityValuesPrimitive } from '~/constants/settings';
import { QuizStatus } from '~/types/quiz';
import { PartialBy } from '~/types/utils';

import { setActiveTranslationAtom } from '../useActiveTranslation/atoms';
import { quizAtom, resetQuizAtom } from '../useQuiz/atom';
import { incrementHoversAtom } from '../useSessionStats/atoms';

import { canNodeTransition, isOnPage } from './helpers';
import {
  BuiltTranslatedNode,
  TranslatedNode,
  TranslatedNodePayload,
  TranslatedNodeSource,
  TranslatedNodeStatus,
} from './types';

/**
 * translatedNodesByIdAtom is a map of translated nodes by their id. This is a map for easy access and lookup of nodes by their id.
 */
export const translatedNodesByIdAtom = atom<Record<string, TranslatedNode>>({});
/**
 * translatedNodesAtom is a derived value of the translatedNodesByIdAtom. It is a list of all the translated nodes.
 */
export const translatedNodesAtom = atom<Array<TranslatedNode>>(get => Object.values(get(translatedNodesByIdAtom)));

/**
 * buildTranslatedNodeAtom is a selector that filters out nodes that are not built yet. This pretty much means that they are not pending.
 */
export const builtTranslatedNodesAtom = atom<Array<BuiltTranslatedNode>>(
  get =>
    get(translatedNodesAtom).filter(node => {
      switch (node.status) {
        case TranslatedNodeStatus.Pending:
          return false;
        default:
          return true;
      }
    }) as BuiltTranslatedNode[],
);

/**
 * translatedNodesOnPageAtom is a selector, and a list of nodes that are in a state that should mean that they are on the page.
 */
export const translatedNodesOnPageAtom = atom<Array<BuiltTranslatedNode>>(
  get => get(translatedNodesAtom).filter(node => isOnPage(node)) as Array<BuiltTranslatedNode>,
);

/**
 * pendingTranslatedNodesAtom is a selector, and a list of translated nodes with the pending state.
 */
export const pendingTranslatedNodesAtom = atom<Array<TranslatedNode>>(get =>
  get(translatedNodesAtom).filter(node => node.status === TranslatedNodeStatus.Pending),
);

/**
 * mountedTranslatedNodesAtom is a selector, and a list of translated nodes with the mounted state.
 */
export const mountedTranslatedNodesAtom = atom<Array<BuiltTranslatedNode>>(
  get => get(translatedNodesAtom).filter(node => node.status === TranslatedNodeStatus.Mounted) as BuiltTranslatedNode[],
);

/**
 * errorTranslatedNodesAtom is a selector, and a list of translated nodes with the error state.
 */
export const erroredTranslatedNodesAtom = atom<Array<TranslatedNode>>(get =>
  get(translatedNodesAtom).filter(node => node.status === TranslatedNodeStatus.Error),
);

/**
 * addTranslatedNodesAtom is a setter that allows for adding TranslatedNode atoms. This will create new translated nodes
 * with a pending state by default. A status may be passed in to override the default, we currently do this for onboarding.
 */
export const addTranslatedNodesAtom = atom<unknown, Array<PartialBy<TranslatedNode, 'status'>>>(
  null,
  (get, set, maybeNewTranslatedNodes) => {
    const translatedNodesById = get(translatedNodesByIdAtom);
    maybeNewTranslatedNodes.forEach(translatedNode => {
      if (!translatedNodesById[translatedNode.id]) {
        translatedNodesById[translatedNode.id] = {
          ...translatedNode,
          status: translatedNode.status ?? TranslatedNodeStatus.Pending,
        };
      }
    });
    set(translatedNodesByIdAtom, { ...translatedNodesById });
  },
);

/**
 * removeTranslatedNodesAtom is a setter that allows for removing TranslatedNode atoms.
 */
export const removeTranslatedNodeAtoms = atom<unknown, (TranslatedNode | TranslatedNodePayload)[]>(
  null,
  (get, set, translatedNodesToRemove) => {
    const translatedNodesById = get(translatedNodesByIdAtom);
    translatedNodesToRemove.forEach(node => {
      delete translatedNodesById[node.id];
    });
    set(translatedNodesByIdAtom, { ...translatedNodesById });
  },
);

/**
 * transitionTranslatedNodeAtom allows us to update atoms in a safe way that takes into account
 * hovers state machines. Since this is the primary way to transition nodes, we added in a override
 * which is mainly here to allow us to transition nodes to a hovered state from a pinned state.
 */
export const transitionTranslatedNodeAtom = atom<
  unknown,
  { translatedNode: TranslatedNodePayload | TranslatedNode; status: TranslatedNodeStatus; override?: boolean }
>(null, (get, set, { translatedNode, status, override }) => {
  if (status !== TranslatedNodeStatus.Hovered && override) {
    throw new TypeError(
      `Override is only available from a "${TranslatedNodeStatus.Hovered}" status, attempted to override to a "${status}" status`,
    );
  }

  const translatedNodesById = get(translatedNodesByIdAtom);
  const unableToTransition = !override && !canNodeTransition(translatedNode.id, status, translatedNodesById);
  const existingTranslatedNode = translatedNodesById[translatedNode.id];

  if (unableToTransition || !existingTranslatedNode) {
    // This is really useful to see and really the extension should not be released with a bunch of these warnings.
    // eslint-disable-next-line no-console
    console.warn(`State transition failed for node ${translatedNode.id} from  ${translatedNode.status} to ${status}`);
    return;
  }

  const { translationId } = existingTranslatedNode ?? {};
  const currentQuiz = get(quizAtom);

  if (
    translationId &&
    !currentQuiz.translationIds.includes(translationId) &&
    currentQuiz.status === QuizStatus.InProgress
  ) {
    set(resetQuizAtom);
  }

  translatedNodesById[translatedNode.id] = {
    ...existingTranslatedNode,
    ...translatedNode,
    status: status ?? TranslatedNodeStatus.Pending,
  };
  set(translatedNodesByIdAtom, { ...translatedNodesById });
});

/**
 * markTranslatedNodeAtom is a setter that allows for marking translated nodes as built.
 * This is special from just the regular transition because it allows us to pass a node to attach
 * to the translated node.
 */
export const markTranslatedNodeAsBuiltAtom = atom<
  unknown,
  { translatedNode: TranslatedNodePayload; node: HTMLSpanElement }
>(null, (get, set, { translatedNode, node }) => {
  set(transitionTranslatedNodeAtom, {
    translatedNode: { ...translatedNode, node },
    status: TranslatedNodeStatus.Built,
  });
});

/**
 * transitionTranslatedNodeToHoverAtom is a setter that allows for transitioning a translated node to the hovered state.
 * This is special from the normal transitions because it allows for overriding the default state transitions. This is
 * used to kick a node out of the Quiz state and into the Hovered state.
 */
export const transitionTranslatedNodeToHoveredAtom = atom<
  unknown,
  { translatedNode: TranslatedNodePayload | TranslatedNode; override?: boolean }
>(null, (get, set, { translatedNode, override }) => {
  // Set active translation on hovered state change
  set(setActiveTranslationAtom, translatedNode);
  set(incrementHoversAtom);
  set(transitionTranslatedNodeAtom, {
    translatedNode,
    status: TranslatedNodeStatus.Hovered,
    override,
  });

  // Reset quiz if needed
  const translatedNodes = get(translatedNodesOnPageAtom);
  const translatedQuizNode = translatedNodes.find(node => node.status === TranslatedNodeStatus.Quiz);
  if (translatedQuizNode && translatedQuizNode.node) {
    set(resetQuizAtom);
  }
});

/**
 * updateTranslatedNodeMetaAtom is a setter that allows for updating the meta data of a translated node. This is
 * primarily used for the `isSaved` flag.
 */
export const updateTranslatedNodeMetaAtom = atom<unknown, { translatedNode: TranslatedNodePayload; meta: any }>(
  null,
  (get, set, { translatedNode, meta }) => {
    const translatedNodesById = get(translatedNodesByIdAtom);
    const existingTranslatedNode = translatedNodesById[translatedNode.id];

    if (!existingTranslatedNode) {
      return;
    }

    translatedNodesById[translatedNode.id] = {
      ...existingTranslatedNode,
      meta: { ...existingTranslatedNode.meta, ...meta },
    };

    set(translatedNodesByIdAtom, { ...translatedNodesById });
  },
);

/**
 * updateTranslatedNodeAtom is a setter that allows for updating the translationNode due to an expanded translation.
 */
export const updateTranslatedNodeAtom = atom<
  unknown,
  { translatedNode: TranslatedNodePayload; replacement: string; targetPhrase: string }
>(null, (get, set, { translatedNode, replacement, targetPhrase }) => {
  const translatedNodesById = get(translatedNodesByIdAtom);
  const existingTranslatedNode = translatedNodesById[translatedNode.id];

  if (!existingTranslatedNode) {
    return;
  }

  translatedNodesById[translatedNode.id] = {
    ...existingTranslatedNode,
    replacement,
    targetPhrase,
    source: TranslatedNodeSource.ExpandTranslation,
  };

  set(translatedNodesByIdAtom, { ...translatedNodesById });
});

/**
 * transitionTranslationByTranslationIdAtom is a way to transition multiple translations at once by their translationId.
 * This is used currently for removing translations when a user marks the translation as inaccurate.
 */
export const transitionTranslationByTranslationIdAtom = atom<
  unknown,
  { translationId: string; status: TranslatedNodeStatus }
>(null, (get, set, { translationId, status }) => {
  const translatedNodes = get(translatedNodesAtom);
  translatedNodes.forEach(translatedNode => {
    if (translatedNode.translationId === translationId) {
      set(transitionTranslatedNodeAtom, {
        translatedNode,
        status,
      });
    }
  });
});

/**
 * updateTranslatedNodeMetaByTranslatedIdAtom just like updating the meta of on translated node, but this allows
 * for multiple nodes to be updated at once. This happens when a user saves, or unsaved a translation.
 */
export const updateTranslatedNodeMetaByTranslatedIdAtom = atom<unknown, { translationId: string; meta: any }>(
  null,
  (get, set, { translationId, meta }) => {
    const translatedNodes = get(translatedNodesAtom);
    const translatedNodesById = get(translatedNodesByIdAtom);

    translatedNodes.forEach(translatedNode => {
      if (translatedNode.translationId !== translationId) {
        return;
      }
      translatedNodesById[translatedNode.id] = {
        ...translatedNode,
        meta: { ...translatedNode.meta, ...meta },
      };
    });

    set(translatedNodesByIdAtom, { ...translatedNodesById });
  },
);

/**
 * deactivateAllTranslatedNodesAtom this is a setter that allows for deactivating all translated nodes by setting them to the inactive state.
 * This is used when the user turns off translations for the page. This makes it look like there is not translations on the page.
 */
export const deactivateAllTranslatedNodesAtom = atom<unknown, void>(null, (get, set) => {
  const translatedNodesOnPage = get(translatedNodesOnPageAtom);
  translatedNodesOnPage.forEach(translatedNode => {
    set(transitionTranslatedNodeAtom, {
      translatedNode,
      status: TranslatedNodeStatus.Inactive,
    });
  });
});

/**
 * reactivateAllTranslatedNodesAtom reactivates any translations that were previously deactivated. This is used when the user turns Toucan on after they
 * have just turned it off.
 */
export const reactivateAllTranslatedNodesAtom = atom<unknown, { densityLevel: DensityValuesPrimitive }>(
  null,
  (get, set, { densityLevel }) => {
    const translatedNodesOnPage = get(translatedNodesOnPageAtom);
    const percent = DensityPercentages[densityLevel];
    translatedNodesOnPage.forEach(translatedNode => {
      if (translatedNode.densityThreshold >= percent) {
        set(transitionTranslatedNodeAtom, {
          translatedNode,
          status: TranslatedNodeStatus.Idle,
        });
      }
    });
  },
);
