/** @jsx jsx */
import React, { useMemo } from 'react';

import createCache from '@emotion/cache';
import { css, jsx, CacheProvider } from '@emotion/react';
import { FinchProvider } from '@finch-graphql/react';
import { ToucanTheme } from '@jointoucan/toucan-design';
import ScopedCssBaseline from '@mui/material/ScopedCssBaseline';
import { ThemeProvider, Theme } from '@mui/material/styles';

import { toucanPrefix } from '~/constants/html';
import { client } from '~/finch-client';

import { useShadow } from './ShadowProvider';

interface RootProvidersProps {
  withCSSBaseline?: boolean;
}

export const RootProviders: React.FC<RootProvidersProps> = ({ children, withCSSBaseline = true }) => {
  const { head, modals } = useShadow();
  // Add in custom provider for EmotionJS
  // From: https://stackoverflow.com/a/57221293/83916
  // From: https://emotion.sh/docs/cache-provider
  const emotionCache = useMemo(
    () =>
      createCache({
        container: head,
        key: toucanPrefix,
        speedy: true,
        prepend: true,
      }),
    [head],
  );

  const theme: Theme = {
    ...ToucanTheme,
    menuContainerNode: modals,
  };

  return (
    <FinchProvider client={client}>
      <ThemeProvider theme={theme}>
        <CacheProvider value={emotionCache}>
          {!withCSSBaseline ? (
            children
          ) : (
            <ScopedCssBaseline
              css={css`
                background-color: transparent;
              `}
            >
              {children}
            </ScopedCssBaseline>
          )}
        </CacheProvider>
      </ThemeProvider>
    </FinchProvider>
  );
};
