/** @jsx jsx */
import React, { useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { range } from 'lodash';

import { Box } from '~/components/Box';
import { HorizontalLine } from '~/components/shared/HorizontalLine';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';
import { QuizStatus } from '~/types/quiz';

const { Typography, useColorScheme, ToucanIcon, Button } = ToucanComponents;

interface FooterProps {
  buttonCTA: string;
  status: QuizStatus;
  onResetExtraPractice: () => void;
  questionNumber: number;
  totalSteps: number;
  setIsConfettiShown: (value: boolean) => void;
}

export const Footer = ({
  buttonCTA,
  status,
  onResetExtraPractice,
  questionNumber: currentQuestionNumber,
  totalSteps,
  setIsConfettiShown,
}: FooterProps) => {
  const { isDarkMode, textLevel3, textLevel4, textLink } = useColorScheme();
  const { t } = useTranslation();
  const theme = useTheme();
  const [isFeedbackShown, setIsFeedbackShown] = useState(false);
  const feedbackUrl = 'https://info014470.typeform.com/to/A1Z7UiMV';

  const onThumbClick = () => {
    setIsFeedbackShown(true);
  };

  const onFeedbackClick = () => {
    window.open(feedbackUrl);
    onResetExtraPractice();
  };

  const onKeepPracticingClick = () => {
    setIsConfettiShown(false);
    onResetExtraPractice();
  };

  return (
    <React.Fragment>
      <HorizontalLine color={isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[200]} />
      <Box width="100%" p={2}>
        {status === QuizStatus.Finished || status === QuizStatus.Fail ? (
          <Box>
            <Box>
              <Button type="secondary" size="small" onClick={onKeepPracticingClick} fullWidth>
                {buttonCTA}
              </Button>
            </Box>
            {isFeedbackShown ? (
              <Box display="flex" flexDirection="column" alignItems="center" mt={2}>
                <Typography
                  variant="xs"
                  css={css`
                    color: ${textLevel3};
                  `}
                >
                  {t('mindTellingUsMoreShortSurvey')}
                </Typography>
                <Box display="flex" alignItems="center">
                  <Typography
                    variant="xs"
                    isBold
                    css={css`
                      color: ${textLink};
                      display: flex;
                      flex-wrap: wrap;
                      align-items: center;
                      justify-content: center;
                    `}
                  >
                    <Button
                      type="text"
                      onClick={onFeedbackClick}
                      css={css`
                        color: inherit;
                      `}
                    >
                      <Box>
                        {t('shareFeedback')}
                        <ToucanIcon
                          icon="link-out"
                          css={css`
                            color: inherit;
                            width: 10px;
                            height: 10px;
                            margin-left: ${theme.spacing(0.5)};
                          `}
                        />
                      </Box>
                    </Button>
                  </Typography>
                </Box>
              </Box>
            ) : (
              <Box display="flex" justifyContent="center" mt={1}>
                <Typography
                  variant="xs"
                  css={css`
                    color: ${textLevel3};
                  `}
                >
                  {t('wasThisHelpful')}
                </Typography>
                <Box display="flex" alignItems="center" ml={2}>
                  <Typography
                    variant="xs"
                    isBold
                    css={css`
                      color: ${textLink};
                      display: flex;
                      flex-wrap: wrap;
                      align-items: center;
                      justify-content: center;
                    `}
                  >
                    <Button
                      type="text"
                      onClick={onThumbClick}
                      css={css`
                        color: inherit;
                      `}
                    >
                      <Box>
                        <ToucanIcon
                          icon="thumbs-down"
                          css={css`
                            color: inherit;
                            width: 10px;
                            height: 10px;
                            margin-right: ${theme.spacing(1)};
                          `}
                        />
                        {t('no')}
                      </Box>
                    </Button>
                  </Typography>
                </Box>
                <Box display="flex" alignItems="center" ml={2}>
                  <Typography
                    variant="xs"
                    isBold
                    css={css`
                      color: ${textLink};
                      display: flex;
                      flex-wrap: wrap;
                      align-items: center;
                      justify-content: center;
                    `}
                  >
                    <Button
                      type="text"
                      onClick={onThumbClick}
                      css={css`
                        color: inherit;
                      `}
                    >
                      <Box>
                        <ToucanIcon
                          icon="thumbs-up"
                          css={css`
                            color: inherit;
                            width: 10px;
                            height: 10px;
                            margin-right: ${theme.spacing(1)};
                          `}
                        />
                        {t('yes')}
                      </Box>
                    </Button>
                  </Typography>
                </Box>
              </Box>
            )}
          </Box>
        ) : (
          <Box display="flex" justifyContent="space-between" width="100%" height="16px">
            <Box display="flex" alignItems="center">
              <Image src="/images/toucan.svg" width={16} height={16} alt={t('toucanLogo')} />
              <Box ml={2} display="flex" alignContent="center">
                {range(totalSteps).map(step => {
                  const mappedStep = step + 1;
                  const isLastStep = mappedStep === totalSteps;
                  if (mappedStep === currentQuestionNumber) {
                    return (
                      <Box
                        key={step}
                        width="12px"
                        height="12px"
                        borderRadius="50%"
                        bgcolor={isDarkMode ? ToucanColors.teal[400] : ToucanColors.blue[200]}
                        mr={isLastStep ? 0 : 1}
                        boxShadow={`inset 0 0 0 2px ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300]}`}
                      />
                    );
                  }
                  if (mappedStep < currentQuestionNumber) {
                    return (
                      <Box
                        key={step}
                        width="12px"
                        height="12px"
                        borderRadius="50%"
                        bgcolor={isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300]}
                        mr={isLastStep ? 0 : 1}
                      />
                    );
                  }
                  return (
                    <Box
                      key={step}
                      width="12px"
                      height="12px"
                      borderRadius="50%"
                      bgcolor={ToucanColors.gray[isDarkMode ? 400 : 200]}
                      mr={isLastStep ? 0 : 1}
                    />
                  );
                })}
              </Box>
            </Box>
            <Box
              display="flex"
              alignItems="center"
              css={css`
                cursor: pointer;
              `}
              onClick={onResetExtraPractice}
            >
              <ToucanIcon
                icon="times"
                css={css`
                  color: ${textLevel4};
                  width: 12px;
                  height: 12px;
                  margin-right: ${theme.spacing(1)};
                `}
              />
              <Typography
                variant="2xs"
                isBold
                css={css`
                  color: ${textLevel4};
                  letter-spacing: 2px;
                  text-transform: uppercase;
                `}
              >
                {t('quizExit')}
              </Typography>
            </Box>
          </Box>
        )}
      </Box>
    </React.Fragment>
  );
};
