import {
  ContextlessLearningType,
  ContextlessLearningMultipleChoice,
  ContextlessLearningFlashCard,
} from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

export type QuestionsType = ContextlessLearningMultipleChoice | ContextlessLearningFlashCard;

export interface CurrentQuiz {
  status: QuizStatus;
  translationIds: Array<string>;
  results: (QuizStatus.Pass | QuizStatus.Fail)[];
  questions: QuestionsType[];
  question?: QuestionsType;
  errorId: string;
  quizType: ContextlessLearningType;
}

export enum ActionTypes {
  NextQuestion = 'next question',
  FinishedQuiz = 'finished quiz',
  StartQuiz = 'start quiz',
  SubmitQuiz = 'submit quiz',
  QuizError = 'error',
  Reset = 'reset quiz',
}

export interface NextQuestionAction {
  type: ActionTypes.NextQuestion;
  payload: QuizStatus.Pass | QuizStatus.Fail;
}

interface SubmitQuizPayloadTypes {
  status: QuizStatus;
  result: QuizStatus.Pass | QuizStatus.Fail;
}

export interface SubmitQuizAction {
  type: ActionTypes.SubmitQuiz;
  payload: SubmitQuizPayloadTypes;
}

export interface FinishedQuizAction {
  type: ActionTypes.FinishedQuiz;
}

interface StartQuizPayloadTypes {
  translationIds: Array<string>;
  questions: QuestionsType[];
  quizType: ContextlessLearningType;
}

export interface StartQuizAction {
  type: ActionTypes.StartQuiz;
  payload: StartQuizPayloadTypes;
}

export interface QuizErrorAction {
  type: ActionTypes.QuizError;
  payload: string;
}

export interface ResetAction {
  type: ActionTypes.Reset;
  payload: CurrentQuiz;
}

export type AnyAction =
  | NextQuestionAction
  | FinishedQuizAction
  | SubmitQuizAction
  | StartQuizAction
  | QuizErrorAction
  | ResetAction;

export enum ContainerSize {
  Small = 'small',
  Medium = 'medium',
  Large = 'large',
}
