import { useGetAdditionalInformationQuery } from '~/schema/background';

export interface AdditionalInfo {
  lastFetchedDate: number;
  translation: string;
  pronunciation?: string;
}

export const useAdditionalInfo = (id: string) => {
  const { data, refetch, error } = useGetAdditionalInformationQuery({ variables: { id } });

  return {
    lastFetchedDate: 0,
    translation: data?.word?.translation ?? '',
    wordList: data?.word?.additionalInfo?.wordList ?? null,
    isKnown: data?.me?.translation?.isKnown ?? undefined,
    isFluent: data?.word?.additionalInfo?.isFluent ?? undefined,
    progressionScore: data?.me?.translation?.progressionScore ?? undefined,
    pronunciation: data?.word?.additionalInfo?.pronunciation ?? null,
    definition: data?.word?.additionalInfo?.definition ?? undefined,
    refetch,
    error,
  };
};
