import React, { useEffect, useState } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { StorageKey } from '~/constants/storage';
import { useModalStorage } from '~/hooks/useModalStorage';
import { PassOrFail } from '~/hooks/useQuiz/types';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { useRemoveExtraPracticeCacheMutation, useUpdateRecordHomonymProgressionMutation } from '~/schema/background';
import { LearningShortcutSubject } from '~/schema/server';
import { QuizStatus } from '~/types/quiz';

const { useColorScheme, Typography, ProgressBar } = ToucanComponents;

export interface ExtraPracticeProgressionType {
  updatedProgressionScore: number;
  nextLearningShortcutTopic?: string | null;
  hasCompletedShortcut: boolean;
  hasCelebrated: boolean;
  celebrationTitle?: string | null;
  subject: LearningShortcutSubject;
}

interface FinishProps {
  progressionScore: number;
  results: PassOrFail[];
  homonymId: string;
  setIsConfettiShown: (isConfettiShown: boolean) => void;
  currentDomain: string;
  title: string;
}

export const Finish = ({
  progressionScore,
  results,
  homonymId,
  setIsConfettiShown,
  currentDomain,
  title,
}: FinishProps) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { isDarkMode, textLevel1, textLevel2 } = useColorScheme();
  const [endPercentage, setEndPercentage] = useState(0);
  const [updateRecordHomonymProgression] = useUpdateRecordHomonymProgressionMutation();
  const [closingMotivationalCopy, setClosingMotivationalCopy] = useState(t('keepGoingYoureDoingGreat'));
  const [nextTopicCopy, setNextTopicCopy] = useState<string | null>(null);
  const [, setIsShortcutsCelebrationModalOpenRequested] = useModalStorage(
    StorageKey.IsShortcutsCelebrationModalOpenRequested,
    false,
  );
  const [extraPracticeProgression, setExtraPracticeProgression] = useStorage<ExtraPracticeProgressionType>(
    StorageKey.ShortcutsExtraPracticeProgression,
    {
      updatedProgressionScore: 0,
      nextLearningShortcutTopic: undefined,
      hasCompletedShortcut: false,
      hasCelebrated: false,
      celebrationTitle: undefined,
      subject: LearningShortcutSubject.Food,
    },
  );
  const [removeExtraPracticeCache] = useRemoveExtraPracticeCacheMutation();
  const progressionIncrease = 5;

  const getUpdatedRecordHomonymProgressionValue = async ({ increase }: { increase: number }) => {
    const progressionUpdate = await updateRecordHomonymProgression({
      input: [{ homonymRecordId: homonymId, progressionDifference: increase }],
    });

    const {
      progressionScore: updatedProgressionScore,
      nextLearningShortcutTopic,
      hasCompletedShortcut,
      hasCelebrated,
      celebrationTitle,
      subject,
    } = progressionUpdate.data.updateRecordHomonymProgression;

    setEndPercentage(updatedProgressionScore ?? 0);

    setExtraPracticeProgression({
      updatedProgressionScore,
      nextLearningShortcutTopic,
      hasCompletedShortcut,
      hasCelebrated,
      celebrationTitle,
      subject,
    });

    if (updatedProgressionScore === 100 && !hasCompletedShortcut) {
      setClosingMotivationalCopy(t('niceJobYoureGettingGood'));
      setNextTopicCopy(t('upNext', { topicName: nextLearningShortcutTopic ?? '' }));
    }
    if (hasCompletedShortcut) {
      setNextTopicCopy(t('upNext', { topicName: nextLearningShortcutTopic ?? '' }));
      setClosingMotivationalCopy(t('keepUpTheGreatWork'));
    }
  };

  useEffect(() => {
    const hasPassed = results.every(result => result === QuizStatus.Pass);
    if (hasPassed) {
      getUpdatedRecordHomonymProgressionValue({ increase: progressionIncrease });
    } else {
      setEndPercentage(progressionScore);
    }
  }, [results]);

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'Extra Practice Session Completed',
      properties: {
        domain: currentDomain,
        activeShortcut: title,
        progressPercentage: progressionScore,
      },
    });
  }, []);

  /**
   * If the user has completed the shortcut, we may want to open the celebration modal if the user
   * has not already opened it previously. In any case, we want to remove the extra practice cache.
   */
  const checkIfCelebrationIsShown = async () => {
    if (!extraPracticeProgression.hasCelebrated) {
      if (extraPracticeProgression.hasCompletedShortcut) {
        setIsShortcutsCelebrationModalOpenRequested(true);
      } else if (endPercentage === 100 || progressionScore === 100) {
        setIsConfettiShown(true);
      }
    }
    await removeExtraPracticeCache({});
  };

  return (
    <Box
      width="100%"
      height="100%"
      display="flex"
      flexDirection="column"
      alignItems="center"
      mt={2}
      data-testid="extra-practice-finish"
    >
      <Image
        src="/images/learningShortcuts/extraPractice/trophy-sparkle.svg"
        width={40}
        height={40}
        alt={t('trophy')}
      />
      <Box mt={2} width="100%" height="16px">
        <ProgressBar
          barCompleteTransitionOptions={{ duration: 0 }}
          beginPercentage={progressionScore}
          endPercentage={endPercentage}
          barColor={ToucanColors.blue[200]}
          barCompleteTransitionColor="linear-gradient(90deg, #85C3D8 -2.86%, #B1D6BF 107.81%)"
          onAnimationComplete={checkIfCelebrationIsShown}
          barBackgroundColor={isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[100]}
        />
      </Box>
      <Typography
        variant="xs"
        css={css`
          color: ${textLevel1};
          margin-top: ${theme.spacing(2)};
        `}
      >
        {closingMotivationalCopy}
      </Typography>
      {endPercentage === 100 && nextTopicCopy && (
        <Typography
          variant="2xs"
          css={css`
            color: ${textLevel2};
            margin-top: ${theme.spacing(0.5)};
          `}
        >
          {nextTopicCopy}
        </Typography>
      )}
    </Box>
  );
};
