import { atom } from 'jotai';

interface ReferralInfo {
  url: string;
  toucanRewardOffered: string;
}

export const referralAtom = atom<ReferralInfo | null>(null);
export const setReferralAtom = atom<unknown, ReferralInfo>(null, (get, set, referralInfo) => {
  set(referralAtom, referralInfo);
});
