import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useGetCurrentTabIdQuery } from '~/schema/background';
import { SidebarInitialState } from '~/types/sidebar';

export const useSidebar = (defaults: { tabId?: number } = {}) => {
  const { tabId: currentPageTabId } = defaults;
  const { data } = useGetCurrentTabIdQuery();
  const currentTabId = currentPageTabId ?? data?.browser?.tab?.id;
  const [
    { isOpen, tabId, panelName, overlayName, slideName, hydration },
    setSidebarInitialState,
  ] = useStorage<SidebarInitialState>(StorageKey.SidebarInitialState, {
    isOpen: false,
    tabId: currentTabId ?? -1,
  });

  return {
    isOpen: isOpen && tabId === currentTabId,
    openSidebar: (options: Omit<SidebarInitialState, 'tabId' | 'isOpen'>) => {
      // call api to open sidebar
      setSidebarInitialState({ ...options, isOpen: true, tabId: currentTabId ?? -1 });
    },
    closeSidebar: () => {
      setSidebarInitialState({ isOpen: false, tabId: -1 });
    },
    initialState: {
      panelName,
      overlayName,
      slideName,
      hydration,
    },
  };
};
