/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { useQuiz } from '~/hooks/useQuiz';
import { useTranslation } from '~/libs/i18n';
import { QuizStatus } from '~/types/quiz';

const { useColorScheme, ToucanIcon, Typography } = ToucanComponents;

interface InlineQuizProps extends BoxProps {
  onQuizExit: () => void;
  isPremiumUpsellShown?: boolean;
}

/**
 * footer for quiz
 */
export const Footer: FC<InlineQuizProps> = ({ onQuizExit, isPremiumUpsellShown = false, ...boxProps }) => {
  const { isDarkMode, uiLink } = useColorScheme();
  const theme = useTheme();
  const { t } = useTranslation();
  const { currentQuiz, questionNumber } = useQuiz();
  const { status, questions } = currentQuiz;
  const isPageNumberingShown = status === QuizStatus.InProgress && questions.length > 1;

  return (
    <Box
      p={2}
      mt={isPremiumUpsellShown ? 0 : 2}
      ml={-2}
      mr={-2}
      mb={-2}
      display="flex"
      flexDirection="row"
      borderTop={`1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}`}
      {...boxProps}
    >
      <Box display="flex" justifyContent="space-between" width="100%" height="16px">
        <Box display="flex" alignItems="center">
          <Image src="/images/toucan.svg" width={16} height={16} alt={t('toucanLogo')} />
          {isPageNumberingShown && (
            <Typography
              variant="2xs"
              css={css`
                margin-left: ${theme.spacing(2)};
                color: ${ToucanColors.gray[isDarkMode ? 300 : 400]};
              `}
            >
              {t('quizOf', { currentQuestion: questionNumber, totalQuestions: questions.length })}
            </Typography>
          )}
        </Box>
        <Box
          display="flex"
          alignItems="center"
          css={css`
            cursor: pointer;
          `}
          onClick={() => onQuizExit()}
        >
          <ToucanIcon
            icon="times"
            css={css`
              color: ${uiLink};
              width: 12px;
              height: 12px;
              margin-right: ${theme.spacing(1)};
            `}
          />
          <Typography
            variant="2xs"
            isBold
            color={uiLink}
            css={css`
              letter-spacing: 2px;
              text-transform: uppercase;
            `}
          >
            {t('quizExit')}
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
