import { useState, useEffect, useMemo, useRef } from 'react';

import { Aracari } from 'aracari';
import { GraphQLFormattedError } from 'graphql';
import { throttle } from 'lodash';
import { v4 } from 'uuid';

import Configs from '~/configs/config';
import { FeatureName } from '~/constants/features';
import { StorageKey } from '~/constants/storage';
import { isError } from '~/helpers/errors';
import { createToucanSpan, isTextNode, getNodesAddress, isExcludedNode, isHTMLElement } from '~/helpers/htmlHelpers';
import { isChildOfExcludedNodeType } from '~/hooks/useCrawler/excludedNodes';
import { useAddDictionaryEntry } from '~/hooks/useDictionary';
import { PendingTranslation } from '~/hooks/useDictionary/types';
import { useAddDomTargets } from '~/hooks/useDomTargets';
import { useMountedRef } from '~/hooks/useMountedRef';
import { useStorage } from '~/hooks/useStorage';
import { useToast } from '~/hooks/useToast';
import {
  useAddTranslatedNodes,
  useTranslatedNodesById,
  useTranslatedNodeTransitions,
} from '~/hooks/useTranslatedNodes';
import { PendingTranslatedNode, TranslatedNodeSource } from '~/hooks/useTranslatedNodes/types';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslateSelectionMutation, TranslateSelectionMutation } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { useSidebar } from '../Sidebar/useSidebar';

import { HighlightedSelection } from './HighlightedSelection';
import { isInWordLimit, isWord } from './helpers';
import { HighlightToTranslateStatus, UseHighlightToTranslateReturn } from './types';
import { useSetHTTSidebar } from './useHTTSidebar';

export const useHighlightToTranslate = ({ isEnabled }: { isEnabled: boolean }): UseHighlightToTranslateReturn => {
  // Integrations
  const [translateSelection, { error }] = useTranslateSelectionMutation();
  const addTranslations = useAddDictionaryEntry();
  const { setBuilt } = useTranslatedNodeTransitions();
  const addTranslatedNodes = useAddTranslatedNodes();
  const addDomTargets = useAddDomTargets();
  const [isToucanEnabled] = useStorage<boolean>(StorageKey.GlobalEnableState, true);
  const isSidebarEnabled = useFeatureEnabled(FeatureName.HighlightToTranslateSidebar);
  const [, setHighlightToTranslateInfo] = useStorage<{
    data?: TranslateSelectionMutation | null;
    errors?: GraphQLFormattedError[] | null;
  }>(StorageKey.HighlightToTranslateSidebarLastInfo, { data: null, errors: null });
  const { showToast } = useToast();
  const translatedNodesById = useTranslatedNodesById();
  const ref = useRef<Record<string, PendingTranslatedNode> | null>(null);
  // Ref is here to make it possible to lookup current selection translated node ids, without
  // updating the function.
  ref.current = translatedNodesById;

  // NOTE: selection is does not update since it a reference to the same object
  // selectionChange is here to trigger that update
  const [changeAmount, setChangeAmount] = useState(0);
  const [selection, setSelection] = useState<Selection | null>(null);
  const [draggingStopped, setDraggingState] = useState<boolean>(false);
  const setHTTSidebar = useSetHTTSidebar();

  // Derived state
  const highlightSelection = useMemo(() => {
    if (selection) {
      return new HighlightedSelection(selection, id => ref.current?.[id]);
    }
    return null;
  }, [selection, changeAmount]);
  const { openSidebar } = useSidebar();
  const fragment = highlightSelection?.fragment ?? null;
  const canReplace = fragment?.childNodes.length === 1 && isTextNode(fragment.childNodes[0]);
  const overWordLimit = !isInWordLimit(highlightSelection?.textContent ?? '');
  const isValid = isWord(highlightSelection?.textContent ?? '');
  const parentElement = isValid ? highlightSelection?.parentElement ?? null : null;
  const isMounted = useMountedRef();
  const isExcluded = useMemo(() => {
    if (parentElement) {
      return isExcludedNode(parentElement) || isChildOfExcludedNodeType(parentElement);
    }
    return true;
  }, [parentElement]);
  const status = useMemo(() => {
    if (!parentElement) {
      return HighlightToTranslateStatus.Idle;
    }

    const translatable = !isExcluded && isValid && draggingStopped;

    if (canReplace && translatable) {
      return HighlightToTranslateStatus.Translatable;
    }

    if (isSidebarEnabled && !canReplace && translatable) {
      return HighlightToTranslateStatus.TranslatableSidebar;
    }

    return HighlightToTranslateStatus.Processing;
  }, [isValid, draggingStopped, parentElement, isExcluded, canReplace, isSidebarEnabled]);
  const { mapping } = highlightSelection ?? {};

  const onOpenTranslationInSidebar = (text: string) => {
    setHighlightToTranslateInfo({ data: null, errors: null });
    setHTTSidebar({
      pendingTranslateSelectionResp: translateSelection({
        selection: { text },
      }),
      sourcePhrase: text,
      preText: '', // TODO: this needs to be re-implemented
      postText: '',
    });
    openSidebar({
      panelName: PanelName.HighlightToTranslate,
      slideName: SlideName.HighlightToTranslate,
    });
    highlightSelection?.clear();
  };

  const onReplaceTranslation = async ({
    text,
    parentNode,
    textMappings,
  }: {
    text: string;
    parentNode: HTMLElement;
    textMappings: Aracari;
  }) => {
    const id = v4();
    const translationId = v4();
    const address = highlightSelection?.anchorAddress ?? '0';
    const originalNode = mapping?.getNodeByAddress(address) ?? '0';
    const span = createToucanSpan({ id, address });
    span.setAttribute('data-testid', 'highlight-to-translate-inline');

    const [domTarget] = addDomTargets([
      {
        parentNode,
        address: getNodesAddress(parentNode),
        textMappings,
      },
    ]);

    try {
      // This can throw in some pretty weird situations with text fragments in a word.
      // Trying to recover has worse side-effects.
      // TODO: logic of selectedIndex needs to be updated;
      const replacementIndex = highlightSelection?.selectedIndex ?? 0;
      addTranslations({
        [translationId]: { translationId, sourcePhrase: text, targetPhrase: PendingTranslation },
      });
      addTranslatedNodes([
        {
          id,
          node: span,
          translationId,
          address,
          source: TranslatedNodeSource.Highlight,
          replacement: text,
          densityThreshold: 1,
          targetId: domTarget.id,
          replacementIndex,
        },
      ]);
      // TODO: combine this with above
      setBuilt({ id }, span);
    } catch (e) {
      selection?.deleteFromDocument();
      return;
    }

    const revertReplacement = () => {
      const node = mapping?.getNodeByAddress(address);
      // This will usually create three nodes [pretext, translation, post-text];
      const nextNode = node?.nextSibling;
      const lastNode = nextNode?.nextSibling;
      nextNode?.remove();
      lastNode?.remove();
      node?.replaceWith(originalNode);
    };

    let resp: { data?: TranslateSelectionMutation | null; errors?: GraphQLFormattedError[] } | null = null;

    try {
      resp = await translateSelection({
        selection: { text },
      });

      if (resp.errors && resp.errors.length) {
        // This is to handle errors passed back from response,
        // since there is not much of a graph to fail here, having
        // any error means we did not get a translation
        // This will throw to the catch which reverts the replacement
        throw resp.errors[0];
      }
    } catch (e) {
      if (isMounted()) {
        revertReplacement();
        // TODO: Add in error messaging
        if (Configs.isDebugging) {
          showToast({
            image: 'images/toucan-circle.svg',
            onClick: () => {},
            timeout: 30000,
            id: domTarget.id,
            title: 'Unable to translate selection',
            subtitle: isError(e) ? e.message : 'Unknown error',
          });
        }
      }
    }
    const translations = resp?.data?.translateSelection ?? null;

    if (isMounted() && translations && translations.translation) {
      addTranslations({
        [translationId]: {
          translationId,
          sourcePhrase: text,
          targetPhrase: translations.translation,
        },
      });
    }
  };

  const onTranslate = async () => {
    const text = highlightSelection?.expandedTextContent ?? '';

    if (highlightSelection && !overWordLimit) {
      switch (status) {
        case HighlightToTranslateStatus.Translatable:
          if (!parentElement || !isHTMLElement(parentElement) || !mapping) {
            return;
          }
          await onReplaceTranslation({
            text,
            parentNode: parentElement,
            textMappings: mapping,
          });
          break;
        case HighlightToTranslateStatus.TranslatableSidebar:
          onOpenTranslationInSidebar(text);
          break;
        default:
          break;
      }
    }
  };

  const clearSelection = () => {
    highlightSelection?.clear();
  };

  // User behavior with the page
  // Select text then mouse up shows highlight to translate chiclet
  useEffect(() => {
    if (isEnabled && isToucanEnabled) {
      const onSelectionChange = throttle(() => {
        const currentSelection = document.getSelection();
        setChangeAmount(amount => amount + 1);
        if (!currentSelection) {
          setSelection(null);
          return;
        }
        setSelection(currentSelection);
      }, 100);
      document.addEventListener('selectionchange', onSelectionChange);
      return () => document.removeEventListener('selectionchange', onSelectionChange);
    }
    clearSelection();

    return () => {};
  }, [isEnabled, isToucanEnabled]);

  useEffect(() => {
    if (isEnabled && isValid && isToucanEnabled) {
      const onMouseUp = () => {
        setDraggingState(true);
      };
      document.addEventListener('mouseup', onMouseUp);
      return () => document.removeEventListener('mouseup', onMouseUp);
    }
    return () => {};
  }, [isValid, isEnabled, isToucanEnabled]);

  // Reset state after lose of selection
  useEffect(() => {
    if (status === HighlightToTranslateStatus.Idle && draggingStopped) {
      setDraggingState(false);
    }
  }, [status]);

  return {
    selection: highlightSelection,
    status,
    onTranslate,
    error,
    overWordLimit,
    clearSelection,
  };
};
