/* @jsx jsx */
import React, { FC, RefObject, useEffect, useRef, useState } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion, useAnimation } from 'framer-motion';

import { Box } from '~/components/Box';
import { maximumZIndex } from '~/constants/styling';
import { useMountedRef } from '~/hooks/useMountedRef';
import { usePrevious } from '~/hooks/usePrevious';

const { Typography, ToucanIcon } = ToucanComponents;

interface AnimatedPointsProps {
  progressionScore?: number;
  pointsRef: RefObject<HTMLElement>;
}

export const AnimatedPoints: FC<AnimatedPointsProps> = ({ progressionScore, pointsRef }) => {
  const theme = useTheme();
  const previousProgressionScore = usePrevious(progressionScore);
  const [progressionScoreChange, setProgressionScoreChange] = useState(0);
  const elementRef = useRef<HTMLDivElement>(null);
  const isMounted = useMountedRef();

  const controls = useAnimation();

  useEffect(() => {
    if (previousProgressionScore && progressionScore && previousProgressionScore !== progressionScore) {
      const scoreChange =
        progressionScore && previousProgressionScore ? progressionScore - previousProgressionScore : 0;
      setProgressionScoreChange(scoreChange);
    }
  }, [previousProgressionScore, progressionScore]);

  useEffect(() => {
    window.setTimeout(async () => {
      if (!isMounted()) return;

      const rect = pointsRef.current?.getBoundingClientRect();
      const elementRect = elementRef.current?.getBoundingClientRect();

      if (!rect || !elementRect) return;

      const x = rect.x + rect.width / 2 - elementRect.width / 2;
      const y = rect.y - parseInt(theme.spacing(4), 10);

      await controls.start({
        y,
        x,
        opacity: 0,
        transition: {
          delay: 0,
          duration: 0,
        },
      });

      if (!isMounted()) return;

      await controls.start({
        y: y - parseInt(theme.spacing(1), 10),
        x,
        opacity: 1,
        transition: {
          duration: 1,
          ease: 'easeInOut',
        },
      });

      if (!isMounted()) return;

      await controls.start({
        y,
        x,
        opacity: 0,
        transition: {
          delay: 0.5,
          duration: 0.5,
          ease: 'easeOut',
        },
      });
    }, 500);
  }, [progressionScoreChange]);

  return progressionScore !== 100 ? (
    <motion.div
      ref={elementRef}
      key={`${progressionScore}-${progressionScoreChange}`}
      animate={controls}
      initial={{
        opacity: 0,
        position: 'fixed',
        top: 0,
        left: 0,
        zIndex: maximumZIndex + 1,
        userSelect: 'none',
      }}
    >
      {progressionScoreChange > 0 && (
        <Box
          data-testid="animated-points"
          css={css`
            background: ${ToucanColors.blue[100]};
            border-radius: 20px;
            padding: ${theme.spacing(0.5)} ${theme.spacing(1)};
            display: flex;
            align-items: center;
          `}
        >
          <ToucanIcon
            icon="plus"
            css={css`
              width: 6px;
              height: 6px;
              color: ${ToucanColors.blue[400]};
            `}
          />
          <Typography
            variant="2xs"
            isBold
            css={css`
              color: ${ToucanColors.blue[400]};
              margin-left: ${theme.spacing(1)};
            `}
          >
            {progressionScoreChange}
          </Typography>
        </Box>
      )}
    </motion.div>
  ) : null;
};
