import './polyfills';

import React from 'react';

import { BirdCage, ModalError } from '~/components/BirdCage';
import { ContextlessLearningMoment } from '~/components/ContextlessLearningMoment';
import { isValidContextlessMomentUrl } from '~/components/ContextlessLearningMoment/urlAdapter';
import { Crawler } from '~/components/Crawler';
import { DatasetLoadingIndicator } from '~/components/DatasetLoadingIndictor';
import { HighlightToTranslate } from '~/components/HighlightToTranslate';
import { InlineTranslations } from '~/components/InlineTranslations';
import { LanguageChangeDetector } from '~/components/LanguageChangeDetector';
import { ExtraPractice } from '~/components/LearningShortcuts/ExtraPractice';
import { ModalContainer } from '~/components/ModalContainer/ModalContainer';
import { OnboardingChecklistDetector } from '~/components/OnboardingChecklistDetector';
import { PauseDetector } from '~/components/PauseDetector';
import { PauseReminder } from '~/components/PauseReminder';
import { environment } from '~/configs/config';
import { FeatureName } from '~/constants/features';
import { Features } from '~/libs/features/client';
import {
  GetInitialValuesDocument,
  GetInitialValuesQuery,
  GetInitialValuesQueryVariables,
  ClearTabTranslationCountsDocument,
  ClearTabTranslationCountsMutation,
  InvalidateTranslationsMutationVariables,
  InvalidateTranslationsDocument,
  IncrementWebPageVisitMutation,
  IncrementWebPageVisitMutationVariables,
  IncrementWebPageVisitDocument,
  DoesCurrentLanguageVectorSupportShortcutsQuery,
  DoesCurrentLanguageVectorSupportShortcutsQueryVariables,
  DoesCurrentLanguageVectorSupportShortcutsDocument,
  InvalidateTranslationsMutation,
} from '~/schema/background';

import { renderContentApp, ContentApp } from './components/ContentApp';
import { RegistrationPrompt } from './components/LoginSignup/RegistrationPrompt';
import { ToastAfterSignUp } from './components/LoginSignup/ToastAfterSignUp';
import { MessageListener } from './components/MessageListener';
import { SupportedSourceLanguages } from './constants/language';
import { ContentMessageProducer } from './contentMessageProducer';
import { client } from './finch-client';
import CurrentDomainHelpers, { isToucanSite } from './helpers/currentDomainHelpers';
import { setupTransitionHelpers } from './helpers/domHelpers';
import { isEnvError } from './helpers/errors';
import { getSupportedUILanguage } from './helpers/i18n';
import { injectionCheck } from './helpers/injectionCheck';
import { canPlayAudio } from './libs/TextToSpeech';
import { createInstance } from './libs/i18n';
import { validateTranslations } from './libs/webPageValidation';
import ContentMessengers from './messengers/contentMessengers';

// Start the main script on every page load
(async function main() {
  try {
    // Background check if script is injected
    injectionCheck();

    const domainHost = await CurrentDomainHelpers.getCurrentDomainHost();

    const { currentDomain = '' } = domainHost ?? {};

    const { data, errors } = await client.query<GetInitialValuesQuery, GetInitialValuesQueryVariables>(
      GetInitialValuesDocument,
      {
        domain: currentDomain,
      },
    );

    const { data: matrixData } = await client.query<
      DoesCurrentLanguageVectorSupportShortcutsQuery,
      DoesCurrentLanguageVectorSupportShortcutsQueryVariables
    >(DoesCurrentLanguageVectorSupportShortcutsDocument, {});

    const sourceLanguage = (data?.me?.sourceLanguage as SupportedSourceLanguages) ?? getSupportedUILanguage();
    const isGloballyEnabled = !!data?.settings.isGloballyEnabled;
    const { domain } = data?.settings ?? {};
    const i18n = createInstance({ language: sourceLanguage as SupportedSourceLanguages });
    const hydratedTranslations = data?.currentPage?.hydratedTranslations ?? [];
    const isAllowingTracking = data?.settings.isAllowingTrackingAnalytics ?? true;
    const isShortcutsEnabled =
      (await Features.isFeatureEnabled(FeatureName.Shortcuts)) && matrixData?.doesCurrentLanguageVectorSupportShortcuts;
    const isUserRegistered = !!data?.me?.email;
    const { pathname, hash, search } = window.location;

    try {
      validateTranslations(hydratedTranslations);
    } catch (e) {
      const invalidTranslations: string[] = [];
      if (
        isEnvError(e) &&
        e.exceptionInfo &&
        'invalidTranslations' in e.exceptionInfo &&
        Array.isArray(e.exceptionInfo.invalidTranslations)
      ) {
        e.exceptionInfo?.invalidTranslations?.forEach(({ id }: { id: string }) => {
          invalidTranslations.push(id);
        });
      }
      ContentMessengers.captureContentError(e);
      await client.mutate<InvalidateTranslationsMutation, InvalidateTranslationsMutationVariables>(
        InvalidateTranslationsDocument,
        {
          translations: invalidTranslations,
        },
      );
    }

    // Considered analytics tracking
    if (window?.location?.href && isAllowingTracking) {
      await client.mutate<IncrementWebPageVisitMutation, IncrementWebPageVisitMutationVariables>(
        IncrementWebPageVisitDocument,
        {
          input: {
            url: `${window.location.origin}${window.location.pathname}`,
          },
        },
      );
    }

    await i18n.init();

    // NOTE: error logging of these errors happens in the background script.
    const hasErrors = !!(errors && errors.length);
    // We're using hasPermissionError to handle an issue (MEOW-702) that's
    // specific to Chrome. For that reason, we don't need a hasPermissionError
    // check inside src/mobileContent.tsx.
    const hasPermissionError = errors?.some(({ extensions }) => extensions?.code === 'PERMISSION_ERROR');

    if (!hasPermissionError && (!isGloballyEnabled || hasErrors)) {
      return;
    }

    ContentMessengers.pageLoadEvent();

    // Reference #toucan for edge cases by preventing the content app from initializing repeatedly when scrolling.
    const isToucanIdPresent = !!document.getElementById('toucan');

    // Temp disables Toucan on Print
    window.addEventListener('beforeprint', async () => {
      await ContentMessageProducer.toggleExtensionForPrintEvent({ enabled: false });
    });
    window.addEventListener('afterprint', async () => {
      await ContentMessageProducer.toggleExtensionForPrintEvent({ enabled: true });
    });

    if (!isToucanIdPresent) {
      renderContentApp(
        <ContentApp
          i18n={i18n}
          supportsAudio={await canPlayAudio()}
          isDomainBlacklisted={hasPermissionError || !!domain?.isBanned}
          experiments={data?.experiments}
          content={
            <>
              {isUserRegistered && (
                <BirdCage errorComponent={ModalError}>
                  <PauseReminder />
                </BirdCage>
              )}
              <ModalContainer />
            </>
          }
          pageEnabledContent={
            <>
              {isToucanSite() && (
                <BirdCage errorComponent={() => null}>
                  <MessageListener />
                </BirdCage>
              )}
              {isValidContextlessMomentUrl({
                currentDomain,
                pathname,
                hash,
                search,
              }) &&
                !isShortcutsEnabled && (
                  <BirdCage errorComponent={() => null}>
                    <ContextlessLearningMoment currentDomain={currentDomain} />
                  </BirdCage>
                )}
              <BirdCage errorComponent={() => null}>
                <RegistrationPrompt />
              </BirdCage>
              <BirdCage errorComponent={() => null}>
                <HighlightToTranslate />
              </BirdCage>
              {isUserRegistered && (
                <>
                  <BirdCage errorComponent={() => null}>
                    <LanguageChangeDetector />
                  </BirdCage>
                  <BirdCage errorComponent={() => null}>
                    <PauseDetector />
                  </BirdCage>
                  <BirdCage errorComponent={() => null}>
                    {environment !== 'production' && <DatasetLoadingIndicator />}
                  </BirdCage>
                  <BirdCage errorComponent={() => null}>
                    <Crawler currentDomain={currentDomain} />
                  </BirdCage>
                  <BirdCage errorComponent={() => null}>
                    <OnboardingChecklistDetector />
                  </BirdCage>
                  <BirdCage errorComponent={ModalError}>
                    <InlineTranslations currentDomain={currentDomain} />
                  </BirdCage>
                  <BirdCage errorComponent={() => null}>
                    <ToastAfterSignUp />
                  </BirdCage>
                </>
              )}
            </>
          }
          pageEnabledContentWithNoPageValidationCheck={
            <>
              {isValidContextlessMomentUrl({
                currentDomain,
                pathname,
                hash,
                search,
              }) &&
                isShortcutsEnabled && (
                  <BirdCage errorComponent={() => null}>
                    <ExtraPractice currentDomain={currentDomain} />
                  </BirdCage>
                )}
            </>
          }
        />,
      );
      setupTransitionHelpers();
      // Clears the current tab translation counts on a new page load
      await client.mutate<ClearTabTranslationCountsMutation>(ClearTabTranslationCountsDocument);
    }
  } catch (error) {
    ContentMessengers.captureContentError(error);
  }
})();
