/** @jsx jsx */
import { useEffect } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useToast } from '~/hooks/useToast';
import { useTranslation } from '~/libs/i18n';
import { useGetCurrentTabIdQuery, useOpenSettingsMutation } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

const { useColorScheme, Button, Typography } = ToucanComponents;

type ToastType = {
  eventType: string;
  title: string;
  amountCompleted: number;
  image: string;
};

export const OnboardingChecklistDetector = () => {
  const { t } = useTranslation();
  const { showToast, removeToast } = useToast();
  const [toastInfo, setToastInfo] = useStorage<null | ToastType>(StorageKey.Toast, null);
  const { isDarkMode } = useColorScheme();
  const { data, loading } = useGetCurrentTabIdQuery();
  const currentTabId = data?.browser?.tab?.id ?? -1;
  const [openSettings] = useOpenSettingsMutation();

  useEffect(() => {
    if (toastInfo?.eventType === 'onboardingChecklist' && !loading) {
      const { amountCompleted, title, image } = toastInfo;

      showToast({
        id: 'onboardingChecklistDetectorToast',
        title: (
          <Typography
            font="primary"
            isBold
            variant="xs"
            css={css`
              color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
            `}
          >
            {title}
          </Typography>
        ),
        subtitle: (
          <Box display="flex" flexDirection="column">
            <Box flexGrow={1}>
              <Typography
                font="primary"
                variant="xs"
                css={css`
                  color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
                `}
              >
                {t(amountCompleted === 100 ? 'youveCompletedToDoList' : 'wellDoneYouCompleted', { amountCompleted })}
              </Typography>
            </Box>
            <Box
              flexGrow={1}
              onClick={() => {
                openSettings({
                  tabId: currentTabId,
                  panelName: PanelName.Notifications,
                  slideName: SlideName.OnboardingChecklist,
                });
                removeToast('onboardingChecklistDetectorToast');
              }}
            >
              <Typography font="primary" variant="xs">
                <Button type="text">{t(amountCompleted === 100 ? 'viewList' : 'viewProgress')}</Button>
              </Typography>
            </Box>
          </Box>
        ),
        image,
        onClick: () => {
          openSettings({
            tabId: currentTabId,
            panelName: PanelName.Notifications,
            slideName: SlideName.OnboardingChecklist,
          });
        },
        timeout: 10000,
        subtitleColor: ToucanColors.gray[isDarkMode ? 200 : 500],
        isAnimation: true,
      });
      setToastInfo(null); // applying this to clear out the useStorage cache before removal
      browser.storage.local.remove(StorageKey.Toast);
    }
  }, [toastInfo, loading]);

  return null;
};
