import React, { useEffect, useRef } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';

import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useToast } from '~/hooks/useToast';
import { useTranslation } from '~/libs/i18n';

import { Box } from '../Box';

const { Typography, useColorScheme } = ToucanComponents;

/**
 * allow only a toast to appear on the subscription selection page immediately after sign up and not allow for any additional extension elements
 * to execute such as inline translations and htt.
 */
export const ToastAfterSignUp = () => {
  const [shouldShowToastAfterSignUp, setShouldShowToastAfterSignUp] = useStorage(
    StorageKey.ShouldShowToastAfterAccountCreation,
    false,
  );
  const { showToast } = useToast();
  const { t } = useTranslation();
  const hasShownToast = useRef<boolean>(false);
  const { textLevel2 } = useColorScheme();

  useEffect(() => {
    if (shouldShowToastAfterSignUp && !hasShownToast.current) {
      setShouldShowToastAfterSignUp(false);
      hasShownToast.current = true;
      showToast({
        id: 'showToastAccountCreation',
        body: (
          <Box>
            <Typography variant="sm" isBold color={textLevel2}>
              {t('youreLoggedInAndReadyToGo')}
            </Typography>
          </Box>
        ),
        image: 'images/toucan-circle.svg',
        onClick: () => window.location.reload(),
        timeout: 10000,
      });
    }
  }, [shouldShowToastAfterSignUp]);

  return null;
};
