/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

const { useColorScheme } = ToucanComponents;

export interface HorizontalLineProps {
  heightPixels?: number;
  color?: string;
}

export const HorizontalLine: FC<HorizontalLineProps> = ({ heightPixels = 1, color }) => {
  const { isDarkMode } = useColorScheme();
  const horizontalLineColor = color || (isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[200]);

  return (
    <hr
      css={css`
        background-color: ${horizontalLineColor};
        width: 100%;
        height: ${heightPixels}px;
        border: 0;
        margin: 0;
      `}
    />
  );
};
