/*
    BirdCage component.
   ---------
   BirdCage is React Error Boundary that wraps around components to make sure errors in a component
   does not to crash our entire application. 

   Read more about Error Boundaries here.
   https://reactjs.org/docs/error-boundaries.html

   If a component misbehaves, errors out, or squawks a lot. The BirdCage stops rendering that component
   and renders a fallback component that is passed into the BirdCage via the **errorComponent** prop.

   BirdCage also is hooked into Sentry via our background process. BirdCage sends the error to the background process
   and logs the relevant information to Sentry with the tag **extension_env=content**.
*/

import React, { ErrorInfo } from 'react';

import Config from '~/configs/config';
import ContentMessengers from '~/messengers/contentMessengers';

export interface ErrorProps {
  error?: Error;
  errorId?: string;
}

interface BirdCageProps {
  // TODO: we might want to pass the error id down to that renderer
  errorComponent: React.FC<ErrorProps>;
}

interface BirdCageState {
  error?: Error;
  hasError: boolean;
  errorId?: string;
}

export class BirdCage extends React.Component<BirdCageProps, BirdCageState> {
  constructor(props: BirdCageProps) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error: Error) {
    // Update state so the next render will show the fallback UI.
    return { hasError: true, error };
  }

  async componentDidCatch(error: Error, errorInfo: ErrorInfo) {
    if (Config.env === 'development' || Config.isDebugging) {
      // eslint-disable-next-line no-console
      console.error(error);
      // eslint-disable-next-line no-console
      console.error(errorInfo);
    } else {
      const errorId = await ContentMessengers.captureContentError({
        message: error.message,
        stack: error.stack,
        info: { domain: window.location.hostname, ...errorInfo },
      });
      this.setState({ errorId });
    }
  }

  render() {
    const { hasError, error, errorId } = this.state;
    const { children, errorComponent: ErrorComponent } = this.props;
    if (hasError) {
      // You can render any custom fallback UI
      return <ErrorComponent error={error} errorId={errorId} />;
    }

    return children;
  }
}

// Error components meant to work with BirdCage
// BirdCage handles all the logging right now.
export const ModalError: React.FC = () => null;

export const createTranslationErrorComponent = (word: string): React.FC => () => <>{word}</>;
