import browser from 'webextension-polyfill';

import Configs, { developmentURL } from '~/configs/config';

export const getDomainFromHostName = (hostName: string) => String(hostName).replace(/^(https?:\/\/)?(www\.)?/, '');

export interface DomainHost {
  currentDomain: string;
  pathName: string;
  port: string;
  href: string;
  protocol: string;
}

export const getDomainInfoFromUrl = (url: string) => {
  const { hostname, pathname, port, href, protocol } = new URL(url);
  const currentDomain = getDomainFromHostName(hostname);
  return { currentDomain, pathName: pathname, port, href, protocol };
};

export const getActiveTabDomainHost = async (): Promise<DomainHost> => {
  const tabs = await browser.tabs.query({ active: true, lastFocusedWindow: true, windowType: 'normal' });
  const tab = tabs[0];
  const url = tab && tab.url ? tab.url : '';

  return getDomainInfoFromUrl(url);
};

const getCurrentDomainHost = async (): Promise<DomainHost | null> => {
  try {
    const activeTabDomain = await getActiveTabDomainHost();
    return activeTabDomain;
  } catch (err) {
    // If we are in the content script, we have to ask for the windows location
    try {
      return getDomainInfoFromUrl(window.location.href);
    } catch (error) {
      return null;
    }
  }
};

const forbiddenKeywords = [
  '/auth/',
  '.auth',
  'billing',
  'cart',
  'checkout',
  'enroll',
  'login',
  'password',
  'personal',
  'register',
  'secure',
  'settings',
  'security',
  'signin',
  'sign-in',
  'sign_in',
  'signup',
  'sign-up',
  'sign_up',
  'wallet',
];

// checks if url contains keywords that are typically associated with inputting sensitive information (ie. checkout, login)
const isCurrentUrlRestricted = async () => {
  // using indexOf is slightly more performant vs regex (`.test()`) and match (`.match()`)
  const { pathname } = new URL(window.location.href.toLowerCase());
  const isUrlRestricted = forbiddenKeywords.some(word => {
    const indexOfOccurrence = pathname.indexOf(word);
    if (indexOfOccurrence === -1) {
      return false;
    }
    return true;
  });
  return isUrlRestricted;
};

export const isToucanSite = (passedURL?: string) => {
  const { href } = document.location;
  const url = passedURL || href;
  let isUrlOurs = url.includes(Configs.siteURL);

  // This allows us to run the crawler on development sites
  if (Configs.isDebugging) {
    isUrlOurs = isUrlOurs || url.includes(developmentURL);
  }

  return isUrlOurs;
};

export default {
  getCurrentDomainHost,
  getDomainFromHostName,
  isCurrentUrlRestricted,
};
