/** @jsx jsx */
import { FC, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { maximumZIndex } from '~/constants/styling';
import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { useHostPermissions } from '~/hooks/useHostPermissions/useHostPermissions';
import { useSessionStats } from '~/hooks/useSessionStats';
import { useDomainUpsellSeenMutation, useGetDomainPermissionUpsellQuery } from '~/schema/background';

import { ModalBaseProps, ModalType } from '../ModalContainer/types';
import { FadeIn } from '../shared/FadeIn';
import Header from '../shared/Header';
import { Image } from '../shared/Image';

const INJECTION_AMOUNT = 3;

interface DomainPermissionUpsellProps extends ModalBaseProps {}

const { Card, Typography, useColorScheme, Button } = ToucanComponents;

export const DomainPermissionUpsell: FC<DomainPermissionUpsellProps> = ({
  selectedModalType,
  onRequestClose,
  onRequestOpen,
}) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { currentDomain } = useCurrentDomain();
  const { hoveredAmount } = useSessionStats();

  const domainPattern = `https://*.${currentDomain}/*`;
  const { data, loading, error, refetch } = useGetDomainPermissionUpsellQuery({
    variables: { domain: currentDomain, domainPattern },
    skip: !currentDomain,
  });
  const [domainUpsellSeen] = useDomainUpsellSeenMutation();
  const { requestPermissions } = useHostPermissions({ hosts: [domainPattern] });

  const isShowing = selectedModalType === ModalType.DomainPermissionUpsell;
  const userHasInteracted = hoveredAmount >= 2;
  const userHasInjectedMultipleTimes = (data?.settings.domain?.injectionAmount ?? 0) > INJECTION_AMOUNT;
  const alreadyShown = data?.settings.domain?.sawPermissionUpsell ?? false;
  const needsPermissionToPage = data?.browser?.permissions === false;

  const closeModal = async () => {
    try {
      await domainUpsellSeen({ domain: currentDomain });
      await refetch();
    } catch (e) {
      // Ignore error
    }
    onRequestClose(ModalType.DomainPermissionUpsell);
  };

  // Show if domain does not have permissions
  // Show if it the highlight has been hovered
  // Local state to not show again...
  useEffect(() => {
    const isLoaded = !isShowing && !loading && needsPermissionToPage;
    const showModal = isLoaded && !alreadyShown && userHasInteracted && userHasInjectedMultipleTimes;
    if (showModal) {
      onRequestOpen(ModalType.DomainPermissionUpsell);
    }
  }, [
    alreadyShown,
    loading,
    userHasInteracted,
    isShowing,
    needsPermissionToPage,
    userHasInjectedMultipleTimes,
    currentDomain,
  ]);

  if (error) {
    return null;
  }

  const textColor = isDarkMode ? ToucanColors.white : ToucanColors.gray[500];
  const textColorSecondary = isDarkMode ? ToucanColors.white : ToucanColors.gray[500];

  return (
    <FadeIn show={isShowing}>
      <Card
        hasPadding={false}
        isDarkMode={isDarkMode}
        css={css`
          position: fixed;
          z-index: ${maximumZIndex};
          right: ${theme.spacing(2)};
          top: ${theme.spacing(2)};
          width: 344px;
          padding: 0;
          display: flex;
          align-items: flex-start;
          overflow: hidden;
        `}
      >
        <Header onRequestClose={closeModal} isMobileReferralShown={false} onMobileReferralClick={() => {}} />
        <div
          css={css`
            padding: ${theme.spacing(4)} ${theme.spacing(4)};
          `}
        >
          <Image
            src="/images/browser-window.svg"
            alt="Browser window"
            width="56px"
            height="56px"
            css={css`
              margin-bottom: ${theme.spacing(2)};
            `}
          />
          <Typography
            variant="md"
            font="secondary"
            isBold
            css={css`
              padding-bottom: ${theme.spacing(1)};
              color: ${textColor};
            `}
          >
            Keep using Toucan on this site?
          </Typography>
          <Typography
            variant="xs"
            color={textColorSecondary}
            css={css`
              padding-bottom: ${theme.spacing(2)};
            `}
          >
            See translations whenever you visit this site in the future.
          </Typography>
          <Button
            onClick={async () => {
              try {
                await requestPermissions();
                await refetch();
              } catch (e) {
                // Ignore errors
              }
              onRequestClose(ModalType.DomainPermissionUpsell);
            }}
            css={css`
              width: 100%;
            `}
          >
            Allow on this site
          </Button>
        </div>
      </Card>
    </FadeIn>
  );
};
