import { useState, useRef } from 'react';

import { ToucanColors } from '@jointoucan/toucan-design';

import contentMessengers from '~/messengers/contentMessengers';
import { useExpandInlineTranslationMutation } from '~/schema/background';

import { useActiveTranslation } from './useActiveTranslation';
import { useUpdateTranslatedNode } from './useTranslatedNodes';

enum Operator {
  MINUS_ONE = -1,
  PLUS_ONE = 1,
}

interface FindSiblingParams {
  operator: Operator;
}

interface CheckIfGoodForExpansionParams {
  node: HTMLSpanElement;
  childNodes: ChildNode[];
  currentNodeIndex: number;
}

interface UpdateSiblingNodeValueParams {
  phrase: string;
  operator: Operator;
}

const transitionAnimation = [
  {
    backgroundColor: ToucanColors.blue[100],
    borderRadius: '2px',
    boxShadow: `2px 0 0 0 ${ToucanColors.blue[100]}, -2px 0 0 0 ${ToucanColors.blue[100]}`,
    color: 'transparent',
  },
  {
    backgroundColor: ToucanColors.blue[200],
    borderRadius: '2px',
    boxShadow: `2px 0 0 0 ${ToucanColors.blue[200]}, -2px 0 0 0 ${ToucanColors.blue[200]}`,
    color: 'transparent',
  },
  {
    backgroundColor: ToucanColors.blue[100],
    borderRadius: '2px',
    boxShadow: `2px 0 0 0 ${ToucanColors.blue[100]}, -2px 0 0 0 ${ToucanColors.blue[100]}`,
    color: 'transparent',
  },
];

export const useExpandInlineTranslation = () => {
  const [requestExpandedTranslation] = useExpandInlineTranslationMutation();
  const [hasExpanded, setHasExpanded] = useState(false);
  const updateTranslatedNode = useUpdateTranslatedNode();
  const currentActiveTranslation = useActiveTranslation();
  const animationRef = useRef<Animation>();
  const siblingNodesRef = useRef<{
    childNodeMinusOne: ChildNode | null;
    childNodePlusOne: ChildNode | null;
  }>({
    childNodeMinusOne: null,
    childNodePlusOne: null,
  });

  /**
   * Finds the sibling source text of the translation node.
   * @param operator
   * @returns sibling text
   */

  const findSibling = ({ operator }: FindSiblingParams) => {
    // TODO: Punctuations used with regex align when the source language is English. Additional punctuations should be included depending on the source language.
    let updatedText = '';
    if (operator === Operator.MINUS_ONE) {
      const findSentenceEndingPunctuation = /[.!?;]/g;
      const nodeValue = siblingNodesRef.current.childNodeMinusOne?.nodeValue ?? '';
      const maybeIncludesPunctuation = nodeValue?.search(findSentenceEndingPunctuation);
      updatedText = nodeValue.slice(maybeIncludesPunctuation + 1);
      const startingCharactersToAvoid = ', ';
      const isCommaAtFront = updatedText.startsWith(startingCharactersToAvoid);
      if (isCommaAtFront) {
        updatedText = nodeValue.replace(startingCharactersToAvoid, '');
      }
    }
    if (operator === Operator.PLUS_ONE) {
      // NOTE: Parenthesis are included to avoid grabbing text incorrectly [ example: node value --> (e.g. this is a test) & what to avoid grabbing --> (e. ]
      const findSentenceEndingPunctuation = /[(.!?;]/g;
      const nodeValue = siblingNodesRef.current.childNodePlusOne?.nodeValue ?? '';
      const maybeIncludesPunctuation = nodeValue?.search(findSentenceEndingPunctuation);
      updatedText = maybeIncludesPunctuation !== -1 ? nodeValue.slice(0, maybeIncludesPunctuation) : nodeValue;
      const endingCharactersToAvoid = ',';
      const isCommaAtEnd = updatedText.endsWith(endingCharactersToAvoid);
      if (isCommaAtEnd) {
        updatedText = nodeValue.substring(0, -1);
      }
    }
    return updatedText;
  };

  /**
   * Determine if the current node and it's immediate neighbors are good candidates for expanded translations.
   * We don't want to replace links on a page. Also, we don't want to replace spans because it may or may not have been translated.
   * NOTE: nodeType of 3 = text node
   * @param node current node
   * @param childNodes all child nodes of the parent node
   * @param currentNodeIndex index value within childNodes where the current node sits
   * @returns boolean
   */

  const checkIfGoodForExpansion = ({ node, childNodes, currentNodeIndex }: CheckIfGoodForExpansionParams) => {
    const invalidNodes = ['A'];
    // current node
    // at minimum, current node must be a good candidate
    if (invalidNodes.includes(node.parentNode?.nodeName ?? '') || childNodes.length === 1) {
      return false;
    }

    // index - 1 node
    if (currentNodeIndex !== 0 && childNodes[currentNodeIndex - 1].nodeType === 3) {
      siblingNodesRef.current.childNodeMinusOne = childNodes[currentNodeIndex - 1];
    }

    // index + 1 node
    if (currentNodeIndex !== childNodes.length - 1 && childNodes[currentNodeIndex + 1].nodeType === 3) {
      siblingNodesRef.current.childNodePlusOne = childNodes[currentNodeIndex + 1];
    }

    // if all are null then not a good candidate for expansion
    return Object.values(siblingNodesRef.current).some(siblingNode => siblingNode);
  };

  /**
   * Remove source language characters that are now part of the expanded translation and should only be
   * visible on hover/tap. Only updates text nodes. Ignores spans and links.
   * @param phrase the expanded source phrase.
   * @param operator enum of Operator
   */

  const updateSiblingNodeValue = ({ phrase, operator }: UpdateSiblingNodeValueParams) => {
    if (phrase === '') {
      return;
    }
    const sliceStart = operator === Operator.MINUS_ONE ? 0 : 1;
    const sliceEnd = operator === Operator.MINUS_ONE ? -1 : undefined;
    const childNode =
      siblingNodesRef.current[operator === Operator.MINUS_ONE ? 'childNodeMinusOne' : 'childNodePlusOne'];
    if (childNode?.nodeValue) {
      const { nodeValue } = childNode;
      const updatedNodeValue = nodeValue.split(phrase).slice(sliceStart, sliceEnd).join(phrase);
      Object.assign(childNode, { ...childNode, nodeValue: updatedNodeValue });
    }
  };

  /**
   * Expand the translation.
   */

  const maybeExpandTranslation = async () => {
    if (!currentActiveTranslation) {
      return;
    }
    const { node, translationId, replacement } = currentActiveTranslation;
    const currentNodeHtmlId = node.id;
    const childNodes = Array.from(node.parentNode?.childNodes ?? []);
    const currentNodeIndex = childNodes.findIndex(childNode => (childNode as HTMLSpanElement).id === currentNodeHtmlId);
    const isGoodForExpansion = checkIfGoodForExpansion({ node, childNodes, currentNodeIndex });
    if (!isGoodForExpansion) {
      return;
    }
    const beforeTranslation = findSibling({
      operator: Operator.MINUS_ONE,
    });

    const afterTranslation = findSibling({
      operator: Operator.PLUS_ONE,
    });

    // start loading animation
    animationRef.current = node.animate(transitionAnimation, {
      duration: 1000,
      iterations: Infinity,
    });

    const input = {
      sourceSentence: `${beforeTranslation}${replacement}${afterTranslation}`.trim(),
      sourcePhraseInstanceIndex: beforeTranslation.split(`${replacement} `).length - 1,
      sourcePhrase: replacement,
      variationId: translationId,
    };

    contentMessengers.trackEvent({ eventName: 'Expanded translation attempt', properties: {} });

    const { data, errors } = await requestExpandedTranslation({ input });
    const response = errors ? {} : data.expandInlineTranslation;
    const { expandedSourcePhrase, targetPhrase } = response;

    if (errors) {
      contentMessengers.trackEvent({
        eventName: 'Expanded translation failed',
        properties: input,
      });
    }

    if (!expandedSourcePhrase || !targetPhrase) {
      animationRef.current?.cancel();
      return;
    }

    animationRef.current?.cancel();
    updateTranslatedNode({
      translatedNode: currentActiveTranslation,
      replacement: expandedSourcePhrase,
      targetPhrase,
    });

    const splitExpandedSourcePhrase = expandedSourcePhrase.split(replacement);

    if (siblingNodesRef.current.childNodeMinusOne !== null) {
      updateSiblingNodeValue({
        phrase: splitExpandedSourcePhrase[0],
        operator: Operator.MINUS_ONE,
      });
    }
    if (siblingNodesRef.current.childNodePlusOne !== null) {
      updateSiblingNodeValue({
        // not using index 1 because there can be instances where there's nothing on the left hand side of the replacement
        phrase: splitExpandedSourcePhrase[splitExpandedSourcePhrase.length - 1],
        operator: Operator.PLUS_ONE,
      });
    }

    contentMessengers.trackEvent({
      eventName: 'Expanded translation success',
      properties: input,
    });
    setHasExpanded(true);
  };

  return {
    maybeExpandTranslation,
    hasExpanded,
  };
};
