import React, { FC } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { BoxProps, TypographyBox } from '~/components/Box';

const { useColorScheme } = ToucanComponents;

interface TagTheme {
  color: string;
  backgroundColor: string;
}

export const TagThemes: Record<
  'grey' | 'teal' | 'invisible',
  {
    dark: TagTheme;
    light: TagTheme;
  }
> = {
  invisible: {
    dark: {
      color: 'transparent',
      backgroundColor: 'transparent',
    },
    light: {
      color: 'transparent',
      backgroundColor: 'transparent',
    },
  },
  grey: {
    dark: {
      color: ToucanColors.gray[200],
      backgroundColor: ToucanColors.gray[400],
    },
    light: {
      color: ToucanColors.gray[400],
      backgroundColor: ToucanColors.gray[200],
    },
  },
  teal: {
    dark: {
      color: ToucanColors.teal[100],
      backgroundColor: ToucanColors.teal[500],
    },
    light: {
      color: ToucanColors.teal[500],
      backgroundColor: ToucanColors.teal[100],
    },
  },
};

interface TagProps extends Pick<BoxProps, 'm' | 'mt' | 'mb' | 'mr' | 'ml'> {
  theme?: 'grey' | 'teal' | 'invisible';
  children: string | React.ReactNode;
}

export const Tag: FC<TagProps> = ({ children, theme = 'grey', ...boxProps }) => {
  const { isDarkMode } = useColorScheme();
  const { color, backgroundColor } = TagThemes[theme][isDarkMode ? 'dark' : 'light'];

  return (
    <TypographyBox
      variant="2xs"
      px={1}
      color={color}
      bgcolor={backgroundColor}
      display="inline-flex"
      borderRadius="8px"
      css={css`
        text-transform: lowercase;
      `}
      {...boxProps}
    >
      {children}
    </TypographyBox>
  );
};
