import { useEffect } from 'react';

/**
 * useHistoryChange injects a script into the current page, and
 * listens for changes. The script will only inject script once.
 * The passed listener will be called when the history changes of the page.
 * @param listener
 */
export const useHistoryChange = (listener: () => any) => {
  useEffect(() => {
    const id = 'toucan-history-listener';
    const eventName = 'toucan-history-change';
    const script = document.createElement('script');
    script.src = browser.runtime.getURL('/history-change-listener.js');
    script.id = id;

    // Only append script if needed
    if (!document.getElementById(id)) {
      document.head.appendChild(script);
    }

    document.addEventListener(eventName, listener);

    return () => {
      document.removeEventListener(eventName, listener);
    };
  }, [listener]);
};
