import browser from 'webextension-polyfill';

import { MessageKey } from '~/constants/messages';

interface InjectionCheckMessage {
  type: MessageKey.InjectionCheck;
}

interface OtherMessage {
  type?: Exclude<MessageKey, MessageKey.InjectionCheck>;
}

export const injectionCheck = () => {
  // eslint-disable-next-line consistent-return
  const onMessage = (message: InjectionCheckMessage | OtherMessage): Promise<boolean> | void => {
    if (message.type === MessageKey.InjectionCheck) {
      return Promise.resolve(true);
    }
  };
  browser.runtime.onMessage.addListener(onMessage);
  return () => {
    browser.runtime.onMessage.removeListener(onMessage);
  };
};
