import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import {
  SnoozeDuration,
  useAddReminderTimeMutation,
  useGetDomainPausedStateQuery,
  useRemoveReminderMutation,
} from '~/schema/background';

export const usePauseReminder = () => {
  const { currentDomain: domain } = useCurrentDomain();
  const { data, loading, error, refetch } = useGetDomainPausedStateQuery({
    variables: { domain },
    skip: !domain,
  });

  const [addReminderTime] = useAddReminderTimeMutation();
  const [removeReminder] = useRemoveReminderMutation();
  const [removeDomainFromBanlist] = useRemoveReminderMutation();
  // TODO: Needs enable domain mutation

  const showReminder = data?.settings.domain?.showReminder ?? false;
  const hasReminder = data?.settings.domain?.hasReminder ?? false;
  const isBanned = data?.settings.domain?.isBanned ?? false;

  const onReminderTimeChange = async (remind: SnoozeDuration) => {
    await addReminderTime({ remind, domain });
    await refetch();
  };

  const onRemoveReminder = async () => {
    await removeReminder({ domain });
    await refetch();
  };

  const onRemoveFromBanlist = async () => {
    await removeDomainFromBanlist({ domain });
    await refetch();
  };

  return {
    refetch,
    loading,
    error,
    showReminder,
    hasReminder,
    isBanned,
    onRemoveReminder,
    onReminderTimeChange,
    onRemoveFromBanlist,
  };
};
