import React from 'react';

import { css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material/styles';

import { Box } from '~/components/Box';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { HexagonalImage } from '~/components/shared/HexagonalImage';
import { PanelName, SlideName } from '~/types/sidebar';

const { ToucanIcon, Typography } = ToucanComponents;

interface ShortcutWrapperProps {
  badgeImageUrl: string;
  percentCompleted: number;
  title: string;
  width?: string;
}

export const ShortcutWrapper = ({ badgeImageUrl, percentCompleted, title, ...props }: ShortcutWrapperProps) => {
  const theme = useTheme();
  const { openSidebar } = useSidebar();

  return (
    <Box
      display="flex"
      alignItems="center"
      justifyContent="flex-start"
      mt={1}
      data-testid="shortcut-footer-wrapper"
      width={props.width}
    >
      <Box display="flex">
        <HexagonalImage imageUrl={badgeImageUrl} width={56} height={56} />
      </Box>
      <Box display="flex" flexDirection="column" ml={2}>
        <Typography
          variant="xs"
          isBold
          color={ToucanColors.white}
          // NOTE: Although -webkit-box-orient is deprecated, per mdn (https://developer.mozilla.org/en-US/docs/Web/CSS/box-orient), it's still supported by Chrome up through v112.
          // This may extend out further in the future. https://caniuse.com/?search=-webkit-box-orient. Additionally, updates to line clamp are being made in the css working group (
          // https://drafts.csswg.org/css-overflow-3/#propdef--webkit-line-clamp) that may address this issue.
          css={css`
            text-overflow: ellipsis;
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          `}
        >
          {title}
        </Typography>
        <Box display="flex" alignItems="center">
          <Typography variant="2xs" color={ToucanColors.white} isBold>
            <Box
              onClick={() =>
                openSidebar({
                  panelName: PanelName.Progress,
                  slideName: SlideName.ProgressOverview,
                })
              }
              sx={{
                cursor: 'pointer',
              }}
            >
              {percentCompleted}% done
              <span>
                <ToucanIcon
                  icon="chevron-right"
                  width={8}
                  height={8}
                  color={ToucanColors.white}
                  css={css`
                    margin-left: ${theme.spacing(0.5)};
                  `}
                />
              </span>
            </Box>
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
