/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box, BoxProps } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

const { useColorScheme, Typography, Button, ToucanIcon } = ToucanComponents;

interface ReferralCtaProps extends BoxProps {
  onClickReferral: () => void;
  compact?: boolean;
  referralCTAText: string;
}

export const ReferralCta: FC<ReferralCtaProps> = ({ onClickReferral, compact, referralCTAText, ...boxProps }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  const { t } = useTranslation();

  return (
    <Box {...boxProps}>
      <Typography variant={compact ? '2xs' : 'xs'} isBold>
        <Button
          type="text"
          onClick={onClickReferral}
          css={
            isDarkMode &&
            css`
              color: ${ToucanColors.white};
              &:hover {
                color: ${ToucanColors.teal[200]};
              }
              &:active {
                color: ${ToucanColors.teal[400]};
              }
            `
          }
        >
          <Box display="flex" alignItems="center">
            <Image
              src="/images/sidebar/referral/paper-airplane.svg"
              alt={t('paperAirplanes')}
              display="block"
              maxWidth={compact ? '16px' : '20px'}
            />
            <Box component="span" ml={1}>
              {referralCTAText}
            </Box>
            <ToucanIcon
              icon="chevron-right"
              css={css`
                width: 10px;
                height: 10px;
                color: inherit;
                margin-left: ${theme.spacing(0.5)};
                &:hover {
                  color: inherit;
                }
                &:active {
                  color: inherit;
                }
              `}
            />
          </Box>
        </Button>
      </Typography>
    </Box>
  );
};
