export const SidebarFooterTabs: FC<SidebarFooterTabsProps> = ({
  tab,
  activePanelName,
  hoveredPanelName,
  clickedPanelName,
  isNewNotification,
}) => {
  const { isDarkMode } = useColorScheme();
  if (!hasTab(tab)) {
    return null;
  }
  const ButtonIcon = ButtonIcons[tab];
  const isSelected = activePanelName === tab;

  let currentScheme: Pick<ButtonIconsProps, 'foreground' | 'foregroundSecondary'>;
  // Note: precedence order important here
  if (tab === activePanelName) {
    currentScheme = Schemes[isDarkMode ? SchemeName.SelectedDark : SchemeName.SelectedLight];
  } else if (tab === clickedPanelName) {
    currentScheme = Schemes[isDarkMode ? SchemeName.ClickedDark : SchemeName.ClickedLight];
  } else if (tab === hoveredPanelName) {
    currentScheme = Schemes[isDarkMode ? SchemeName.HoveredDark : SchemeName.HoveredLight];
  } else {
    currentScheme = Schemes[isDarkMode ? SchemeName.Dark : SchemeName.Light];
  }

  return (
    <svg width="21" height="21" viewBox="0 0 21 21" fill="none">
      <ButtonIcon {...currentScheme} isNewNotification={isNewNotification} isSelected={isSelected} />
    </svg>
  );
};
