import { useCallback, useEffect, useRef, useState } from 'react';

import noop from 'lodash/noop';

import { ModalInfoProps, ModalType } from './types';

const ModalInfo: Record<ModalType, ModalInfoProps> = {
  [ModalType.Sidebar]: {
    priority: 0,
    canOverride: true,
  },
  [ModalType.MicroFeedback]: {
    priority: 1,
    canOverride: false,
  },
  [ModalType.DomainPermissionUpsell]: {
    priority: 2,
    canOverride: false,
  },
  [ModalType.LanguageNowLive]: {
    priority: 0,
    canOverride: false,
  },
  [ModalType.RemoveWord]: {
    priority: 0,
    canOverride: true,
  },
  [ModalType.ReportError]: {
    priority: 0,
    canOverride: true,
  },
  [ModalType.ExpandTranslation]: {
    priority: 0,
    canOverride: true,
  },
  [ModalType.ShortcutsCelebration]: {
    priority: 0,
    canOverride: true,
  },
  // priority: 2 is going to be for transactional modals like toast, errors
};

export const useModalController = () => {
  const [selectedModalType, selectModalType] = useState<ModalType | null>(null);
  const cleanupFn = useRef(noop);

  const onRequestClose = useCallback((modalType: ModalType) => {
    let didClose = false;
    selectModalType(currentModalType => {
      // Correct modal can be closed.
      // TODO: there is probably more cases here.
      if (modalType === currentModalType) {
        cleanupFn.current();
        cleanupFn.current = noop;
        didClose = true;
        return null;
      }

      didClose = false;
      return currentModalType;
    });

    return didClose;
  }, []);

  const onRequestOpen = useCallback((nextModalType: ModalType, cleanup?: () => void) => {
    let didOpen = false;
    selectModalType(currentModalType => {
      const currentModal = currentModalType ? ModalInfo[currentModalType] : null;

      // No modal open or modal already open;
      if (!currentModal) {
        cleanupFn.current = cleanup ?? noop;
        didOpen = true;
        return nextModalType;
      }

      // Higher priority or can override
      const nextModal = ModalInfo[nextModalType];
      if (currentModal.priority > nextModal.priority || nextModal.canOverride) {
        cleanupFn.current(); // cleanup past modal
        cleanupFn.current = cleanup ?? noop;
        didOpen = true;
        return nextModalType;
      }

      didOpen = false;
      return currentModalType;
    });

    return didOpen;
  }, []);

  // NOTE: call clean on unmount of component
  useEffect(() => () => cleanupFn.current(), []);

  return {
    selectedModalType,
    onRequestOpen,
    onRequestClose,
  };
};
