import React, { FC } from 'react';

import { Pinchpoints } from '~/constants/pinchpoints';
import { useSupportsAudio } from '~/hooks/useSupportAudio';
import { useTranslationCard } from '~/hooks/useTranslationCard';
import { QuizType } from '~/schema/background';

import { WordListCard } from './WordListCard';
import { WordListCardContainerProps } from './types';

export const WordListCardContainer: FC<
  WordListCardContainerProps & {
    hideProgressButton?: boolean;
    onQuizEnter?: (translationId: string, quizType: QuizType) => void;
    isPremiumSubscribed: boolean;
    openSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
  }
> = ({
  onQuizEnter,
  isPremiumSubscribed,
  openSubscribePage,
  hideProgressButton,
  isDisabled,
  isIKnowThisWordDisabled,
  ...props
}) => {
  const supportsAudio = useSupportsAudio();
  const { translationId, sourcePhrase, targetPhrase, isKnown, isSaved, targetLanguage, refetch } = props;
  const translationCard = useTranslationCard({
    translationId,
    sourcePhrase,
    targetPhrase,
    isKnown,
    isSaved,
    hideProgressButton,
    refreshTranslation: () => refetch(),
    openSubscribePage: async () => {
      await openSubscribePage({ pinchpoint: Pinchpoints.SidebarPracticeQuizCta });
    },
    isPremiumSubscribed,
    targetLanguage,
    source: 'sidebar',
    onQuizEnter: id => {
      onQuizEnter?.(id, QuizType.Practice);
    },
  });

  return (
    <WordListCard
      {...props}
      {...translationCard}
      supportsAudio={supportsAudio}
      isDisabled={isDisabled}
      isIKnowThisWordDisabled={isIKnowThisWordDisabled}
    />
  );
};
