const FONTS_ID = 'google-fonts-toucan';

const linkGoogleFonts = () => {
  if (document.getElementById(FONTS_ID)) {
    return;
  }
  const link = document.createElement('link');
  link.id = FONTS_ID;
  link.rel = 'stylesheet';
  link.type = 'text/css';
  link.href = 'https://fonts.googleapis.com/css?family=Karla:400,700|Lora:700&display=swap';
  document.head.appendChild(link);
};

export default { linkGoogleFonts };
