import React, { useState } from 'react';

import { css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { AnimatePresence, motion } from 'framer-motion';

import { Box } from '~/components/Box';
import { noneEmoji } from '~/constants/shortcuts';

const { Typography, useColorScheme, ToucanIcon } = ToucanComponents;

interface CustomizationMenuProps {
  isEmojiCustomizationMenuEnabled: boolean;
  learningShortcutEmoji?: string;
  children: React.ReactNode;
  setIsEmojiCustomizationMenuEnabled: (value: boolean) => void;
  onShortcutEmojiSelection: (emoji: string) => void;
}

const emojiOptions = ['', '', '', '', '', noneEmoji];
const wavingHandSkinTones = ['', '', '', '', '', ''];

export const CustomizationMenu = ({
  children,
  isEmojiCustomizationMenuEnabled,
  learningShortcutEmoji,
  setIsEmojiCustomizationMenuEnabled,
  onShortcutEmojiSelection,
}: CustomizationMenuProps) => {
  const { isDarkMode, backgroundBase, backgroundAccent, textLinkReversed } = useColorScheme();
  const isWavingHandCurrentEmoji = wavingHandSkinTones.includes(learningShortcutEmoji ?? '');
  const [isSkinTonesShown, setIsSkinTonesShown] = useState(false);

  const onEmojiClick = (emoji: string) => {
    if (wavingHandSkinTones.includes(emoji)) {
      setIsSkinTonesShown(true);
    } else {
      setIsEmojiCustomizationMenuEnabled(false);
      onShortcutEmojiSelection(emoji);
    }
  };

  return (
    <Box position="relative">
      <AnimatePresence>
        {isEmojiCustomizationMenuEnabled && (
          <motion.div
            key="customization-menu"
            variants={{
              hidden: { opacity: 0 },
              show: { opacity: 1 },
              exit: { opacity: 0 },
            }}
            initial="hidden"
            animate="show"
            exit="exit"
            transition={{ duration: 0.25, ease: 'easeInOut' }}
          >
            <Box
              position="absolute"
              maxWidth="225px"
              bgcolor={backgroundBase}
              borderRadius="4px"
              box-shadow="0px 12px 36px rgba(22, 22, 22, 0.08)"
              border={`1px solid ${isDarkMode ? 'rgba(112, 112, 112, 0.5)' : ToucanColors.gray[200]}`}
              bottom="39px"
              p={1}
              display="flex"
            >
              {isSkinTonesShown ? (
                <>
                  <Box display="flex" alignItems="center" onClick={() => setIsSkinTonesShown(false)} mr={1}>
                    <ToucanIcon
                      icon="chevron-left"
                      height={14}
                      width={14}
                      css={css`
                        cursor: pointer;
                      `}
                    />
                  </Box>
                  <Box
                    display="grid"
                    gridTemplateColumns="repeat(3, 1fr)"
                    gridTemplateRows="repeat(2, 1fr)"
                    gap="4px"
                    data-testid="skin-tone-options"
                  >
                    {wavingHandSkinTones.map(skinTone => {
                      const isCurrentEmoji = learningShortcutEmoji === skinTone;
                      return (
                        <Box
                          key={skinTone}
                          width="51px"
                          height="42px"
                          border={`1px solid ${isDarkMode ? 'rgba(112, 112, 112, 0.5)' : ToucanColors.gray[200]}`}
                          borderRadius="4px"
                          bgcolor={isCurrentEmoji ? ToucanColors.teal[400] : backgroundBase}
                          display="flex"
                          justifyContent="center"
                          alignItems="center"
                          sx={{
                            cursor: 'pointer',
                            '&:hover': {
                              backgroundColor: isDarkMode ? ToucanColors.teal[500] : ToucanColors.gray[100],
                            },
                            '&:active': {
                              boxShadow: `inset 0 0 0 2px ${ToucanColors.teal[400]}`,
                            },
                          }}
                          onClick={() => {
                            // send mutation directly from here
                            setIsEmojiCustomizationMenuEnabled(false);
                            setIsSkinTonesShown(false);
                            onShortcutEmojiSelection(skinTone);
                          }}
                        >
                          <Box
                            bgcolor={backgroundAccent}
                            width="24px"
                            height="24px"
                            display="flex"
                            alignItems="center"
                            justifyContent="center"
                            borderRadius="16px"
                            fontSize="0.75em"
                          >
                            <Typography>{skinTone}</Typography>
                          </Box>
                        </Box>
                      );
                    })}
                  </Box>
                </>
              ) : (
                <Box
                  display="grid"
                  gridTemplateColumns="repeat(3, 1fr)"
                  gridTemplateRows="repeat(2, 1fr)"
                  gap="4px"
                  data-testid="shortcuts-emoji-options"
                >
                  {emojiOptions.map(emoji => {
                    const isCurrentEmoji =
                      learningShortcutEmoji === emoji ||
                      (wavingHandSkinTones.includes(emoji) && isWavingHandCurrentEmoji) ||
                      (emoji === noneEmoji && !learningShortcutEmoji);
                    return (
                      <Box
                        key={emoji}
                        width="67px"
                        height="42px"
                        border={
                          isCurrentEmoji
                            ? undefined
                            : `1px solid ${isDarkMode ? 'rgba(112, 112, 112, 0.5)' : ToucanColors.gray[200]}`
                        }
                        borderRadius="4px"
                        bgcolor={isCurrentEmoji ? ToucanColors.teal[400] : backgroundBase}
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                        sx={{
                          cursor: 'pointer',
                          '&:hover': {
                            border: `1px solid ${isDarkMode ? 'rgba(112, 112, 112, 0.5)' : ToucanColors.gray[200]}`,
                            backgroundColor: isDarkMode ? ToucanColors.teal[500] : ToucanColors.gray[100],
                          },
                          '&:active': {
                            boxShadow: `inset 0 0 0 2px ${ToucanColors.teal[400]}`,
                          },
                        }}
                        onClick={() => onEmojiClick(emoji)}
                      >
                        <Box
                          bgcolor={backgroundAccent}
                          width="24px"
                          height="24px"
                          display="flex"
                          alignItems="center"
                          justifyContent="center"
                          borderRadius="16px"
                          fontSize="0.75em"
                        >
                          <Typography>{emoji}</Typography>
                        </Box>
                        {isCurrentEmoji && (
                          <Box
                            bgcolor={ToucanColors.teal[500]}
                            width="12px"
                            height="12px"
                            display="flex"
                            alignItems="center"
                            justifyContent="center"
                            borderRadius="16px"
                            ml={1}
                          >
                            <ToucanIcon
                              icon="check"
                              css={css`
                                color: ${textLinkReversed};
                                width: 6px;
                                height: 6px;
                              `}
                            />
                          </Box>
                        )}
                      </Box>
                    );
                  })}
                </Box>
              )}
            </Box>
          </motion.div>
        )}
      </AnimatePresence>
      {children}
    </Box>
  );
};
