import { useEffect, useCallback } from 'react';

import moment from 'moment';

import { StorageKey } from '~/constants/storage';
import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { useStorage } from '~/hooks/useStorage';

import { usePrevious } from './usePrevious';

type DomainEnabledEffect = (value: boolean) => void;

export const useDomainEnabled = () => {
  const [globalEnableState] = useStorage<boolean>(StorageKey.GlobalEnableState, true);
  const [banListedDomains] = useStorage<string[]>(StorageKey.BanlistedDomains, []);
  const [printEventDisabledAt] = useStorage<string>(StorageKey.PrintEventDisabledAt, '');
  // Post print event will clear this if fired, otherwise time based fallback
  const isPrintEventDisabled = moment(printEventDisabledAt).add(15, 'seconds').isAfter(moment());
  const { currentDomain } = useCurrentDomain();
  const isCurrentDomainBanned = banListedDomains.includes(currentDomain);
  const isEnabled = !isCurrentDomainBanned && globalEnableState && !isPrintEventDisabled;

  return isEnabled;
};

export const useDomainEnabledEffect = (fn: DomainEnabledEffect, dependencies: unknown[]) => {
  const isEnabled = useDomainEnabled();
  const cachedFn = useCallback(fn, dependencies);
  const prevIsEnabledState = usePrevious(isEnabled);

  useEffect(() => {
    if (prevIsEnabledState !== isEnabled) {
      cachedFn(isEnabled);
    }
  }, [isEnabled, cachedFn, prevIsEnabledState]);

  return null;
};
