import { FinchMessage } from '@finch-graphql/types';
import { StorageValue } from 'webextension-polyfill';

import { MessageKey, StorageConnectionKeys } from '~/constants/messages';
import { NativeMessageTypes } from '~/constants/nativeMessaging';
import { StorageKey } from '~/constants/storage';

export interface StorageConnectionMessage {
  msg: StorageConnectionKeys;
  key: StorageKey;
  value: StorageValue;
}

export enum BackgroundMessageKey {
  Generic = 'Tanager-message',
}

export interface CaptureContentErrorBody {
  msg: MessageKey.CaptureContentError;
  contentError: {
    message: string;
    stack: string;
    info: Record<string, unknown>;
  };
}

export interface FeatureEnabledMessageBody {
  msg: MessageKey.IsFeatureEnabled;
  feature: string;
}

// TODO: Type message bodys then remove general message;
export type AnyBackgroundMessage =
  | CaptureContentErrorBody
  | FeatureEnabledMessageBody
  | StorageConnectionMessage
  | FinchMessage
  | {
      msg: Exclude<
        MessageKey,
        // NOTE: specific types need to be excluded from here to work
        MessageKey.CaptureContentError | MessageKey.IsFeatureEnabled
      >;
      data: any;
    } // This is the general message
  | {};

export type AnyNativeMessage =
  | {}
  | { type: NativeMessageTypes.PaymentSuccess; data: string }
  | { type: NativeMessageTypes.PaymentError; message?: string };

export type NativeMessageHandler = (message: AnyNativeMessage) => void;
