/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion } from 'framer-motion';

import { Image } from '~/components/shared/Image';
import { useQuiz } from '~/hooks/useQuiz';
import { useTranslation } from '~/libs/i18n';

import { QuizSize, useQuizSize } from './useQuizSize';

const { useColorScheme, Typography, Button } = ToucanComponents;

export const QuizRetry: FC = () => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const { t } = useTranslation();
  const { retryQuiz } = useQuiz();
  const { fontVariant, size, inputPadding } = useQuizSize();
  const headerSize = size === QuizSize.Small ? 'sm' : 'md';
  const imageSize = size === QuizSize.Small ? 64 : 80;

  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
      <Image src="/images/inline/toucan-books.svg" width={imageSize} height={imageSize} alt={t('toucanWithBooks')} />
      <Typography
        isBold
        variant={headerSize}
        css={css`
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[600]};
          margin-top: ${theme.spacing(inputPadding)};
        `}
      >
        {t('quizAlmostThere')}
      </Typography>
      <Typography
        variant={fontVariant}
        css={css`
          color: ${isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]};
        `}
      >
        {t('quizLearningTakesPractice')}
      </Typography>
      <Button
        size={size}
        type="utility"
        onClick={() => retryQuiz()}
        css={css`
          margin-top: ${theme.spacing(inputPadding + 1)};
        `}
      >
        {t('quizTryAgain')}
      </Button>
    </motion.div>
  );
};
