import { useState, useEffect } from 'react';

import DomainHelper, { DomainHost } from '~/helpers/currentDomainHelpers';

// useCurrentDomain is used for getting both the domain in the popup
// and in the main page.
export const useCurrentDomain = () => {
  const [domain, setDomain] = useState<DomainHost>({
    currentDomain: '',
    port: '',
    href: '',
    pathName: '',
    protocol: '',
  });

  const updateDomain = async () => {
    const currentDomain = await DomainHelper.getCurrentDomainHost();

    if (currentDomain && !currentDomain.href.startsWith('chrome-extension://')) {
      setDomain(currentDomain);
    }
  };

  useEffect(() => {
    updateDomain();
  }, []);

  return domain;
};
