import { useMemo } from 'react';

import { useAtom } from 'jotai';

import { hoversAtom, incrementHoversAtom } from './atoms';

export const useIncrementHovers = () => {
  const [, incrementHovers] = useAtom(incrementHoversAtom);
  return incrementHovers;
};

export const useSessionStats = () => {
  const [hoveredAmount] = useAtom(hoversAtom);
  return useMemo(
    () => ({
      hoveredAmount,
    }),
    [hoveredAmount],
  );
};
