/** @jsx jsx */
import React from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';
import { omit } from 'lodash';

import Configs from '~/configs/config';
import { StorageKey } from '~/constants/storage';
import { getElementIdentifier } from '~/helpers/htmlHelpers';
import { useActiveTranslation } from '~/hooks/useActiveTranslation';
import { useDictionaryEntries } from '~/hooks/useDictionary';
import { useDomTargets } from '~/hooks/useDomTargets';
import { useStorage } from '~/hooks/useStorage';
import { useTranslatedNodes, useTranslatedNodeTransitions } from '~/hooks/useTranslatedNodes';
import { TranslatedNodeStatus } from '~/hooks/useTranslatedNodes/types';

const { Typography, Button } = ToucanComponents;

const DebuggerHeader: React.FC = ({ children }) => (
  <Typography
    css={css`
      position: sticky;
      top: 0;
      color: ${ToucanColors.white};
      background-color: ${ToucanColors.gray[400]};
      padding: 8px 24px;
      z-index: 2;
    `}
    isBold
  >
    {children}
  </Typography>
);

interface DebuggerButtonProps {
  status?: TranslatedNodeStatus;
  node: HTMLSpanElement;
  id: string;
  onClick?: () => void;
}

const DebuggerButton: React.FC<DebuggerButtonProps> = ({
  children,
  status = TranslatedNodeStatus.Idle,
  node,
  id,
  onClick = () => {},
}) => {
  const { setPinned, setIdle } = useTranslatedNodeTransitions();
  return (
    <Button
      size="small"
      onClick={() => {
        if (status === TranslatedNodeStatus.Pinned) {
          setIdle({ node, id });
          return;
        }
        if (status !== TranslatedNodeStatus.Error && status !== TranslatedNodeStatus.Inactive) {
          setPinned({ node, id });
        }

        onClick();
        // eslint-disable-next-line no-console
        console.log(node);
        node.scrollIntoView({ block: 'center', behavior: 'smooth' });
      }}
      css={css`
        border-radius: 0 !important;
      `}
    >
      {children}
    </Button>
  );
};

interface DebuggerRowProps {
  secondaryContent?: React.ReactNode;
}

const DebuggerRow: React.FC<DebuggerRowProps> = ({ children, secondaryContent }) => (
  <Box
    display="flex"
    flexDirection="row"
    alignItems="center"
    css={css`
      border-bottom: 1px solid ${ToucanColors.gray[500]};
      z-index: 1;
    `}
  >
    <Box
      flex="1"
      css={css`
        padding: 4px 24px;
      `}
    >
      {children}
    </Box>
    <Box flex="0" alignSelf="flex-start">
      {secondaryContent}
    </Box>
  </Box>
);

export const Debugger: React.FC = () => {
  const [isDebuggerOpen, setDebuggerOpen] = useStorage<boolean>(StorageKey.Debugger, false);
  const translations = useDictionaryEntries();
  const translatedNodes = useTranslatedNodes();
  const { targets: domTargets } = useDomTargets();
  const current = useActiveTranslation();

  const activeTranslationInfo = translations[current?.translationId ?? ''];

  if (!Configs.isDebugging) {
    return null;
  }

  window.toucanDebug = (flag: boolean) => {
    setDebuggerOpen(flag);
  };

  if (!isDebuggerOpen || !domTargets) {
    return null;
  }

  return (
    <div
      css={css`
        position: fixed;
        bottom: 12px;
        right: 12px;
        border-radius: 8px;
        z-index: 9999999;
        background-color: ${ToucanColors.gray[500]};
        max-height: 300px;
        width: 300px;
        overflow: scroll;
      `}
    >
      <div
        css={css`
          position: relative;
        `}
      >
        <DebuggerHeader>Active translation</DebuggerHeader>
        <DebuggerRow
          secondaryContent={
            current && (
              <DebuggerButton node={current.node} id={current.id} status={current.status}>
                {current.status === TranslatedNodeStatus.Pinned ? 'Unpin' : 'Pin'}
              </DebuggerButton>
            )
          }
        >
          <Typography
            css={css`
              color: ${ToucanColors.white};
            `}
          >
            <strong>{activeTranslationInfo?.targetPhrase || 'none'}</strong>
            <span> </span>
            {current && <em>{current?.status}</em>}
          </Typography>
        </DebuggerRow>
        <DebuggerHeader>{`Nodes (${translatedNodes.length})`}</DebuggerHeader>
        {translatedNodes
          .map(node => ({
            info: translations[node?.translationId ?? ''],
            id: node?.id,
            node,
            status: node.status,
          }))
          .map(({ info, id, node, status }) => (
            <DebuggerRow
              key={id}
              secondaryContent={
                node &&
                node.node &&
                node?.id && (
                  <DebuggerButton
                    node={node.node}
                    id={node.id}
                    status={status}
                    onClick={() => {
                      // eslint-disable-next-line no-console
                      console.table(omit(node, 'node'));
                    }}
                  >
                    {status === TranslatedNodeStatus.Pinned ? 'Unpin' : 'Pin'}
                  </DebuggerButton>
                )
              }
            >
              <Typography
                key={id}
                css={css`
                  color: ${ToucanColors.white};
                `}
              >
                <strong>{`${info?.targetPhrase}`}</strong>
                <span> </span>
                <em>{status}</em>
              </Typography>
            </DebuggerRow>
          ))}
        <DebuggerHeader>{`Targets (${domTargets.length})`}</DebuggerHeader>
        {domTargets.map(({ parentNode, ...targetProperties }) => {
          const [tagname, ...selector] = getElementIdentifier(parentNode).split(' ');
          return (
            <DebuggerRow key={`target-${targetProperties.id}-${selector}`}>
              <Typography>
                <span
                  css={css`
                    color: ${ToucanColors.blue[100]};
                    text-decoration: underline;
                    cursor: pointer;
                    display: inline-block;
                    width: 250px;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                  `}
                  onKeyDown={() => {}}
                  onClick={() => {
                    // eslint-disable-next-line no-console
                    console.table(targetProperties);
                    // eslint-disable-next-line no-console
                    console.log(parentNode);
                    if (parentNode) {
                      parentNode.scrollIntoView({ block: 'center', behavior: 'smooth' });
                    }
                  }}
                  role="button"
                  tabIndex={0}
                >
                  <strong>{tagname}</strong>
                  <em>{selector.join('')}</em>
                </span>
              </Typography>
            </DebuggerRow>
          );
        })}
      </div>
      <div
        css={css`
          padding-bottom: 24px;
        `}
      />
    </div>
  );
};
