/** @jsx jsx */
import React, { FC, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { useUserInfo } from '~/hooks/useUserInfo';
import { Trans } from '~/libs/i18n/Trans';
import { PracticeType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { isSingleWordQuestion } from '../helpers';
import { QuestionBaseProps } from '../types';
import { useQuizSize } from '../useQuizSize';

import { Button } from './Button';

const { useColorScheme, Typography } = ToucanComponents;

export const Swap: FC<QuestionBaseProps> = ({ question, submitQuestion }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const [hasBeenClicked, setHasBeenClicked] = useState(false);
  const [userSelectedChoice, setUserSelectedChoice] = useState<string | null>(null);
  const { targetLanguage } = useUserInfo();
  const { fontVariant } = useQuizSize();

  if (!question || !isSingleWordQuestion(question)) {
    return null;
  }

  const { answer, options, displayPhrase, questionType } = question;
  const showAudio = questionType === PracticeType.SwapPracticeQuestion;

  const onChoiceClick = (option: string) => {
    if (hasBeenClicked) {
      return;
    }
    setUserSelectedChoice(option);
    setHasBeenClicked(true);
    setTimeout(() => {
      submitQuestion(option === answer ? QuizStatus.Pass : QuizStatus.Fail);
    }, 1000);
  };

  return (
    <Box>
      <Typography
        data-testid="swap-question"
        variant={fontVariant}
        css={css`
          color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
          margin-bottom: ${theme.spacing(2)};
          ${showAudio
            ? `
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            > button {
              margin-right: ${theme.spacing(0.5)};
            }
          `
            : ''}
        `}
      >
        {showAudio ? (
          <Trans
            i18nKey="quizMeans"
            key={displayPhrase}
            values={{ translation: displayPhrase }}
            components={[
              <AudioPlayer language={targetLanguage} targetPhrase={displayPhrase} marginRight={0.5}>
                a
              </AudioPlayer>,
            ]}
          />
        ) : (
          <Trans i18nKey="quizHowDoYouSay" values={{ translation: displayPhrase }} components={[<strong>a</strong>]} />
        )}
      </Typography>
      {options.map((option: string, idx: number) => {
        const isCorrectAnswer = option === answer;
        const formattedString = option.replace(/_canonicalKey /g, '');
        return (
          <Button
            key={option}
            onClick={() => {
              onChoiceClick(option);
            }}
            hasBeenClicked={hasBeenClicked}
            userSelection={{
              isCorrectAnswer,
              isUserSelected: option === userSelectedChoice,
            }}
            index={idx}
            option={formattedString}
          />
        );
      })}
    </Box>
  );
};
