/**
 * shouldShowTranslation is a helper that does the business logic for
 * whether or not to show a translation. This takes into account difficulty weights to
 * help determine this.
 * @param baseValue This is a random number between 0 and 1.
 * @param difficultyWeight This is a offset that is bases on the difficulty of the word and user selected difficulty.
 * @param density this is the users density
 * @returns a boolean value
 */
export const shouldShowTranslation = (baseValue: number, difficultyWeight: number, density: number): boolean => {
  if (baseValue >= 1) {
    return true;
  }
  return baseValue >= density + difficultyWeight;
};
