import { useCallback, useMemo } from 'react';

import { useAtom } from 'jotai';

import { addDictionaryEntriesAtom, dictionaryAtom, removeTranslationsAtom } from './atoms';

export const useDictionaryEntries = () => useAtom(dictionaryAtom)[0];
export const useGetDictionaryEntries = () => {
  const [entries] = useAtom(dictionaryAtom);
  return useCallback(
    (translationId: string) => Object.values(entries).find(entry => entry.translationId.startsWith(translationId)),
    [entries],
  );
};

export const useDictionary = (id: string) => {
  const getEntry = useGetDictionaryEntries();
  return useMemo(() => getEntry(id), [useGetDictionaryEntries, id]);
};

export const useAddDictionaryEntry = () => {
  const [, addDictionaryEntry] = useAtom(addDictionaryEntriesAtom);
  return addDictionaryEntry;
};

export const useRemoveDictionaryEntry = () => {
  const [, removeDictionaryEntry] = useAtom(removeTranslationsAtom);
  return removeDictionaryEntry;
};
