        [PremiumFeatureName.ShowProgress]: {
          numFreeUses: 3,
        },
        [PremiumFeatureName.DifficultySettings]: true,
        [PremiumFeatureName.SaveWord]: true,
        [PremiumFeatureName.RemoveWord]: true,
        [PremiumFeatureName.IKnowThisWord]: true,
        [PremiumFeatureName.ReviewQuiz]: true,
