import { Aracari } from 'aracari';
import uniqWith from 'lodash/uniqWith';

import { getNodeFromAddress, isExcludedNode } from '~/helpers/htmlHelpers';
import {
  TranslateMappingDocument,
  TranslateMappingMutation,
  TranslateMappingMutationVariables,
} from '~/schema/background';

import { TranslationReplacementInElementOptions, ReplacementOptions } from './types';

const findTranslationReplacementInElement = ({
  node,
  mainAddress,
  client,
  excludedTags = [],
}: TranslationReplacementInElementOptions) => {
  const { parentNode, id } = node;
  const textMappings = new Aracari(parentNode);

  // Return if the node is an excluded class or is editable.
  // Can be expanded on for one off cases.
  const knownExcludedClasses = ['SearchFilters_filterContainer'];
  const isExcludedOrEditable = knownExcludedClasses.some(
    excludedClass => parentNode?.classList?.contains?.(excludedClass) || parentNode?.isContentEditable,
  );
  if (isExcludedOrEditable) {
    return {
      parentNode,
      id,
      excluded: true,
    };
  }

  if (!parentNode || !id) {
    return {
      parentNode,
      id,
      excluded: true,
    };
  }

  // NOTE: This takes excluded node logic used from above
  // and applies it here.
  const excludedSubNodes = uniqWith(
    // Grab all addresses of the sub nodes within a parentNode
    textMappings.mapping.flatMap(([, address]) =>
      address
        .split('.')
        .map(n => parseInt(n, 10))
        .slice(0, -1)
        .map((_x, i, siblingAddresses) => siblingAddresses.slice(0, i + 1)),
    ),
    // Remove dupes of those addresses
    (prev, next) => prev.every((x, i) => x === next[i]) && prev.length === next.length,
  )
    // Filter out only the subNodes that are to be excluded.
    .filter(address => {
      const sibling = getNodeFromAddress(address, parentNode);
      return sibling ? isExcludedNode(sibling, excludedTags) : false;
    })
    // Join the address again.
    .map(address => address.join('.'));

  const startTranslation = performance.now();

  // Send the mapping, and excluded nodes to the background script.
  const pendingResp = client.mutate<TranslateMappingMutation, TranslateMappingMutationVariables>(
    TranslateMappingDocument,
    {
      mapping: {
        mapping: textMappings.mapping,
        id,
        excludedSubNodes,
        mainAddress,
      },
    },
  );

  return {
    parentNode,
    id,
    textMappings,
    startTranslation,
    pendingResp,
  };
};

/**
 * findReplacements() Returns updated nodes with either a translation or hover for more if it is available.
 * @param {Object} Object - contains challenges, nodes, and difficulty percentage
 */
export const findReplacements = ({ nodes, mainAddress, client, excludedTags }: ReplacementOptions) => {
  const updates = nodes.map(node =>
    findTranslationReplacementInElement({
      node,
      mainAddress,
      client,
      excludedTags,
    }),
  );

  return updates.filter(update => update !== null);
};
