import { BuiltTranslatedNode } from '~/hooks/useTranslatedNodes/types';
import { TranslationInaccuracyError } from '~/schema/server';

import { useTranslatedNodesActive, useTranslatedNodeTransitions } from './useTranslatedNodes';

export const useHideTranslations = () => {
  const { setInactive } = useTranslatedNodeTransitions();
  const { deactivate, deactivateByTranslationId } = useTranslatedNodesActive();

  /**
   * @param info
   * remove single instance on the page if inaccuracy is contextual
   *
   * remove all instances on the page if inaccuracy is grammatical or misspelled
   * @param setActive
   * @param currentActiveTranslation
   * @param translatedNodes
   */
  const removeTranslationInaccuracy = async (
    info: TranslationInaccuracyError,
    currentActiveTranslation: BuiltTranslatedNode,
  ) => {
    if (info === TranslationInaccuracyError.Context) {
      setInactive(currentActiveTranslation);
    } else if (info === TranslationInaccuracyError.Grammar || info === TranslationInaccuracyError.Misspelling) {
      deactivateByTranslationId(currentActiveTranslation?.translationId);
    }
  };

  return {
    removeTranslationInaccuracy,
    hideAllVisibleTranslations: deactivate,
  };
};
