import { Translation } from '~/types/dictionary';

export enum ActionTypes {
  RemoveTranslations = 'remove translations',
  AddTranslations = 'add translations',
}

export const PendingTranslation = 'pending-translation';

export interface Translations {
  [id: string]: Translation;
}

export interface AddTranslationsAction {
  type: ActionTypes.AddTranslations;
  translations: Translations;
}

export interface RemoveTranslationsAction {
  type: ActionTypes.RemoveTranslations;
  translations: string[];
}

export type AnyAction = AddTranslationsAction | RemoveTranslationsAction;
