import * as Sentry from '@sentry/browser';
import { Dedupe, ExtraErrorData } from '@sentry/integrations';
import { v4 } from 'uuid';
import browser from 'webextension-polyfill';

import { ErrorWithCode, EnvError, ContentError, BackgroundError, PopupError } from './errorClasses';
import { ErrorLoggerOptions, BeforeSendFn, ExtensionEnv, ErrorUser, ExceptionInfo } from './types';

export { ErrorWithCode, EnvError, ContentError, BackgroundError, PopupError } from './errorClasses';

/*

   ErrorLogger is a small wrapper around the Sentry interface.
  --------
  The reason for this little interface is to be able to bake in some 
  defaults that we will like for Toucan, as well as make it so that we are not scattering 
  sentry all over our application.

*/

type AnyErrorType = unknown | Error | ErrorWithCode | EnvError | ContentError | BackgroundError | PopupError;

let environment: string;
let disableDevelopment: boolean;
let customBeforeSend: BeforeSendFn;
let defaultExtensionEnv: ExtensionEnv;
let isDebugging: boolean;

const init = ({
  environment: env,
  dsn,
  version,
  disableDevelopment: disableDev,
  defaultExtensionEnv: extEnv,
  isDebugging: debugging,
}: ErrorLoggerOptions) => {
  // Configure Sentry

  Sentry.init({
    environment: env,
    dsn,
    attachStacktrace: true,
    release: `toucan@v${version || 'unknown'}`,
    // @ts-ignore seems like these types are not great
    integrations: [new ExtraErrorData({ depth: 5 }), new Dedupe()],
    normalizeDepth: 6,
    ignoreErrors: [
      // General server/network issues
      'Failed to fetch',
      'Unexpected token < in JSON at position 0', // Server timeout
      'We are unable to reach our servers',
      // Basic authentication failure ( handled )
      'User was not found',
      'Auth token has expired',
      'Incorrect Email or password',
      'Authentication failed', // General user is not logged in message
      'Email was already taken by another user',
      // Errors thrown globally with no context or info
      'Non-Error promise rejection captured with keys: message',
      'Non-Error exception captured with keys: message',
      //  Old extension version & Spammy Errors
      'No host permissions for cookies at url',
      'Unable to reauthorize or create anonymous user',
      'QuotaExceededError',
      'Toucan is running in issues because we can not access our sites cookies',
      // Chrome application is in bad state.
      'ChromeMethodBFE',
      'Seems like Chrome is running into some issues',
    ],
    beforeSend: handleBeforeSend,
  });
  environment = env;
  disableDevelopment = disableDev ?? true;
  defaultExtensionEnv = extEnv ?? 'background';
  isDebugging = debugging ?? false;
};

const beforeSend = (handler: BeforeSendFn) => {
  customBeforeSend = handler;
};

const setUser = (user: ErrorUser | null) => {
  Sentry.configureScope(scope => {
    scope.setUser(
      user
        ? {
            userId: user.id,
            userEmail: user.email,
          }
        : null,
    );
  });
};

const addTags = (tags: Record<string, string>) => {
  Sentry.configureScope(scope => {
    scope.setTags(tags);
  });
};

const captureException = (error: AnyErrorType, info?: ExceptionInfo) => {
  // NOTE: ErrorWithCode is to send codes to client to handle
  // these are intentionally made
  if (error instanceof ErrorWithCode) {
    // Fake id
    return `tou-${v4}`;
  }

  return Sentry.captureException(error, scope => {
    const env = (error instanceof EnvError ? error.env : info?.extensionEnv) ?? defaultExtensionEnv;
    const exceptionInfo = error instanceof EnvError ? error.exceptionInfo : info;
    if (exceptionInfo) {
      scope.setExtras(exceptionInfo);
    }
    scope.setTag('extension_env', env);
    return scope;
  });
};

const handleBeforeSend = (event: Sentry.Event, hints?: Sentry.EventHint) => {
  // Normalizing the filename to help source mapping.
  // This essentially changes
  // [chrome|safari]-extension://<ID>/static/js/background.js => /static/js/background.js
  const extensionPathSegments = browser.runtime
    .getURL('./')
    .split('/')
    .filter(x => x !== '.');

  if (event.exception && event.exception?.values?.[0]) {
    const value = event.exception.values[0];

    value.stacktrace?.frames?.forEach(frame => {
      const filename = extensionPathSegments.reduce(
        (currentFilename, segment) => currentFilename.replace(segment, '').replace('//', '/'),
        frame.filename ?? '',
      );
      Object.assign(frame, {
        filename,
      });
    });
  }

  if ((disableDevelopment && environment === 'development') || isDebugging) {
    // eslint-disable-next-line no-console
    console.error(event);
    return null;
  }

  if (customBeforeSend) {
    return customBeforeSend(event, hints);
  }
  return event;
};

export const ErrorLogger = {
  addTags,
  captureException,
  beforeSend,
  init,
  setUser,
};
