import { useCallback } from 'react';

import Configs, { browserVendor } from '~/configs/config';
import { Cookies } from '~/constants/cookies';
import { Events } from '~/constants/events';
import { Pinchpoints } from '~/constants/pinchpoints';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  useGetSubscriptionInfoQuery,
  useRequestSafariInAppPaymentsMutation,
  useSetCookieMutation,
  useCreateTabMutation,
} from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';

/**
 * useSubscription is a hook that allows you to get the users subscription
 * status but then also the ability to open the proper In-App payments modal
 * or our subscription page.
 */
export const useSubscription = () => {
  const { data, loading: isDataLoading } = useGetSubscriptionInfoQuery();
  const [setCookie] = useSetCookieMutation();
  const [requestSafariInAppPayments] = useRequestSafariInAppPaymentsMutation();
  const [createTab] = useCreateTabMutation();

  const isPremiumSubscribed = data?.me?.isPremiumSubscribed ?? false;

  const openSubscribePage = useCallback(async ({ pinchpoint }: { pinchpoint: Pinchpoints }) => {
    if (isPremiumSubscribed) {
      return;
    }

    ContentMessengers.trackEvent({
      eventName: Events.SubscriptionCtaClicked,
      properties: {
        pinchpointLocation: pinchpoint,
      },
    });

    try {
      await setCookie({
        name: Cookies.LastPinchPoint,
        value: pinchpoint,
        numDaysActive: 7,
      });
    } catch {
      // Don't block navigation if setting errors for any reason
    }

    if (browserVendor === BrowserVendors.Safari || browserVendor === BrowserVendors.SafariiOS) {
      await requestSafariInAppPayments({
        input: { completionRedirectUrl: `${Configs.siteURL}/subscribe/confirmation` },
      });
      return;
    }

    await createTab({ url: `${Configs.siteURL}/subscribe` });
  }, []);

  return {
    isPremiumSubscribed,
    isDataLoading,
    openSubscribePage,
  };
};
