import { getLanguageInfo, getLanguageByLanguageId, getUpcomingLanguages } from '~/helpers/languageHelpers';
import { useTranslation } from '~/libs/i18n';
import { LanguageId } from '~/schema/server';

import { useUserInfo } from './useUserInfo';

export const useLanguageInfo = () => {
  const { t } = useTranslation();
  const { targetLanguage, sourceLanguage } = useUserInfo();
  const languageInfo = getLanguageInfo(t);
  const upcomingLanguages = getUpcomingLanguages(t);

  // TODO: add coming soon languages
  return {
    languageInfo,
    upcomingLanguages,
    targetLanguageInfo: targetLanguage ? languageInfo[targetLanguage] : null,
    sourceLanguageInfo: sourceLanguage
      ? languageInfo[getLanguageByLanguageId((sourceLanguage as unknown) as LanguageId)]
      : null,
    getLanguageInfoFromLanguageId: (suppliedLanguageId: LanguageId) =>
      suppliedLanguageId ? languageInfo[getLanguageByLanguageId((suppliedLanguageId as unknown) as LanguageId)] : null,
  };
};
