import { toucanPrefix } from '~/constants/html';

import { TranslatedNode, TranslatedNodeStatus } from './types';

export const createElementId = (node: Pick<TranslatedNode, 'address' | 'id'>) =>
  `${toucanPrefix}-${node.address}-${node.id}`;

export const shouldShowTooltip = (translation: TranslatedNode | null) => {
  if (!translation) return false;
  switch (translation.status) {
    case TranslatedNodeStatus.Hovered:
    case TranslatedNodeStatus.Focused:
    case TranslatedNodeStatus.Pinned:
    case TranslatedNodeStatus.Quiz:
      return true;
    default:
      return false;
  }
};

export const isOnPage = (translation: TranslatedNode | null) => {
  if (!translation) return false;
  switch (translation.status) {
    case TranslatedNodeStatus.Inactive:
    case TranslatedNodeStatus.Idle:
    case TranslatedNodeStatus.Hovered:
    case TranslatedNodeStatus.Focused:
    case TranslatedNodeStatus.Pinned:
    case TranslatedNodeStatus.Quiz:
      return true;
    default:
      return false;
  }
};

export const isActive = (translation: TranslatedNode | null) => {
  if (!translation) return false;
  switch (translation.status) {
    case TranslatedNodeStatus.Idle:
    case TranslatedNodeStatus.Hovered:
    case TranslatedNodeStatus.Focused:
    case TranslatedNodeStatus.Pinned:
    case TranslatedNodeStatus.Quiz:
      return true;
    default:
      return false;
  }
};

export const canNodeTransition = (
  id: string,
  status: TranslatedNodeStatus,
  translatedNodes: Record<string, TranslatedNode>,
) => {
  const translatedNode = translatedNodes[id];
  if (!translatedNode) {
    return false;
  }
  switch (status) {
    case TranslatedNodeStatus.Built:
      return translatedNode.status === TranslatedNodeStatus.Pending;
    case TranslatedNodeStatus.Mounted:
      return translatedNode.status === TranslatedNodeStatus.Built;
    case TranslatedNodeStatus.Inactive:
      // If the node is on the page is or is immediately transitioning to inactive;
      return isOnPage(translatedNode) || translatedNode.status === TranslatedNodeStatus.Mounted;
    case TranslatedNodeStatus.Hovered:
      return (
        translatedNode.status !== TranslatedNodeStatus.Focused &&
        translatedNode.status !== TranslatedNodeStatus.Inactive &&
        translatedNode.status !== TranslatedNodeStatus.Pinned &&
        translatedNode.status !== TranslatedNodeStatus.Quiz
      );
    case TranslatedNodeStatus.Idle:
      return (
        translatedNode.status !== TranslatedNodeStatus.Built &&
        translatedNode.status !== TranslatedNodeStatus.Pending &&
        translatedNode.status !== TranslatedNodeStatus.Quiz &&
        translatedNode.status !== TranslatedNodeStatus.Pinned
      );
    case TranslatedNodeStatus.Focused:
      return (
        translatedNode.status !== TranslatedNodeStatus.Pinned && translatedNode.status !== TranslatedNodeStatus.Quiz
      );

    case TranslatedNodeStatus.Pinned:
    case TranslatedNodeStatus.Quiz:
      return isOnPage(translatedNode);

    case TranslatedNodeStatus.Error:
      return true;
    default:
      return false;
  }
};
