import { FC, useEffect } from 'react';

import { StorageKey } from '~/constants/storage';
import { useExpandInlineTranslation } from '~/hooks/useExpandInlineTranslation';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useStorage } from '~/hooks/useStorage';

export const ExpandTranslation: FC = () => {
  const { maybeExpandTranslation, hasExpanded } = useExpandInlineTranslation();

  const [hasDismissedExpandTranslationModal] = useStorage(StorageKey.HasDismissedExpandTranslationModal, false);

  const [isExpandTranslationModalOpenRequested, setIsExpandTranslationModalOpenRequested] = useModalStorage(
    StorageKey.IsExpandTranslationModalOpenRequested,
    false,
  );

  // maybe show modal
  useEffect(() => {
    if (hasDismissedExpandTranslationModal) {
      return;
    }
    setIsExpandTranslationModalOpenRequested(true);
  }, [hasDismissedExpandTranslationModal, isExpandTranslationModalOpenRequested]);

  // maybe extended inline translation
  useEffect(() => {
    if (!hasExpanded && hasDismissedExpandTranslationModal) {
      maybeExpandTranslation();
    }
  }, [hasExpanded, hasDismissedExpandTranslationModal]);

  return null;
};
