import { useEffect, useState } from 'react';

interface UseIntersectionObserverOptions {
  callback: IntersectionObserverCallback;
  root?: Element | null;
  rootMargin?: string;
  threshold?: number;
}

export const visibilityRatio = 0.1;

/**
 * useIntersectionObserver creates a intersection observer that will not
 * update in the scope of a react component.
 * @todo currently values are not updatable on the intersection observer. We need to eventually make this work
 * @param option.callback the function to run when the value a node updates
 * @param option.root the root element of the observer, defaults to null
 * @param option.rootMargin grow or shrink each side of the root element's bounding box before computing intersections.
 * defaults to '0% 0% 50% 0%'. this will grow the bounding box an additional 50% on the bottom of the viewport below the fold.
 * @param option.threshold the amount of the node that is visible in the root to execute the callback. since the default rootMargin is
 * 50% below the viewport the threshold default is set to 1. meaning all of the node must be "visible" within the bounding box
 * before the callback in invoked.
 */
export const useIntersectionObserver = ({ callback, root, rootMargin, threshold }: UseIntersectionObserverOptions) => {
  const [intersectionObserver] = useState(
    () =>
      new window.IntersectionObserver(callback, {
        root: root ?? null,
        rootMargin: rootMargin ?? '300px 0px 300px 0px',
        threshold: threshold ?? visibilityRatio,
      }),
  );

  useEffect(
    () => () => {
      intersectionObserver.disconnect();
    },
    [intersectionObserver],
  );

  return intersectionObserver;
};
