import React, { cloneElement, FC, isValidElement, memo } from 'react';

import HTML from 'html-parse-stringify';

import { useTranslation } from './context';
import { TranslationKeys, Variables } from './types';

interface TransProps {
  components: Array<React.ReactNode>;
  i18nKey: TranslationKeys;
  values?: Variables;
}

/**
 * renderNodes takes and array of components, and a string
 * and replaces ReactNodes.
 * @param components Array of components to replace
 * @param translation String of the translation.
 */
const renderNodes = (components: Array<React.ReactNode>, translation: string) => {
  const ast = HTML.parse(`<t>${translation}</t>`);
  const root = ast[0];
  const nodes = root.children;
  // NOTE: currently we only support one level of nesting.
  const resp: Array<string | React.ReactNode> = nodes.map((node, i) => {
    if (node.type === 'text') {
      return node.content;
    }

    const index = parseInt(node.name, 10);
    const reactNode = components[index];
    if (isValidElement(reactNode)) {
      const textFromTranslation = node.children[0]?.content;
      const props = {
        ...reactNode.props,
        key: `trans-${i}`,
        children: textFromTranslation || reactNode.props.children,
      };
      return cloneElement(reactNode, props);
    }
    return '';
  });
  return resp;
};

/**
 * Trans emulates the Trans component from i18next
 * https://react.i18next.com/latest/trans-component
 * This is a very simple implementation of the component
 * and is missing some implementation details.
 * @param props
 * @param props.components An array of components to replace
 * @param props.i18key String value of the key to lookup in the translation
 * @param props.values Object for values for variables.
 */
export const Trans: FC<TransProps> = memo(({ components, i18nKey, values = {} }) => {
  const { t } = useTranslation();

  const key = i18nKey;
  const translation = t(key, values);

  const content = renderNodes(components, translation);

  return <>{content}</>;
});
