import React from 'react';

import { css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { useUserInfo } from '~/hooks/useUserInfo';

const { useColorScheme, Typography } = ToucanComponents;

interface ConfirmationProps {
  answer: string;
  displayPhrase: string;
}

export const Confirmation = ({ answer, displayPhrase }: ConfirmationProps) => {
  const { textLevel2, textLink } = useColorScheme();
  const { targetLanguage } = useUserInfo();
  const theme = useTheme();

  return (
    <Box
      height="100%"
      width="100%"
      display="flex"
      flexDirection="column"
      justifyContent="center"
      alignItems="center"
      data-testid="example-sentence-confirmation"
    >
      <Typography
        variant="md"
        isBold
        css={css`
          color: ${textLink};
          display: flex;
          align-items: center;
          justify-content: center;
        `}
      >
        <AudioPlayer language={targetLanguage} targetPhrase={answer} marginRight={0.5} key={answer}>
          {answer}
        </AudioPlayer>
      </Typography>
      <Typography
        variant="xs"
        css={css`
          color: ${textLevel2};
          margin-top: ${theme.spacing(0.5)};
        `}
      >
        {displayPhrase}
      </Typography>
    </Box>
  );
};
