/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { AnimatePresence, motion } from 'framer-motion';

export enum SlideDirection {
  Forward = 'forward',
  Backward = 'backward',
}

interface SlidesProps {
  activeSlideName: string;
  activeSlideDirection: SlideDirection;
  collapseHeight?: boolean;
}

/**
 * Container for slides
 * It sets up an animation container to be used to transition slides
 */
export const Slides: FC<SlidesProps> = ({
  activeSlideName,
  activeSlideDirection,
  children,
  collapseHeight = false,
}) => {
  const childrenWithActiveSlideName = React.Children.toArray(children).map(child => {
    if (React.isValidElement(child)) {
      return React.cloneElement(child, { ...child.props, activeSlideName });
    }
    return child;
  });

  return (
    <div
      css={css`
        flex: 1;
        position: relative;
        width: 100%;
        height: ${collapseHeight ? 'auto' : '100%'};
      `}
    >
      <AnimatePresence initial={false} custom={activeSlideDirection}>
        <motion.div
          key={activeSlideName}
          css={css`
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: ${collapseHeight ? 'auto' : '100%'};
          `}
          variants={{
            enter: (slideDirection: SlideDirection) => ({
              x: slideDirection === SlideDirection.Backward ? '-100%' : '100%',
            }),
            center: { x: 0 },
            exit: (slideDirection: SlideDirection) => ({
              x: slideDirection === SlideDirection.Backward ? '100%' : '-100%',
            }),
          }}
          custom={activeSlideDirection}
          initial="enter"
          animate="center"
          exit="exit"
          transition={{ duration: 0.25, ease: 'easeInOut' }}
        >
          {childrenWithActiveSlideName}
        </motion.div>
      </AnimatePresence>
    </div>
  );
};
