/** @jsx jsx */
import React, { FC, useEffect, useRef } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { BirdCage } from '~/components/BirdCage';
import { ModalCard } from '~/components/ModalContainer/ModalCard';
import Header from '~/components/shared/Header';
import { KeyboardShortcut } from '~/constants/keyboardShortcuts';
import { defaultSidebarWidth } from '~/constants/sidebar';
import { StorageKey } from '~/constants/storage';
import { replaceKeyboardCharacters } from '~/helpers/utilityHelpers';
import { useMediaQuery } from '~/hooks/useMediaQuery';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';
import {
  useGetKeyboardShortcutsInfoQuery,
  useGetOnboardingChecklistCompletionAmountQuery,
  useGetInitialSidebarValuesQuery,
} from '~/schema/background';
import { OverlayName, SlideName } from '~/types/sidebar';

import { Box } from '../Box';
import { Footer as QuizFooter } from '../InlineTranslations/InlineQuiz/Footer';
import { UnregisteredFooter } from '../LoginSignup/UnregisteredFooter';
import { UnregisteredHeader } from '../LoginSignup/UnregisteredHeader';

import { HorizontalLine } from './HorizontalLine';
import { PanelSelector } from './PanelSelector';
import { PersistentReferralLink } from './PersistentReferralLink';
import { useSidebarContext } from './SidebarContext';
import { StickyCTA } from './content/Notifications/Checklist/StickyCTA';

const { Typography, useColorScheme, Tooltip, Switch } = ToucanComponents;

interface SidebarLayoutProps {
  isInPopup: boolean;
  onRequestClose: () => void;
  className?: string;
  isExtensionGloballyEnabled: boolean;
  setIsExtensionGloballyEnabled: (enabled: boolean) => Promise<void>;
  isQuizOpen: boolean;
  onQuizExit: () => void;
  hideFooter?: boolean;
  isProgressCtaVisible?: boolean;
  sidebarWidth?: string;
  isPersistentPremiumReferralShown: boolean;
  onPersistentReferralClick: () => void;
  isMobileReferralShown: boolean;
  onMobileReferralClick: () => void;
  isUserRegistered: boolean;
}

/**
 * Creates overall structure of the sidebar
 * It is meant accept a series of panels that each control their own visibility via context
 * @param props
 * @param props.isInPopup A flag to indicate if the sidebar is being shown via the browser action or from the hover container
 * @param props.onRequestClose To close a modal
 */
export const SidebarLayout: FC<SidebarLayoutProps> = ({
  isInPopup,
  children,
  onRequestClose,
  className,
  isExtensionGloballyEnabled,
  setIsExtensionGloballyEnabled,
  isQuizOpen,
  onQuizExit,
  hideFooter,
  isProgressCtaVisible = true,
  sidebarWidth = defaultSidebarWidth,
  isPersistentPremiumReferralShown,
  onPersistentReferralClick,
  isMobileReferralShown,
  onMobileReferralClick,
  isUserRegistered,
}) => {
  const { textLevel1, isDarkMode, backgroundBase } = useColorScheme();
  const { t } = useTranslation();
  const { data } = useGetKeyboardShortcutsInfoQuery();
  const isKeyboardShortcutsEnabled = data ? data.settings.isKeyboardShortcutsEnabled : false;
  const keyboardShortcuts = data ? data.browser?.keyboardShortcuts : null;
  const theme = useTheme();
  const { activeOverlayName, activeSlideName, activePanelName, shouldDisableClickAway } = useSidebarContext();
  const { data: checklistData } = useGetOnboardingChecklistCompletionAmountQuery();
  const { amountCompleted } = checklistData?.onboarding?.checklist ?? { amountCompleted: 0 };
  const [isToastAvailable] = useStorage(StorageKey.Toast, null);
  const { refetch } = useGetInitialSidebarValuesQuery();
  const isViewportBelowSm = useMediaQuery(theme.breakpoints.down('sm'));
  const scrollableBoxRef = useRef<HTMLDivElement | null>(null);
  const shouldDisplayUnregisteredFooter = !isUserRegistered && !isInPopup;
  const isLineVisible = isUserRegistered;

  const onOffShortcut =
    keyboardShortcuts?.filter(shortcut => shortcut.name === KeyboardShortcut.ToggleToucanOnOff)[0]?.shortcut ?? null;

  useEffect(() => {
    if (isToastAvailable) {
      refetch();
    }
  }, [isToastAvailable]);

  useEffect(() => {
    if (amountCompleted < 100) {
      refetch();
    }
  }, [activePanelName]);

  useEffect(() => {
    if (scrollableBoxRef.current && activeSlideName) {
      scrollableBoxRef.current.scrollTo({ top: 0 });
    }
  }, [activeSlideName, scrollableBoxRef]);

  const footerContent =
    activeOverlayName === OverlayName.WordQuiz || (activeOverlayName === OverlayName.PracticeQuiz && isQuizOpen) ? (
      <QuizFooter flex={1} borderTop="none" mt={0} mb={0} ml={0} mr={0} alignItems="center" onQuizExit={onQuizExit} />
    ) : (
      <PanelSelector />
    );

  return (
    <ModalCard
      isInSidebar
      isInPopup={isInPopup}
      className={className}
      onRequestClose={onRequestClose}
      modalWidth={sidebarWidth}
      isUserRegistered={isUserRegistered}
      disableClickAway={shouldDisableClickAway}
      shouldPromptUserToRegister
    >
      <React.Fragment>
        {isPersistentPremiumReferralShown && !isViewportBelowSm && (
          <BirdCage errorComponent={() => null}>
            <PersistentReferralLink onClick={onPersistentReferralClick} />
          </BirdCage>
        )}
        <Box flex="0" width="100%" zIndex="3" data-testid="sidebar-header" position="sticky">
          {!isUserRegistered ? (
            <UnregisteredHeader
              data-testid="enable-extension-unregistered"
              onRequestClose={onRequestClose}
              isDarkMode={isDarkMode}
            />
          ) : (
            <Header
              data-testid="enable-extension"
              onRequestClose={onRequestClose}
              isMobileReferralShown={isMobileReferralShown}
              onMobileReferralClick={onMobileReferralClick}
              isLineVisible={isLineVisible}
              middleContent={
                <Box display="flex" justifyContent="flex-end" alignItems="center">
                  <Typography
                    variant="xs"
                    css={css`
                      color: ${textLevel1};
                    `}
                  >
                    {isExtensionGloballyEnabled ? t('on') : t('paused')}
                  </Typography>
                  {isKeyboardShortcutsEnabled && !isExtensionGloballyEnabled && onOffShortcut ? (
                    <Tooltip
                      title={t('shortcut', { keyCombination: replaceKeyboardCharacters(onOffShortcut ?? '') })}
                      placement="bottom"
                    >
                      {/* wrapper needed on Switch to work with Tooltip */}
                      <Box>
                        <Switch
                          value="turnOnExtension"
                          isChecked={isExtensionGloballyEnabled}
                          onChange={async (event: React.ChangeEvent<HTMLInputElement>) => {
                            await setIsExtensionGloballyEnabled(event.target.checked);
                          }}
                          size="small"
                          css={css`
                            margin-left: ${theme.spacing(1)};
                            margin-right: ${theme.spacing(2)};
                          `}
                        />
                      </Box>
                    </Tooltip>
                  ) : (
                    <Switch
                      value="turnOnExtension"
                      isChecked={isExtensionGloballyEnabled}
                      onChange={async (event: React.ChangeEvent<HTMLInputElement>) => {
                        await setIsExtensionGloballyEnabled(event.target.checked);
                      }}
                      size="small"
                      css={css`
                        margin-left: ${theme.spacing(1)};
                        margin-right: ${theme.spacing(2)};
                      `}
                    />
                  )}
                </Box>
              }
            />
          )}
        </Box>
        <Box
          flex="1"
          width="100%"
          css={css`
            overflow-y: auto;
            overflow-x: hidden;
            ${shouldDisplayUnregisteredFooter ? `margin-bottom: ${theme.spacing(12.5)}` : ''};
          `}
          ref={scrollableBoxRef}
          bgcolor={backgroundBase}
        >
          {children}
        </Box>
        {isLineVisible && <HorizontalLine heightPixels={1} />}
        {hideFooter ||
        !isProgressCtaVisible ||
        amountCompleted === 100 ||
        activeSlideName !== SlideName.MainSettings ||
        activeOverlayName ? null : (
          <StickyCTA amountCompleted={amountCompleted} activePanelName={activePanelName} />
        )}
        {!hideFooter && (
          <Box
            flex="0"
            width="100%"
            zIndex="3"
            position="sticky"
            bottom={0}
            display="flex"
            justifyContent="center"
            css={css`
              min-height: 78px;
            `}
          >
            {footerContent}
          </Box>
        )}
        {shouldDisplayUnregisteredFooter && <UnregisteredFooter isDarkMode={isDarkMode} />}
      </React.Fragment>
    </ModalCard>
  );
};
