import React, { FC } from 'react';

import { crawlerDebug } from '~/configs/config';
import { highlightDebuggingNode } from '~/helpers/htmlHelpers';
import { filterNull } from '~/helpers/utilityHelpers';
import { useUpdateDomTargets } from '~/hooks/useDomTargets';
import { useIntersectionObserver, visibilityRatio } from '~/hooks/useIntersectionObserver';

interface IntersectionObserverProps {
  children: FC<{ intersectionObserver: IntersectionObserver }>;
}

/**
 * IntersectionObserver component creates a IntersectionObserver and passes it the the children
 * of the component. It does this though a Render Prop interface and may eventually get a dedicated hook to pull from
 * context.
 *
 * @param {FC} props.children a render prop child that will receive the intersection observer
 */
export const IntersectionObserver: FC<IntersectionObserverProps> = ({ children }) => {
  const updateNodes = useUpdateDomTargets();
  const intersectionObserver = useIntersectionObserver({
    callback: entries => {
      const updatedNodes = entries
        .map(entry => {
          const parentNode = entry.target as HTMLElement;
          if (parentNode.dataset.toucanCrawled) {
            return null;
          }
          if (crawlerDebug && entry.intersectionRatio > visibilityRatio) {
            highlightDebuggingNode(parentNode, { color: 'green' });
            parentNode.dataset.toucanCrawled = 'true';
            intersectionObserver.unobserve(parentNode);
          }
          return {
            intersectionRatio: entry.intersectionRatio,
            parentNode,
          };
        })
        .filter(filterNull);
      updateNodes(updatedNodes);
    },
  });

  return <>{typeof children === 'function' && children({ intersectionObserver })}</>;
};
