import { useState } from 'react';

import { PremiumFeatureName, premiumFeatureConfig, PremiumExperimentName } from '~/constants/premiumFeatureConfig';
import { StorageKey } from '~/constants/storage';
import { isToucanSite } from '~/helpers/currentDomainHelpers';
import { getFeaturePaywallStatus } from '~/helpers/premiumFeatureStatus';
import { useVariant, ExperimentName } from '~/libs/experiments';
import { useGetActivePremiumExperimentNameQuery } from '~/schema/background';

import { useStorage } from './useStorage';

export const usePremiumExperiment = () => {
  const { data, loading } = useGetActivePremiumExperimentNameQuery({});

  const activePremiumExperimentName = data?.activePremiumExperimentName as ExperimentName;
  const [isToucanSitePage] = useState(() => isToucanSite());
  let experimentName: ExperimentName | null =
    isToucanSitePage || !premiumFeatureConfig[(activePremiumExperimentName as unknown) as PremiumExperimentName]
      ? ExperimentName.ToucanWebsite
      : activePremiumExperimentName;
  let variation: string | null = useVariant(experimentName) || 'control';
  let config = premiumFeatureConfig[(experimentName as unknown) as PremiumExperimentName][variation];

  // If a custom premium experiment config is provided, use that instead of the official one
  // Push Ctrl-Shift-E in the sidebar to edit or clear the custom config
  // Note: This will only work on staging or as an admin on production
  const [customPremiumExperimentConfig] = useStorage<string | null>(StorageKey.CustomPremiumExperimentConfig, null);
  if (customPremiumExperimentConfig) {
    config = JSON.parse(customPremiumExperimentConfig);
    experimentName = null;
    variation = null;
  }

  return {
    experimentName,
    variation,
    config,
    isLoading: loading,
    getFeaturePaywallStatus: (featureName: PremiumFeatureName) => getFeaturePaywallStatus(featureName, config),
  };
};
