import React, { useCallback } from 'react';

import { omit } from 'lodash';

import { PostMessageEvent } from '~/constants/events';
import { SupportedSourceLanguages } from '~/constants/language';
import { isDifficultyTier } from '~/helpers/difficultyTier';
import { sendPostMessage } from '~/helpers/sendPostMessage';
import { useSetActiveTranslation } from '~/hooks/useActiveTranslation';
import { useOnboardingPageCrawler } from '~/hooks/useCrawler';
import { OnboardingDifficultyTierMap } from '~/hooks/useCrawler/useOnboardingPageCrawler';
import { useMount } from '~/hooks/useMount';
import { usePostMessageListener } from '~/hooks/usePostMessageListener';
import { useTranslatedNodesOnPage, useTranslatedNodeTransitions } from '~/hooks/useTranslatedNodes';
import { useTranslation } from '~/libs/i18n';
import { useUpdateDifficultyTierMutation } from '~/schema/background';
import { DifficultyTier } from '~/schema/server';

import { TranslationInfo } from '../InlineTranslations/InlineTranslationCard/types';

/**
 * OnboardingPageCrawler is a component that is called only when the user visits Toucan's
 * onboarding page. It is responsible for crawling the onboarding page for the datasets
 * defined by Toucan within the website content.
 */
export const OnboardingPageCrawler: React.FC = () => {
  const { setIdle, setPinned, setInactive } = useTranslatedNodeTransitions();
  const translatedNodes = useTranslatedNodesOnPage();
  const setActiveTranslation = useSetActiveTranslation();
  const { i18n } = useTranslation();
  const [updateDifficultyTierMutation] = useUpdateDifficultyTierMutation();

  const { crawlPage } = useOnboardingPageCrawler();

  const changeActiveStateOfTranslations = useCallback(
    ({ active, difficultyTier }: { active: boolean; difficultyTier?: DifficultyTier }) => {
      const difficultyTierNumber = difficultyTier && OnboardingDifficultyTierMap[difficultyTier];
      translatedNodes.forEach(translatedNode => {
        // value of 13 reserved for words that should always appear regardless of tier
        const isCorrectDensity =
          translatedNode.densityThreshold === difficultyTierNumber ||
          translatedNode.densityThreshold === 13 ||
          !difficultyTier;
        if (active && isCorrectDensity) {
          setIdle(translatedNode);
        } else {
          setInactive(translatedNode);
        }
      });
    },
    [translatedNodes],
  );

  const updateDifficultyTier = useCallback(
    async (difficultyTier: string | DifficultyTier) => {
      if (!isDifficultyTier(difficultyTier)) return;

      const difficultyTierNumber = OnboardingDifficultyTierMap[difficultyTier];

      // Show the proper difficulty tier in the onboarding page
      translatedNodes.forEach(translatedNode => {
        if (
          translatedNode.densityThreshold === difficultyTierNumber ||
          translatedNode.densityThreshold === OnboardingDifficultyTierMap.all
        ) {
          setIdle(translatedNode);
        } else {
          setInactive(translatedNode);
        }
      });

      await updateDifficultyTierMutation({
        difficultyTier,
      });
    },
    [translatedNodes],
  );

  useMount(() => {
    sendPostMessage(PostMessageEvent.SupportsDifficultyTier);
  });

  // Listen for messages from onboarding page
  usePostMessageListener<{
    type: PostMessageEvent;
    translationInfo: Omit<TranslationInfo, 'node'>;
    sourceLanguageId: SupportedSourceLanguages;
    difficultyTier?: string;
  }>(event => {
    if (!event.data || typeof event.data !== 'object' || !('type' in event.data)) {
      return;
    }
    const { type, translationInfo, sourceLanguageId, difficultyTier = DifficultyTier.Beginner } = event.data;
    switch (type) {
      case PostMessageEvent.ChangeLanguage:
        i18n.changeLanguage(sourceLanguageId);
        break;
      case PostMessageEvent.PinTranslation:
        // For some reason this does not open the translation
        setPinned(omit(translationInfo, 'status'));
        setActiveTranslation(translationInfo);
        break;
      case PostMessageEvent.CloseHover:
        setIdle(omit(translationInfo, 'status'));
        break;
      case PostMessageEvent.RemoveTranslations:
        changeActiveStateOfTranslations({ active: false });
        break;
      case PostMessageEvent.ReactivateTranslations:
        changeActiveStateOfTranslations({
          active: true,
          difficultyTier: (difficultyTier as DifficultyTier) ?? undefined,
        });
        break;
      case PostMessageEvent.CrawlPage:
        crawlPage({ difficultyTier: (difficultyTier as DifficultyTier) ?? undefined });
        break;
      case PostMessageEvent.UpdateDifficultyTier:
        updateDifficultyTier(difficultyTier);
        break;
      default:
        break;
    }
  });

  return null;
};
