/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { useSidebarContext } from '~/components/Sidebar/SidebarContext';
import { Image } from '~/components/shared/Image';
import { Events } from '~/constants/events';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { SlideName, PanelName } from '~/types/sidebar';

const { Typography, Button, ToucanIcon, useColorScheme } = ToucanComponents;

interface StickyCTAProps {
  amountCompleted: number;
  activePanelName: PanelName;
}

export const StickyCTA: FC<StickyCTAProps> = ({ amountCompleted, activePanelName }) => {
  const { t } = useTranslation();
  const { onNavigateSidebar } = useSidebarContext();
  const hasNoProgress = amountCompleted === 0;
  const theme = useTheme();
  const { textAlert, textLink, textLinkActive, textLinkHover, uiAlert } = useColorScheme();

  const navigateToSlide = () => {
    onNavigateSidebar({ panel: PanelName.Notifications, slide: SlideName.OnboardingChecklist });

    contentMessengers.trackEvent({
      eventName: Events.OnboardingChecklistCTAClicked,
      properties: {
        panel: activePanelName,
      },
    });
  };

  return (
    <Box display="flex" width="100%" p={2} justifyContent="flex-start" bgcolor={uiAlert}>
      <Box display="flex" alignItems="center">
        <Image src="/images/sparkle-40x40.svg" width={40} height={40} alt={t('sparkle')} />
        <Box display="flex" flexDirection="column" pl={2}>
          <Typography
            variant="xs"
            isBold
            css={css`
              color: ${textAlert};
              padding-bottom: ${theme.spacing(0.5)};
            `}
          >
            {t(hasNoProgress ? 'getToKnowToucan' : 'keepGoing')}
          </Typography>
          <Typography
            variant="xs"
            css={css`
              color: ${textAlert};
              padding-bottom: ${theme.spacing(0.5)};
            `}
          >
            {t(hasNoProgress ? 'exploreToucan' : 'throughYourToDo', { amountCompleted })}
          </Typography>
          <Typography variant="xs" isBold>
            <Button type="text" onClick={navigateToSlide}>
              <Box
                display="flex"
                alignItems="center"
                css={css`
                  :hover {
                    path {
                      fill: ${textLinkHover};
                    }
                  }
                  :active {
                    path {
                      fill: ${textLinkActive};
                    }
                  }
                `}
              >
                {t(hasNoProgress ? 'viewToDoList' : 'viewProgress')}
                <ToucanIcon
                  icon="chevron-right"
                  css={css`
                    width: 12px;
                    height: 12px;
                    color: ${textLink};
                  `}
                />
              </Box>
            </Button>
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
