import React, { useState, useEffect, useCallback, useRef, memo } from 'react';

import { ToucanComponents } from '@jointoucan/toucan-design';
import { AnimatePresence } from 'framer-motion';

import { Box, MotionBox } from '~/components/Box';
import { urlAdapter as extraPracticeUrlAdapter } from '~/components/ContextlessLearningMoment/urlAdapter';
import { RootProviders } from '~/components/RootProviders';
import { Events, EventSources } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { isDarkColor } from '~/helpers/colors';
import { getErrorId } from '~/helpers/errors';
import {
  filterDomain,
  getExtraPracticeContainerWidth,
  useIsExtraPracticeEnabledOnDomain,
} from '~/helpers/extraPractice';
import { useDomainEnabledEffect } from '~/hooks/useDomainEnabledEffect';
import { useHistoryChange } from '~/hooks/useHistoryChange';
import { useQuiz } from '~/hooks/useQuiz';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import { createShadowPortal } from '~/libs/reactUtils';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  useGetShortcutsExtraPracticeQuery,
  useDoesCurrentLanguageVectorSupportShortcutsQuery,
  useRemoveExtraPracticeCacheMutation,
} from '~/schema/background';

import { ExtraPractice } from './ExtraPractice';
import { Loading } from './Loading';
import { RegistrationPrompt } from './RegistrationPrompt';

const { ColorScheme } = ToucanComponents;

interface ExtraPracticeContainerProps {
  currentDomain: string;
}

export const ExtraPracticeContainer = memo(({ currentDomain }: ExtraPracticeContainerProps) => {
  const [extraPracticeContainer, setExtraPracticeContainer] = useState<HTMLElement | null>(null);
  const [isToucanEnabledOnDomain, setIsToucanEnabledOnDomain] = useState(true);
  const [historyChangeAmount, setHistoryChangeAmount] = useState(0);
  const numChecksForPageContainer = useRef(0);
  const [currentUrl, setCurrentUrl] = useState<string>('');
  const [errorId, setErrorId] = useState<string | undefined>(undefined);
  const { startQuiz, currentQuiz, onReset, retryQuiz, submitQuestion, questionNumber } = useQuiz();
  const [removeExtraPracticeCache] = useRemoveExtraPracticeCacheMutation();
  const [hasFiredInitialLoadEvent, setHasFiredInitialLoadEvent] = useState(false);
  const [isConfettiShown, setIsConfettiShown] = useState(false);
  const { isContextlessLearningMomentEnabled, isUserRegistered } = useUserInfo();
  const isShortcutsFeatureEnabled = useFeatureEnabled(FeatureName.Shortcuts);
  const { data: matrixData } = useDoesCurrentLanguageVectorSupportShortcutsQuery();
  const isShortcutsEnabled = isShortcutsFeatureEnabled && matrixData?.doesCurrentLanguageVectorSupportShortcuts;
  const [hasShownRegistrationPrompt, setHasShownRegistrationPrompt] = useState(false);

  const possiblyFilteredDomain = filterDomain(currentDomain);

  // enable extra practice on various domains
  const isExtraPracticeEnabledOnDomain = useIsExtraPracticeEnabledOnDomain(currentDomain);

  const { data, loading, error, refetch } = useGetShortcutsExtraPracticeQuery({
    timeout: 100000, // This may take a long time if there will be many examples and questions
  });
  const timeEstimation = 2;
  const isShortcutSelected = data ? !!data.shortcutsExtraPractice?.title : false;

  useEffect(() => {
    if (error) {
      setErrorId(getErrorId(error));
    }
  }, [error]);

  useEffect(() => {
    /**
     * For when going back to the main feed.
     */
    if (currentDomain === 'twitter.com') {
      onpopstate = e => {
        // @ts-ignore - location type does not recognize it is part of PopStateEvent
        const { href } = e.target?.location;
        if (href !== currentUrl) {
          attemptToEmbedExtraPractice({ forcefullyAddContainer: true });
          setCurrentUrl(prevUrl => (!prevUrl || prevUrl !== href ? href : prevUrl));
        }
      };
    }
  }, [currentDomain]);

  useEffect(() => {
    if (!data || hasFiredInitialLoadEvent) {
      return;
    }
    setHasFiredInitialLoadEvent(true);
    if (!isUserRegistered) {
      ContentMessengers.trackEvent({
        eventName: Events.SignedOutExtraPracticeSeen,
        properties: {
          domain: currentDomain,
        },
      });
      return;
    }
    setHasFiredInitialLoadEvent(true);
    ContentMessengers.trackEvent({
      eventName: Events.ExtraPracticeSessionSeen,
      properties: {
        domain: currentDomain,
        activeShortcut: data.shortcutsExtraPractice?.title,
        progressPercentage: data.shortcutsExtraPractice?.homonym.progressionScore,
      },
    });
    ContentMessengers.trackEvent({
      eventName: Events.LearningExperienceStarted,
      properties: {
        exercise: Events.ExtraPracticeStarted,
        source: EventSources.ExtraPractice,
        url: currentDomain,
      },
      userOptions: { includePremiumSubscriptionStatus: true },
    });
  }, [data]);

  useEffect(() => {
    if (!isUserRegistered) {
      setHasShownRegistrationPrompt(true);
    }
  }, [isUserRegistered, setHasShownRegistrationPrompt]);

  /**
   * Attempt to embed the extra practice for 10 seconds (10 tries) if the container isn't present on page load.
   * If the page container hasn't been found in 10 tries, set an error message on Sentry so we can be aware of it.
   * Add the extra practice container to a specific area on page.
   * */
  const attemptToEmbedExtraPractice = ({ forcefullyAddContainer = false }: { forcefullyAddContainer?: boolean }) => {
    const learningMomentResetInterval = setInterval(() => {
      if (!isExtraPracticeEnabledOnDomain) {
        return;
      }
      const containerOnPage = extraPracticeUrlAdapter[possiblyFilteredDomain].getParentElement();
      if (!containerOnPage) {
        if (numChecksForPageContainer.current >= 10) {
          ContentMessengers.captureContentError({
            message: 'extra practice site container not found',
            info: { domain: currentDomain },
          });
          clearInterval(learningMomentResetInterval);
          numChecksForPageContainer.current = 0;
          return;
        }

        numChecksForPageContainer.current += 1;
        return;
      }

      if (!extraPracticeContainer || forcefullyAddContainer) {
        const embeddedElement = document.createElement('div');
        embeddedElement.setAttribute('data-testid', 'extra-practice-parent');

        extraPracticeUrlAdapter[possiblyFilteredDomain].appendElement(containerOnPage, embeddedElement);
        setExtraPracticeContainer(embeddedElement);
      }
      numChecksForPageContainer.current = 0;
      clearInterval(learningMomentResetInterval);
    }, 1000);

    return () => {
      clearInterval(learningMomentResetInterval);
    };
  };

  useHistoryChange(
    useCallback(() => {
      setExtraPracticeContainer(null);
      setHistoryChangeAmount(currentAmount => currentAmount + 1);
    }, []),
  );

  useEffect(() => attemptToEmbedExtraPractice({}), [
    historyChangeAmount,
    possiblyFilteredDomain,
    isExtraPracticeEnabledOnDomain,
  ]);

  /**
   * Hide extra practice if user disables extension or pauses domain with extra practice.
   * Display extra practice if user (enables extension or unpauses current domain) and was not dismissed by user previously.
   */
  useDomainEnabledEffect(
    isDomainEnabled => {
      if (!isDomainEnabled) {
        setIsToucanEnabledOnDomain(false);
      } else if (isDomainEnabled && !isToucanEnabledOnDomain) {
        setIsToucanEnabledOnDomain(true);
      }
    },
    [isToucanEnabledOnDomain],
  );

  if (!extraPracticeContainer) {
    return null;
  }

  const onResetExtraPractice = async () => {
    ContentMessengers.trackEvent({
      eventName: Events.LearningExperienceCompleted,
      properties: {
        exercise: Events.ExtraPracticeCompleted,
        source: EventSources.ExtraPractice,
        url: currentDomain,
      },
      userOptions: { includePremiumSubscriptionStatus: true },
    });
    await removeExtraPracticeCache({});
    await refetch();
    onReset();
    setIsConfettiShown(false);
  };

  const isSiteBackgroundDark = isDarkColor(extraPracticeUrlAdapter[possiblyFilteredDomain].getBackgroundColor());

  return createShadowPortal(
    <RootProviders>
      {isContextlessLearningMomentEnabled && isShortcutsEnabled && (
        <ColorScheme isDarkMode={isSiteBackgroundDark}>
          <AnimatePresence>
            <MotionBox
              key="extra-practice-container"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1, ease: 'easeIn', duration: 0.3 }}
              exit={{ opacity: 0, transition: { ease: 'easeOut', duration: 0.6 } }}
              display="flex"
              justifyContent="center"
              width="100%"
            >
              <Box
                width={getExtraPracticeContainerWidth({ currentDomain })}
                borderRadius="8px"
                m={extraPracticeUrlAdapter[possiblyFilteredDomain].containerMarginExtraPractice}
              >
                {(() => {
                  if (!isUserRegistered) {
                    return <RegistrationPrompt />;
                  }

                  if (loading) {
                    return <Loading loading={loading} />;
                  }

                  /**
                   * If an EP registration prompt is shown and a user registers but has not refreshed the page we want to hide it because the page is unable to reauthorize the user immediately after which
                   * causes useGetShortcutsExtraPracticeQuery() to throw an error. This is also why a user will not see translation immediately after registration on the same page without a page refresh.
                   * Alternatively, if design were to provide assets we could show a message to the user asking them to refresh the page.
                   */
                  if (
                    isUserRegistered &&
                    hasShownRegistrationPrompt &&
                    error?.message === 'Unable to reauthorize or create anonymous user'
                  ) {
                    return null;
                  }

                  return (
                    <ExtraPractice
                      title={data.shortcutsExtraPractice?.title ?? ''}
                      topicName={data.shortcutsExtraPractice?.topicName ?? ''}
                      badgeImage={
                        data.shortcutsExtraPractice?.badgeImage ?? { badgeImageUrl: '', badgeImageAltText: '' }
                      }
                      timeEstimation={timeEstimation}
                      errorId={errorId}
                      isShortcutSelected={isShortcutSelected}
                      currentQuiz={currentQuiz}
                      practice={data.shortcutsExtraPractice?.practice ?? []}
                      retryQuiz={retryQuiz}
                      submitQuestion={submitQuestion}
                      homonym={
                        data.shortcutsExtraPractice?.homonym ?? {
                          homonymId: '',
                          progressionScore: 0,
                          possibleProgressionScore: 0,
                        }
                      }
                      startQuiz={startQuiz}
                      questionNumber={questionNumber}
                      onResetExtraPractice={onResetExtraPractice}
                      currentDomain={currentDomain}
                      setIsConfettiShown={setIsConfettiShown}
                      isConfettiShown={isConfettiShown}
                    />
                  );
                })()}
              </Box>
            </MotionBox>
          </AnimatePresence>
        </ColorScheme>
      )}
    </RootProviders>,
    extraPracticeContainer,
  );
});
