import React, { FC } from 'react';

import { ToucanColors } from '@jointoucan/toucan-design';
import { alpha } from '@mui/material';

// NOTE: SkeletonLoader for inline translations needs to use
// background positioning because overflow hidden does not work
// if the element is inline.
// The only other way to potentially achieve this is to use canvas.
// but even that may be tricky with inline.
export const SkeletonLoader: FC = ({ children }) => (
  <span
    style={{
      width: 'max-content',
      height: 'max-content',
      overflow: 'hidden',
      position: 'relative',
      backgroundPosition: '-50vw 0',
      background: `Linear-gradient(to right, transparent 0%, ${alpha(ToucanColors.white, 0.8)} 50%, transparent 75%)`,
      backgroundRepeat: 'no-repeat',
      // Animations are breaking insertion. insertRule not found
      // animation: `${loading} 3s cubic-bezier(0.4, 0, 0.2, 1) infinite`,
      color: 'transparent',
    }}
  >
    {children}
  </span>
);
