import { useEffect } from 'react';

import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useGetCurrentTabIdQuery } from '~/schema/background';
import { PanelName, SlideName } from '~/types/sidebar';

import { useSidebar } from '../Sidebar/useSidebar';

/**
 * allow the sidebar with sign up/in messaging to slide out and be displayed automatically on a Toucan eligible page.
 * this will execute only once and fairly soon after installing the extension.
 */
export const RegistrationPrompt = () => {
  const { isUserRegistered } = useUserInfo();
  const { openSidebar } = useSidebar();
  const [hasSeenRegistrationSidebar, setHasSeenRegistrationSidebar] = useStorage<boolean>(
    StorageKey.HasSeenRegistrationSidebar,
    false,
  );
  const { data } = useGetCurrentTabIdQuery();
  const tabId = data?.browser?.tab.id;

  useEffect(() => {
    if (!isUserRegistered && !hasSeenRegistrationSidebar && tabId !== undefined) {
      openSidebar({
        panelName: PanelName.Settings,
        slideName: SlideName.MainSettings,
      });
      setHasSeenRegistrationSidebar(true);
    }
  }, [hasSeenRegistrationSidebar, tabId, isUserRegistered, openSidebar, setHasSeenRegistrationSidebar]);
  return null;
};
