import { useReducer } from 'react';

import { getNodesAddress } from '~/helpers/htmlHelpers';

enum DynamicCrawlerActionType {
  Add = 'addObserver',
  Teardown = 'teardownObserver',
}

export interface DynamicCrawlerObserver {
  node: HTMLElement;
  observer: MutationObserver;
  address: string;
}

export type PartialDynamicCrawlerObserver = Omit<DynamicCrawlerObserver, 'type' | 'address'>;

export interface AddObserverAction {
  type: DynamicCrawlerActionType.Add;
  observer: DynamicCrawlerObserver;
}

export interface TeardownObserverAction {
  type: DynamicCrawlerActionType.Teardown;
  observer: PartialDynamicCrawlerObserver;
}

type AnyAction = AddObserverAction | TeardownObserverAction;

type DynamicCrawlerCache = DynamicCrawlerObserver[];

const dynamicCrawlerCacheReducer = (state: DynamicCrawlerCache, action: AnyAction) => {
  switch (action.type) {
    case DynamicCrawlerActionType.Add:
      return [...state, action.observer];
    case DynamicCrawlerActionType.Teardown: {
      const index = state.findIndex(({ node }) => node === action.observer.node);
      const observerCache = state[index];
      const exitingObservers = [...state.slice(0, index), ...state.slice(index + 1)];
      // What we need to do here is disconnect, then reconnect all the existing connections
      // eg. disconnect, and loop and reconnect
      if (observerCache) {
        const mutationObserver = observerCache.observer;
        mutationObserver.disconnect();
        exitingObservers.forEach(({ node }) => {
          mutationObserver.observe(node, { subtree: true, childList: true });
        });
      }
      return exitingObservers;
    }
    default:
      return state;
  }
};

/**
 * useDynamicCrawlerCache is the cache for the dynamic crawler. We essentially
 * create a small state machine that allows us to do some smart things with the mutation
 * observer and will allow us to re-initialize the dynamic content crawler on history change
 */
export const useDynamicCrawlerCache = () => {
  const [cache, dispatch] = useReducer(dynamicCrawlerCacheReducer, []);

  return {
    /**
     * addElement will add in a observer cache for a given element
     */
    addElement: (observerCache: PartialDynamicCrawlerObserver) => {
      dispatch({
        type: DynamicCrawlerActionType.Add,
        observer: {
          ...observerCache,
          address: getNodesAddress(observerCache.node),
        },
      });
    },
    /**
     * teardownObserver will tear down a observer, remove it from cache
     * and stop it from being observed.
     */
    teardownObserver: (observerCache: PartialDynamicCrawlerObserver) => {
      dispatch({
        type: DynamicCrawlerActionType.Teardown,
        observer: observerCache,
      });
    },
    observerCache: cache,
  };
};
