import { GraphQLFormattedError } from 'graphql';

import { HighlightedSelection } from './HighlightedSelection';

export enum HighlightToTranslateStatus {
  Idle = 'idle', // No selection
  Invalid = 'invalid', // Bad selection
  Translatable = 'translatable', // Good selection
  TranslatableSidebar = 'translatable-in-sidebar', // Good selection but cant replace
  Processing = 'processing', // Checking to see if the selection is good.
}

export interface UseHighlightToTranslateReturn {
  status: HighlightToTranslateStatus;
  selection: HighlightedSelection | null;
  onTranslate: () => Promise<void>;
  error?: Error | GraphQLFormattedError<Record<string, any>>;
  overWordLimit: boolean;
  clearSelection: () => void;
}
