import { toucanPrefix } from '~/constants/html';
import { PendingTranslatedNode } from '~/hooks/useTranslatedNodes/types';

import { isHTMLElement } from './htmlHelpers';

export type TextFromNodesReplaceMethod = (word: string, id: string) => string;

const excludedTags = ['script', 'style', 'sup'];

export const getTextFromNodes = (
  nodes: NodeListOf<ChildNode>,
  replace: TextFromNodesReplaceMethod,
  text: string[] = [],
) => {
  Array.from(nodes).forEach(node => {
    if (isHTMLElement(node)) {
      const isTranslation = (node.id ?? '').match(toucanPrefix);
      if (isTranslation) {
        // NOTE: get the real dom node since this is a clone in the selection fragment.
        const el = document.getElementById(node.id);
        if (el && el.shadowRoot) {
          const body = el.shadowRoot.querySelector(`[class="${toucanPrefix}-body"]`);
          // eg. [tou]-[18]-[322f124f-6167-4a6d-bed7-89675c4ee592] we want this last section
          // which is the id of the translated node.
          const translatedNodeId = node.id.split('-').slice(2).join('-');
          text.push(replace((body ? body.textContent : '') ?? '', translatedNodeId ?? ''));
        }
      } else if (node.childNodes.length >= 1 && !excludedTags.includes(node.tagName)) {
        getTextFromNodes(node.childNodes, replace, text);
      }
    } else {
      text.push(node.textContent ?? '');
    }
  });
  return text.join('').trim();
};

const PUNCTUATION_PATTERN = /[!?.]/g;

export const getSentenceWithTranslations = (
  childNodes: NodeListOf<ChildNode>,
  translatedNodesById: Record<string, PendingTranslatedNode>,
  translatedNodeId?: string,
  delimiters: [string, string] = ['[', ']'],
) => {
  const [startDelimiter, endDelimiter] = delimiters;
  const fullText = getTextFromNodes(childNodes, (word, id) => {
    const isActiveNode = id === translatedNodeId;
    const translatedNode = translatedNodesById[id];
    return isActiveNode
      ? `{{${translatedNode?.replacement}}}`
      : `${startDelimiter}${translatedNode?.replacement}${endDelimiter}` ?? word;
  });
  const [fullTextBefore, , fullTextAfter] = fullText.split(/{{(.)*}}/g);
  const before = fullTextBefore.split(PUNCTUATION_PATTERN).pop();
  const after = fullTextAfter.split(PUNCTUATION_PATTERN).shift();
  const punctuation = after ? fullTextAfter.replace(after, '').substring(0, 1) : '';
  return `${before}${startDelimiter}${
    translatedNodesById[translatedNodeId ?? '']?.replacement ?? ''
  }${endDelimiter}${after}${punctuation}`.trim();
};
