import React, { useState, useEffect } from 'react';

import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Skeleton, useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { HexagonalImage } from '~/components/shared/HexagonalImage';
import { HorizontalLine } from '~/components/shared/HorizontalLine';

const { useColorScheme } = ToucanComponents;

interface LoadingProps {
  loading: boolean;
}

export const Loading = ({ loading }: LoadingProps) => {
  const { isDarkMode, backgroundBase } = useColorScheme();
  const numberOfLoadingDots = [1, 2, 3];
  const [loadingIsOnPosition, setLoadingIsOnPosition] = useState(1);
  const theme = useTheme();

  useEffect(() => {
    let value = 1;
    const startTimer = setInterval(() => {
      if (value === 3) {
        setLoadingIsOnPosition(value);
        value = 1;
      } else {
        setLoadingIsOnPosition(value);
        value += 1;
      }
    }, 500);
    if (!loading) {
      clearInterval(startTimer);
    }
    return () => clearInterval(startTimer);
  }, [loading]);

  return (
    <Box display="flex" flexDirection="column" alignItems="center" data-testid="extra-practice-loading">
      <Box display="flex">
        <Box zIndex={2}>
          <HexagonalImage
            imageUrl={browser.runtime.getURL('/images/learningShortcuts/extraPractice/no-badge.svg')}
            width={48}
            height={48}
            borderColor={backgroundBase}
          />
        </Box>
      </Box>

      <Box
        width="100%"
        border={`1px solid ${ToucanColors.gray[isDarkMode ? 500 : 200]}`}
        mt="-24px"
        borderRadius="8px"
        overflow="hidden"
      >
        <Box height="6px" width="100%" bgcolor={ToucanColors.blue[300]} />
        <>
          <Box mt={3} mx={4} mb={2}>
            <Box display="flex" flexDirection="column" alignItems="center">
              <Box display="flex">
                {numberOfLoadingDots.map(i => (
                  <Box
                    key={i}
                    sx={{
                      width: '12px',
                      height: '12px',
                      backgroundColor: i === loadingIsOnPosition ? ToucanColors.blue[300] : ToucanColors.blue[200],
                      borderRadius: '50%',
                      marginRight: theme.spacing(i === numberOfLoadingDots.length ? 0 : 1),
                    }}
                  />
                ))}
              </Box>
            </Box>
          </Box>
          <HorizontalLine />
        </>
        <Box mx={2} my={4}>
          <Box display="flex" flexDirection="column" alignItems="center">
            <>
              <Skeleton
                variant="rectangular"
                width={240}
                height={24}
                sx={{
                  borderRadius: '8px',
                  bgcolor: ToucanColors.gray[100],
                }}
              />
              <Skeleton
                variant="rectangular"
                width={100}
                height={16}
                sx={{
                  marginTop: theme.spacing(1),
                  borderRadius: '8px',
                  bgcolor: ToucanColors.gray[100],
                }}
              />
            </>
          </Box>
        </Box>
        <Box width="100%" p={2}>
          <Skeleton
            variant="rectangular"
            width="100%"
            height={30}
            sx={{
              borderRadius: '8px',
              bgcolor: ToucanColors.gray[100],
            }}
          />
        </Box>
      </Box>
    </Box>
  );
};
