/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';

const { Typography, useColorScheme } = ToucanComponents;

interface HeaderProps {
  title: string;
  topicName?: string | undefined;
}

export const Header = ({ title, topicName }: HeaderProps) => {
  const { textLevel3, textLevel2 } = useColorScheme();

  return (
    <Box display="flex" flexDirection="column" alignItems="center">
      {(() => (
        <React.Fragment>
          {topicName && (
            <Typography
              variant="2xs"
              css={css`
                color: ${textLevel3};
              `}
            >
              {topicName}
            </Typography>
          )}
          <Typography
            variant="xs"
            isBold
            css={css`
              color: ${textLevel2};
            `}
          >
            {title}
          </Typography>
        </React.Fragment>
      ))()}
    </Box>
  );
};
