import React, { FC } from 'react';

import { css } from '@emotion/react';

import { BirdCage } from '~/components/BirdCage';
import { PanelName } from '~/types/sidebar';

import { Box, BoxProps } from '../Box';

import { useSidebarContext } from './SidebarContext';
import { SidebarError } from './SidebarError';

interface PanelProps extends BoxProps {
  name: PanelName;
}

/**
 * Container for panel content
 * It is meant to accept a series of slides which can potentially be shown for that panel
 * @param props
 * @param props.name String that is used to determine if the panel should be visible
 */
export const Panel: FC<PanelProps> = ({ children, name, ...boxProps }) => {
  const { activePanelName, supportedPanels } = useSidebarContext();

  if (name !== activePanelName || !supportedPanels.includes(name)) {
    return null;
  }

  return (
    <BirdCage errorComponent={SidebarError}>
      <Box
        {...boxProps}
        css={css`
          position: relative;
          height: 100%;
        `}
      >
        {children}
      </Box>
    </BirdCage>
  );
};
