import { atom } from 'jotai';

import { QuizStatus } from '~/types/quiz';

import { initialCurrentQuiz } from './helpers';
import { CurrentQuiz, PassOrFail, QuestionsType, StartQuizParams } from './types';

export const quizAtom = atom<CurrentQuiz>(initialCurrentQuiz);

export const startQuizAtom = atom<unknown, StartQuizParams>(null, (get, set, payload) => {
  set(quizAtom, {
    ...get(quizAtom),
    ...payload,
    question: payload.questions[0],
    status: QuizStatus.InProgress,
    results: [],
    errorId: '',
  });
});

export const nextQuestionAtom = atom<unknown, PassOrFail>(null, (get, set, result) => {
  const currentQuiz = get(quizAtom);
  const { question, questions, results } = currentQuiz;
  if (!question) {
    return;
  }
  set(quizAtom, {
    ...currentQuiz,
    question: questions[questions.indexOf(question) + 1],
    results: [...results, result],
  });
});

export const submitQuizAtom = atom<unknown, { result: PassOrFail; status: PassOrFail }>(
  null,
  (get, set, { result, status }) => {
    const currentQuiz = get(quizAtom);
    const { results } = currentQuiz;
    set(quizAtom, {
      ...currentQuiz,
      status,
      results: [...results, result],
    });
  },
);

export const finishedQuizAtom = atom<unknown, void>(null, (get, set) => {
  set(quizAtom, {
    ...get(quizAtom),
    status: QuizStatus.Finished,
  });
});

export const quizErrorAtom = atom<unknown, string>(null, (get, set, errorId) => {
  set(quizAtom, {
    ...get(quizAtom),
    status: QuizStatus.Error,
    errorId,
  });
});

export const retryQuizAtom = atom<unknown, Array<QuestionsType>>(null, (_get, set, questions) => {
  set(quizAtom, {
    ...initialCurrentQuiz,
    status: QuizStatus.InProgress,
    questions,
    question: questions[0],
  });
});

export const resetQuizAtom = atom<unknown, void>(null, (_get, set) => {
  set(quizAtom, initialCurrentQuiz);
});
