/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';
import { motion } from 'framer-motion';

import { QuizSize, useQuizSize } from './useQuizSize';

const { useColorScheme } = ToucanComponents;

export const QuizPlaceholder: FC = () => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const numberOfRows = [1, 2, 3];
  const { size, inputPadding } = useQuizSize();
  const smallPlaceholderHeight = size === QuizSize.Small ? 16 : 24;
  const mediumPlaceholderHeight = size === QuizSize.Small ? 24 : 56;

  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
      <Skeleton
        variant="rectangular"
        width="53%"
        height={smallPlaceholderHeight}
        css={css`
          border-radius: 8px;
          margin-bottom: ${theme.spacing(2)};
          &.MuiSkeleton-root {
            background-color: ${isDarkMode ? 'rgba(112, 112, 112, 0.5)' : ToucanColors.gray[200]};
          }
        `}
      />
      {numberOfRows.map(row => (
        <Skeleton
          key={row}
          variant="rectangular"
          width="100%"
          height={mediumPlaceholderHeight}
          css={css`
            border-radius: 4px;
            margin-bottom: ${theme.spacing(row === numberOfRows.length ? 7 : inputPadding)};
            &.MuiSkeleton-root {
              background-color: rgba(${isDarkMode ? '112, 112, 112' : '230, 230, 230'}, 0.5);
            }
          `}
        />
      ))}
    </motion.div>
  );
};
