import React, { FC, useMemo, useState } from 'react';

import { css } from '@emotion/react';
import { IconName, ToucanColors, ToucanComponents, TooltipProps } from '@jointoucan/toucan-design';

import { Box, BoxProps } from '~/components/Box';
import { RecursivePartial } from '~/types/utils';

const { useColorScheme, Tooltip, TransparentButton, ToucanIcon } = ToucanComponents;

interface ButtonColors {
  background: {
    idle: string;
    hover: string;
    active: string;
    disabled?: string;
  };
  color: {
    idle: string;
    hover: string;
    active: string;
    disabled?: string;
  };
  border: {
    idle: string;
    hover: string;
    active: string;
    disabled?: string;
  };
}

interface Colors {
  light?: RecursivePartial<ButtonColors>;
  dark?: RecursivePartial<ButtonColors>;
}

interface IconButtonProps extends BoxProps {
  active?: boolean;
  disabled?: boolean;
  onClick: () => void;
  alt: string;
  activeIcon?: IconName;
  icon: IconName;
  tooltipProps?: Partial<TooltipProps>;
  colors?: Colors;
  iconSize?: string;
  buttonSize?: string;
  isToolTipVisible?: boolean;
}

export const IconButton: FC<IconButtonProps> = ({
  alt,
  active,
  disabled,
  activeIcon,
  icon,
  iconSize = '14px',
  onClick,
  tooltipProps,
  colors,
  buttonSize = '30px',
  isToolTipVisible = true,
  ...boxProps
}) => {
  const { scheme } = useColorScheme();
  const [hovered, setHovered] = useState(false);

  const buttonColors = {
    light: {
      background: {
        idle: colors?.light?.background?.idle ?? 'transparent',
        hover: colors?.light?.background?.hover ?? ToucanColors.blue[100],
        active: colors?.light?.background?.active ?? ToucanColors.blue[200],
        disabled: colors?.light?.background?.disabled ?? ToucanColors.gray[100],
      },
      color: {
        idle: colors?.light?.color?.idle ?? ToucanColors.blue[300],
        hover: colors?.light?.color?.hover ?? ToucanColors.blue[400],
        active: colors?.light?.color?.active ?? ToucanColors.blue[300],
        disabled: colors?.light?.color?.disabled ?? ToucanColors.gray[300],
      },
      border: {
        idle: colors?.light?.border?.idle ?? ToucanColors.blue[100],
        hover: colors?.light?.border?.hover ?? ToucanColors.blue[100],
        active: colors?.light?.border?.active ?? ToucanColors.blue[200],
        disabled: colors?.light?.border?.disabled ?? 'transparent',
      },
    },
    dark: {
      background: {
        idle: colors?.dark?.background?.idle ?? 'transparent',
        hover: colors?.dark?.background?.hover ?? ToucanColors.teal[600],
        active: colors?.dark?.background?.active ?? ToucanColors.teal[500],
        disabled: colors?.dark?.background?.disabled ?? ToucanColors.gray[600],
      },
      color: {
        idle: colors?.dark?.color?.idle ?? ToucanColors.teal[300],
        hover: colors?.dark?.color?.hover ?? ToucanColors.teal[200],
        active: colors?.dark?.color?.active ?? ToucanColors.teal[300],
        disabled: colors?.dark?.color?.disabled ?? ToucanColors.gray[400],
      },
      border: {
        idle: colors?.dark?.border?.idle ?? ToucanColors.gray[600],
        hover: colors?.dark?.border?.hover ?? ToucanColors.teal[600],
        active: colors?.dark?.border?.active ?? ToucanColors.teal[500],
        disabled: colors?.dark?.border?.disabled ?? 'transparent',
      },
    },
  };

  const currentIcon = useMemo(() => (active && activeIcon && !disabled ? activeIcon : icon), [
    active,
    activeIcon,
    icon,
    hovered,
  ]);

  const mainContent = (
    <Box
      component={TransparentButton}
      onClick={onClick}
      // Hovered events needed to change icon on hover
      onMouseOver={() => setHovered(true)}
      onMouseOut={() => setHovered(false)}
      role="button"
      onKeyPress={e => {
        if (e.key === 'Enter') {
          onClick();
        }
      }}
      display="flex"
      justifyContent="center"
      alignItems="center"
      css={css`
        width: ${buttonSize};
        height: ${buttonSize};
        border-radius: 50%;
        cursor: ${disabled ? 'inherit' : 'pointer'};
        transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        background-color: ${buttonColors[scheme].background.idle};
        box-shadow: inset 0 0 0 2px ${buttonColors[scheme].border.idle};
        & > svg {
          color: ${buttonColors[scheme].color.idle};
        }
        &:hover {
          ${!disabled &&
          `
          background-color: ${buttonColors[scheme].background.hover};
          box-shadow: inset 0 0 0 2px ${buttonColors[scheme].border.hover};
          & > svg {
            color: ${buttonColors[scheme].color.hover};
          }
        `}
        }
        &:active {
          ${!disabled &&
          `
          background-color: ${buttonColors[scheme].background.active};
          box-shadow: inset 0 0 0 2px ${buttonColors[scheme].border.active};
          & > svg {
            color: ${buttonColors[scheme].color.active};
          }
        `}
        }
        ${active &&
        `
        background-color: ${buttonColors[scheme].background.active};
        box-shadow: inset 0 0 0 2px ${buttonColors[scheme].border.active};
        & > svg {
          color: ${buttonColors[scheme].color.active};
        }
      `}
        ${disabled &&
        `
        background-color: ${buttonColors[scheme].background.disabled};
        box-shadow: inset 0 0 0 2px ${buttonColors[scheme].border.disabled};  
        & > svg {
          color: ${buttonColors[scheme].color.disabled};
        }
      `}
      `}
      {...boxProps}
    >
      <ToucanIcon
        icon={currentIcon}
        css={css`
          width: ${iconSize};
        `}
      />
    </Box>
  );

  if (!isToolTipVisible) {
    return mainContent;
  }

  return (
    <Tooltip {...tooltipProps} title={alt}>
      {mainContent}
    </Tooltip>
  );
};
