import React from 'react';

import { useDynamicCrawler } from '~/hooks/useCrawler';

interface DynamicContentCrawlerProps {
  intersectionObserver: IntersectionObserver;
  hydrateTargets?: { selector: string }[];
  onDynamicPage: () => void;
  excludedTags?: Array<string>;
}

/**
 * DynamicContentCrawler is a component that is wrapped around the inline translation crawler hooks.
 * this allows use to reload the hook just by changing the key of the InlinePageCrawler.
 */
export const DynamicContentCrawler: React.FC<DynamicContentCrawlerProps> = ({
  intersectionObserver,
  hydrateTargets,
  onDynamicPage,
  excludedTags = [],
}) => {
  useDynamicCrawler({ intersectionObserver, hydrateTargets, onDynamicPage, excludedTags });
  return null;
};
