import { useCallback, useEffect } from 'react';

import { useIncrementPossibleTranslationsMutation } from '~/schema/background';

import { useAddDomTargets, useRemoveDomTargets } from '../useDomTargets';
import { DomTarget } from '../useDomTargets/types';

import { crawlDomain } from './crawler';

/**
 * useCrawler is a hooks that crawls the page to find **domTargets**
 * @param options.crawlOnLoad passing true will crawl the full page immediately.
 * @returns ret.crawlElement a function that will crawl the passed element.
 */
export const useCrawler = ({
  crawlOnLoad = false,
  intersectionObserver,
  excludedTags = [],
}: {
  crawlOnLoad: boolean;
  intersectionObserver: IntersectionObserver;
  excludedTags?: Array<string>;
}) => {
  const addNodes = useAddDomTargets();
  const removeNodes = useRemoveDomTargets();
  const [incrementPossibleTranslationAmount] = useIncrementPossibleTranslationsMutation();

  const crawlElement = useCallback(
    (element: HTMLElement = document.body) => {
      const crawlerNodes: HTMLSpanElement[] = [];
      const addCrawlerNodes = (nodes: DomTarget[]) => {
        incrementPossibleTranslationAmount({ amount: nodes.length });
        addNodes(nodes);
        nodes.forEach(node => {
          crawlerNodes.push(node.parentNode);
        });
      };

      const cleanup = crawlDomain({
        addNodes: addCrawlerNodes,
        element,
        intersectionObserver,
        excludedTags,
      });

      return () => {
        // NOTE: Once we unmount we want to cleanup the
        // old intersection observer, old dom targets
        removeNodes(crawlerNodes.map(parentNode => ({ parentNode })));
        cleanup();
      };
    },
    [addNodes, removeNodes, incrementPossibleTranslationAmount, intersectionObserver],
  );

  // Crawl website on load for nodes to translate.
  useEffect(() => {
    if (crawlOnLoad) {
      return crawlElement();
    }
    return () => {};
  }, []);

  return {
    crawlElement,
  };
};
