import React from 'react';

import ReactDOM from 'react-dom';

import { ShadowProvider } from '../components/ShadowProvider';

import { createRootElements } from './createRootElements';

interface RootRenderPayload {
  root: ShadowRoot;
  head: HTMLElement;
  body: HTMLElement;
  modals: HTMLElement;
  jssElement: HTMLElement;
}

interface ShadowPortalOptions {
  key?: string | null;
  tagName?: string;
}

export const createShadowPortal = (
  children: React.ReactNode,
  container: HTMLElement,
  options: ShadowPortalOptions = {},
): React.ReactPortal | null => {
  const rootElements = createRootElements(container, options.tagName);
  if (!rootElements) {
    return null;
  }
  const { body, root, head, modals, jssElement } = rootElements;
  return ReactDOM.createPortal(
    <ShadowProvider root={root} head={head} body={body} modals={modals} jssElement={jssElement}>
      {children}
    </ShadowProvider>,
    body,
    options.key,
  );
};

// Create a dom node and mount that element
type ReactRenderElement = React.ReactElement;
export const renderRootless = (app: ReactRenderElement | ((root: RootRenderPayload) => ReactRenderElement)) => {
  const elements = createRootElements(process.env.CONTENT_APP_ID ?? 'toucan');

  if (!elements) return;

  const { body, head, root, modals, jssElement, element } = elements;
  document.body.appendChild(element);
  ReactDOM.render(typeof app === 'function' ? app({ root, head, body, modals, jssElement }) : app, body);
};
