import React, { createContext, FC, useContext, useState } from 'react';

import { Language, SupportedSourceLanguages } from '~/constants/language';
import { LanguageId } from '~/schema/server';

import { i18n as i18nClass } from './i18n';
import { TranslationKeys, Variables } from './types';

interface LocaleContext {
  i18n: i18nClass;
  targetLanguage: Language;
}

const localeContext = createContext<LocaleContext>({
  i18n: i18nClass.from({ language: LanguageId.En }),
  targetLanguage: Language.Spanish,
});

export const LocaleProvider: FC<LocaleContext> = ({ children, i18n, targetLanguage }) => (
  <localeContext.Provider value={{ i18n, targetLanguage }}>{children}</localeContext.Provider>
);

export const useLocalContext = () => useContext(localeContext);

export const useTranslation = () => {
  const [, updateIncrement] = useState(0);
  const { i18n } = useLocalContext();
  return {
    t: (key: TranslationKeys, variables?: Variables) => i18n.getMessage(key, variables),
    i18n,
    changeLanguage: (language: SupportedSourceLanguages) => {
      i18n.changeLanguage(language);
      // NOTE: only here to let React know this updated.
      updateIncrement(x => x + 1);
    },
  };
};
