/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { omit } from 'lodash';

import { Box } from '~/components/Box';
import { ProgressBarCallToAction } from '~/components/InlineTranslations/ProgressBarCallToAction';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { IconButton } from '~/components/shared/IconButton';
import { Tag } from '~/components/shared/Tag';
import { Pinchpoints } from '~/constants/pinchpoints';
import { formatDifficulty } from '~/helpers/difficultyTier';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';

import { WordListCardProps } from './types';

const { useColorScheme, Typography, ProgressBar } = ToucanComponents;

export const WordListCard: FC<WordListCardProps> = ({
  onClickCheckMark,
  onClickSaveWord,
  onProgressButtonClick,
  sourcePhrase,
  targetPhrase,
  isSaved,
  isKnown,
  isFluent,
  progressionScore,
  progressButtonText,
  progressButtonImgSrc,
  progressButtonImgAlt,
  progressButtonExtraText,
  showProgressButton,
  languageKey,
  difficultyLevel,
  isDisabled,
  isIKnowThisWordDisabled,
  onAudioPlayRequested,
  ...boxProps
}) => {
  const { t } = useTranslation();
  const { isDarkMode, textLevel1, textLevel3, backgroundBase, backgroundAccent } = useColorScheme();
  const { openSubscribePage } = useSubscription();
  const isLearned = progressionScore === 100;
  const difficultyTier = formatDifficulty(difficultyLevel ?? undefined, t);

  let bgColor = backgroundBase;

  let barCompleteTransitionColor: string | undefined;
  const checkButtonColors = {
    background: {
      colorGroup: ToucanColors.gray,
      shades: {
        dark: {
          idle: 600,
          hover: 400,
          active: 400,
        },
        light: {
          idle: 100,
          hover: 100,
          active: 100,
        },
      },
    },
    icon: {
      colorGroup: ToucanColors.gray,
      shades: {
        dark: {
          idle: 400,
          hover: 200,
          active: 200,
        },
        light: {
          idle: 300,
          hover: 300,
          active: 300,
        },
      },
    },
    border: {
      colorGroup: ToucanColors.green,
      shades: {
        dark: {
          active: 200,
        },
        light: {
          active: 200,
        },
      },
    },
  };

  if (isLearned) {
    barCompleteTransitionColor = 'linear-gradient(90deg, #85C3D8 0%, #B1D6BF 100%)';
    checkButtonColors.background.colorGroup = ToucanColors.green;
    checkButtonColors.background.shades.dark.idle = 200;
    checkButtonColors.background.shades.light.idle = 200;
    checkButtonColors.icon.colorGroup = ToucanColors.green;
    checkButtonColors.icon.shades.dark.idle = 300;
    checkButtonColors.icon.shades.light.idle = 300;
  }

  if (isFluent) {
    // eslint-disable-next-line prefer-destructuring
    barCompleteTransitionColor = ToucanColors.green[300];
    checkButtonColors.background.colorGroup = ToucanColors.green;
    checkButtonColors.background.shades.dark.idle = 300;
    checkButtonColors.background.shades.light.idle = 300;
    checkButtonColors.icon.colorGroup = {
      100: ToucanColors.white,
      200: ToucanColors.white,
      300: ToucanColors.white,
      400: ToucanColors.white,
      500: ToucanColors.white,
      600: ToucanColors.white,
    };
    checkButtonColors.icon.shades.dark.idle = (ToucanColors.white as unknown) as number;
    checkButtonColors.icon.shades.light.idle = (ToucanColors.white as unknown) as number;
    bgColor = isDarkMode ? ToucanColors.green[500] : ToucanColors.green[100];
  }

  return (
    <Box
      position="relative"
      {...omit(boxProps, 'targetLanguage', 'refetch', 'translationId', 'fluentAt', 'practicedToday')}
      data-testid={isDisabled ? 'disabled-word-list-card' : 'word-list-card'}
      pt={2}
      px={2}
    >
      <Box bgcolor={bgColor} borderRadius="8px" p={2} display="flex" flexDirection="column">
        <Box display="flex" justifyContent="space-between" width="100%" flex="0">
          {languageKey && (
            <Box pt={1} display="flex" alignItems="flex-start">
              <AudioPlayer
                targetPhrase={targetPhrase}
                language={languageKey}
                onPlay={onAudioPlayRequested}
                marginRight={1}
                isDisabled={isDisabled}
              />
            </Box>
          )}
          <Box flex="1">
            <Typography variant="sm" color={isDisabled ? textLevel3 : textLevel1} isBold>
              {targetPhrase}
            </Typography>
            <Typography variant="xs" color={textLevel3}>
              {sourcePhrase}
            </Typography>
          </Box>
          <Box flex="0" display="flex" flexDirection="row" justifyContent="flex-end">
            <Box pl={1} data-testid={`${isSaved ? 'remove' : 'add'}-save-word`}>
              <IconButton
                active={isSaved}
                icon="bookmark-outline"
                activeIcon="bookmark-solid"
                alt={t('save')}
                onClick={
                  isDisabled
                    ? () => {
                        openSubscribePage({
                          pinchpoint: Pinchpoints.SavedWordListIKnowSaveWordCta,
                        });
                      }
                    : onClickSaveWord
                }
                disabled={isDisabled}
                isToolTipVisible={!isDisabled}
              />
            </Box>

            <Box position="relative" display="flex" justifyContent="flex-end">
              <Box position="relative" pl={1} data-testid={`${isKnown ? 'remove' : 'add'}-known-word`}>
                <IconButton
                  active={isKnown || isFluent || isLearned}
                  disabled={isFluent || isDisabled || isIKnowThisWordDisabled}
                  isToolTipVisible={!isDisabled}
                  icon="check"
                  activeIcon={isFluent ? 'double-check' : 'check'}
                  alt={t(isIKnowThisWordDisabled ? 'iKnowThisPremiumFeature' : 'iKnowThis')}
                  tooltipProps={{ width: 100 }}
                  onClick={
                    isIKnowThisWordDisabled
                      ? () => {
                          openSubscribePage({
                            pinchpoint: Pinchpoints.SavedWordListIKnowThisWordCta,
                          });
                        }
                      : onClickCheckMark
                  }
                  colors={{
                    ...(isFluent
                      ? {
                          light: {
                            background: {
                              idle: ToucanColors.green[300],
                              hover: ToucanColors.green[300],
                              active: ToucanColors.green[300],
                            },
                            color: {
                              idle: ToucanColors.white,
                              hover: ToucanColors.white,
                              active: ToucanColors.white,
                            },
                          },
                          dark: {
                            background: {
                              idle: ToucanColors.green[300],
                              hover: ToucanColors.green[300],
                              active: ToucanColors.green[300],
                            },
                            color: {
                              idle: ToucanColors.white,
                              hover: ToucanColors.white,
                              active: ToucanColors.white,
                            },
                          },
                        }
                      : {}),
                  }}
                />
              </Box>
            </Box>
          </Box>
        </Box>

        <Box pt={2} flex="0">
          <ProgressBar
            barCompleteTransitionOptions={{ duration: 0 }}
            beginPercentage={progressionScore}
            endPercentage={progressionScore}
            barColor={isFluent ? ToucanColors.green[300] : ToucanColors.blue[200]}
            barCompleteTransitionColor={barCompleteTransitionColor}
            onAnimationComplete={async () => {}}
            css={css`
              ${isDisabled &&
              `
                background: ${backgroundAccent};
                & div {
                  background: ${ToucanColors.gray[isDarkMode ? 400 : 300]};
                }
              `}
            `}
          />
          <ProgressBarCallToAction
            isVisible={showProgressButton}
            onClick={onProgressButtonClick}
            imgSrc={progressButtonImgSrc}
            imgAlt={progressButtonImgAlt}
            extraText={progressButtonExtraText}
          >
            {progressButtonText}
          </ProgressBarCallToAction>
        </Box>
        <Box display="flex">
          {difficultyTier && !isDisabled && (
            <Tag mt={2} theme="teal">
              {difficultyTier}
            </Tag>
          )}
        </Box>
      </Box>
    </Box>
  );
};
