/** @jsx jsx */
import { FC, memo } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

import Config from '../../configs/config';

const { Button } = ToucanComponents;

interface CallToActionButtonProps {
  onCloseWithTracking: () => void;
}

export const CallToActionButton: FC<CallToActionButtonProps> = memo(({ onCloseWithTracking }) => {
  const theme = useTheme();
  const { t } = useTranslation();

  const onButtonClick = () => {
    window.open(`${Config.siteURL}/onboarding`, '_blank');
    onCloseWithTracking();
  };

  return (
    <Button
      onClick={onButtonClick}
      css={css`
        margin-top: ${theme.spacing(2)};
      `}
    >
      {t('gettingStarted')}
    </Button>
  );
});
