import React, { FC, createContext, useContext, useEffect, useState } from 'react';

import { ExperimentsClient } from './ExperimentsClient';
import { ExperimentName, ExperimentsStatus } from './type';

const experimentsContext = createContext<ExperimentsClient | null>(null);

export const useExperimentsInstance = () => useContext(experimentsContext);

export const useIsBucketed = (experimentName: ExperimentName, variant: string) => {
  const experiments = useExperimentsInstance();
  if (!experiments) {
    throw new Error('Experiments instance must be passed to context used before useIsBucketed');
  }
  return experiments.isBucketed(experimentName, variant);
};

export const useVariant = (experimentName: ExperimentName) => {
  const experiments = useExperimentsInstance();
  if (!experiments) {
    throw new Error('Experiments instance must be passed to context used before useVariant');
  }
  return experiments.getVariant(experimentName);
};

export const useIsExperimentsReady = () => {
  const experiments = useExperimentsInstance();
  const [isReady, setIsReady] = useState(() => experiments?.status === ExperimentsStatus.Ready);

  useEffect(() => {
    if (!experiments) {
      return () => {};
    }

    return experiments.onStatusChange(status => {
      if (status === ExperimentsStatus.Ready) {
        setIsReady(true);
      } else {
        setIsReady(false);
      }
    });
  }, [isReady, experiments]);

  if (!experiments) {
    throw new Error('Experiments instance must be passed to context used before useVariant');
  }

  return isReady;
};

export const ExperimentsProvider: FC<{ instance: ExperimentsClient }> = ({ children, instance }) => (
  <experimentsContext.Provider value={instance}>{children}</experimentsContext.Provider>
);
