import { AudioContext, audioContextSupport } from './utils';

/**
 * TextToSpeechAudio is a player that plays an audio buffer
 * using audio context. The benefit of using audio context is
 * that we can get around CSP issues because no object URL is created.
 *
 * There is also the added benefit of being able to defer the loading of
 * the audio buffer after the AudioContext has be started.
 *
 * @see https://developer.mozilla.org/en-US/docs/Web/API/AudioContext
 */
export class TextToSpeechAudioContext {
  private tag: string = 'TextToSpeechAudioContext';

  private audioContext: AudioContext;

  private audioBuffer?: Promise<AudioBuffer>;

  private audioSource?: AudioBufferSourceNode;

  private playbackRate?: number;

  public onended: () => void;

  static from = (buffer?: Uint8Array) => new TextToSpeechAudioContext(buffer);

  constructor(buffer?: Uint8Array) {
    if (!audioContextSupport) {
      throw new Error(`Audio context is needed to play ${this.tag}`);
    }
    this.audioContext = new AudioContext();
    this.onended = () => {};
    this.playbackRate = 1;
    if (buffer) {
      this.audioBuffer = this.createPendingAudioBuffer(buffer);
    }
    return this;
  }

  createPendingAudioBuffer(buffer: Uint8Array): Promise<AudioBuffer> {
    return new Promise((resolve, reject) => {
      if (!buffer) {
        return;
      }
      if (buffer.buffer.byteLength > 0) {
        this.audioContext.decodeAudioData(
          buffer.buffer,
          arrayBuffer => {
            resolve(arrayBuffer);
          },
          reject,
        );
      } else {
        reject(new Error(`Buffer with no length loaded into ${this.tag}`));
      }
    });
  }

  private createAudioSource() {
    this.audioSource = this.audioContext.createBufferSource();
  }

  async pause() {
    const { audioContext } = this;
    await audioContext.suspend();
    return this;
  }

  async loadBuffer(buffer: Uint8Array) {
    this.audioBuffer = this.createPendingAudioBuffer(buffer);
    const audioBuffer = await this.audioBuffer;
    // Create new audio source if it doesn't exist
    if (!this.audioSource) {
      this.createAudioSource();
    }

    if (this.audioSource && !this.audioSource.buffer) {
      this.setPlaybackRate(this.playbackRate ?? 1);
      this.audioSource.buffer = audioBuffer;
      this.audioSource.connect(this.audioContext.destination);
      this.audioSource.start(0);
      this.audioSource.addEventListener('ended', () => {
        this.onended();
        this.audioSource?.stop();
        this.audioSource?.disconnect(this.audioContext.destination);
        this.audioSource = undefined;
      });
    }
  }

  async play() {
    const { audioContext } = this;
    audioContext.resume();

    // Create new audio source if it doesn't exist
    if (!this.audioSource) {
      this.createAudioSource();
    }

    if (this.audioSource && !this.audioSource.buffer && this.audioBuffer) {
      const audioBuffer = await this.audioBuffer;
      this.setPlaybackRate(this.playbackRate ?? 1);
      this.audioSource.buffer = audioBuffer;
      this.audioSource.connect(audioContext.destination);
      this.audioSource.start(0);
      this.audioSource.addEventListener('ended', () => {
        this.onended();
        this.audioSource?.stop();
        this.audioSource?.disconnect(audioContext.destination);
        this.audioSource = undefined;
      });
    }
    return this;
  }

  async setPlaybackRate(rate: number) {
    this.playbackRate = rate;
    if (!this.audioSource) {
      this.createAudioSource();
    }
    if (this.audioSource) {
      this.audioSource.playbackRate.value = rate;
    }
    return this;
  }
}
