import { client } from '~/finch-client';
import {
  InjectSidebarIntoTabMutation,
  InjectSidebarIntoTabDocument,
  InjectSidebarIntoTabMutationVariables,
} from '~/schema/background';

export const sidebarLazyLoad = async () => {
  await client.mutate<InjectSidebarIntoTabMutation, InjectSidebarIntoTabMutationVariables>(
    InjectSidebarIntoTabDocument,
    {},
  );
  return { default: window.__sidebar, __esModule: true };
};
