/** @jsx jsx */
import { useState } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import Configs from '~/configs/config';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

const { Typography, useColorScheme, Card, Button, ToucanIcon } = ToucanComponents;

interface PremiumUpsellChicletProps {
  onLayout: (size: { width: number; height: number }) => void;
  onClickDontShowAgain: () => void;
}

export const PremiumUpsellChiclet = ({ onLayout, onClickDontShowAgain }: PremiumUpsellChicletProps) => {
  const theme = useTheme();
  const { backgroundBase, backgroundAccent, textLevel2 } = useColorScheme();
  const [hasSentLayout, setLayoutSent] = useState(false);
  const { t } = useTranslation();
  const { openSubscribePage } = useSubscription();

  return (
    <Card
      css={css`
        background-color: ${backgroundBase};
        border: 0;
        border-radius: ${theme.spacing(1)};
        padding: ${theme.spacing(2)};
        flex-direction: column;
        max-width: 229px;
      `}
      hasPadding={false}
      ref={(el: HTMLDivElement) => {
        if (el && !hasSentLayout) {
          const styles = window.getComputedStyle(el);
          onLayout({
            width: parseInt(styles.width, 10),
            height: parseInt(styles.height, 10),
          });
          setLayoutSent(true);
        }
      }}
    >
      <Box display="flex" flexDirection="row" alignItems="center">
        <Image
          css={css`
            width: 16px;
            height: 16px;
            margin-right: ${theme.spacing(1)};
          `}
          src={`/images/icons/${Configs.env}/icon48.png`}
          alt="Toucan"
        />
        <Typography variant="xs" isBold color={textLevel2}>
          {t('highlightToTranslate')}
        </Typography>
      </Box>
      <Box my={1} height="1px" bgcolor={backgroundAccent} />
      <Typography variant="xs" color={textLevel2}>
        {t('translateWordsPhrasesOnDemandPremium')}
      </Typography>
      <Box my={2}>
        <Button
          type="alert"
          size="small"
          onClick={() => openSubscribePage({ pinchpoint: Pinchpoints.HighlightToTranslatePremiumUpsellModal })}
        >
          {t('startFreeTrialFirstLetterCapitalized')}
        </Button>
      </Box>
      <Typography variant="xs">
        <Button
          onClick={() => {
            ContentMessengers.trackEvent({
              eventName: 'Dont show again highlight to translate premium upsell',
              properties: {
                enabled: false,
              },
            });
            onClickDontShowAgain();
          }}
          type="text"
          css={css`
            display: flex;
          `}
        >
          <ToucanIcon
            icon="eye-slash"
            css={css`
              width: 12px;
              height: 12px;
              margin-right: ${theme.spacing(1)};
              color: inherit;
            `}
          />
          {t('dontShowAgain')}
        </Button>
      </Typography>
    </Card>
  );
};
