/** @jsx jsx */
import { forwardRef } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme, Input } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

import { QuizSize, useQuizSize } from '../useQuizSize';

const { useColorScheme } = ToucanComponents;

interface FormProps {
  setEntry: (value: string) => void;
  language: string;
  entry: string;
  onSubmit: () => void;
}

export const Form = forwardRef<HTMLInputElement, FormProps>((props, ref) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const { inputPadding, size } = useQuizSize();
  const padding = theme.spacing(inputPadding);

  return (
    <Box display="flex" alignItems="center" width="100%" mb={inputPadding}>
      <form
        css={css`
          border-radius: 4px;
          border: 1px solid ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[300]};
          width: 100%;
          display: flex;
          font-size: 14px;
        `}
        onSubmit={event => {
          event.preventDefault();
          props.onSubmit();
        }}
      >
        <Input
          value={props.entry}
          onChange={event => props.setEntry(event.target.value)}
          disableUnderline
          placeholder={t('quizTypeIn', { language: props.language })}
          css={css`
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.gray[500]};
            & .MuiInputBase-input {
              font-size: ${size === QuizSize.Medium ? '16' : '14'}px;
              padding: ${padding};
              height: auto;
            }
          `}
          inputRef={ref}
          inputProps={{ spellCheck: 'false' }}
        />
      </form>
    </Box>
  );
});
