import React, { FC, memo } from 'react';

import { css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';

const { Typography } = ToucanComponents;

interface PronunciationProps {
  pronunciation?: string;
  isVisible: boolean;
}

export const Pronunciation: FC<PronunciationProps> = memo(({ pronunciation, isVisible }) =>
  isVisible ? (
    <Box
      pl={2}
      pr={2}
      pt={0.5}
      pb={0.5}
      width="100%"
      borderRadius="10px 10px 0 0"
      bgcolor={ToucanColors.blue[100]}
      css={css`
        & h5 {
          color: ${pronunciation ? ToucanColors.gray[500] : 'transparent'} !important;
        }
      `}
    >
      <Typography variant="xs">{pronunciation ?? 'x'}</Typography>
    </Box>
  ) : null,
);
