import { useEffect } from 'react';

import { useFinchClient } from '@finch-graphql/react';

import { mainSelector } from '~/constants/html';
import { getNodesAddress, isHTMLElement } from '~/helpers/htmlHelpers';

import { useRemoveDomTargets, useUpdateDomTargets, useVisibleDomTargets } from '../useDomTargets';

import { findReplacements } from './findReplacements';

/**
 * useProcessVisibleNodes will process any nodes that are marked as visible.
 * Processing means it will take the node and split apart the the content, and then
 * send it the background process to work with the dictionary to find any replacements.
 */
export const useProcessVisibleNodes = ({ excludedTags = [] }: { excludedTags?: Array<string> }) => {
  const updateNodes = useUpdateDomTargets();
  const removeNodes = useRemoveDomTargets();
  const visibleNodes = useVisibleDomTargets();
  const { client } = useFinchClient();

  useEffect(() => {
    const nodes = visibleNodes.filter(target => !target.textMappings && !target.excluded && target?.id);
    const onIdle = async () => {
      const eligibleNodes = visibleNodes.filter(target => !target.textMappings && !target.excluded && target?.id);
      const mainNode = document.querySelector(mainSelector);
      const mainAddress = mainNode && isHTMLElement(mainNode) ? getNodesAddress(mainNode) : null;
      const updates = findReplacements({
        nodes: eligibleNodes,
        mainAddress,
        client,
        excludedTags,
      });

      const nodesToRemove = updates.filter(node => node.excluded);
      if (nodesToRemove.length) {
        removeNodes(nodesToRemove);
      }

      const nodesToUpdate = updates.filter(node => !node.excluded);
      if (nodesToUpdate.length) {
        updateNodes(nodesToUpdate);
      }
    };
    let ts: number | null;
    if (nodes.length) {
      ts = window.requestAnimationFrame(onIdle);
    }
    return () => {
      if (ts) {
        window.cancelAnimationFrame(ts);
      }
    };
  }, [visibleNodes]);
};
