import EventEmitter from 'eventemitter3';

import { ExperimentName, ExperimentsStatus } from './type';

export class ExperimentsClient {
  private emitter: EventEmitter;

  /**
   * Cache will store the experiment name and the variant
   */
  cache: Map<ExperimentName, string>;

  status: ExperimentsStatus = ExperimentsStatus.NotInitialized;

  /**
   * Constructor that will initialize the 3rd party integration.
   * will probably needs to take in some param to allow for initialization
   * of the the 3rd party integration.
   */
  constructor() {
    this.cache = new Map<ExperimentName, string>();
    this.emitter = new EventEmitter();
    this.setStatus(ExperimentsStatus.Initialized);
  }

  // update cache with values from graphql
  async hydrateCache(experiments?: { experiment: string; variation: string }[]) {
    if (experiments) {
      experiments.forEach(experiment => {
        this.cache.set(experiment.experiment as ExperimentName, experiment.variation);
      });
      this.setStatus(ExperimentsStatus.Ready);
    }
  }

  /**
   * isBucketed is a method that will return if the user is bucketed into
   * a given experiment.
   * @param experimentName the name of the experiment to check.
   */
  isBucketed(experimentName: ExperimentName, variant: string): boolean {
    return this.cache.get(experimentName) === variant;
  }

  /**
   * getVariant is a methods that you can pull the variant an experiment
   * is currently bucketed into.
   * @param experimentName the name of the experiment to check.
   */
  getVariant(experimentName: ExperimentName): string | null {
    return this.cache.get(experimentName) ?? null;
  }

  /**
   * setStatus is a method that will set the status of the experiments
   * and also emit the status.
   */
  private setStatus(status: ExperimentsStatus) {
    this.status = status;
    this.emitter.emit('statusChange', status);
  }

  /**
   * onStatusChange is a method that will listen for status changes.
   */
  onStatusChange(cb: (status: ExperimentsStatus) => void) {
    this.emitter.on('statusChange', cb);
    return () => this.emitter.off('statusChange', cb);
  }

  /**
   * Common helper to make class instances, will be the equivalent of
   * calling new Experiments
   */
  static from() {
    return new ExperimentsClient();
  }
}
