import { useEffect, useRef } from 'react';

export const usePostMessageListener = <Payload extends {}>(callback: (event: MessageEvent<Payload>) => void) => {
  const methodRef = useRef<(event: MessageEvent<Payload>) => void>(callback);
  methodRef.current = callback;

  useEffect(() => {
    const proxyMethod = (event: MessageEvent<Payload>) => {
      methodRef.current(event);
    };
    window.addEventListener('message', proxyMethod);
    return () => {
      window.removeEventListener('message', proxyMethod);
    };
  });
};
