import { useEffect } from 'react';

import { getSelectionText } from '~/helpers/selectionHelpers';

import { useDictionaryEntries } from './useDictionary';
import { Translations } from './useDictionary/types';

export const replaceTranslation = (translations: Translations) => (word: string) => {
  const foundTranslation = Object.values(translations).find(({ targetPhrase }) => targetPhrase === word);
  return foundTranslation ? foundTranslation.sourcePhrase : word;
};

export const useCopySwap = () => {
  const translations = useDictionaryEntries();
  const replace = replaceTranslation(translations);

  useEffect(() => {
    const copyHandler = (e: Event) => {
      if (!(e instanceof ClipboardEvent) && e) {
        return;
      }

      const selection = document.getSelection();
      const text = selection ? getSelectionText(selection, replace) : null;

      if (text && e.clipboardData) {
        e.clipboardData.setData('text/plain', text);
        e.preventDefault();
      }
    };
    window.addEventListener('copy', copyHandler);
    return () => {
      window.removeEventListener('copy', copyHandler);
    };
  }, [translations]);
};
