import bowser from 'bowser';

/**
 * getBrowserProperties collects some browser properties and returns them as an object
 * to be consumed by the analytics service.
 */
export const getBrowserProperties = () => {
  let browserName = 'unknown';
  let browserVersion = 'unknown';
  let operatingSystem = 'unknown';
  let operatingSystemVersion = 'unknown';
  try {
    const parsedUA = bowser.parse(window.navigator.userAgent);
    browserName = parsedUA.browser.name ?? 'unknown';
    browserVersion = parsedUA.browser.version ?? 'unknown';
    operatingSystem = parsedUA.os.name ?? 'unknown';
    operatingSystemVersion = parsedUA.os.version ?? 'unknown';
  } catch (e) {
    // Unable to parse user agent
  }

  // Return human readable browser properties
  return {
    'Browser Name': browserName,
    'Browser Version': browserVersion,
    OS: operatingSystem,
    'OS Version': operatingSystemVersion,
  };
};
