import { ModalBaseProps } from '~/components/ModalContainer/types';
import type { useSettings } from '~/components/Sidebar/content/Settings/useSettings';
import { Language } from '~/constants/language';
import { QuizType, HttContainedPhrase } from '~/schema/background';

export type SettingsType = ReturnType<typeof useSettings>;

export interface SidebarContainerProps extends ModalBaseProps {
  isInPopup: boolean;
  isAnimationComplete?: boolean;
  sidebarWidth?: string;
  isSettingsProgressCtaVisible?: boolean;
  isLoadingExperimentsList?: boolean;
}

export interface SettingsPanelProps extends Omit<SidebarContainerProps, 'selectedModalType' | 'isAnimationComplete'> {
  setIsSidebarOpen: (isOpen: boolean) => void;
  targetLanguage: Language;
}

export enum PanelName {
  Settings = 'settings',
  Notifications = 'notifications',
  Progress = 'progress',
  SavedWords = 'savedWords',
  HighlightToTranslate = 'highlightToTranslate',
  Referral = 'referral',
}

export enum SettingName {
  ChangeLanguage = 'changeLanguage',
  AllowOnSite = 'allowOnSite',
  TranslationDifficultySelector = 'translationDifficultySelector',
  DisplayAlphabet = 'displayAlphabet',
  PhoneticSpelling = 'PhoneticSpelling',
  HighlightToTranslateSettings = 'HighlightToTranslateSettings',
  ContextlessLearningMoment = 'ContextlessLearningMoment',
  KeyboardShortcuts = 'KeyboardShortcuts',
  DarkMode = 'DarkMode',
  DataConsent = 'DataConsent',
  AudioControls = 'AudioControls',
}

export enum SlideName {
  MainSettings = 'mainSettings',
  ChangeLanguage = 'changeLanguage',
  BlockedSites = 'blockedSites',
  Authenticate = 'authenticate',
  MainNotifications = 'mainNotifications',
  ProgressOverview = 'progressOverview',
  WordList = 'wordList',
  OnboardingChecklist = 'onboardingChecklist',
  SavedWords = 'savedWords',
  HighlightToTranslate = 'highlightToTranslate',
  MainReferral = 'mainReferral',
  EmailReferral = 'emailReferral',
  AudioControls = 'audioControls',
  TopicWordList = 'topicWordList',
  LearningShortcut = 'learningShortcut',
  LearningShortcutTopic = 'learningShortcutTopic',
  AccountDeletionConfirm = 'accountDeletionConfirm',
  AccountDeletionConfirmation = 'accountDeletionConfirmation',
  ExperimentInformation = 'experimentInformation',
}

export enum SlideDirection {
  Forward = 'forward',
  Backward = 'backward',
}

export enum OverlayName {
  Snooze = 'snooze',
  MakingProgress = 'makingProgress',
  WordQuiz = 'wordQuiz',
  PracticeQuiz = 'practiceQuiz',
  PostPracticeQuiz = 'postPracticeQuiz',
}

// Allows use to hydrate state for sidebar views.
export type SidebarHydratedState = Partial<{
  entryTranslationId: string;
  pastQuiz: { translationIds: Array<string>; quizType: QuizType };
  containedPhrases: Array<HttContainedPhrase>;
  targetPhrase: string;
  targetLanguage: string;
  sourcePhrase: string;
  sourceLanguage: string;
  learningShortcutTopicId: string;
  learningShortcutId: string;
}>;

export interface SidebarInitialState {
  overlayName?: OverlayName;
  slideName?: SlideName;
  panelName?: PanelName;
  isOpen: boolean;
  tabId: number;
  hydration?: SidebarHydratedState;
}
