import { ComingSoonLanguageId } from '~/schema/background';
import { LanguageId } from '~/schema/server';

export enum Language {
  French = 'french',
  German = 'german',
  Italian = 'italian',
  Japanese = 'japanese',
  Korean = 'korean',
  Portuguese = 'portuguese',
  Spanish = 'spanish',
  English = 'english',
  Chinese = 'chinese',
  Arabic = 'arabic',
  Hebrew = 'hebrew',
  Hindi = 'hindi',
}

export enum LanguageVariant {
  Roman = 'roman',
  Kanji = 'kanji',
  HiraganaKatakana = 'hiraganaKatakana',
  Abjadiyyah = 'abjadiyyah',
  KtavAshuri = 'ktavAshuri',
  Devanagari = 'devanagari',
  Hangul = 'hangul',
  SimplifiedChinese = 'simplifiedChinese',
}

interface LanguageVariantsInformation {
  [key: string]: LanguageVariantInformation;
}

export interface LanguageVariantInformation {
  languageId: LanguageId;
  key: LanguageVariant;
  isDefault?: boolean;
}

export interface LanguageInformation {
  languageId: LanguageId;
  key: Language;
  flagRound: string;
  flagEmoji: string;
  inLanguage: string;
  variants?: LanguageVariantsInformation;
  hasLatinOrigin: boolean;
}

export type InDevLanguageInformation = Omit<
  LanguageInformation,
  'languageId' | 'country' | 'flagEmoji' | 'key' | 'inLanguage' | 'hasLatinOrigin'
> & {
  languageId: LanguageISOCode;
};

