export const getDefaultTargetLanguage = (sourceLanguage: SupportedSourceLanguages) =>
  sourceLanguage === LanguageId.En ? Language.Spanish : Language.English;

export const isSupportedSourceLanguage = (maybeSupported: string): maybeSupported is SupportedSourceLanguages =>
  supportedSourceLanguageIds.includes(maybeSupported as SupportedSourceLanguages);

