export const checkSourceLanguageIsLatinBased = (sourceLanguage: SupportedSourceLanguages) => {
  const languageInformation = Object.values(LanguageInfo).find(
    info => info.languageId === ((sourceLanguage as unknown) as LanguageId),
  );
  return languageInformation?.hasLatinOrigin ?? false;
};
