/** @jsx jsx */
import { FC, memo } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

const { Typography } = ToucanComponents;

export const Title: FC<{ language: string }> = memo(({ language }) => {
  const theme = useTheme();
  const { t } = useTranslation();

  return (
    <Typography
      variant="md"
      font="secondary"
      isBold
      color={ToucanColors.gray[500]}
      css={css`
        margin-top: ${theme.spacing(2)};
      `}
    >
      {t('langIsNowLive', { lang: language })}
    </Typography>
  );
});
