import React, { FC, useMemo } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { ReferralCta } from '~/components/Referral/ReferralCta';
import Configs from '~/configs/config';
import { QuizType } from '~/schema/background';
import { InlineTranslationLearningShortcutInfo } from '~/types/learningShortcut';

import { InlineQuiz } from '../InlineQuiz';
import { QuizSize } from '../InlineQuiz/useQuizSize';

import { Pronunciation } from './Pronunciation';
import { ShortcutWrapper, ShortcutError, ShortcutPlaceholder } from './Shortcut';
import { usePopperModifiers } from './usePopperModifiers';

const {
  Highlight: { HighlightHover, HighlightHoverBody, HighlightHoverAfter, HighlightHoverBefore, HighlightHoverWrapper },
  Typography,
  useColorScheme,
} = ToucanComponents;

interface InlineTranslationCardLayoutProps {
  translationId: string;
  pronunciation?: string;
  isPronunciationEnabled: boolean;
  isQuizActive: boolean;
  onQuizExit: () => void;
  domain: string;
  node: HTMLSpanElement | undefined;
  onFocus: () => void;
  onBlur: () => void;
  forceCloseCard: () => void;
  isOpen: boolean;
  showReferral: boolean;
  onClickReferral: () => void;
  toucanRewardOfferedPerReferral?: string;
  referralCTAText: string;
  isShortcutLoading?: boolean;
  shortcutError?: Error;
  shortcutData?: InlineTranslationLearningShortcutInfo;
  isActiveLearningShortcutPhrase: boolean;
  hasNoMorePracticeQuizzes: boolean;
}

/**
 * Inline Translation Card Layout is a component that renders a card that shows the translation
 * it is essentially a layout presentational component that pretty much sets up the layout of the
 * Inline Translation Card. This component has two main pieces of the Inline Translation Card.
 *
 * - Own the Word
 * - Pronunciation
 *
 * This is largely because they are two items that are tacked onto the top and bottom of the
 * Inline translation card.
 */
export const InlineTranslationCardLayout: FC<InlineTranslationCardLayoutProps> = ({
  translationId,
  pronunciation,
  isQuizActive,
  onQuizExit,
  forceCloseCard,
  domain,
  node,
  onFocus,
  onBlur,
  isOpen,
  children,
  isPronunciationEnabled,
  showReferral,
  onClickReferral,
  toucanRewardOfferedPerReferral,
  referralCTAText,
  isShortcutLoading,
  shortcutError,
  shortcutData,
  isActiveLearningShortcutPhrase,
  hasNoMorePracticeQuizzes,
}) => {
  const popperModifiers = usePopperModifiers({ domain });
  const translationIdsForQuiz = useMemo(() => [translationId], [translationId]);
  const { isDarkMode } = useColorScheme();

  return node ? (
    <HighlightHover
      disablePortal
      onFocus={onFocus}
      onBlur={onBlur}
      node={node}
      isOpen={isOpen}
      isWrapperActive={isActiveLearningShortcutPhrase}
      {...popperModifiers}
    >
      {isPronunciationEnabled && !isQuizActive && (
        <HighlightHoverBefore>
          <Pronunciation pronunciation={pronunciation} isVisible={!isQuizActive} />
        </HighlightHoverBefore>
      )}
      <HighlightHoverBody>
        {isQuizActive ? (
          <InlineQuiz
            quizSize={QuizSize.Small}
            onQuizExit={onQuizExit}
            translationIds={translationIdsForQuiz}
            quizType={QuizType.Practice}
            onSetIdle={forceCloseCard}
            hasNoMorePracticeQuizzes={hasNoMorePracticeQuizzes}
          />
        ) : (
          children
        )}
      </HighlightHoverBody>
      {showReferral && toucanRewardOfferedPerReferral && (
        <HighlightHoverAfter>
          <Box
            px={2}
            py={1}
            borderRadius="0 0 8px 8px"
            bgcolor={isDarkMode ? ToucanColors.teal[500] : ToucanColors.teal[100]}
            width="100%"
          >
            <ReferralCta referralCTAText={referralCTAText} onClickReferral={onClickReferral} compact />
          </Box>
        </HighlightHoverAfter>
      )}
      {Configs.isDebugging && (
        <HighlightHoverAfter>
          <Box px={2} py={1} borderRadius="0 0 8px 8px" bgcolor={ToucanColors.teal[600]} width="100%">
            <Typography variant="2xs" color={ToucanColors.gray[100]}>
              {translationId}
            </Typography>
          </Box>
        </HighlightHoverAfter>
      )}
      {isActiveLearningShortcutPhrase && (
        <HighlightHoverWrapper>
          {(() => {
            if (isShortcutLoading) {
              return <ShortcutPlaceholder />;
            }
            if (shortcutError) {
              return <ShortcutError shortcutError={shortcutError} />;
            }
            const { badgeImage, percentCompleted, title } = shortcutData ?? {};
            return (
              <ShortcutWrapper
                badgeImageUrl={badgeImage?.badgeImageUrl ?? ''}
                percentCompleted={percentCompleted ?? 0}
                title={title ?? ''}
                {...popperModifiers}
              />
            );
          })()}
        </HighlightHoverWrapper>
      )}
    </HighlightHover>
  ) : null;
};
