import { Aracari } from 'aracari';

import { nodeTypeList } from '~/constants/html';
import { getParentElements } from '~/helpers/htmlHelpers';

import { stampDomTarget } from '../useDomTargets/helpers';

import { filterValidTargets } from './excludedNodes';
import { CrawlDomainOptions } from './types';

/**
 * crawlDomain is a function that will search for **domTargets** in a
 * given element. If no element is given then the document body is used.
 * This method also attach an intersection observer to the domTargets to
 * let the other parts of the state machine pick up the nodes and process
 * them.
 * @param option.addNodes a function to add domTarget nodes
 * @param option.updateNodes a function to update domTarget nodes
 * @param option.element a element that is root of where the crawler should search
 * @param option.intersectionObserver an intersection observer to add nodes to.
 * @param option.supportTwitter flag if we should be crawling twitter
 * @returns a function that will clean up and information that is created with the run of this function
 */
export const crawlDomain = ({ addNodes, element, intersectionObserver, excludedTags = [] }: CrawlDomainOptions) => {
  const nodes = getParentElements(Array.from(element.querySelectorAll(nodeTypeList.join(', '))))
    .map(({ htmlElement, address }) =>
      stampDomTarget({
        id: address,
        parentNode: htmlElement,
      }),
    )
    .filter(target => filterValidTargets(target, excludedTags));

  addNodes(nodes);

  nodes.forEach(node => {
    if (node.parentNode instanceof HTMLElement) {
      intersectionObserver.observe(node.parentNode);
    }
  });

  return () => {
    nodes.forEach(node => {
      if (node.parentNode instanceof HTMLElement) {
        intersectionObserver.unobserve(node.parentNode);
      }
    });
  };
};

/**
 * shouldCrawl is a helper function that determines if an element should be crawled.
 * @param el HTML element to crawl
 */
export const shouldCrawl = (el: HTMLElement) => {
  const mapping = new Aracari(el);
  const text = mapping.getText().trim();
  return !!text && text.length > 3;
};
