import React, { useEffect, useMemo } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';
import { GraphQLFormattedError } from 'graphql';
import { useDeepCompareMemoize } from 'use-deep-compare-effect';

import { ErrorMessage } from '~/components/shared/ErrorMessage';
import { FadeIn } from '~/components/shared/FadeIn';
import { Events } from '~/constants/events';
import { Pinchpoints } from '~/constants/pinchpoints';
import { StorageKey } from '~/constants/storage';
import { getErrorId, isError } from '~/helpers/errors';
import { filterNull } from '~/helpers/utilityHelpers';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useStorage } from '~/hooks/useStorage';
import { useSubscription } from '~/hooks/useSubscription';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { useGetHighlightToTranslateScreenQuery, TranslateSelectionMutation } from '~/schema/background';

import { WordListCard } from '../../Sidebar/content/Progress/WordList/WordListCard';
import { WordListPlaceholder } from '../../Sidebar/content/Progress/WordList/WordListPlaceholder';
import { useHTTSidebar } from '../useHTTSidebar';

import { ActionBar } from './ActionBar';
import { TranslationView } from './TranslationView';

const { useColorScheme } = ToucanComponents;

export const HighlightToTranslateContainer = () => {
  const highlightToTranslateState = useHTTSidebar();
  const { isDarkMode } = useColorScheme();
  const { openSubscribePage } = useSubscription();
  const { targetLanguage } = useUserInfo();
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();
  const languageKey = targetLanguageInfo?.key;
  const [{ data: selectionData, errors }, setHighlightToTranslateInfo] = useStorage<{
    data?: TranslateSelectionMutation | null;
    errors?: GraphQLFormattedError[] | null;
  }>(StorageKey.HighlightToTranslateSidebarLastInfo, { data: null, errors: null });

  const sourcePhrase = selectionData?.translateSelection.original ?? highlightToTranslateState?.sourcePhrase ?? '';
  const containedPhrases = selectionData?.translateSelection.containedPhrases.filter(filterNull) ?? [];
  const containedTranslations = containedPhrases.map(phrase => phrase?.translationId).filter(filterNull) ?? [];
  const targetPhrase = selectionData?.translateSelection.translation ?? '';
  const { loading, error, data, refetch } = useGetHighlightToTranslateScreenQuery({
    variables: { input: { translationIds: containedTranslations } },
    skip: containedTranslations.length === 0,
  });

  const containedWords = containedPhrases.map(translation => translation.targetPhrase) ?? [];
  const wordList = data?.screens.highlightToTranslateScreen.translations ?? [];
  const pageError = errors?.[0];

  // Split target phrase by contained words to get the words to highlight
  // TODO this has lots of issues we should send down with the API on initial translate
  // the word that is replaced with the translation, so we can match it up correctly.
  const targetPhraseSegments = useMemo(
    () =>
      wordList.reduce(
        (accum, phrase) => {
          accum.forEach(({ text, highlight }, accumIndex) => {
            if (text.indexOf(phrase.targetPhrase) === -1 || highlight) {
              return;
            }
            const [before, ...after] = text.split(phrase.targetPhrase);
            accum.splice(
              accumIndex,
              1,
              { text: before, highlight: false, underline: false },
              { text: phrase.targetPhrase, highlight: true, underline: phrase?.isSaved ?? false },
              { text: after.join(phrase.targetPhrase), highlight: false, underline: false },
            );
          });
          return accum;
        },
        [{ highlight: false, text: targetPhrase, underline: false }],
      ),
    useDeepCompareMemoize([containedWords, wordList, targetPhrase]),
  );

  const onFeedback = (feedback: 'liked' | 'disliked') => () => {
    contentMessengers.trackEvent({
      eventName: Events.HighlightToTranslateSidebarFeedback,
      properties: {
        feedback,
        targetPhrase,
        targetLanguage,
        sourcePhrase,
      },
    });
  };

  useEffect(() => {
    if (highlightToTranslateState.pendingTranslateSelectionResp) {
      highlightToTranslateState.pendingTranslateSelectionResp.then(setHighlightToTranslateInfo).catch(err => {
        if (isError(err)) {
          setHighlightToTranslateInfo({ errors: [err] });
        }
      });
    }
  }, [highlightToTranslateState.pendingTranslateSelectionResp]);

  if (pageError) {
    return (
      <Box p={3}>
        <ErrorMessage message={t('wereExperiencingIssues')} id={getErrorId(pageError)} />
      </Box>
    );
  }

  return (
    <>
      <TranslationView
        sourcePhrase={sourcePhrase}
        targetPhrase={targetPhraseSegments}
        targetLanguage={targetLanguage}
      />
      <ActionBar onThumbsDown={onFeedback('disliked')} onThumbsUp={onFeedback('liked')} />
      {containedTranslations.length && (
        <Box width="100%" flex="1" height="100%">
          {(() => {
            if (loading) {
              return <WordListPlaceholder />;
            }
            if (error) {
              const errorId = getErrorId(error);
              return (
                <Box p={2}>
                  <ErrorMessage message={error.message} errorId={errorId} />
                </Box>
              );
            }

            return (
              <FadeIn
                show
                py={1}
                flex="1"
                minHeight="100%"
                bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]}
              >
                {wordList.map(translation => (
                  <WordListCard
                    key={translation.translationId}
                    {...translation}
                    hideProgressButton
                    refetch={refetch}
                    isPremiumSubscribed
                    openSubscribePage={() => {
                      openSubscribePage({ pinchpoint: Pinchpoints.HighlightToTranslateSidebar });
                    }}
                    languageKey={languageKey}
                  />
                ))}
              </FadeIn>
            );
          })()}
        </Box>
      )}
    </>
  );
};
