import { useMemo } from 'react';

import { useAtom } from 'jotai';

import {
  domTargetsAtom,
  addDomTargetsAtom,
  updateDomTargetsAtom,
  removeDomTargetsAtom,
  visibleDomTargetsAtom,
  translatableDomTargetsAtom,
  clearDomTargetsAtom,
} from './atoms';
import { DomTargets, PartialDomTargets } from './types';

export const useDomTargets = () => {
  const [targets] = useAtom(domTargetsAtom);
  return useMemo(
    () => ({
      targets,
    }),
    [targets],
  );
};

export const useVisibleDomTargets = () => useAtom(visibleDomTargetsAtom)[0];
export const useTranslatableDomTargets = () => useAtom(translatableDomTargetsAtom)[0];

export const useAddDomTargets = () =>
  // Casted because Jotai doesn't support setting the return type of a  atom.
  useAtom(addDomTargetsAtom)[1] as (update: PartialDomTargets) => DomTargets;

export const useUpdateDomTargets = () => useAtom(updateDomTargetsAtom)[1];
export const useRemoveDomTargets = () => useAtom(removeDomTargetsAtom)[1];
export const useClearDomTargets = () => useAtom(clearDomTargetsAtom)[1];
