import {
  PremiumExperimentConfig,
  PremiumFeatureName,
  PremiumFeaturePaywallStatus,
} from '~/constants/premiumFeatureConfig';

export const getFeaturePaywallStatus = (
  featureName: PremiumFeatureName,
  experimentConfig?: PremiumExperimentConfig,
) => {
  const featureConfig = experimentConfig?.premiumFeatures?.[featureName];
  const numFreeUses = typeof featureConfig === 'boolean' ? 0 : featureConfig?.numFreeUses ?? 0;

  if (typeof featureConfig === 'boolean' && featureConfig === true) {
    return PremiumFeaturePaywallStatus.Paid;
  }
  if (numFreeUses) {
    return PremiumFeaturePaywallStatus.FreeUses;
  }
  return PremiumFeaturePaywallStatus.Free;
};
