import React, { FC, useState } from 'react';

import { Box } from '~/components/Box';
import { SidebarSection } from '~/components/Sidebar/SidebarSection';
import { IconButton } from '~/components/shared/IconButton';

interface ActionBarProps {
  onThumbsUp: () => void;
  onThumbsDown: () => void;
}

export const ActionBar: FC<ActionBarProps> = ({ onThumbsDown, onThumbsUp }) => {
  const [selectedFeedback, setSelectedFeedback] = useState<'up' | 'down' | null>(null);

  const onFeedbackClick = (feedback: 'up' | 'down') => () => {
    setSelectedFeedback(feedback);
    if (feedback === 'up') {
      onThumbsUp();
    } else {
      onThumbsDown();
    }
  };

  return (
    <SidebarSection hasBottomLine={false} display="flex" alignItems="center">
      <Box flex="1" />
      <Box display="flex" flex="0">
        <IconButton
          active={selectedFeedback === 'down'}
          disabled={selectedFeedback === 'up'}
          icon="thumbs-down"
          activeIcon="thumbs-down"
          onClick={selectedFeedback ? () => {} : onFeedbackClick('down')}
          alt="Dislike"
          buttonSize="30px"
          iconSize="16px"
        />
        <IconButton
          ml={1}
          active={selectedFeedback === 'up'}
          disabled={selectedFeedback === 'down'}
          icon="thumbs-up"
          activeIcon="thumbs-up"
          onClick={selectedFeedback ? () => {} : onFeedbackClick('up')}
          alt="Like"
          buttonSize="30px"
          iconSize="16px"
        />
      </Box>
    </SidebarSection>
  );
};
