import React, { FC, useEffect, useMemo } from 'react';

import { shuffle } from 'lodash';
import { useDeepCompareMemoize } from 'use-deep-compare-effect';

import { Box, BoxProps } from '~/components/Box';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { StorageKey } from '~/constants/storage';
import { filterNull } from '~/helpers/utilityHelpers';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useQuiz } from '~/hooks/useQuiz';
import { QuestionsType } from '~/hooks/useQuiz/types';
import { useStorage } from '~/hooks/useStorage';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { QuizType, useQuizQuery, PracticeType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { Slide } from '../../shared/Slide';
import { Slides, SlideDirection } from '../../shared/Slides';

import { Footer } from './Footer';
import { Listen } from './Listen';
import { Match } from './Match';
import { PremiumUpsell } from './PremiumUpsell';
import { QuizError } from './QuizError';
import { QuizPlaceholder } from './QuizPlaceholder';
import { QuizRetry } from './QuizRetry';
import { Spelling } from './Spelling';
import { Swap } from './Swap';
import { isSingleWordQuestion } from './helpers';
import { QuestionBaseProps } from './types';
import { QuizSize, QuizSizeProvider } from './useQuizSize';

const makeQuestionKey = (question: QuestionsType) =>
  isSingleWordQuestion(question)
    ? `${question.translationId}:${question.questionType}`
    : `${question?.translationIds.join(':')}:${question.questionType}`;

interface InlineQuizProps extends BoxProps {
  onQuizExit: () => void;
  translationIds: Array<string>;
  quizType: QuizType;
  quizSize?: QuizSize;
  hideFooter?: boolean;
  onSetIdle: () => void;
  isInlineTranslation?: boolean;
  randomizeQuestions?: boolean;
  displayAnswerTimeout?: number;
  hasNoMorePracticeQuizzes?: boolean;
}

export const QuestionComponentMap: Record<PracticeType, React.FC<QuestionBaseProps>> = {
  [PracticeType.SwapPracticeQuestion]: Swap,
  [PracticeType.ListenPracticeQuestion]: Listen,
  [PracticeType.NonLatinSpellingPracticeQuestion]: Swap,
  [PracticeType.LatinSpellingPracticeQuestion]: Spelling,
  [PracticeType.MatchPracticeQuestion]: Match,
  [PracticeType.SourcePhraseSwapPracticeQuestion]: Swap,
};

/**
 * container for quiz
 */
export const InlineQuiz: FC<InlineQuizProps> = ({
  onQuizExit,
  translationIds,
  quizType,
  quizSize = QuizSize.Small,
  hideFooter = false,
  onSetIdle,
  isInlineTranslation = true,
  randomizeQuestions = true,
  displayAnswerTimeout,
  hasNoMorePracticeQuizzes = false,
  ...boxProps
}) => {
  const { startQuiz, currentQuiz, onError, onReset, retryQuiz, submitQuestion, stopQuiz } = useQuiz();
  const { status, question } = currentQuiz;
  const { data, error } = useQuizQuery({
    variables: {
      translationIds,
      quizType,
    },
  });
  const { t } = useTranslation();
  const { getFeaturePaywallStatus, isLoading: isConfigLoading } = usePremiumExperiment();
  const { isPremiumSubscribed } = useUserInfo();
  const isFeatureBehindPaywall =
    !isPremiumSubscribed &&
    getFeaturePaywallStatus(PremiumFeatureName.InlinePractice) === PremiumFeaturePaywallStatus.Paid;
  const [hasFreeInlinePracticePaywallUseOccurred, setHasFreeInlinePracticePaywallUseOccurred] = useStorage(
    StorageKey.HasFreeInlinePracticePaywallUseOccurred,
    false,
  );

  const questions = useMemo(
    () => (data?.quiz?.questions ?? []).filter(filterNull),
    useDeepCompareMemoize([data?.quiz?.questions ?? []]),
  );

  const showInlineQuizPremiumUpsell =
    status === QuizStatus.Finished &&
    ((quizType === QuizType.Practice && hasNoMorePracticeQuizzes) || isFeatureBehindPaywall);

  useEffect(() => {
    const hasReachedPaywalledLimit = isFeatureBehindPaywall && hasFreeInlinePracticePaywallUseOccurred;
    const hasReachedFreeLimit = !isFeatureBehindPaywall && hasNoMorePracticeQuizzes && !isConfigLoading;

    if (hasReachedPaywalledLimit || hasReachedFreeLimit) {
      stopQuiz();
      return;
    }

    if (questions.length) {
      startQuiz({
        questions: randomizeQuestions ? shuffle(questions) : questions,
        translationIds,
        quizType,
      });
    }
  }, [questions]);

  useEffect(() => {
    if (error) {
      const errorId = ('extensions' in error && error.extensions?.errorId) ?? error.message;
      onError(errorId);
    }
  }, [error]);

  useEffect(() => {
    if (status === QuizStatus.Finished && !showInlineQuizPremiumUpsell) {
      onReset();
      onQuizExit();
    }
  }, [status, showInlineQuizPremiumUpsell]);

  return (
    <QuizSizeProvider value={quizSize}>
      <Box
        display="flex"
        justifyContent="space-between"
        height={showInlineQuizPremiumUpsell ? 240 : 232}
        mb={showInlineQuizPremiumUpsell ? 0 : 2}
        flexDirection="column"
        width="240px"
        mr="16px"
        ml="-16px"
        px="16px"
        overflow="hidden"
        mt={showInlineQuizPremiumUpsell && isInlineTranslation ? '-16px' : 0}
        {...boxProps}
      >
        <Slides
          activeSlideName={question ? makeQuestionKey(question) : ''}
          activeSlideDirection={SlideDirection.Forward}
        >
          {status === QuizStatus.InProgress &&
            questions.map(thisQuestion => {
              const questionKey = makeQuestionKey(thisQuestion);
              const QuestionComponent = QuestionComponentMap[thisQuestion.questionType];

              return (
                <Slide slideName={questionKey} key={questionKey}>
                  <QuestionComponent
                    question={thisQuestion}
                    displayAnswerTimeout={displayAnswerTimeout}
                    retryQuiz={retryQuiz}
                    submitQuestion={result => {
                      if (isFeatureBehindPaywall && !hasFreeInlinePracticePaywallUseOccurred) {
                        setHasFreeInlinePracticePaywallUseOccurred(true);
                      }

                      if (hasNoMorePracticeQuizzes || isFeatureBehindPaywall) {
                        stopQuiz();
                      } else {
                        submitQuestion(result);
                      }
                    }}
                    quizType={quizType}
                  />
                </Slide>
              );
            })}
          {(() => {
            switch (status) {
              case QuizStatus.Error:
                return <QuizError />;
              case QuizStatus.Fail:
                return <QuizRetry />;
              case QuizStatus.Finished:
                return showInlineQuizPremiumUpsell ? (
                  <PremiumUpsell
                    onSetIdle={onSetIdle}
                    quizType={quizType}
                    headline={t(
                      isFeatureBehindPaywall ? 'youDiscoveredToucanPremiumFeature' : 'youreOutOfFreeQuestions',
                    )}
                    blurb={t('unlockUnlimitedQuizzesMoreToucanPremium')}
                    isInlineTranslation={isInlineTranslation}
                    onNotNowClick={() => {
                      onReset();
                      onQuizExit();
                    }}
                  />
                ) : (
                  <QuizPlaceholder />
                );
              case QuizStatus.Pending:
                return <QuizPlaceholder />;
              default:
                return null;
            }
          })()}
        </Slides>
      </Box>
      {!hideFooter && (
        <Footer
          isPremiumUpsellShown={showInlineQuizPremiumUpsell}
          onQuizExit={() => {
            onReset();
            onQuizExit();
          }}
        />
      )}
    </QuizSizeProvider>
  );
};
