/** @jsx jsx */
import React, { useEffect } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { StorageKey } from '~/constants/storage';
import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { useStorage } from '~/hooks/useStorage';
import { useToast } from '~/hooks/useToast';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import {
  SnoozeDuration,
  useChangeGloballyEnabledStateMutation,
  useRemoveDomainFromBanlistMutation,
} from '~/schema/background';
import { OverlayName, PanelName, SlideName } from '~/types/sidebar';

import { useSidebar } from '../Sidebar/useSidebar';

const { Button } = ToucanComponents;

export const PauseDetector = () => {
  const { t } = useTranslation();
  const { isOpen, openSidebar } = useSidebar();
  const { showToast, removeToast } = useToast();
  const [enableTranslations] = useChangeGloballyEnabledStateMutation();
  const [removeDomain] = useRemoveDomainFromBanlistMutation();

  const [isGloballyEnabled] = useStorage(StorageKey.GlobalEnableState, true);
  const [disabledDuration] = useStorage<SnoozeDuration | null>(StorageKey.GlobalDisabledDuration, null);
  const [banlist] = useStorage<string[]>(StorageKey.BanlistedDomains, []);
  const { currentDomain } = useCurrentDomain();
  const isDomainBanned = banlist.includes(currentDomain);

  const DurationIncrements: Record<SnoozeDuration, string> = {
    [SnoozeDuration.FiveMinutes]: t('forFiveMinutes'),
    [SnoozeDuration.OneHour]: t('forOneHour'),
    [SnoozeDuration.Evening]: t('until6pm'),
    [SnoozeDuration.Tomorrow]: t('untilTomorrow'),
    [SnoozeDuration.DailyNineToSix]: t('daily9amTo6pm'),
    [SnoozeDuration.Indefinitely]: t('indefinitely'),
  };

  // Global pause detection.
  useEffect(() => {
    if (!isGloballyEnabled && !isOpen && disabledDuration) {
      showToast({
        id: 'pauseDetectorToast',
        title: (
          <Trans
            i18nKey="youPausedToucan"
            components={[
              <span
                css={css`
                  text-transform: lowercase;
                `}
              >
                a
              </span>,
            ]}
            values={{ state: DurationIncrements[disabledDuration] }}
          />
        ),
        subtitle: (
          <Box display="flex" justifyContent="flex-start">
            <Box
              flexGrow={1}
              onClick={() => {
                removeToast('pauseDetectorToast');
                openSidebar({
                  panelName: PanelName.Settings,
                  slideName: SlideName.MainSettings,
                  overlayName: OverlayName.Snooze,
                });
              }}
            >
              <Button type="text">{t('editDuration')}</Button>
            </Box>
            <Box
              flexGrow={1}
              onClick={() => {
                removeToast('pauseDetectorToast');
                enableTranslations({ enabled: true });
              }}
            >
              <Button type="text">{t('turnToucanBackOn')}</Button>
            </Box>
          </Box>
        ),
        image: '/images/toucan-sleep.svg',
        onClick: () => {},
        timeout: 10000,
      });
    }
  }, [isGloballyEnabled, isOpen, disabledDuration]);

  // Domain pause detection.
  useEffect(() => {
    if (isDomainBanned && !isOpen) {
      showToast({
        id: 'pauseDetectorToastDomain',
        title: t('toucanPausedForDomain', { domain: currentDomain }),
        subtitle: (
          <Box display="flex" justifyContent="flex-start">
            <Box
              flexGrow={1}
              onClick={async () => {
                await enableTranslations({ enabled: false });
                await removeDomain({ domain: currentDomain });
                removeToast('pauseDetectorToastDomain');
              }}
            >
              <Button type="text">{t('pauseOnAllSites')}</Button>
            </Box>
            <Box
              flexGrow={1}
              onClick={async () => {
                await removeDomain({ domain: currentDomain });
                removeToast('pauseDetectorToastDomain');
              }}
            >
              <Button type="text">{t('turnToucanBackOn')}</Button>
            </Box>
          </Box>
        ),
        image: '/images/toucan-sleep.svg',
        onClick: () => {},
      });
    }
  }, [isDomainBanned, isOpen]);

  return null;
};
