/** @jsx jsx */
import { FC, ReactNode } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';
import browser from 'webextension-polyfill';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

import { HorizontalLine, HorizontalLineProps } from '../Sidebar/HorizontalLine';

const { ToucanIcon, useColorScheme } = ToucanComponents;
const logoUrl = browser.runtime.getURL('/images/logo-color-horizontal.svg');

interface HeaderProps {
  onRequestClose: () => void;
  className?: string;
  isLineVisible?: boolean;
  middleContent?: ReactNode;
  horizontalLineProps?: HorizontalLineProps;
  isMobileReferralShown: boolean;
  onMobileReferralClick: () => void;
}

const ToucanLogo = () => (
  <img
    src={logoUrl}
    alt="Toucan logo"
    css={css`
      height: 22px;
    `}
  />
);

const CloseButton: FC<Pick<HeaderProps, 'onRequestClose'>> = ({ onRequestClose }) => (
  <div
    onClick={() => onRequestClose()}
    onKeyDown={() => onRequestClose()}
    role="button"
    tabIndex={0}
    css={css`
      cursor: pointer;
    `}
  >
    <ToucanIcon
      icon="times"
      css={css`
        color: ${ToucanColors.gray[300]};
        width: 16px;
        height: 16px;
        display: flex;
        justify-content: flex-start;
      `}
    />
  </div>
);

/**
 * Shared header that shows the logo and close button
 * It is meant accept a series of panels that each control their own visibility via context
 * @param props
 * @param props.onRequestClose To close a modal
 * @param props.isLineVisible A flag to indicate if a bottom border is shown
 * @param props.middleContent JSX content to display between the logo and close button
 * @param props.horizontalLineProps Props to be passed to the HorizontalLine component
 */
const Header: FC<HeaderProps> = ({
  onRequestClose,
  className,
  isLineVisible = true,
  middleContent,
  horizontalLineProps,
  isMobileReferralShown,
  onMobileReferralClick,
  ...props
}) => {
  const { backgroundBase } = useColorScheme();
  const { t } = useTranslation();

  return (
    <div
      className={className}
      css={css`
        background-color: ${backgroundBase};
        width: 100%;
      `}
      {...props}
    >
      <Box
        display="flex"
        flex={1}
        alignItems="center"
        justifyContent="space-between"
        p={2}
        pl={isMobileReferralShown ? 0 : 2}
      >
        {isMobileReferralShown && (
          <Box data-testid="persistent-referral-upsell" role="button" onClick={onMobileReferralClick} marginRight={1}>
            <Image src="/images/sidebar/referral/paper-plane.svg" width={40} height={40} alt={t('paperAirplanes')} />
          </Box>
        )}
        <ToucanLogo />
        <Box flex="1">{middleContent}</Box>
        <CloseButton onRequestClose={onRequestClose} />
      </Box>
      {isLineVisible && <HorizontalLine heightPixels={1} {...horizontalLineProps} />}
    </div>
  );
};

export default Header;
