import xorWith from 'lodash/xorWith';

import { mainSelector } from '~/constants/html';
import { getNodeFromAddress, isHTMLElement } from '~/helpers/htmlHelpers';
import { TranslationData, TranslationReplacement, HydratedTranslationReplacement } from '~/schema/background';

import { ContentError } from './errorLogger';

type HydratedTranslation = Pick<
  HydratedTranslationReplacement,
  'id' | 'densityThreshold' | 'source' | 'targetAddress'
> & { replacement: Pick<TranslationReplacement, 'sourcePhrase' | 'address' | 'index' | 'translationId'> } & {
  translation: Pick<TranslationData, 'sourcePhrase' | 'targetPhrase' | 'translationId' | 'partOfSpeech'>;
};

/**
 * validateTranslations this is a method that takes in a list of translations that come form the initial
 * API call and will transform the data which formats it in a way that is the shape of out extension session data.
 * There is a couple of behaviors to know about this method.
 * @throws if there are a lot of invalid translations so we do not use them. It will also log the invalidation.
 * @param {boolean} isValid - the translations are valid or not.
 *
 */
export const validateTranslations = (hydratedTranslations: Array<HydratedTranslation>): boolean => {
  const mainNode = document.querySelector(mainSelector);
  if (!mainNode || !isHTMLElement(mainNode) || hydratedTranslations.length === 0) {
    return false;
  }
  const validatedTranslations = hydratedTranslations
    // Pull out the node that the translation is for.
    .map(translation => {
      const node = getNodeFromAddress(
        translation.targetAddress.split('.').map(i => parseInt(i, 10)),
        mainNode,
      );
      const hasText = node?.textContent?.includes(translation.translation.sourcePhrase);
      return {
        ...translation,
        node,
        hasText,
      };
    })
    // filter out nodes that we can not find the node for or we can not find the text in the node.
    .filter(translation => translation.node && isHTMLElement(translation.node) && translation.hasText);

  const invalidTranslations = xorWith(hydratedTranslations, validatedTranslations, (a, b) => a.id === b.id);
  const percentInvalid = (invalidTranslations.length / hydratedTranslations.length) * 100;

  if (percentInvalid > 10) {
    throw new ContentError('Translation hydration has been invalidated', {
      url: window.location.href,
      translations: hydratedTranslations,
      invalidTranslations,
    });
  }

  return true;
};
