import React, { useCallback, useEffect, useMemo } from 'react';

import { browserVendor } from '~/configs/config';
import { Events, EventSources } from '~/constants/events';
import { FeatureName } from '~/constants/features';
import { DensityValuesPrimitive } from '~/constants/settings';
import { noneEmoji } from '~/constants/shortcuts';
import { StorageKey } from '~/constants/storage';
import { getLanguageIdFromLanguage, hasPronunciation } from '~/helpers/languageHelpers';
import { useActiveTranslation, useSetActiveTranslation } from '~/hooks/useActiveTranslation';
import { useCopySwap } from '~/hooks/useCopySwap';
import { useDictionaryEntries } from '~/hooks/useDictionary';
import { useDomainEnabledEffect } from '~/hooks/useDomainEnabledEffect';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useQuiz } from '~/hooks/useQuiz';
import { useRemoveWord } from '~/hooks/useRemoveWord';
import { useStorage } from '~/hooks/useStorage';
import { useSubscription } from '~/hooks/useSubscription';
import {
  useBuiltTranslatedNodes,
  shouldShowTooltip,
  useTranslatedNodeTransitions,
  isActive,
  useTranslatedNodesActive,
} from '~/hooks/useTranslatedNodes';
import { TranslatedNodeSource } from '~/hooks/useTranslatedNodes/types';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useFeatureEnabled } from '~/libs/features/client';
import ContentMessengers from '~/messengers/contentMessengers';
import { useChangeGloballyEnabledStateMutation, useAddDomainFromBanlistMutation } from '~/schema/background';
import { LanguageId } from '~/schema/server';
import { BrowserVendors } from '~/types/browsers';
import { QuizStatus } from '~/types/quiz';

import { BirdCage, createTranslationErrorComponent, ModalError } from '../BirdCage';
import { useSidebar } from '../Sidebar/useSidebar';

import { Debugger } from './Debugger';
import { InlineTranslation } from './InlineTranslation';
import { InlineTranslationCard } from './InlineTranslationCard';
import { InlineTranslationCardInfo } from './InlineTranslationCard/InlineTranslationCardContainer';
import { TranslationInfo } from './InlineTranslationCard/types';
import { MobileTouchEventListeners } from './MobileTouchEventListeners';

const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;

interface InlineTranslationsProps {
  currentDomain: string;
}

export const InlineTranslations = ({ currentDomain }: InlineTranslationsProps) => {
  const {
    targetLanguage: userLanguage,
    isPremiumSubscribed,
    targetLanguageVariant,
    sourceLanguage,
    learningShortcutEmoji,
  } = useUserInfo();
  const {
    requestIdle,
    requestHovered,
    setIdle,
    setMounted,
    pinForQuiz,
    setFocused,
    setPinned,
  } = useTranslatedNodeTransitions();
  const currentActiveTranslation = useActiveTranslation();
  const translations = useDictionaryEntries();
  const translatedNodes = useBuiltTranslatedNodes();
  const [, setIsReportErrorModalOpenRequested] = useModalStorage(StorageKey.IsReportErrorModalOpenRequested, false);
  const [hasRemoveWordMessageDisplayed] = useStorage<boolean>(StorageKey.HasRemoveWordMessageDisplayed, false);
  const [, setIsRemoveWordModalOpenRequested] = useModalStorage(StorageKey.IsRemoveWordModalOpenRequested, false);
  const [, setTranslationIdToBeRemoved] = useStorage<string | null>(StorageKey.TranslationIdToBeRemoved, null);
  const [pronunciationEnabled] = useStorage(StorageKey.PronunciationEnabled, true);
  const { removeWord } = useRemoveWord();
  const isDomainPauseEnabled = useFeatureEnabled(FeatureName.DomainPauseHover);
  const hasSavedWordsSidebar = useFeatureEnabled(FeatureName.SavedWordsV2);
  const [changeGloballyEnabledState] = useChangeGloballyEnabledStateMutation();
  const [addDomainToBanlist] = useAddDomainFromBanlistMutation();
  const { openSidebar } = useSidebar();
  const setActiveTranslation = useSetActiveTranslation();
  const { currentQuiz } = useQuiz();
  const { reactivate, deactivate } = useTranslatedNodesActive();
  const { openSubscribePage } = useSubscription();
  const activeTranslationInfo = translations[currentActiveTranslation?.translationId ?? ''];
  const currentActiveTranslationId = currentActiveTranslation?.id;
  const currentQuizStatus = currentQuiz?.status;
  const targetLanguage = useMemo(
    () => (userLanguage ? getLanguageIdFromLanguage(userLanguage, targetLanguageVariant) : LanguageId.Es),
    [userLanguage, targetLanguageVariant],
  );
  const isPronunciationEnabled = pronunciationEnabled && userLanguage && hasPronunciation(userLanguage);
  const [hasSeenExpandedTranslation, setHasSeenExpandedTranslation] = useStorage<boolean>(
    StorageKey.HasSeenExpandedTranslation,
    false,
  );

  useEffect(() => {
    ContentMessengers.trackEvent({
      eventName: 'learning_opportunity_seen',
      properties: {
        source: 'inline_translation',
        domain: window.location.hostname,
      },
      destinationOptions: {
        datalake: true,
        skipSegment: true,
      },
    });
  }, []);

  const onClickRemoveWord = useCallback(
    async ({ translationId }: InlineTranslationCardInfo) => {
      if (currentActiveTranslation) {
        ContentMessengers.trackEvent({
          eventName: 'Remove word clicked',
          properties: {
            translation: currentActiveTranslation.targetPhrase,
            sourcePhrase: currentActiveTranslation.replacement,
            source: 'inline translation',
          },
        });
      }

      if (!hasRemoveWordMessageDisplayed) {
        setTranslationIdToBeRemoved(translationId);
        onShowRemoveWordModal();
        return;
      }

      await removeWord(translationId);
    },
    [hasRemoveWordMessageDisplayed, currentActiveTranslation],
  );

  const onClickReportError = useCallback(() => {
    if (currentActiveTranslation) {
      ContentMessengers.trackErrorReportFlagClicked({
        language: userLanguage,
        URL: window.location.href,
        translation: currentActiveTranslation.targetPhrase ?? '',
      });
    }
    onShowReportErrorModal();
  }, [currentActiveTranslation]);

  const onClickPause = useCallback(() => {
    if (currentActiveTranslation) {
      setIdle(currentActiveTranslation);
      setActiveTranslation({ id: 'fake' });
    }

    if (isDomainPauseEnabled) {
      addDomainToBanlist({ domain: currentDomain });
    } else {
      changeGloballyEnabledState({ enabled: false });
    }
  }, [currentActiveTranslation, currentDomain]);

  const onShowRemoveWordModal = useCallback(() => {
    if (currentActiveTranslation) {
      setIdle(currentActiveTranslation);
    }
    setIsRemoveWordModalOpenRequested(true);
  }, [currentActiveTranslation]);

  const onShowReportErrorModal = useCallback(() => {
    if (currentActiveTranslation) {
      setIdle(currentActiveTranslation);
    }
    setIsReportErrorModalOpenRequested(true);
  }, [currentActiveTranslation]);

  const onFocusHighlight = useCallback(
    ({ id, node, source }: TranslationInfo) => {
      // this fixes so that quiz doesn't revert back to hover when mousing over inline
      // TODO: update to please use hooks
      if (
        (currentQuizStatus === QuizStatus.InProgress && currentActiveTranslationId === id) ||
        (currentQuizStatus === QuizStatus.Finished && currentActiveTranslationId === id)
      ) {
        return;
      }
      requestHovered({ id, node });
      ContentMessengers.trackInlineTranslationHoverInHeartbeat();
      if (source === TranslatedNodeSource.Server) {
        ContentMessengers.serverDrivenTranslationsHoverCount();
      }
    },
    [currentQuizStatus, currentActiveTranslationId],
  );

  const onBlurHighlight = useCallback(
    ({ id, node }: TranslationInfo) => {
      requestIdle({
        id,
        node,
        status: currentActiveTranslation?.status,
      });
    },
    [currentActiveTranslation, currentActiveTranslation],
  );

  const onQuizEnter = useCallback(() => {
    if (!currentActiveTranslation) {
      return;
    }
    ContentMessengers.trackEvent({
      eventName: Events.LearningExperienceStarted,
      properties: {
        exercise: Events.InlinePracticeStarted,
        source: EventSources.InlineTranslation,
        url: currentDomain,
      },
      userOptions: { includePremiumSubscriptionStatus: true },
    });
    pinForQuiz(currentActiveTranslation);
    // Need to have some other indictor for quiz
  }, [currentActiveTranslation]);
  const onQuizExit = useCallback(() => {
    if (!currentActiveTranslation) {
      return;
    }
    ContentMessengers.trackEvent({
      eventName: Events.LearningExperienceCompleted,
      properties: {
        exercise: Events.InlinePracticeCompleted,
        source: EventSources.InlineTranslation,
        url: currentDomain,
      },
      userOptions: { includePremiumSubscriptionStatus: true },
    });
    requestHovered(currentActiveTranslation, true);
  }, [currentActiveTranslation]);

  // Replace content in clipboard
  useCopySwap();

  const [difficultyLevel] = useStorage<DensityValuesPrimitive>(StorageKey.DensityLevel, 1);

  useDomainEnabledEffect(
    isDomainEnabled => {
      if (!isDomainEnabled) {
        deactivate();
      } else {
        reactivate({ densityLevel: difficultyLevel });
      }
    },
    [deactivate, reactivate],
  );

  return (
    <>
      <Debugger />
      {isMobileSafari && <MobileTouchEventListeners />}
      {translatedNodes.map(translatedNode => {
        const { meta, node, replacement, translationId, id, source, targetPhrase } = translatedNode ?? {};
        const { sourcePhrase, targetPhrase: translationTargetPhrase } = translations[translationId] ?? {};
        if (!translationId || !node || !sourcePhrase) {
          return null;
        }
        return (
          <BirdCage key={id} errorComponent={createTranslationErrorComponent(replacement)}>
            <InlineTranslation
              id={id}
              isActive={isActive(translatedNode)}
              onFocus={onFocusHighlight}
              onBlur={onBlurHighlight}
              node={node}
              translationId={translationId ?? ''}
              source={source}
              replacement={replacement}
              targetLanguage={userLanguage}
              mountNode={setMounted}
              isQuizActive={
                currentQuiz.translationIds.some(thisId => thisId === translationId) &&
                (currentQuiz.status === QuizStatus.InProgress || currentQuiz.status === QuizStatus.Finished)
              }
              sourcePhrase={sourcePhrase}
              targetPhrase={targetPhrase ?? translationTargetPhrase}
              isSaved={hasSavedWordsSidebar ? translatedNode.meta?.isSaved : false}
              hasSeenExpandedTranslation={hasSeenExpandedTranslation}
              setHasSeenExpandedTranslation={() => setHasSeenExpandedTranslation(true)}
              learningShortcutEmoji={meta?.isActiveLearningShortcutPhrase ? learningShortcutEmoji : undefined}
              isActiveLearningShortcutPhrase={meta?.isActiveLearningShortcutPhrase ?? false}
            />
          </BirdCage>
        );
      })}
      {activeTranslationInfo && currentActiveTranslation && (
        <BirdCage errorComponent={ModalError}>
          <InlineTranslationCard
            key={currentActiveTranslation.translationId}
            targetLanguage={(currentActiveTranslation.targetLanguage ?? targetLanguage) as LanguageId}
            sourcePhrase={currentActiveTranslation.replacement}
            targetPhrase={
              currentActiveTranslation.targetPhrase ?? translations[currentActiveTranslation.translationId].targetPhrase
            }
            source={currentActiveTranslation.source}
            status={currentActiveTranslation.status}
            translatedNodeId={currentActiveTranslation.id}
            node={currentActiveTranslation.node}
            translationId={currentActiveTranslation.translationId}
            isOpen={shouldShowTooltip(currentActiveTranslation)}
            onClickRemoveWord={onClickRemoveWord}
            onClickReportError={onClickReportError}
            onClickPause={onClickPause}
            onQuizEnter={onQuizEnter}
            onQuizExit={onQuizExit}
            domain={currentDomain}
            openSidebar={openSidebar}
            isPremiumSubscribed={isPremiumSubscribed}
            openSubscribePage={openSubscribePage}
            setFocused={setFocused}
            setIdle={setIdle}
            requestIdle={requestIdle}
            isPronunciationEnabled={isPronunciationEnabled}
            sourceLanguage={sourceLanguage}
            difficultyLevel={currentActiveTranslation.difficultyLevel}
            isActiveLearningShortcutPhrase={currentActiveTranslation.meta?.isActiveLearningShortcutPhrase ?? false}
            learningShortcutEmoji={
              currentActiveTranslation.meta?.isActiveLearningShortcutPhrase ? learningShortcutEmoji : noneEmoji
            }
            setPinned={setPinned}
            requestHovered={requestHovered}
          />
        </BirdCage>
      )}
    </>
  );
};
