import { supportedSourceLanguageIds, SupportedSourceLanguages } from '~/constants/language';
import { LanguageId } from '~/schema/server';

/**
 * getSupportedUILanguage is a method that parses output of getUILanguage and
 * finds a Toucan compatible language id. The possible formats for this header are as follows:
 * - en
 * - en-US
 * - en-US,en;q=0.5
 */
export const getSupportedUILanguage = (): SupportedSourceLanguages => {
  const parsedLanguage = browser.i18n.getUILanguage().split(',').shift()?.split(/-|_/).shift() ?? LanguageId.En;

  if (supportedSourceLanguageIds.includes(parsedLanguage as SupportedSourceLanguages)) {
    return parsedLanguage as SupportedSourceLanguages;
  }
  // Fallback to English ( includes "*" )
  return LanguageId.En;
};
