/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { ContextlessLearningType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { LearningBlurb } from '../LearningBlurb';
import { QuestionsType, ContainerSize } from '../types';

import { FlashCardContent } from './FlashCardContent';
import { Footer } from './Footer';
import { MultipleChoiceContent } from './MultipleChoiceContent';
import { PremiumUpsell } from './PremiumUpsell';
import { UpsellNotice } from './UpsellNotice';

const { Typography, useColorScheme } = ToucanComponents;

interface ContextlessLearningQuizProps {
  question: QuestionsType;
  onExitQuiz: () => void;
  onClickUnlockPremium: () => void;
  onCompleteQuestion: (quizStatus: QuizStatus.Pass | QuizStatus.Fail) => void;
  questionNumber: number;
  totalNumQuestions: number;
  quizTranslationIds: string[];
  referralCTAText?: string;
  isCLMIntroScreenDisabled: boolean;
  adhocEventTracking: ({ quizType }: { quizType: ContextlessLearningType }) => void;
  containerSize: ContainerSize;
}

export const ContextlessLearningQuiz: FC<ContextlessLearningQuizProps> = ({
  question,
  onExitQuiz,
  onClickUnlockPremium,
  onCompleteQuestion,
  questionNumber,
  totalNumQuestions,
  quizTranslationIds,
  referralCTAText,
  isCLMIntroScreenDisabled,
  adhocEventTracking,
  containerSize,
}) => {
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const { isPremiumSubscribed } = useUserInfo();
  const { targetLanguageInfo } = useLanguageInfo();
  const { config, getFeaturePaywallStatus } = usePremiumExperiment();

  const isNonLatin = !!targetLanguageInfo?.variants;
  const languageKey = targetLanguageInfo?.key;
  const featurePaywallStatus = getFeaturePaywallStatus(PremiumFeatureName.ExtraPractice);
  const isPremiumFeature = featurePaywallStatus === PremiumFeaturePaywallStatus.Paid;
  const extraPracticeConfig = config?.premiumFeatures?.extraPractice;
  const isLimitedPremiumFeature = featurePaywallStatus === PremiumFeaturePaywallStatus.FreeUses;
  const numFreeUses = typeof extraPracticeConfig === 'boolean' ? 0 : extraPracticeConfig?.numFreeUses ?? 0;

  const getUpsellContent = () => {
    if (isPremiumSubscribed) {
      return null;
    }

    // If the feature is behind a paywall, show the premium upsell after 2 free uses
    if (questionNumber === 2 && isPremiumFeature) {
      return (
        <PremiumUpsell
          onClickStartFreeTrial={onClickUnlockPremium}
          onClickNotNow={onExitQuiz}
          containerSize={containerSize}
        />
      );
    }

    // If the feature has limited free uses, show the upsell after the last free use
    if (isLimitedPremiumFeature && questionNumber === numFreeUses + 1) {
      return <UpsellNotice onClickUnlockPremium={onClickUnlockPremium} />;
    }

    return null;
  };

  const upsellContent = getUpsellContent();

  return (
    <Box width="100%" display="flex" flexDirection="column">
      <LearningBlurb referralCTAText={referralCTAText}>
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.blue[500]};
          `}
        >
          {t('doYouRememberThisWord')}
        </Typography>
      </LearningBlurb>

      {upsellContent ? (
        <Box flex="1">{upsellContent}</Box>
      ) : (
        // We assume that the learning type of the first learning is the same as the rest and then know which quiz to use
        <React.Fragment>
          {question && 'sourcePhraseAnswer' in question ? (
            <MultipleChoiceContent
              onCompleteQuestion={onCompleteQuestion}
              questionNumber={questionNumber}
              questionTargetPhrase={question.targetPhrase}
              questionSourcePhraseAnswer={question.sourcePhraseAnswer}
              questionSourcePhraseOptions={question.sourcePhraseOptions}
              languageKey={languageKey}
              isCLMIntroScreenDisabled={isCLMIntroScreenDisabled}
              adhocEventTracking={adhocEventTracking}
            />
          ) : (
            <FlashCardContent
              quizTranslationIds={quizTranslationIds}
              onCompleteQuestion={onCompleteQuestion}
              questionNumber={questionNumber}
              questionTargetPhrase={question.targetPhrase}
              questionSourcePhrase={question.sourcePhrase}
              isNonLatin={isNonLatin}
              languageKey={languageKey}
              isCLMIntroScreenDisabled={isCLMIntroScreenDisabled}
              adhocEventTracking={adhocEventTracking}
              isPremiumSubscribed={isPremiumSubscribed}
            />
          )}
        </React.Fragment>
      )}

      <Footer
        onExitQuiz={onExitQuiz}
        onClickUnlockPremium={onClickUnlockPremium}
        questionNumber={questionNumber}
        totalNumQuestions={totalNumQuestions}
        areStepsVisible={!isPremiumFeature || isPremiumSubscribed}
      />
    </Box>
  );
};
