import { useEffect } from 'react';

import { useAtom } from 'jotai';

import { useTranslation } from '~/libs/i18n';
import { TranslationKeys } from '~/libs/i18n/types';
import { AccountCreditRewardInfo, UserReferralInfoQuery, useUserReferralInfoQuery } from '~/schema/background';

import { useUserInfo } from '../useUserInfo';

import { referralAtom, setReferralAtom } from './atoms';

const useReferralInfo = () => useAtom(referralAtom)[0];
const useSetReferralInfo = () => useAtom(setReferralAtom)[1];

const referralRewardMap: Record<
  'accountCredit' | 'schoolDonation',
  { rewardDescriptionKey: TranslationKeys; ctaTextKey: TranslationKeys }
> = {
  accountCredit: {
    rewardDescriptionKey: 'toucanThanksYou',
    ctaTextKey: 'inviteFriendsEarn',
  },
  schoolDonation: {
    rewardDescriptionKey: 'toucanGivesFreePremium',
    ctaTextKey: 'inviteFriendHelpSchool',
  },
};

export const isAccountCreditReferralReward = (
  rewardInfo: NonNullable<NonNullable<UserReferralInfoQuery['me']>['userReferral']>['rewardInfo'],
): rewardInfo is Pick<AccountCreditRewardInfo, 'rewardType' | 'metadata'> => rewardInfo.rewardType === 'accountCredit';

export const useReferral = () => {
  const { t } = useTranslation();
  const referralInfo = useReferralInfo();
  const setReferralInfo = useSetReferralInfo();
  const { data, loading, error } = useUserReferralInfoQuery();
  const { sourceLanguage } = useUserInfo();
  const referralUrl = data?.me?.userReferral.linkUrl;
  const errorId = error ? ('extensions' in error && error.extensions?.errorId) ?? error.message : null;
  const userCountryCode = data?.me?.countryCode;
  const rewardInfo = data?.me?.userReferral?.rewardInfo;
  const toucanRewardOfferedCurrencyCode =
    (rewardInfo && isAccountCreditReferralReward(rewardInfo) && rewardInfo.metadata.currencyCode) || 'USD';
  const toucanRewardOfferedAmount =
    (rewardInfo && isAccountCreditReferralReward(rewardInfo) && rewardInfo.metadata.amount) || 0;
  const rewardInfoType = data?.me?.userReferral?.rewardInfo?.rewardType;

  let toucanRewardOfferedPerReferral;

  useEffect(() => {
    if (toucanRewardOfferedCurrencyCode && sourceLanguage && userCountryCode && referralUrl && !referralInfo) {
      toucanRewardOfferedPerReferral = new Intl.NumberFormat(`${sourceLanguage}-${userCountryCode}`, {
        style: 'currency',
        currency: toucanRewardOfferedCurrencyCode,
        ...(Number.isInteger(toucanRewardOfferedAmount) && { minimumFractionDigits: 0, maximumFractionDigits: 0 }),
      }).format(toucanRewardOfferedAmount);

      setReferralInfo({
        toucanRewardOffered: toucanRewardOfferedPerReferral,
        url: referralUrl,
      });
    }
  }, [toucanRewardOfferedCurrencyCode, sourceLanguage, userCountryCode, referralUrl, referralInfo]);

  const rewardKeys =
    referralRewardMap[rewardInfoType as keyof typeof referralRewardMap] ?? referralRewardMap.accountCredit;
  const referralSidebarTypeDescription = t(rewardKeys.rewardDescriptionKey, {
    moneyReward: referralInfo?.toucanRewardOffered ?? '',
  });
  const referralCTAText = t(rewardKeys.ctaTextKey, {
    moneyReward: referralInfo?.toucanRewardOffered ?? '',
  });

  return {
    toucanRewardOfferedPerReferral: referralInfo?.toucanRewardOffered,
    referralUrl: referralInfo?.url,
    loading,
    errorId,
    referralSidebarTypeDescription,
    referralCTAText,
  };
};
