/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

import { ContainerSize } from '../types';

const { useColorScheme, Typography, Button } = ToucanComponents;

interface PremiumUpsellProps {
  onClickStartFreeTrial: () => void;
  onClickNotNow: () => void;
  containerSize: ContainerSize;
}

export const PremiumUpsell = ({ onClickStartFreeTrial, onClickNotNow, containerSize }: PremiumUpsellProps) => {
  const theme = useTheme();
  const { isDarkMode, textLevel1, textLevel2 } = useColorScheme();
  const { t } = useTranslation();
  const containerHeight = {
    [ContainerSize.Small]: '100%',
    [ContainerSize.Medium]: '14vw',
    [ContainerSize.Large]: '36.2vw',
  };

  return (
    <Box data-testid="premium-upsell" my={2} position="relative" height={containerHeight[containerSize]}>
      <Box position="absolute" width="100%" height="100%" top={0}>
        <Image
          src={`/images/contextless-learning-moment/bg-${isDarkMode ? 'dark' : 'light'}-${containerSize}.svg`}
          width="100%"
          height="100%"
          alt={t('skyWithCloud')}
        />
      </Box>
      <Box display="flex" flexDirection="column" mx={2} alignItems="center">
        <Typography
          variant="md"
          isBold
          color={textLevel1}
          css={css`
            margin-top: ${theme.spacing(containerSize === ContainerSize.Large ? 5 : 3)};
            text-align: center;
            z-index: 1;
          `}
        >
          {t('youDiscoveredPremiumFeature')}
        </Typography>
        <Typography
          variant="xs"
          color={textLevel2}
          css={css`
            margin-top: ${theme.spacing(0.5)};
            text-align: center;
            z-index: 1;
          `}
        >
          {t('accessUnlimitedQuizzesToucanPremium')}
        </Typography>

        <Button
          size="small"
          type="alert"
          onClick={onClickStartFreeTrial}
          css={css`
            margin-top: ${theme.spacing(3)};
          `}
        >
          {t('startFreeTrial')}
        </Button>
        <Typography
          variant="xs"
          css={css`
            margin-top: ${theme.spacing(1)};
          `}
        >
          <Button
            type="text"
            onClick={onClickNotNow}
            css={css`
              margin-bottom: ${theme.spacing(3)};
            `}
          >
            {t('notNow')}
          </Button>
        </Typography>
      </Box>
    </Box>
  );
};
