/* eslint-disable max-classes-per-file */
import { ErrorCode } from './codes';
import { ExceptionInfo, ExtensionEnv } from './types';

export class ErrorWithCode extends Error {
  message: string;

  code: ErrorCode;

  constructor(message: string, code: ErrorCode) {
    super(message);
    this.message = message;
    this.code = code;
  }
}

export class EnvError extends Error {
  env: ExtensionEnv;

  exceptionInfo?: ExceptionInfo;

  constructor(env: ExtensionEnv, message: string) {
    super(message);
    this.env = env;
  }

  static from(env: ExtensionEnv, message: string, exceptionInfo?: ExceptionInfo) {
    switch (env) {
      case 'content':
        return new ContentError(message, exceptionInfo);
      case 'popup':
        return new PopupError(message, exceptionInfo);
      case 'background':
      default:
        return new BackgroundError(message, exceptionInfo);
    }
  }
}

export class ContentError extends EnvError {
  exceptionInfo?: ExceptionInfo;

  constructor(message: string, exceptionInfo?: ExceptionInfo) {
    super('content', message);
    this.exceptionInfo = exceptionInfo;
  }
}

export class PopupError extends EnvError {
  exceptionInfo?: ExceptionInfo;

  constructor(message: string, exceptionInfo?: ExceptionInfo) {
    super('popup', message);
    this.exceptionInfo = exceptionInfo;
  }
}

export class BackgroundError extends EnvError {
  exceptionInfo?: ExceptionInfo;

  constructor(message: string, exceptionInfo?: ExceptionInfo) {
    super('background', message);
    this.exceptionInfo = exceptionInfo;
  }
}
