/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';

import { LearningBlurb } from '../LearningBlurb';

const { Typography, Button, useColorScheme, ToucanIcon } = ToucanComponents;

interface LauncherProps {
  onClickBegin: () => void;
  onClickDontShowMe: () => void;
  isReferralShown?: boolean;
}

export const Launcher: FC<LauncherProps> = ({ onClickBegin, onClickDontShowMe, isReferralShown }) => {
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();

  return (
    <LearningBlurb isReferralShown={isReferralShown}>
      <Typography
        variant="sm"
        isBold
        css={css`
          color: ${isDarkMode ? ToucanColors.white : ToucanColors.blue[500]};
        `}
      >
        {t('upLittleExtraLanguagePractice', {
          targetLanguage: targetLanguageInfo?.language as string,
          context: targetLanguageInfo?.languageId as string,
        })}
      </Typography>

      <Box mt={0.25}>
        <Typography variant="xs">
          <Button
            onClick={onClickDontShowMe}
            type="text"
            css={css`
              display: flex;
            `}
          >
            <ToucanIcon
              icon="eye-slash"
              css={css`
                width: 12px;
                height: 12px;
                margin-right: ${theme.spacing(1)};
                color: inherit;
              `}
            />
            {t('dontShowMeThis')}
          </Button>
        </Typography>
      </Box>

      <Button
        type="utility"
        onClick={onClickBegin}
        css={css`
          margin: ${theme.spacing(2)} -${theme.spacing(2)} -${theme.spacing(2)} -${theme.spacing(2)};
          border-radius: 0 0 8px 8px;
        `}
      >
        {t('begin')}
      </Button>
    </LearningBlurb>
  );
};
