import React, { createContext, FC, useContext, useEffect, useRef, useState } from 'react';

export enum PortStatus {
  Connected,
  Disconnected,
}

interface PortDisconnectedContext {
  status: PortStatus.Disconnected;
  port: null;
}

interface PortConnectedContext {
  status: PortStatus.Connected;
  port: browser.runtime.Port;
}

interface PortProps {
  name: string;
}

type PortContext = PortConnectedContext | PortDisconnectedContext;

export const portContext = createContext<PortContext>({ port: null, status: PortStatus.Disconnected });

export const Port: FC<PortProps> = ({ children, name }) => {
  const [port, setPort] = useState<browser.runtime.Port | null>(null);
  const errorRef = useRef(0);
  useEffect(() => {
    let thisPort = browser.runtime.connect({ name });
    setPort(thisPort);

    const onDisconnect = () => {
      errorRef.current += 1;

      if (errorRef.current <= 3) {
        // Cleanup old port
        thisPort.onDisconnect.removeListener(onDisconnect);
        // Try to connect a new port
        thisPort = browser.runtime.connect({ name });
        setPort(thisPort);
        // Rebind retry logic
        thisPort.onDisconnect.addListener(onDisconnect);
      }
    };

    thisPort.onDisconnect.addListener(onDisconnect);

    return () => {
      // NOTE: Disconnect first to avoid attempting reconnect
      thisPort.onDisconnect.removeListener(onDisconnect);
      thisPort.disconnect();
      setPort(null);
    };
  }, [name]);
  return (
    <portContext.Provider
      value={port ? { status: PortStatus.Connected, port } : { status: PortStatus.Disconnected, port: null }}
    >
      {children}
    </portContext.Provider>
  );
};

export const usePort = () => useContext(portContext).port;
export const usePortStatus = () => useContext(portContext).status;
