import React, { FC } from 'react';

import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { ReferralCta } from '~/components/Referral/ReferralCta';
import { Image } from '~/components/shared/Image';
import { Events } from '~/constants/events';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { PanelName, SlideName } from '~/types/sidebar';

import { useSidebar } from '../Sidebar/useSidebar';

const { useColorScheme } = ToucanComponents;

interface LearningBlurbProps {
  blurbColor?: string;
  imagePath?: string;
  isReferralShown?: boolean;
  referralCTAText?: string;
}

export const LearningBlurb: FC<LearningBlurbProps> = ({
  children,
  blurbColor,
  imagePath,
  isReferralShown,
  referralCTAText,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const { openSidebar } = useSidebar();

  const onClickReferral = () => {
    contentMessengers.trackEvent({
      eventName: Events.ReferralCtaClicked,
      properties: {
        pinchpointLocation: Pinchpoints.ContextlessLearningReferralCta,
      },
    });
    openSidebar({
      panelName: PanelName.Referral,
      slideName: SlideName.MainReferral,
    });
  };

  return (
    <Box display="flex" gap={theme.spacing(1)} alignItems="flex-start">
      <Image src={imagePath || '/images/toucan-circle.svg'} alt={t('toucanInCircle')} width="48px" height="48px" />
      <Box display="flex" flexDirection="column" alignItems="center">
        <Box
          flex={1}
          display="flex"
          flexDirection="column"
          p={2}
          bgcolor={blurbColor || (isDarkMode ? ToucanColors.gray[500] : ToucanColors.blue[100])}
          borderRadius="8px"
        >
          {children}
        </Box>
        {isReferralShown && referralCTAText && (
          <ReferralCta referralCTAText={referralCTAText} pt={1} onClickReferral={onClickReferral} />
        )}
      </Box>
    </Box>
  );
};
