/** @jsx jsx */
import React, { RefObject, useEffect, useState } from 'react';

import { css, jsx, keyframes } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import browser from 'webextension-polyfill';

import { Box } from '~/components/Box';
import { CustomizationMenu } from '~/components/InlineTranslations/InlineTranslationCard/Shortcut';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { SubscriptionRedirectContainer } from '~/components/shared/SubscriptionRedirectContainer';
import { Tag } from '~/components/shared/Tag';
import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { noneEmoji } from '~/constants/shortcuts';
import { StorageKey } from '~/constants/storage';
import { formatDifficulty } from '~/helpers/difficultyTier';
import { getLanguageByLanguageId } from '~/helpers/languageHelpers';
import { useMediaQuery } from '~/hooks/useMediaQuery';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useStorage } from '~/hooks/useStorage';
import { useSubscription } from '~/hooks/useSubscription';
import { useTranslationCard } from '~/hooks/useTranslationCard';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { PartOfSpeech, LanguageId } from '~/schema/server';
import { PanelName, SlideName } from '~/types/sidebar';

import { IconButton } from '../../shared/IconButton';
import { LottieAnimation } from '../../shared/LottieAnimation';
import { Callout } from '../Callout';
import { ProgressBarCallToAction } from '../ProgressBarCallToAction';
import { ViewWords } from '../ViewWords';

import { PremiumUpsell } from './PremiumUpsell';

const {
  Highlight: { Action },
  useColorScheme,
  Typography,
  ProgressBar,
} = ToucanComponents;

interface InlineTranslationCardProps extends ReturnType<typeof useTranslationCard> {
  difficultyLevel?: string;
  onOpenSettings: () => void;
  onClickReportError: () => void;
  onClickPause: () => void;
  onClickBook: () => void;
  onClickRemoveWord: () => void;
  onProgressBarAnimationComplete: () => void;
  targetPhrase: string;
  sourcePhrase: string;
  partOfSpeech?: string;
  textToSpeak?: string;
  isKnown: boolean;
  isSaved: boolean;
  isLearned: boolean;
  hasUserTranslation: boolean;
  progressionScore?: number;
  lastSeenProgressionScore?: number;
  forceCloseCard: () => void;
  showSettingCallout: boolean;
  showPracticeCallout: boolean;
  loading: boolean;
  audioRef: RefObject<HTMLDivElement>;
  progressBarRef: RefObject<HTMLDivElement>;
  hasCelebrated: boolean;
  targetLanguage: LanguageId;
  learningShortcutEmoji?: string;
  onShortcutEmojiClick: () => void;
  isEmojiCustomizationMenuEnabled: boolean;
  setIsEmojiCustomizationMenuEnabled: (value: boolean) => void;
  onShortcutEmojiSelection: (emoji: string) => void;
  isActiveLearningShortcutPhrase: boolean;
}

const RANGE_LOWER = 0;
const BASE_LINE = 15;
const RANGE_UPPER = 100 + BASE_LINE;
const calculateProgressWithBaseline = (input: number) =>
  ((input + BASE_LINE - RANGE_LOWER) * 100) / (RANGE_UPPER - RANGE_LOWER);
const pulse = keyframes`
  0% {
    box-shadow: 0px 0px 0px 0px ${ToucanColors.yellow[300]};
  }
  75% {
    box-shadow: 0px 0px 0px 16px ${ToucanColors.yellow[300]}00;
  }
`;

export const InlineTranslationCard = ({
  targetPhrase,
  targetLanguage,
  sourcePhrase,
  partOfSpeech,
  textToSpeak,
  difficultyLevel,
  onClickCheckMark,
  onClickSaveWord,
  hasUserTranslation,
  isKnown,
  isSaved,
  isLearned,
  progressionScore,
  lastSeenProgressionScore,
  onOpenSettings,
  onClickReportError,
  onClickRemoveWord,
  onClickBook,
  onClickPause,
  forceCloseCard,
  showSettingCallout,
  showPracticeCallout,
  progressButtonExtraText,
  progressButtonText,
  onProgressButtonClick,
  progressButtonImgAlt,
  progressButtonImgSrc,
  loading,
  onProgressBarAnimationComplete,
  audioRef,
  progressBarRef,
  hasCelebrated,
  onAudioPlayRequested,
  learningShortcutEmoji,
  onShortcutEmojiClick,
  isEmojiCustomizationMenuEnabled,
  setIsEmojiCustomizationMenuEnabled,
  onShortcutEmojiSelection,
  isActiveLearningShortcutPhrase,
}: InlineTranslationCardProps) => {
  const theme = useTheme();
  const { isDarkMode, backgroundAccent, textLevel1 } = useColorScheme();
  const { t } = useTranslation();
  const [isCelebrationAnimationStopped, setIsCelebrationAnimationStopped] = useState(true);
  const isViewportBelowSm = useMediaQuery(theme.breakpoints.down('sm'));
  const language = getLanguageByLanguageId(targetLanguage as LanguageId);
  const difficultyTier = formatDifficulty(difficultyLevel, t);
  const { openSidebar } = useSidebar();
  const { openSubscribePage } = useSubscription();
  const { isPremiumSubscribed } = useUserInfo();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const [hasSeenShortcutsCustomization, setHasSeenShortcutsCustomization] = useStorage<boolean>(
    StorageKey.HasSeenShortcutsCustomization,
    false,
  );

  const isRemoveWordPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.RemoveWord) === PremiumFeaturePaywallStatus.Paid;
  const isSaveWordPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.SaveWord) === PremiumFeaturePaywallStatus.Paid;
  const isIKnowThisPremiumFeature =
    getFeaturePaywallStatus(PremiumFeatureName.IKnowThisWord) === PremiumFeaturePaywallStatus.Paid;

  let iconButtonWidth = '30px';
  let iconButtonIconWidth = '14px';
  let titleVariant: 'sm' | 'md' = 'sm';
  let toucanLogoWidth = '16px';
  let actionButtonWidth = '14px';
  let isProgressBarCtaVisible = true;

  if (isViewportBelowSm) {
    iconButtonWidth = '40px';
    iconButtonIconWidth = '16px';
    titleVariant = 'md';
    toucanLogoWidth = '20px';
    actionButtonWidth = '16px';
    isProgressBarCtaVisible = false;
  }

  useEffect(() => {
    if (progressionScore === 100 && lastSeenProgressionScore && lastSeenProgressionScore < 100 && !hasCelebrated) {
      setIsCelebrationAnimationStopped(false);
    }
  }, [progressionScore, lastSeenProgressionScore, hasCelebrated]);

  /**
   * Only show pulsing animation once. Save attribute locally.
   */
  const onEmojiClick = () => {
    // for pulsing animation
    if (!hasSeenShortcutsCustomization) {
      setHasSeenShortcutsCustomization(true);
    }
    onShortcutEmojiClick();
  };

  return (
    <React.Fragment>
      <Box
        position="relative"
        data-testid="inline-translation-card"
        aria-live="polite"
        aria-busy={loading ? 'true' : 'false'}
      >
        {!isCelebrationAnimationStopped && (
          <Box
            position="absolute"
            top="0"
            left="0"
            width={`calc(100% + ${theme.spacing(4)})`}
            height={`calc(100% + ${theme.spacing(4)})`}
            ml={-2}
            mt={-2}
            css={css`
              pointer-events: none;
            `}
          >
            <LottieAnimation
              filePath="/animations/hoverstate-fluent-confetti.json"
              loop={false}
              rendererSettings={{ preserveAspectRatio: 'xMidYMin slice' }}
              isStopped={isCelebrationAnimationStopped}
            />
          </Box>
        )}
        <Box display="flex" justifyContent="space-between" alignItems="center" width="100%" mb={2}>
          <Box ref={audioRef} data-testid="play-audio">
            <AudioPlayer
              targetPhrase={textToSpeak || ''}
              language={language}
              onPlay={onAudioPlayRequested}
              onTapHold={() => {
                const eventName = 'Audio Controls opened';
                const properties = {
                  source: 'hoverState',
                };
                contentMessengers.trackEvent({ eventName, properties });
                openSidebar({ panelName: PanelName.Settings, slideName: SlideName.AudioControls });
              }}
              isDisabled={!textToSpeak}
              disabledMessage={t('toucanUnableAudio')}
            />
          </Box>
          <Box flex="1" display="flex" flexDirection="row" justifyContent="flex-end">
            {hasUserTranslation && (
              <React.Fragment>
                <Box
                  ml={1}
                  data-testid={`${isSaved ? 'remove' : 'add'}-save-word`}
                  width={isViewportBelowSm ? '40px' : '30px'}
                >
                  <IconButton
                    active={isSaved}
                    icon="bookmark-outline"
                    activeIcon="bookmark-solid"
                    alt={t(isSaveWordPremiumFeature && !isPremiumSubscribed ? 'saveWordPremiumFeature' : 'save')}
                    onClick={
                      isSaveWordPremiumFeature && !isPremiumSubscribed
                        ? () => {
                            openSubscribePage({
                              pinchpoint: Pinchpoints.InlineTranslationSaveWordCta,
                            });
                            forceCloseCard();
                          }
                        : onClickSaveWord
                    }
                    buttonSize={iconButtonWidth}
                    iconSize={iconButtonIconWidth}
                    disabled={isSaveWordPremiumFeature && !isPremiumSubscribed}
                  />
                </Box>
                <Box position="relative" display="flex" justifyContent="flex-end">
                  <Box
                    ml={1}
                    data-testid={`${isKnown ? 'remove' : 'add'}-known-word`}
                    position="relative"
                    width={isViewportBelowSm ? '40px' : '30px'}
                  >
                    <IconButton
                      active={isKnown || isLearned}
                      icon="check"
                      activeIcon="check"
                      alt={t(
                        isIKnowThisPremiumFeature && !isPremiumSubscribed ? 'iKnowThisPremiumFeature' : 'iKnowThis',
                      )}
                      tooltipProps={{ width: 100 }}
                      onClick={
                        isIKnowThisPremiumFeature && !isPremiumSubscribed
                          ? () => {
                              openSubscribePage({
                                pinchpoint: Pinchpoints.InlineTranslationIKnowThisWordCta,
                              });
                              forceCloseCard();
                            }
                          : onClickCheckMark
                      }
                      buttonSize={iconButtonWidth}
                      iconSize={iconButtonIconWidth}
                      disabled={isIKnowThisPremiumFeature && !isPremiumSubscribed}
                    />
                  </Box>
                </Box>
              </React.Fragment>
            )}
          </Box>
        </Box>
        <CustomizationMenu
          isEmojiCustomizationMenuEnabled={isEmojiCustomizationMenuEnabled}
          learningShortcutEmoji={learningShortcutEmoji}
          setIsEmojiCustomizationMenuEnabled={setIsEmojiCustomizationMenuEnabled}
          onShortcutEmojiSelection={onShortcutEmojiSelection}
        >
          <Typography
            variant={titleVariant}
            isBold
            color={textLevel1}
            css={css`
              display: inline-flex;
              align-items: center;
            `}
          >
            {isActiveLearningShortcutPhrase && (
              <span
                css={css`
                  margin-right: ${theme.spacing(1)};
                  background-color: ${backgroundAccent};
                  width: 26px;
                  height: 26px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  border-radius: 16px;
                  font-size: 0.75em;
                  cursor: pointer;
                  &:hover {
                    background-color: ${ToucanColors.green[100]};
                  }
                  &:active {
                    box-shadow: inset 0 0 0 2px ${ToucanColors.green[200]};
                  }
                  ${!hasSeenShortcutsCustomization && {
                    animation: `${pulse} 1300ms infinite`,
                  }}
                `}
                role="button"
                tabIndex={0}
                onKeyDown={event => {
                  event.preventDefault();
                  onEmojiClick();
                }}
                onClick={onEmojiClick}
                data-testid="inline-translation-card-shortcut-emoji"
              >
                {learningShortcutEmoji ?? noneEmoji}
              </span>
            )}
            {sourcePhrase}
          </Typography>
        </CustomizationMenu>
        {hasUserTranslation && (
          <React.Fragment>
            <Box ref={progressBarRef}>
              <ProgressBar
                key={loading ? 'loading-bar' : targetPhrase}
                css={css`
                  margin-top: ${theme.spacing(2)};
                `}
                beginPercentage={calculateProgressWithBaseline(lastSeenProgressionScore ?? 0)}
                endPercentage={calculateProgressWithBaseline(progressionScore ?? 0)}
                barColor={ToucanColors.blue[200]}
                barCompleteTransitionColor={isLearned ? 'linear-gradient(90deg, #85C3D8 0%, #B1D6BF 100%)' : undefined}
                onAnimationComplete={onProgressBarAnimationComplete}
              />
            </Box>

            <Callout
              title={t('practiceThisWord')}
              isOpen={showPracticeCallout}
              left="25%"
              key={progressButtonText}
              isLeftSideCallout
            >
              <ProgressBarCallToAction
                isVisible={isProgressBarCtaVisible}
                onClick={onProgressButtonClick}
                imgSrc={progressButtonImgSrc}
                imgAlt={progressButtonImgAlt}
                extraText={progressButtonExtraText}
              >
                {progressButtonText}
              </ProgressBarCallToAction>
            </Callout>

            <Box display="flex" pt={2}>
              {partOfSpeech && <Tag theme="grey">{partOfSpeech}</Tag>}
              {difficultyTier && (
                <Tag theme="teal" ml={partOfSpeech ? 0.5 : 0}>
                  {difficultyTier.toLowerCase()}
                </Tag>
              )}
              {/* handles loading state */}
              {!partOfSpeech && <Tag theme="invisible">{PartOfSpeech.Noun}</Tag>}
            </Box>

            <PremiumUpsell />
          </React.Fragment>
        )}
      </Box>
      <Box
        p={2}
        mt={2}
        ml={-2}
        mr={-2}
        mb={-2}
        display="flex"
        flexDirection="row"
        css={css`
          border-top: 1px solid ${isDarkMode ? ToucanColors.gray[600] : ToucanColors.gray[100]};
        `}
      >
        <Box pr={1} display="flex" justifyContent="space-between" width="100%" height="16px" alignItems="center">
          <Box display="flex" justifyContent="flex-start" alignItems="center">
            <img
              src={browser.runtime.getURL('/images/toucan.svg')}
              alt={t('toucanLogo')}
              data-testid="toucan-logo"
              css={css`
                width: ${toucanLogoWidth};
                height: ${toucanLogoWidth};
              `}
            />

            <Box pl={2} data-testid="pause">
              <Action
                icon="pause"
                iconSize={actionButtonWidth}
                title={t('hoverPauseTranslations')}
                onClick={onClickPause}
              />
            </Box>
          </Box>
          <Box display="flex" justifyContent="space-between" alignItems="center">
            {isSaved && <ViewWords iconWidth={actionButtonWidth} onClick={onClickBook} />}
            {hasUserTranslation && (
              <Box pl={3} data-testid="remove-word">
                <SubscriptionRedirectContainer
                  shouldRedirect={isRemoveWordPremiumFeature && !isPremiumSubscribed}
                  pinchpoint={Pinchpoints.InlineTranslationRemoveWordCta}
                >
                  <Action
                    icon="clear"
                    iconSize={actionButtonWidth}
                    title={t(
                      isRemoveWordPremiumFeature && !isPremiumSubscribed ? 'dontTranslatePremiumFeature' : 'removeWord',
                    )}
                    data-testid="whatever"
                    disabled={isRemoveWordPremiumFeature && !isPremiumSubscribed}
                    onClick={onClickRemoveWord}
                  />
                </SubscriptionRedirectContainer>
              </Box>
            )}
            <Box pl={3}>
              <Action icon="flag" iconSize={actionButtonWidth} title={t('reportError')} onClick={onClickReportError} />
            </Box>
            <Box pl={3}>
              <Callout title={t('openSettings')} isOpen={showSettingCallout} key={t('openSettings')}>
                <Action
                  icon="cog"
                  iconSize={actionButtonWidth}
                  title={t('settings')}
                  onClick={onOpenSettings}
                  color={showSettingCallout ? ToucanColors.yellow : ToucanColors.blue}
                  hoverColor={showSettingCallout ? ToucanColors.yellow : ToucanColors.blue}
                  activeColor={showSettingCallout ? ToucanColors.yellow : ToucanColors.blue}
                  darkColor={showSettingCallout ? ToucanColors.yellow : ToucanColors.teal}
                  darkHoverColor={showSettingCallout ? ToucanColors.yellow : ToucanColors.teal}
                  darkActiveColor={showSettingCallout ? ToucanColors.yellow : ToucanColors.teal}
                  colorShade={{
                    light: {
                      idle: showSettingCallout ? 400 : 300,
                      hover: showSettingCallout ? 300 : 200,
                      active: 400,
                    },
                    dark: {
                      idle: showSettingCallout ? 400 : 300,
                      hover: showSettingCallout ? 300 : 200,
                      active: showSettingCallout ? 400 : 400,
                    },
                  }}
                />
              </Callout>
            </Box>
          </Box>
        </Box>
      </Box>
    </React.Fragment>
  );
};
