import React, { FC } from 'react';

import { css, SerializedStyles } from '@emotion/react';

import { BirdCage } from '~/components/BirdCage';

import { Box } from '../Box';
import { SidebarError } from '../Sidebar/SidebarError';

interface SlideProps {
  slideName: string;
  activeSlideName?: string;
  css?: SerializedStyles;
}

/**
 * Container for slide content
 * @param props
 * @param props.slideName String that is used to determine if the slide should be visible
 * @param props.activeSlideName String that is used to determine if the slide should be visible
 */
export const Slide: FC<SlideProps> = ({ children, slideName, activeSlideName, css: passedCss }) => {
  if (activeSlideName !== slideName) {
    return null;
  }

  return (
    <BirdCage errorComponent={SidebarError}>
      <Box
        css={css`
          height: 100%;
          width: 100%;
          ${passedCss || ''}
        `}
      >
        {children}
      </Box>
    </BirdCage>
  );
};
