/** @jsx jsx */
import React, { FC, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { useMutation, useQuery } from '@finch-graphql/react';
import { ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  SetComingSoonLanguageMutation,
  SetComingSoonLanguageMutationVariables,
  SetComingSoonLanguageDocument,
  GetComingSoonLanguageIdQueryVariables,
  GetComingSoonLanguageIdQuery,
  GetComingSoonLanguageIdDocument,
} from '~/schema/background';

import { ModalCard } from '../ModalContainer/ModalCard';
import { ModalBaseProps, ModalType } from '../ModalContainer/types';
import { Image } from '../shared/Image';

import { Body } from './Body';
import { CallToActionButton } from './CallToActionButton';
import { Header } from './Header';
import { HorizontalLine } from './HorizontalLine';
import { Title } from './Title';

interface LanguageNowLiveModalProps extends ModalBaseProps {}

export const LanguageNowLiveModal: FC<LanguageNowLiveModalProps> = ({
  onRequestClose,
  onRequestOpen,
  selectedModalType,
}) => {
  const { targetLanguage } = useUserInfo();
  const { t } = useTranslation();
  const { data, error } = useQuery<GetComingSoonLanguageIdQuery, GetComingSoonLanguageIdQueryVariables>(
    GetComingSoonLanguageIdDocument,
  );
  const [setComingSoonLanguage] = useMutation<SetComingSoonLanguageMutation, SetComingSoonLanguageMutationVariables>(
    SetComingSoonLanguageDocument,
  );
  const { targetLanguageInfo } = useLanguageInfo();

  const onCloseWithTracking = async () => {
    onRequestClose(ModalType.LanguageNowLive);

    await setComingSoonLanguage({ languageId: null });
    ContentMessengers.trackEvent({
      eventName: 'Language Now Live Modal Closed',
      properties: { language: targetLanguage },
    });
  };

  const showLanguageNowLiveModal = () => {
    const isOpen = onRequestOpen(ModalType.LanguageNowLive);

    if (isOpen) {
      ContentMessengers.trackEvent({
        eventName: 'Language Now Live Modal Viewed',
        properties: { language: targetLanguage },
      });
    }
  };

  useEffect(() => {
    if (data?.settings?.selectedComingSoonLanguageId && !error) {
      const comingSoonLanguageId = data.settings.selectedComingSoonLanguageId as string;
      const userLang = targetLanguageInfo?.languageId as string;
      const isModalShown = comingSoonLanguageId === userLang;
      if (isModalShown) {
        showLanguageNowLiveModal();
      }
    }
  }, [data]);

  if (selectedModalType !== ModalType.LanguageNowLive || !data || !targetLanguage) {
    return null;
  }

  return (
    <ModalCard onRequestClose={onCloseWithTracking}>
      <React.Fragment>
        <Header onRequestClose={onCloseWithTracking} isBackgroundWhite />
        <HorizontalLine />
        <Box
          p={4}
          css={css`
            background-color: ${ToucanColors.white};
          `}
        >
          <Image
            src={`images/newLanguageNotification/img-${targetLanguage}Added.svg`}
            alt={t('langIsNowLive', {
              language: targetLanguageInfo?.language as string,
              context: targetLanguageInfo?.languageId as string,
            })}
            data-testid="new-language-notification-img"
          />
          <Title language={targetLanguageInfo?.language ?? ''} />
          <Body />
          <CallToActionButton onCloseWithTracking={() => onCloseWithTracking()} />
        </Box>
      </React.Fragment>
    </ModalCard>
  );
};
