/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { Image } from '~/components/shared/Image';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';

const { Typography, useColorScheme, Button, ToucanIcon } = ToucanComponents;

interface FooterProps {
  onExitQuiz: () => void;
  onClickUnlockPremium: () => void;
  questionNumber: number;
  totalNumQuestions: number;
  areStepsVisible: boolean;
}

export const Footer: FC<FooterProps> = ({
  onExitQuiz,
  onClickUnlockPremium,
  questionNumber,
  totalNumQuestions,
  areStepsVisible,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { isDarkMode, uiLink } = useColorScheme();
  const { isPremiumSubscribed } = useUserInfo();

  return (
    <Box
      display="flex"
      alignItems="center"
      justifyContent="space-between"
      borderTop={`1px solid ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[100]}`}
      p={2}
      marginLeft={-2}
      marginRight={-2}
      marginBottom={-2}
      marginTop="auto"
    >
      <Box display="flex" alignItems="center">
        <Image src="/images/toucan.svg" alt={t('toucanLogo')} width="16px" height="16px" mr={2} />
        {areStepsVisible && (
          <Box>
            <Box display="flex" alignItems="center" gap={theme.spacing(1)}>
              {Array(totalNumQuestions)
                .fill('x')
                .map((_, index) => {
                  const isCurrentStep = questionNumber === index + 1;
                  const isCompletedStep = questionNumber > index + 1;
                  const isPremiumOnlyStep = index >= 2;
                  const isNonPremiumUserOnPremiumOnlyStep = isPremiumOnlyStep && !isPremiumSubscribed;
                  return (
                    <Box
                      key={index}
                      width="12px"
                      height="12px"
                      borderRadius="50%"
                      display="flex"
                      alignItems="center"
                      justifyContent="center"
                      bgcolor={(() => {
                        if (isNonPremiumUserOnPremiumOnlyStep) {
                          return ToucanColors.yellow[300];
                        }
                        if (isCurrentStep) {
                          return isDarkMode ? ToucanColors.teal[400] : ToucanColors.gray[400];
                        }
                        if (isCompletedStep) {
                          return isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300];
                        }
                        return isDarkMode ? ToucanColors.gray[400] : ToucanColors.gray[200];
                      })()}
                      border={(() => {
                        const borderColor = isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300];
                        return `2px solid ${
                          isCurrentStep && !isNonPremiumUserOnPremiumOnlyStep ? borderColor : 'transparent'
                        }`;
                      })()}
                    >
                      {isNonPremiumUserOnPremiumOnlyStep && (
                        <ToucanIcon
                          icon="lock"
                          css={css`
                            width: 6px;
                            height: 6px;
                            color: ${ToucanColors.yellow[500]};
                          `}
                        />
                      )}
                    </Box>
                  );
                })}
            </Box>
            {!isPremiumSubscribed && (
              <Box mt={0.5}>
                <Typography variant="2xs">
                  <Button
                    type="text"
                    onClick={onClickUnlockPremium}
                    css={css`
                      display: inline-flex;
                      align-items: center;
                    `}
                  >
                    {t('unlockMoreWithPremium')}
                    <ToucanIcon
                      icon="chevron-right"
                      css={css`
                        width: 8px;
                        color: inherit;
                        margin-left: ${theme.spacing(0.75)};
                      `}
                    />
                  </Button>
                </Typography>
              </Box>
            )}
          </Box>
        )}
      </Box>
      <Box
        display="flex"
        alignItems="center"
        css={css`
          cursor: pointer;
        `}
        onClick={onExitQuiz}
      >
        <ToucanIcon
          icon="times"
          css={css`
            color: ${uiLink};
            width: 12px;
            height: 12px;
            margin-right: ${theme.spacing(1)};
          `}
        />
        <Typography
          variant="2xs"
          isBold
          color={uiLink}
          css={css`
            letter-spacing: 2px;
            text-transform: uppercase;
          `}
        >
          {t('quizExit')}
        </Typography>
      </Box>
    </Box>
  );
};
