/** @jsx jsx */
import { FC, useEffect, useRef } from 'react';

import { jsx, css } from '@emotion/react';
import { useTheme } from '@mui/material';
import { useMotionValue, motion, animate } from 'framer-motion';

import { maximumZIndex } from '~/constants/styling';
import { isHTMLElement } from '~/helpers/htmlHelpers';

import { HighlightedSelection } from './HighlightedSelection';
import { HighlightToTranslateStatus } from './types';

interface UserSelectionProps {
  chicletSize?: { width: number; height: number };
  selection: HighlightedSelection | null;
  status: HighlightToTranslateStatus;
}

const OFFSCREEN_AMOUNT = -1000;

export const UserSelection: FC<UserSelectionProps> = ({ children, selection, chicletSize, status }) => {
  const theme = useTheme();

  // Animation values
  const animations = useRef<ReturnType<typeof animate>[]>([]);
  const x = useMotionValue(OFFSCREEN_AMOUNT);
  const y = useMotionValue(OFFSCREEN_AMOUNT);
  const opacity = useMotionValue(0);

  const stopAnimations = () => {
    if (animations.current.length) {
      animations.current.forEach(animation => {
        animation.stop();
      });
    }
  };

  useEffect(() => {
    if (selection) {
      try {
        const { boundingRect } = selection;
        const htmlRect =
          document.body.parentNode && isHTMLElement(document.body.parentNode)
            ? document.body.parentNode.getBoundingClientRect()
            : null;

        if (htmlRect && boundingRect && selection.textContent) {
          // Set values if not showing
          const top = boundingRect.bottom - htmlRect.top;
          const left = boundingRect.right - htmlRect.left - (chicletSize?.width ? chicletSize?.width / 2 : 0);
          const isHidden = opacity.get() === 0;
          const isOffScreen = x.get() === OFFSCREEN_AMOUNT || y.get() === OFFSCREEN_AMOUNT;

          if (isHidden || status !== HighlightToTranslateStatus.Translatable || isOffScreen) {
            y.set(top + 10);
            x.set(left);
          } else {
            // Animate if showing
            stopAnimations();
            if (Math.abs(y.get() - top + 5) > 30) {
              y.set(top + 10);
            }
            animations.current = [animate(y, top + 5, { type: 'spring' }), animate(x, left, { type: 'spring' })];
          }
        }
      } catch (e) {
        //
      }
    }
  }, [selection, status]);

  useEffect(() => {
    if (
      status === HighlightToTranslateStatus.Translatable ||
      status === HighlightToTranslateStatus.TranslatableSidebar
    ) {
      stopAnimations();
      animations.current = [animate(y, y.get() - 5)];
      animate(opacity, 1);
    } else {
      animate(opacity, 0, {
        onComplete: () => {
          x.set(OFFSCREEN_AMOUNT);
          y.set(OFFSCREEN_AMOUNT);
        },
      });
    }
  }, [status]);

  return (
    <motion.div
      data-testid="highlight-to-translate"
      aria-hidden={
        status !== HighlightToTranslateStatus.Translatable && status !== HighlightToTranslateStatus.TranslatableSidebar
      }
      css={css`
        position: absolute;
        top: 0;
        left: 0;
        z-index: ${maximumZIndex};
        padding-top: ${theme.spacing(0.5)};
      `}
      style={{ x, y, opacity }}
    >
      {children}
    </motion.div>
  );
};
