import { SupportedSourceLanguages } from '~/constants/language';

import { TranslationKeys, Variables } from './types';

interface I18nOptions {
  language: SupportedSourceLanguages;
}

interface Translations {
  [key: string]: {
    message: string;
    description?: string;
    placeholders?: { [key: string]: { content: string; example: string } };
  };
}

// Expects some this `hello {{world}}` or `hi there {{firstName}}`
const TRANSLATION_PATTERN = /{{[a-z]+}}/gi;

/**
 * i18n is a class that exposes some basic functionality that leverages the locations of
 * the existing web extension platform.
 */
export class i18n {
  sourceLanguage: SupportedSourceLanguages;

  translationsUrl: string;

  translations: undefined | Translations;

  initialized: boolean = false;

  constructor({ language }: I18nOptions) {
    this.sourceLanguage = language;
    this.translationsUrl = browser.runtime.getURL(`/_locales/${this.sourceLanguage}/messages.json`);
  }

  /**
   * init is an async method that pull the translation file from the background script.
   */
  public async init() {
    const resp = await fetch(this.translationsUrl);
    this.translations = await resp.json();
    this.initialized = true;

    return this;
  }

  /**
   * getMessage is similar to the getMessage of the browser.i18n.getMessage
   * except the variable are named. This is because it closer to i18next and
   * we were not able to support the standard browser variables.
   * @param messageKey string key on the message file.
   * @param variables and object with the variables keyed by the variable name
   * @returns a string that no translation is found or translation
   */
  public getMessage(messageKey: TranslationKeys, variables: Variables = {}) {
    if (!this.initialized) {
      throw new Error('i18n is not initialized');
    }

    const isContextMessage = variables.context !== undefined;
    const isPluralMessage = variables.count !== undefined && variables.count > 1;
    let messageKeyToUse: string = messageKey;
    if (isContextMessage) {
      // If a context option is passed in, attempt to get the message with the appended context on the message key
      messageKeyToUse = `${messageKey}_${variables.context}`;
    }
    if (isPluralMessage) {
      // If a count option over 1 is passed in, attempt to get the message with "_plural" appended to the message key
      messageKeyToUse = `${messageKey}_plural`;
    }
    const translation = this.translations?.[messageKeyToUse] || this.translations?.[messageKey];

    if (translation) {
      return i18n.variableInterpolation(translation.message, variables);
    }
    return messageKey;
  }

  public async changeLanguage(language: SupportedSourceLanguages) {
    this.sourceLanguage = language;
    this.translationsUrl = browser.runtime.getURL(`/_locales/${this.sourceLanguage}/messages.json`);
    return this.init();
  }

  static variableInterpolation(initialMessage: string, variables: Variables) {
    const translationVariables = initialMessage.match(TRANSLATION_PATTERN);
    if (translationVariables && variables) {
      return translationVariables.reduce((message, variable) => {
        const cleanVariable = variable.replace(/{|}/g, '');
        const replacement = variables[cleanVariable];
        // although the number zero (0) is considered a falsy value, do not short circuit if it is a replacement value
        if (!replacement && replacement !== 0) {
          return message;
        }
        return message.replace(variable, replacement.toString());
      }, initialMessage);
    }
    return initialMessage;
  }

  static from(options: I18nOptions) {
    const I18N = i18n;
    return new I18N(options);
  }
}
