import { FinchClient, QueryCache } from '@finch-graphql/react';

import Configs, { browserVendor } from './configs/config';
import { BrowserVendors } from './types/browsers';
import { BackgroundMessageKey } from './types/messages';

// TODO wrap in a function to create this.
export const client = new FinchClient({
  cache: new QueryCache(),
  messageKey: BackgroundMessageKey.Generic,
  useMessages: Configs.isTesting || browserVendor === BrowserVendors.SafariiOS,
  messageTimeout: 10000,
  maxPortTimeoutCount: 2,
});
