import { useCallback, useMemo, useRef } from 'react';

import { useAtom } from 'jotai';

import {
  addTranslatedNodesAtom,
  transitionTranslatedNodeAtom,
  translatedNodesAtom,
  markTranslatedNodeAsBuiltAtom,
  removeTranslatedNodeAtoms,
  updateTranslatedNodeMetaAtom,
  builtTranslatedNodesAtom,
  translatedNodesOnPageAtom,
  pendingTranslatedNodesAtom,
  mountedTranslatedNodesAtom,
  updateTranslatedNodeMetaByTranslatedIdAtom,
  transitionTranslatedNodeToHoveredAtom,
  erroredTranslatedNodesAtom,
  reactivateAllTranslatedNodesAtom,
  deactivateAllTranslatedNodesAtom,
  transitionTranslationByTranslationIdAtom,
  updateTranslatedNodeAtom,
  translatedNodesByIdAtom,
} from './atoms';
import { TranslatedNodePayload, TranslatedNodeStatus } from './types';

export const useTranslatedNodes = () => useAtom(translatedNodesAtom)[0];
export const useTranslatedNodesById = () => useAtom(translatedNodesByIdAtom)[0];
export const useTranslatedNode = (translatedId: string) => {
  const [translatedNodes] = useAtom(translatedNodesByIdAtom);
  return translatedNodes[translatedId];
};
export const useBuiltTranslatedNodes = () => useAtom(builtTranslatedNodesAtom)[0];
export const useTranslatedNodesOnPage = () => useAtom(translatedNodesOnPageAtom)[0];
export const usePendingTranslatedNodes = () => useAtom(pendingTranslatedNodesAtom)[0];
export const useMountedTranslatedNodes = () => useAtom(mountedTranslatedNodesAtom)[0];
export const useErroredTranslatedNodes = () => useAtom(erroredTranslatedNodesAtom)[0];
export const useAddTranslatedNodes = () => useAtom(addTranslatedNodesAtom)[1];
export const useRemoveTranslatedNodes = () => useAtom(removeTranslatedNodeAtoms)[1];
export const useUpdateTranslatedNodeMeta = () => useAtom(updateTranslatedNodeMetaAtom)[1];
export const useUpdateTranslatedNodeMetaByTranslatedId = () => useAtom(updateTranslatedNodeMetaByTranslatedIdAtom)[1];
export const useUpdateTranslatedNode = () => useAtom(updateTranslatedNodeAtom)[1];

export const useTranslatedNodesActive = () => {
  const reactivate = useAtom(reactivateAllTranslatedNodesAtom)[1];
  const deactivate = useAtom(deactivateAllTranslatedNodesAtom)[1];
  const transitionByTranslationId = useAtom(transitionTranslationByTranslationIdAtom)[1];

  const deactivateByTranslationId = useCallback(
    (translationId: string) => {
      transitionByTranslationId({
        translationId,
        status: TranslatedNodeStatus.Inactive,
      });
    },
    [transitionByTranslationId],
  );

  return useMemo(
    () => ({
      reactivate,
      deactivate,
      deactivateByTranslationId,
    }),
    [reactivate, deactivate, deactivateByTranslationId],
  );
};

/**
 * useTranslatedNodeTransitions is a hook that exports a bunch of hooks to state transition translated nodes.
 * These hooks methods come in two flavors:
 * - set* methods that take a translated node and attempts transition the node to that status.
 * - request* methods that take a translated node and attempts transition the node to that status after a cancellable timeout. Timeouts
 *  are cancelled when the node is transitioned to a different status before the timeout.
 * @returns an object with a bunch of transition methods.
 */
export const useTranslatedNodeTransitions = () => {
  const transitionTranslatedNode = useAtom(transitionTranslatedNodeAtom)[1];
  const markTranslatedNodeAsBuilt = useAtom(markTranslatedNodeAsBuiltAtom)[1];
  const transitionToHovered = useAtom(transitionTranslatedNodeToHoveredAtom)[1];
  const timer = useRef(0);

  const requestIdle = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      clearTimeout(timer.current);
      timer.current = window.setTimeout(() => {
        // TODO: add reset quiz
        transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Idle });
      }, 200);
    },
    [transitionTranslatedNode],
  );

  const setInactive = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Inactive });
    },
    [transitionTranslatedNode],
  );

  const setBuilt = useCallback(
    (translatedNode: TranslatedNodePayload, node: HTMLSpanElement) => {
      markTranslatedNodeAsBuilt({ translatedNode, node });
    },
    [markTranslatedNodeAsBuilt],
  );

  const setMounted = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Mounted });
    },
    [transitionTranslatedNode],
  );

  const setErrored = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Error });
    },
    [transitionTranslatedNode],
  );

  const setIdle = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Idle });
    },
    [transitionTranslatedNode],
  );

  const requestHovered = useCallback(
    (translatedNode: TranslatedNodePayload, overridePinned?: boolean) => {
      clearTimeout(timer.current);
      timer.current = window.setTimeout(() => {
        transitionToHovered({ translatedNode, override: overridePinned });
      }, 200);
    },
    [transitionTranslatedNode],
  );
  const setFocused = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      clearTimeout(timer.current);
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Focused });
    },
    [transitionTranslatedNode],
  );

  const setPinned = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      clearTimeout(timer.current);
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Pinned });
    },
    [transitionTranslatedNode],
  );
  const pinForQuiz = useCallback(
    (translatedNode: TranslatedNodePayload) => {
      clearTimeout(timer.current);
      transitionTranslatedNode({ translatedNode, status: TranslatedNodeStatus.Quiz });
    },
    [transitionTranslatedNode],
  );

  return useMemo(
    () => ({
      requestIdle,
      setBuilt,
      setMounted,
      setErrored,
      setIdle,
      requestHovered,
      setFocused,
      setPinned,
      setInactive,
      pinForQuiz,
    }),
    [
      requestIdle,
      setBuilt,
      setMounted,
      setErrored,
      setIdle,
      requestHovered,
      setFocused,
      setPinned,
      setInactive,
      pinForQuiz,
    ],
  );
};
