import { useMemo } from 'react';

import { HighlightHoverProps } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { useMediaQuery } from '~/hooks/useMediaQuery';

export const usePopperModifiers = ({
  domain,
}: {
  domain: string;
}): { popperModifiers: HighlightHoverProps['popperModifiers']; width: string } => {
  const theme = useTheme();
  const isViewportAboveSm = useMediaQuery(theme.breakpoints.up('sm'));
  const gpuAcceleration = !domain.includes('cnn.com');

  return useMemo(
    () =>
      isViewportAboveSm
        ? {
            width: '240px',
            popperModifiers: [
              {
                name: 'computeStyles',
                options: {
                  gpuAcceleration,
                },
              },
            ],
          }
        : {
            width: `calc(100vw - ${theme.spacing(4)})`,
            popperModifiers: [
              {
                name: 'computeStyles',
                enabled: true,
                options: {
                  gpuAcceleration,
                },
                fn({ state }) {
                  const referenceRect = state.rects.reference;
                  Object.assign(state, {
                    styles: {
                      ...state.styles,
                      popper: {
                        ...state.styles.popper,
                        left: '0',
                        top: '0',
                        // NOTE: this does not accommodate flipping the popper
                        transform: `translate(${theme.spacing(2)}, ${referenceRect.y + referenceRect.height}px)`,
                      },
                    },
                  });
                  return state;
                },
              },
            ],
          },
    [isViewportAboveSm],
  );
};
