import React, { FC } from 'react';

import { AnimatePresence } from 'framer-motion';

import { MotionBox, MotionBoxProps } from '../Box';

interface FadeInProps extends MotionBoxProps {
  show: boolean;
  className?: string;
}

export const FadeIn: FC<FadeInProps> = ({ show, children, className, ...boxProps }) => (
  <AnimatePresence>
    {show ? (
      <MotionBox
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ ease: 'easeIn', duration: 0.3 }}
        exit={{ opacity: 0, transition: { ease: 'easeOut', duration: 0.3 } }}
        className={className}
        {...boxProps}
      >
        {children}
      </MotionBox>
    ) : null}
  </AnimatePresence>
);
