import React, { FC, useEffect, useState, lazy, Suspense } from 'react';

import { css } from '@emotion/react';
import { useTheme } from '@mui/material';
import { AnimatePresence } from 'framer-motion';

import { getBrowserProperties } from '~/analytics/browser';
import Configs, { browserVendor } from '~/configs/config';
import { defaultSidebarWidth, mobileSidebarWidth } from '~/constants/sidebar';
import { maximumZIndex } from '~/constants/styling';
import { sidebarLazyLoad } from '~/helpers/sidebarHelpers';
import { useMediaQuery } from '~/hooks/useMediaQuery';
import { useQuiz } from '~/hooks/useQuiz';
import { useUserInfo } from '~/hooks/useUserInfo';
import { BrowserVendors } from '~/types/browsers';
import { PanelName, SettingName, SidebarContainerProps } from '~/types/sidebar';

import { BirdCage } from '../BirdCage';
import { MotionBox } from '../Box';
import { ModalType } from '../ModalContainer/types';
import { ErrorMessage } from '../shared/ErrorMessage';

import { SidebarProvider, useSidebarContext } from './SidebarContext';
import { SidebarLayout } from './SidebarLayout';
import { SidebarPanelPlaceholder } from './SidebarPanelPlaceholder';
import { useSidebar } from './useSidebar';

const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;
const browserProperties = getBrowserProperties();
// The user agent OS for iPad comes back as Mac OS so we can use that to differentiate between iPad and iPhone
const isNotIpad = browserProperties.OS === 'iOS';

const SidebarContainer = lazy(sidebarLazyLoad);

export const SideBarView: FC<Omit<SidebarContainerProps, 'isAnimationComplete'>> = ({
  selectedModalType,
  isInPopup,
  onRequestOpen,
  onRequestClose,
  ...props
}) => {
  const theme = useTheme();
  const { isOpen, initialState, closeSidebar } = useSidebar();
  const { onNavigateSidebar } = useSidebarContext();
  const {
    panelName: initialPanelName,
    slideName: initialSlideName,
    overlayName: initialOverlayName,
    hydration: initialHydration,
  } = initialState;
  const [isAnimationComplete, setIsAnimationComplete] = useState<boolean>(false);
  const { onReset } = useQuiz();
  const isViewportBelowSm = useMediaQuery(theme.breakpoints.down('sm'));
  const sidebarWidth = isViewportBelowSm && isMobileSafari && isNotIpad ? mobileSidebarWidth : defaultSidebarWidth;
  const { isUserRegistered } = useUserInfo();

  useEffect(() => {
    if (selectedModalType !== ModalType.Sidebar && isOpen) {
      onRequestOpen(ModalType.Sidebar);
      // This function is never the proper function
      onNavigateSidebar({
        panel: initialPanelName,
        slide: initialSlideName,
        overlay: initialOverlayName,
        hydration: initialHydration,
        shouldDisableClickAway: false,
      });
    }

    if (!isInPopup && selectedModalType === ModalType.Sidebar && !isOpen) {
      onRequestClose(ModalType.Sidebar);
    }
  }, [selectedModalType, isOpen, onNavigateSidebar]);

  const closeWidget = async () => {
    closeSidebar();
    if (isInPopup) {
      // call directly when opened from action
      onRequestClose(ModalType.Sidebar);
    }
  };

  const onQuizExit = () => {
    onReset();
  };

  return (
    <AnimatePresence>
      {selectedModalType === ModalType.Sidebar ? (
        <MotionBox
          css={css`
            position: ${isInPopup ? 'static' : 'fixed'};
            z-index: ${maximumZIndex + 1};
            right: 0;
            top: 0;
            width: ${sidebarWidth};
            max-width: ${sidebarWidth};
            height: ${(() => {
              if (isMobileSafari && isNotIpad) {
                return '100%';
              }
              return isInPopup ? '600px' : '100vh';
            })()};
            border: 0;
          `}
          key="sidebar"
          initial={isInPopup ? false : { x: '300px' }}
          animate={{ x: '0px' }}
          exit={{ x: '300px', transition: { duration: 0.15 } }}
          transition={{ duration: 0.3, ease: 'easeInOut' }}
          onAnimationComplete={() => setIsAnimationComplete(true)}
          onAnimationStart={() => setIsAnimationComplete(false)}
        >
          <BirdCage
            errorComponent={({ errorId, error }) => (
              <SidebarLayout
                isInPopup={isInPopup}
                onRequestClose={closeWidget}
                isExtensionGloballyEnabled={false}
                setIsExtensionGloballyEnabled={async () => {}}
                isQuizOpen={false}
                onQuizExit={onQuizExit}
                hideFooter
                sidebarWidth={sidebarWidth}
                isPersistentPremiumReferralShown={false}
                onPersistentReferralClick={() => {}}
                isMobileReferralShown={false}
                onMobileReferralClick={() => {}}
                isUserRegistered={isUserRegistered}
              >
                <ErrorMessage
                  m={3}
                  message={Configs.isDebugging ? error?.message : 'Authentication failed'}
                  id={errorId}
                />
              </SidebarLayout>
            )}
          >
            <Suspense
              fallback={
                <SidebarLayout
                  isInPopup={isInPopup}
                  onRequestClose={closeWidget}
                  isExtensionGloballyEnabled={false}
                  setIsExtensionGloballyEnabled={async () => {}}
                  isQuizOpen={false}
                  onQuizExit={onQuizExit}
                  hideFooter
                  sidebarWidth={sidebarWidth}
                  isPersistentPremiumReferralShown={false}
                  onPersistentReferralClick={() => {}}
                  isMobileReferralShown={false}
                  onMobileReferralClick={() => {}}
                  isUserRegistered={isUserRegistered}
                >
                  <SidebarPanelPlaceholder />
                </SidebarLayout>
              }
            >
              <SidebarContainer
                isInPopup={isInPopup}
                onRequestOpen={onRequestOpen}
                onRequestClose={onRequestClose}
                selectedModalType={selectedModalType}
                isAnimationComplete={isAnimationComplete}
                sidebarWidth={sidebarWidth}
                {...props}
              />
            </Suspense>
          </BirdCage>
        </MotionBox>
      ) : null}
    </AnimatePresence>
  );
};

interface SidebarProps extends SidebarContainerProps {
  supportedPanels: Array<PanelName>;
  supportedSettings: Array<SettingName>;
}

export const Sidebar: FC<SidebarProps> = ({ supportedPanels, supportedSettings, ...props }) => (
  <SidebarProvider supportedPanels={supportedPanels} supportedSettings={supportedSettings}>
    <SideBarView {...props} />
  </SidebarProvider>
);
