import React, { FC, memo, CSSProperties } from 'react';

import Lottie, { EventListener } from 'react-lottie';
import browser from 'webextension-polyfill';

interface LottieAnimationProps {
  filePath: string;
  style?: CSSProperties;
  loop?: boolean;
  autoplay?: boolean;
  rendererSettings?: {
    preserveAspectRatio?: string;
    context?: any;
    scaleMode?: any;
    clearCanvas?: boolean;
    progressiveLoad?: boolean;
    hideOnTransparent?: boolean;
    className?: string;
  };
  eventListeners?: ReadonlyArray<EventListener>;
  isStopped?: boolean;
}

const defaultRendererSettings = { preserveAspectRatio: 'xMidYMid meet' };

export const LottieAnimation: FC<LottieAnimationProps> = memo(
  ({
    filePath,
    style,
    loop = false,
    autoplay = true,
    rendererSettings = defaultRendererSettings,
    eventListeners,
    isStopped = false,
  }) => (
    <Lottie
      options={{
        // @ts-ignore Lottie lib types need updating
        path: browser.runtime.getURL(filePath),
        loop,
        autoplay,
        rendererSettings,
      }}
      style={style}
      isClickToPauseDisabled
      eventListeners={eventListeners}
      isStopped={isStopped}
    />
  ),
);
