/** @jsx jsx */
import { FC } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, BoxProps } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

const { Typography, useColorScheme } = ToucanComponents;

interface ErrorMessageProps extends BoxProps {
  message?: Array<string> | string | undefined;
  errorId?: string;
  frameless?: boolean;
}

export const ErrorMessage: FC<ErrorMessageProps> = ({ message, errorId, frameless, ...boxProps }) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();

  if (!message || !message.length) {
    return null;
  }

  let textColor = ToucanColors.red[300];
  let backgroundColor: string | undefined = ToucanColors.red[100];
  let borderRadius = '4px';
  let padding = 1;
  if (frameless) {
    backgroundColor = undefined;
    borderRadius = '0';
    padding = 0;
    textColor = isDarkMode ? ToucanColors.red[200] : ToucanColors.red[300];
  }

  const messages = Array.isArray(message) ? message : [message];
  let errorMessage = messages[0];
  if (errorMessage === 'We ran into an issue sending you an email') {
    errorMessage = t('wereExperiencingIssues');
  } else if (errorMessage === 'Authentication failed') {
    errorMessage = t('sorryTryAgain');
  }

  return (
    <Box borderRadius={borderRadius} bgcolor={backgroundColor} p={padding} {...boxProps}>
      <Typography
        variant="xs"
        css={css`
          color: ${textColor};
        `}
      >
        {errorMessage}
      </Typography>
      {errorId ? (
        <Typography
          variant="xs"
          css={css`
            color: ${textColor};
          `}
        >
          {t('errorId', { id: errorId })}
        </Typography>
      ) : null}
    </Box>
  );
};
