/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { motion } from 'framer-motion';

import Config from '~/configs/config';
import { useQuiz } from '~/hooks/useQuiz';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';

const { useColorScheme, Typography, Link } = ToucanComponents;

export const QuizError: FC = () => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const { t } = useTranslation();
  const { currentQuiz } = useQuiz();

  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
      <Typography
        isBold
        variant="xs"
        css={css`
          color: ${ToucanColors.red[isDarkMode ? 200 : 300]};
        `}
      >
        {t('quizCouldntCompleteAction')}
      </Typography>
      <Typography
        variant="xs"
        css={css`
          color: ${ToucanColors.red[isDarkMode ? 200 : 300]};
          margin-top: ${theme.spacing(1)};
        `}
      >
        <Trans i18nKey="quizItsNotYouItsOurServers" components={[<Link href={`${Config.siteURL}/contact`}>a</Link>]} />
      </Typography>
      <Typography
        variant="xs"
        css={css`
          color: ${ToucanColors.red[isDarkMode ? 200 : 300]};
          margin-top: ${theme.spacing(1)};
          margin-bottom: ${theme.spacing(3)};
          word-wrap: break-word;
        `}
      >
        {t('errorId', { id: currentQuiz.errorId ?? '' })}
      </Typography>
    </motion.div>
  );
};
