/** @jsx jsx */
import { FC } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import Skeleton from '@mui/material/Skeleton';

import { Box } from '~/components/Box';
import { FadeIn } from '~/components/shared/FadeIn';

const { useColorScheme } = ToucanComponents;

export const WordListPlaceholder: FC = () => {
  const { isDarkMode } = useColorScheme();
  const theme = useTheme();
  const loaders = [0, 1, 2, 3];

  return (
    <Box display="flex" height="100%" overflow="hidden" flexDirection="column" p={2} data-testid="wordList-loading">
      <FadeIn show>
        {loaders.map(x => (
          <Skeleton
            key={x}
            css={css`
              height: 138px;
              border-radius: 8px;
              transform: none;
              margin-bottom: ${theme.spacing(2)};
              background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[200]};
            `}
          />
        ))}
      </FadeIn>
    </Box>
  );
};
