import React, { memo, ReactNode, FC, useState } from 'react';

import { Global, css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { getDefaultTargetLanguage } from '~/constants/language';
import { StorageKey } from '~/constants/storage';
import { usePageLanguage } from '~/hooks/usePageLanguage/usePageLanguage';
import { Port } from '~/hooks/usePort';
import { StorageProvider, useStorage } from '~/hooks/useStorage';
import { useHydrateSupportsAudio } from '~/hooks/useSupportAudio';
import { ToastProvider } from '~/hooks/useToast';
import { useUserInfo } from '~/hooks/useUserInfo';
import { ExperimentsProvider, ExperimentsClient } from '~/libs/experiments';
import { i18n as i18nInstance, LocaleProvider } from '~/libs/i18n';
import { LanguageId } from '~/schema/server';

import FontHelpers from '../helpers/fontHelpers';
import * as ReactUtils from '../libs/reactUtils';

import { RootProviders } from './RootProviders';
import { ShadowProvider } from './ShadowProvider';

const { ColorScheme } = ToucanComponents;
export interface ContentBaseProps {
  currentDomain: string;
  isDomainBlacklisted: boolean;
  i18n: i18nInstance;
  supportsAudio: boolean;
}

interface ContentAppProps {
  i18n: i18nInstance;
  isDomainBlacklisted: boolean;
  supportsAudio: boolean;
  disableToast?: boolean;
  content: ReactNode;
  pageEnabledContent: ReactNode;
  experiments: { experiment: string; variation: string }[];
  pageEnabledContentWithNoPageValidationCheck: ReactNode;
}

/**
 * Injection point wrapper for all upcoming react components
 * @param pageEnabledContent content that is only enabled if webpage language matches source language (ie. <html lang="en"> & source language is "en")
 * @param pageEnabledContentWithNoPageValidationCheck content that should be rendered regardless of webpage language not matching source language (ie. <html lang="es"> & source language is "en")
 */
export const ContentApp: FC<ContentAppProps> = memo(
  ({
    i18n,
    supportsAudio,
    disableToast = false,
    experiments,
    content,
    pageEnabledContent,
    isDomainBlacklisted,
    pageEnabledContentWithNoPageValidationCheck,
  }) => {
    const [targetLanguage] = useStorage(StorageKey.TargetLanguage, getDefaultTargetLanguage(i18n.sourceLanguage));
    const { isDarkModeEnabled, sourceLanguage } = useUserInfo();
    const isSourceLanguageKO = sourceLanguage === LanguageId.Ko;
    const { isValidPage } = usePageLanguage(i18n.sourceLanguage);
    const [experimentsInstance] = useState(() => {
      const newExperimentsInstance = ExperimentsClient.from();
      newExperimentsInstance.hydrateCache(experiments);
      return newExperimentsInstance;
    });

    useHydrateSupportsAudio(supportsAudio);

    // TODO: This should be a hook
    FontHelpers.linkGoogleFonts();

    const allContent = (
      <>
        {content}
        {!isDomainBlacklisted && isValidPage && pageEnabledContent}
        {!isDomainBlacklisted && pageEnabledContentWithNoPageValidationCheck}
      </>
    );

    return (
      <ExperimentsProvider instance={experimentsInstance}>
        <LocaleProvider i18n={i18n} targetLanguage={targetLanguage}>
          {isSourceLanguageKO && (
            <Global
              styles={css`
                * {
                  word-break: keep-all;
                }
              `}
            />
          )}
          <ColorScheme isDarkMode={isDarkModeEnabled}>
            {!disableToast ? <ToastProvider>{allContent}</ToastProvider> : allContent}
          </ColorScheme>
        </LocaleProvider>
      </ExperimentsProvider>
    );
  },
);

export const renderContentApp = (content: ReactNode) =>
  ReactUtils.renderRootless(({ root, head, body, modals, jssElement }) => (
    <ShadowProvider root={root} head={head} body={body} modals={modals} jssElement={jssElement}>
      <RootProviders withCSSBaseline>
        <Port name="content">
          <StorageProvider>{content}</StorageProvider>
        </Port>
      </RootProviders>
    </ShadowProvider>
  ));
