import React, { FC, useState } from 'react';

import { Events } from '~/constants/events';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { StorageKey } from '~/constants/storage';
import { useCrawlerConfig } from '~/hooks/useCrawlerConfig';
import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useStorage } from '~/hooks/useStorage';
import { useUserInfo } from '~/hooks/useUserInfo';
import contentMessengers from '~/messengers/contentMessengers';
import { PanelName, SlideName } from '~/types/sidebar';

import { useSidebar } from '../Sidebar/useSidebar';

import { PremiumModal } from './PremiumModal';
import { PremiumUpsellChiclet } from './PremiumUpsellChiclet';
import { TranslateChiclet } from './TranslateChiclet';
import { UserSelection } from './UserSelection';
import { HighlightToTranslateStatus } from './types';
import { useHighlightToTranslate } from './useHighlightToTranslate';

export const HighlightToTranslate: FC = () => {
  const { isPremiumSubscribed, isUserRegistered } = useUserInfo();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const isFeatureBehindPaywall =
    !isPremiumSubscribed &&
    getFeaturePaywallStatus(PremiumFeatureName.HighlightToTranslate) === PremiumFeaturePaywallStatus.Paid;
  const [isUpsellModalShowing, setModalVisibility] = useState<boolean>(false);
  const [isFeatureEnabled] = useStorage<boolean>(StorageKey.HighlightToTranslatedEnabled, true);
  const { status, selection, onTranslate, overWordLimit, clearSelection } = useHighlightToTranslate({
    isEnabled: isFeatureEnabled || isFeatureBehindPaywall,
  });
  const [chicletSize, setStyles] = useState<{ width: number; height: number }>();
  const { openSidebar } = useSidebar();
  const { isCrawlerEnabled } = useCrawlerConfig();
  const { currentDomain } = useCurrentDomain();

  const [isHttPremiumUpsellVisible, setIsHttPremiumUpsellVisible] = useStorage(
    StorageKey.HighlightToTranslatePremiumUpsellVisible,
    true,
  );

  if (!isCrawlerEnabled) {
    return null;
  }

  // restrict HTT from being usable on YouTube which is enabled due to contextless moments being present
  if (currentDomain === 'youtube.com') {
    return null;
  }

  if (isFeatureBehindPaywall) {
    if (!isHttPremiumUpsellVisible) {
      return null;
    }
    return (
      <UserSelection status={status} selection={selection} chicletSize={chicletSize}>
        <PremiumUpsellChiclet
          onLayout={setStyles}
          onClickDontShowAgain={() => {
            setIsHttPremiumUpsellVisible(false);
          }}
        />
      </UserSelection>
    );
  }

  return (
    <>
      <UserSelection status={status} selection={selection} chicletSize={chicletSize}>
        {(status === HighlightToTranslateStatus.Translatable ||
          status === HighlightToTranslateStatus.TranslatableSidebar) && (
          <TranslateChiclet
            onLayout={setStyles}
            onClick={() => {
              // TODO: Add condition for user signed out once reAuth logic is removed.
              if (!isUserRegistered) {
                openSidebar({
                  panelName: PanelName.Settings,
                  slideName: SlideName.Authenticate,
                });
                contentMessengers.trackEvent({
                  eventName: Events.SettingsOpened,
                  properties: { source: 'signed-out htt' },
                });
                return;
              }
              if (
                status === HighlightToTranslateStatus.Translatable ||
                status === HighlightToTranslateStatus.TranslatableSidebar
              ) {
                contentMessengers.trackEvent({
                  eventName: 'Highlight to Translate attempt translate',
                  properties: {
                    target: status === HighlightToTranslateStatus.Translatable ? 'inline' : 'sidebar',
                  },
                });
                onTranslate();
              }
            }}
            onOpenSettings={() => {
              clearSelection();
              openSidebar({
                panelName: PanelName.Settings,
                slideName: SlideName.MainSettings,
              });
            }}
            overWordLimit={overWordLimit}
          />
        )}
      </UserSelection>
      <PremiumModal isOpen={isUpsellModalShowing} onRequestClose={() => setModalVisibility(false)} />
    </>
  );
};
