import { useCallback, useState } from 'react';

import { browserVendor } from '~/configs/config';
import { GLOBAL_PERMISSION, SUBSTITUTES } from '~/constants/permissions';
import { useGetHostPermissionsQuery, useRequestHostPermissionsMutation } from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';

interface UseHostPermissionsOptions {
  hosts: string[];
  globalSubstitute?: boolean;
}

/**
 * useHostPermissions has some build in fallbacks that allows for consistency with requesting permissions
 * cross browser. For Safari only runtime permissions can be requested via the popup, on Chrome permissions
 * can be requested from all scripts.
 * @param options Options to configure how the hook behaves
 * @param options.hosts An array of hosts to request or check to see if the extension has the permission
 * @param options.globalSubstitute A flag to use substitute list of domains when requesting global mostly for Safari
 */
export const useHostPermissions = ({ hosts, globalSubstitute }: UseHostPermissionsOptions) => {
  const isSubstitute = globalSubstitute && hosts.includes(GLOBAL_PERMISSION) && hosts.length === 1;
  const origins = isSubstitute ? SUBSTITUTES : hosts ?? [];
  const canRequestRuntimePermissions = 'permissions' in browser;
  // These are common loading states
  const skip = hosts[0] === '*:///*' || hosts[0] === 'https://*./*';
  // Safari and Firefox do not support background permission request
  const canRequestBackgroundPermissions =
    browserVendor !== BrowserVendors.Safari && browserVendor !== BrowserVendors.Firefox;
  const [isRequesting, setRequesting] = useState<boolean>(false);
  const { data, refetch } = useGetHostPermissionsQuery({ variables: { hosts: origins }, skip });
  const [requestPermissionsBackground] = useRequestHostPermissionsMutation();
  const requestPermissions = useCallback(async () => {
    setRequesting(true);
    let result = false;
    if (canRequestRuntimePermissions) {
      result = await browser.permissions.request({ origins });
    } else if (canRequestBackgroundPermissions) {
      const resp = await requestPermissionsBackground({ hosts: origins });
      result = resp.data.requestPermission.permissions ?? false;
    }
    await refetch();
    setRequesting(false);
    return result;
  }, [...origins]);

  const hasPermissions = data?.browser?.permissions ?? null;
  const canRequestPermissions = canRequestRuntimePermissions || canRequestBackgroundPermissions;

  return {
    hasPermissions,
    requestPermissions,
    canRequestPermissions,
    isRequesting,
    isSubstitute,
  };
};
