import React, { useCallback, useEffect, useMemo } from 'react';

import { PostMessageEvent } from '~/constants/events';
import { Language } from '~/constants/language';
import { sendPostMessage } from '~/helpers/sendPostMessage';
import { TranslatedNodePayload, TranslatedNodeSource } from '~/hooks/useTranslatedNodes/types';

import { shouldSendPostMessage } from '../InlineTranslationCard/helpers';
import { TranslationInfo, TranslationInfoProps } from '../InlineTranslationCard/types';

import { Translation } from './Translation';

interface InlineTranslationProps extends TranslationInfoProps {
  onFocus: (info: TranslationInfo) => void;
  onBlur: (info: TranslationInfo) => void;
  isActive: boolean;
  source: TranslatedNodeSource;
  replacement: string;
  targetPhrase: string;
  sourcePhrase: string;
  mountNode: (node: TranslatedNodePayload) => void;
  targetLanguage: Language;
  isQuizActive: boolean;
  isSaved?: boolean;
  hasSeenExpandedTranslation: boolean;
  setHasSeenExpandedTranslation: () => void;
  learningShortcutEmoji?: string;
  isActiveLearningShortcutPhrase: boolean;
}

// This component creates a portal to the word and also gathers information for that word.
// It is considered a container component that has little information on the actual view of the component
// one important reason this component is pulled away from the view logic is to allow React.memo to avoid unessasary renders.
export const InlineTranslationContainer: React.FC<InlineTranslationProps> = React.memo(
  ({
    id,
    node,
    translationId,
    onBlur,
    onFocus,
    isActive,
    source,
    replacement: word,
    mountNode,
    targetLanguage,
    isQuizActive,
    targetPhrase,
    isSaved,
    hasSeenExpandedTranslation,
    setHasSeenExpandedTranslation,
    learningShortcutEmoji,
    isActiveLearningShortcutPhrase,
  }) => {
    const sendPostMessages = shouldSendPostMessage(source);
    // Makes it so this one value on updates when a value inside changes
    const translationInfo = useMemo(
      () => ({
        id,
        node,
        translationId,
        word,
        targetPhrase,
        // placeholder values
        isKnown: false,
        progressionScore: 0,
        isSaved: false,
        source,
      }),
      [node, translationId, id, word, targetPhrase, source],
    );
    // Memoize all these functions all functions going through these inline translations should be memoized at some level to avoid thrashing updates
    // the the individual inline-translations.
    const onFocusMemoized = useCallback(() => {
      if (sendPostMessages) {
        sendPostMessage(PostMessageEvent.FocusTranslation, {
          translationInfo,
        });
      }
      onFocus(translationInfo);

      if (source === TranslatedNodeSource.ExpandTranslation && !hasSeenExpandedTranslation) {
        setHasSeenExpandedTranslation();
      }
    }, [translationInfo, onFocus, source, hasSeenExpandedTranslation]);
    const onBlurMemoized = useCallback(() => {
      if (sendPostMessages) {
        sendPostMessage(PostMessageEvent.BlurTranslation, {
          translationInfo,
        });
      }
      onBlur(translationInfo);
    }, [translationInfo, onBlur]);
    useEffect(() => {
      if (sendPostMessages) {
        sendPostMessage(PostMessageEvent.MountTranslation, {
          translationInfo,
        });
      }
      mountNode({ id, node });
    }, []);

    return (
      <Translation
        isHighlightToTranslate={source === TranslatedNodeSource.Highlight}
        onFocus={onFocusMemoized}
        onBlur={onBlurMemoized}
        isActive={isActive}
        node={node}
        translation={targetPhrase}
        word={word}
        language={targetLanguage}
        isQuizActive={isQuizActive}
        isSaved={isSaved ?? false}
        isSparkleShown={source === TranslatedNodeSource.ExpandTranslation && !hasSeenExpandedTranslation}
        learningShortcutEmoji={learningShortcutEmoji}
        isActiveLearningShortcutPhrase={isActiveLearningShortcutPhrase}
      />
    );
  },
);

export const InlineTranslation = InlineTranslationContainer;
