import React, { FC, ImgHTMLAttributes } from 'react';

import browser from 'webextension-polyfill';

import { Box, BoxProps } from '../Box';

type BaseProps = ImgHTMLAttributes<HTMLImageElement> & BoxProps;

interface ImageProps extends BaseProps {
  src: string;
  alt: string;
}

export const Image: FC<ImageProps> = ({ src, alt, width, height, ...props }) => {
  const imageUrl = src.includes('https://') ? src : browser.runtime.getURL(src);
  return (
    <Box
      component="img"
      // @ts-ignore
      src={imageUrl}
      alt={alt}
      width={width}
      height={height}
      {...props}
    />
  );
};
