/** @jsx jsx */
import { FC, useEffect, useRef } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { ModalBaseProps, ModalType } from '~/components/ModalContainer/types';
import Header from '~/components/shared/Header';
import { Image } from '~/components/shared/Image';
import { Modal, ModalContent, ModalHeader } from '~/components/shared/Modal';
import { browserVendor } from '~/configs/config';
import { StorageKey } from '~/constants/storage';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { BrowserVendors } from '~/types/browsers';

const { Typography, Button } = ToucanComponents;

interface ExpandTranslationModalProps extends ModalBaseProps {}

export const ExpandTranslationModal: FC<ExpandTranslationModalProps> = ({
  onRequestClose,
  onRequestOpen,
  selectedModalType,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const isMobileSafari = browserVendor === BrowserVendors.SafariiOS;
  const viewed = useRef(0);

  const [hasDismissedExpandTranslationModal, setHasDismissedExpandTranslationModal] = useStorage<boolean>(
    StorageKey.HasDismissedExpandTranslationModal,
    false,
  );

  const [isExpandTranslationModalOpenRequested, setIsExpandTranslationModalOpenRequested] = useModalStorage(
    StorageKey.IsExpandTranslationModalOpenRequested,
    false,
  );

  // NOTE: Since the source of truth for this modal is based off of storage
  // we just respond to the changing of that state, this modal has a priority of 0
  // so it always overrides
  useEffect(() => {
    if (isExpandTranslationModalOpenRequested && !hasDismissedExpandTranslationModal && !viewed.current) {
      onRequestOpen(ModalType.ExpandTranslation);

      ContentMessengers.trackEvent({
        eventName: 'Expand Translation Modal Viewed',
        properties: {},
      });
      viewed.current = 1;
    }
  }, [selectedModalType, isExpandTranslationModalOpenRequested, hasDismissedExpandTranslationModal]);

  const closeModal = () => {
    setIsExpandTranslationModalOpenRequested(false);
    setHasDismissedExpandTranslationModal(true);
    onRequestClose(ModalType.ExpandTranslation);
  };

  return (
    <Modal
      css={css`
        text-align: center;
        align-items: center;
        width: ${isMobileSafari ? '100%' : '418px'};
      `}
      onRequestClose={closeModal}
      isOpen={!!(selectedModalType === ModalType.ExpandTranslation)}
    >
      <ModalHeader>
        <Header
          onRequestClose={closeModal}
          css={css`
            background-color: transparent;
          `}
          horizontalLineProps={{
            color: ToucanColors.gray[200],
          }}
          isMobileReferralShown={false}
          onMobileReferralClick={() => {}}
        />
      </ModalHeader>
      <ModalContent p={4}>
        <Box pb={4}>
          <Image src="/images/sparkle-56x56.svg" width={56} height={56} alt={t('sparkle')} />
        </Box>
        <Box pb={1}>
          <Typography variant="md" font="secondary" color={`${ToucanColors.gray[600]}`}>
            {t('timeToLevelUp')}
          </Typography>
        </Box>
        <Box pb={4}>
          <Typography variant="sm" color={`${ToucanColors.gray[500]}`}>
            {t('nowThatYouKnowThisWord')}
          </Typography>
        </Box>
        <Box>
          <Button
            type="utility"
            onClick={closeModal}
            css={css`
              ${theme.breakpoints.down('sm')} {
                width: 100%;
              }
            `}
          >
            {t('okay')}
          </Button>
        </Box>
      </ModalContent>
    </Modal>
  );
};
