import { useReducer, useCallback } from 'react';

import { isEqual } from 'lodash';
import defaults from 'lodash/defaults';

import { Events, EventSources } from '~/constants/events';
import { isError } from '~/helpers/errors';
import ContentMessengers from '~/messengers/contentMessengers';
import { ContextlessLearningType, useRecordContextlessLearningMomentQuizResultsMutation } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { AnyAction, ActionTypes, CurrentQuiz, QuestionsType } from './types';

const StartQuizEventMap: Record<ContextlessLearningType, Events> = {
  [ContextlessLearningType.MultipleChoice]: Events.ContextlessLearningMomentMultipleChoiceQuizStarted,
  [ContextlessLearningType.FlashCard]: Events.ContextlessLearningMomentFlashCardQuizStarted,
};

const QuizSubmissionEventMap: Record<ContextlessLearningType, Events> = {
  [ContextlessLearningType.MultipleChoice]: Events.ContextlessLearningMomentMultipleChoiceQuizCompleted,
  [ContextlessLearningType.FlashCard]: Events.ContextlessLearningMomentFlashCardQuizCompleted,
};

const reducer = (currentQuiz: CurrentQuiz, action: AnyAction): CurrentQuiz => {
  const { results, questions, question } = currentQuiz;
  switch (action.type) {
    case ActionTypes.StartQuiz: {
      return {
        ...currentQuiz,
        quizType: action.payload.quizType,
        status: QuizStatus.InProgress,
        translationIds: action.payload.translationIds,
        questions: action.payload.questions,
        question: action.payload.questions[0],
        results: [],
        errorId: '',
      };
    }
    case ActionTypes.NextQuestion: {
      if (!question) {
        return currentQuiz;
      }
      return {
        ...currentQuiz,
        question: questions[questions.indexOf(question) + 1],
        results: [...results, action.payload],
      };
    }
    case ActionTypes.SubmitQuiz: {
      return {
        ...currentQuiz,
        status: action.payload.status,
        results: [...results, action.payload.result],
      };
    }
    case ActionTypes.FinishedQuiz:
      return { ...currentQuiz, status: QuizStatus.Finished };
    case ActionTypes.QuizError:
      return { ...currentQuiz, status: QuizStatus.Error, errorId: action.payload };
    case ActionTypes.Reset:
      return action.payload;
    default:
      return currentQuiz;
  }
};

const initialState: CurrentQuiz = {
  quizType: ContextlessLearningType.MultipleChoice,
  status: QuizStatus.Pending,
  translationIds: [],
  results: [],
  questions: [],
  question: undefined,
  errorId: '',
};

export const useContextlessLearningMoment = (
  { currentDomain }: { currentDomain: string },
  preloadedQuiz: Partial<CurrentQuiz> = initialState,
) => {
  const [currentQuiz, dispatch] = useReducer(reducer, defaults(preloadedQuiz, initialState));
  const [recordContextlessLearningMomentQuizResults] = useRecordContextlessLearningMomentQuizResultsMutation();
  const quizTranslationIds = currentQuiz?.translationIds ?? [];

  /**
   * start quiz by adding translation id, all questions, and setting the first question.
   * @param quizType
   * @param questions
   * @param translationIds
   * @param isCLMIntroScreenDisabled if true, will not show the intro screen and event tracking will not fire
   */
  const startQuiz = useCallback(
    ({
      questions,
      translationIds,
      quizType,
      isCLMIntroScreenDisabled,
    }: {
      questions: QuestionsType[];
      translationIds: Array<string>;
      quizType: ContextlessLearningType;
      isCLMIntroScreenDisabled: boolean;
    }) => {
      dispatch({
        type: ActionTypes.StartQuiz,
        payload: { questions, translationIds, quizType },
      });

      if (isCLMIntroScreenDisabled) {
        return;
      }
      ContentMessengers.trackEvent({
        eventName: StartQuizEventMap[quizType],
        properties: {},
      });
      ContentMessengers.trackEvent({
        eventName: Events.LearningExperienceStarted,
        properties: {
          exercise: Events.ExtraPracticeStarted,
          source: EventSources.ContextlessLearningMoment,
          url: currentDomain,
        },
        userOptions: { includePremiumSubscriptionStatus: true },
      });
    },
    [dispatch],
  );

  /**
   * Update currentQuiz state after every question submission based on passing or failing quiz by setting the next question.
   * Setting status to finished will trigger quiz close. an error status will trigger error notification.
   * @param result QuizStatus.Pass | QuizStatus.Fail
   */
  const submitQuestion = useCallback(
    async (result: QuizStatus.Pass | QuizStatus.Fail) => {
      // If not the last question go to the next question.
      if (!isEqual(currentQuiz.questions[currentQuiz.questions.length - 1], currentQuiz.question)) {
        dispatch({ type: ActionTypes.NextQuestion, payload: result });
        return;
      }

      const isPassed =
        currentQuiz?.results.every(questionResult => questionResult === QuizStatus.Pass) && result === QuizStatus.Pass;
      const passOrFail = isPassed ? QuizStatus.Pass : QuizStatus.Fail;

      ContentMessengers.trackEvent({
        eventName: QuizSubmissionEventMap[currentQuiz.quizType],
      });

      try {
        dispatch({
          type: ActionTypes.SubmitQuiz,
          payload: { status: passOrFail, result },
        });

        // Sync with server, and background
        await recordContextlessLearningMomentQuizResults({
          input: {
            quizType: currentQuiz.quizType,
            translationIds: [...currentQuiz?.translationIds],
            results: [...currentQuiz?.results, result],
          },
        });

        dispatch({ type: ActionTypes.FinishedQuiz });
        ContentMessengers.trackEvent({
          eventName: Events.LearningExperienceCompleted,
          properties: {
            exercise: Events.ExtraPracticeCompleted,
            source: EventSources.ContextlessLearningMoment,
            url: currentDomain,
          },
          userOptions: { includePremiumSubscriptionStatus: true },
        });
      } catch (e) {
        dispatch({ type: ActionTypes.QuizError, payload: isError(e) ? e.message : 'Unknown error' });
      }
    },
    [dispatch, currentQuiz],
  );

  /**
   * when user quits quiz midway. setting status to finished will trigger quiz close.
   */
  const stopQuiz = useCallback(() => {
    dispatch({ type: ActionTypes.FinishedQuiz });
  }, [dispatch]);

  const onReset = useCallback(() => {
    dispatch({ type: ActionTypes.Reset, payload: initialState });
  }, [dispatch, currentQuiz]);

  /**
   * error status will trigger error notification.
   */
  const onError = useCallback(
    (errorId = '') => {
      dispatch({ type: ActionTypes.QuizError, payload: errorId });
    },
    [dispatch],
  );

  const adhocEventTracking = ({ quizType }: { quizType: ContextlessLearningType }) => {
    ContentMessengers.trackEvent({
      eventName: StartQuizEventMap[quizType],
      properties: {
        hasIntroScreen: false,
        domain: currentDomain,
      },
    });

    ContentMessengers.trackEvent({
      eventName: Events.LearningExperienceStarted,
      properties: {
        exercise: Events.ExtraPracticeStarted,
        source: EventSources.ContextlessLearningMoment,
        url: currentDomain,
      },
      userOptions: { includePremiumSubscriptionStatus: true },
    });
  };

  return {
    currentQuiz,
    startQuiz,
    submitQuestion,
    stopQuiz,
    onReset,
    onError,
    questionNumber: currentQuiz.questions.indexOf(currentQuiz.question as QuestionsType) + 1,
    quizTranslationIds,
    adhocEventTracking,
  };
};
