/** @jsx jsx */
import React, { useState, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { useTheme } from '@mui/material';

import { maximumZIndex } from '~/constants/styling';

import { PausedNotification } from './PausedNotification';
import { SecondaryMenu } from './SecondaryMenu';
import { usePauseReminder } from './usePauseReminder';

export const PauseReminderContainer = () => {
  const theme = useTheme();

  const [scrollYOffsetValue, setScrollYOffsetValue] = useState(1);
  const [isReminderExpanded, setIsReminderExpanded] = useState(false);
  const [isSecondaryMenuShown, setIsSecondaryMenuShown] = useState(false);

  const {
    showReminder,
    loading,
    error,
    isBanned,
    onRemoveReminder,
    onReminderTimeChange,
    onRemoveFromBanlist,
  } = usePauseReminder();

  const calculateAndSetScrollOpacity = () => {
    const scrollLocation = window.scrollY;
    // only grab values 0 to 100
    if (scrollLocation <= 100) {
      // get inverse value to prepare for opacity
      const inverseOfYOffsetValue = Math.abs(scrollLocation - 100);
      const opacityValue = inverseOfYOffsetValue / 100;
      if (opacityValue > 0.04 && opacityValue < 0.96) {
        // decrementing by 0.02 in order to speed up the fade;
        setScrollYOffsetValue(opacityValue - 0.02);
        return;
      }
      setScrollYOffsetValue(opacityValue);
    }
  };

  const onMouseLeave = () => {
    setIsReminderExpanded(false);
    setIsSecondaryMenuShown(false);
  };

  useEffect(() => {
    if (showReminder) {
      const handler = calculateAndSetScrollOpacity;
      window.addEventListener('scroll', handler);
      return () => window.removeEventListener('scroll', handler);
    }
    return () => {};
  }, [showReminder]);

  if (loading || error || !showReminder || !isBanned) {
    return null;
  }

  return (
    <div
      css={css`
        position: absolute;
        z-index: ${maximumZIndex};
        right: ${theme.spacing(2)};
        top: ${theme.spacing(2)};
        opacity: ${scrollYOffsetValue};
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      `}
      onMouseLeave={onMouseLeave}
    >
      <PausedNotification
        isSecondaryMenuShown={isSecondaryMenuShown}
        setIsSecondaryMenuShown={setIsSecondaryMenuShown}
        onChangeReminderDate={onReminderTimeChange}
        isReminderExpanded={isReminderExpanded}
        setIsReminderExpanded={setIsReminderExpanded}
        onRemoveFromBanlist={onRemoveFromBanlist}
        onRemoveReminder={onRemoveReminder}
        reloadPage={() => window.location.reload()}
      />
      <SecondaryMenu
        isSecondaryMenuShown={isSecondaryMenuShown}
        onChangeReminderDate={onReminderTimeChange}
        onRemoveReminder={onRemoveReminder}
        reloadPage={() => window.location.reload()}
      />
    </div>
  );
};
