import { TranslatedNodeSource } from '~/types/dictionary';

export { TranslatedNodeSource } from '~/types/dictionary';

export enum TranslatedNodeStatus {
  Pending = 'pending', // Newly created cache
  Built = 'built', // Created new nodes
  Mounted = 'mounted', // React has now created portal to node
  Idle = 'idle', // Not tooltip showing but on page
  Inactive = 'inactive', // Tooltip on page not active
  Hovered = 'hovered', // Hovered over highlight
  Focused = 'focused', // Hovered over tooltip
  Pinned = 'pinned', // Not changeable by mouse leave, used for onboarding
  Quiz = 'quiz', // Quiz is active very close to the pinned state
  Error = 'error', // Error occurred while building, will be removed
}

export enum ShortcutCelebration {
  ShowCelebration = 'showCelebration',
  HideCelebration = 'hideCelebration',
}

export type TranslatedNodeMeta = Partial<{
  isSaved: boolean;
  isActiveLearningShortcutPhrase: boolean;
}>;

interface BaseTranslatedNode {
  status: TranslatedNodeStatus;
  translationId: string;
  address?: string;
  replacement: string;
  targetPhrase?: string;
  id: string;
  targetId: string;
  densityThreshold: number; // 0 - 1
  source: TranslatedNodeSource;
  replacementIndex?: number;
  meta?: TranslatedNodeMeta; // bit of a type hole
  targetLanguage?: string;
  difficultyLevel?: string;
}

// Pending state has no node
export interface PendingTranslatedNode extends BaseTranslatedNode {
  node?: HTMLSpanElement;
  status: TranslatedNodeStatus;
}
// Built interface with required node
export interface BuiltTranslatedNode extends BaseTranslatedNode {
  node: HTMLSpanElement;
  status: Exclude<TranslatedNodeStatus, TranslatedNodeStatus.Built>;
  source: TranslatedNodeSource;
}

export type TranslatedNode = PendingTranslatedNode;

export type TranslatedNodePayload = Partial<BuiltTranslatedNode> & Pick<BuiltTranslatedNode, 'id'>;
