import { FinchClient, useFinchClient } from '@finch-graphql/react';

import { getBrowserProperties } from '~/analytics/browser';
import { browserVendor } from '~/configs/config';
import { Language } from '~/constants/language';
import { textToSpeech, VoiceNumber } from '~/libs/TextToSpeech';
import { TextToSpeechAudioContext } from '~/libs/TextToSpeech/TextToSpeechAudioContext';
import { TextToSpeechAudioElement } from '~/libs/TextToSpeech/TextToSpeechAudioElement';
import { audioContextSupport, base64ToUint8 } from '~/libs/TextToSpeech/utils';
import { isGreaterThanOrEqual } from '~/libs/semver';
import {
  FetchTextToSpeechQuery,
  FetchTextToSpeechDocument,
  FetchTextToSpeechQueryVariables,
} from '~/schema/background';
import { BrowserVendors } from '~/types/browsers';

const { 'Browser Version': browserVersion } = getBrowserProperties();

/**
 * @todo deprecate this when we are able to support only version greater the 15.5 Safari
 */
const getInScriptTextToSpeech = async ({
  targetPhrase,
  language,
  playbackRate,
  voiceNumber,
}: {
  targetPhrase: string;
  language: Language;
  playbackRate: number;
  voiceNumber: VoiceNumber;
}) => {
  const audioContent = await textToSpeech(targetPhrase, language, playbackRate, voiceNumber);
  const audioBuffer = base64ToUint8(audioContent);

  if (!audioContextSupport) {
    return TextToSpeechAudioElement.from(audioBuffer);
  }
  return TextToSpeechAudioContext.from(audioBuffer);
};

const getBackgroundTextToSpeech = async ({
  targetPhrase,
  language,
  playbackRate,
  voiceNumber,
  client,
}: {
  targetPhrase: string;
  language: Language;
  playbackRate: number;
  voiceNumber: number;
  client: FinchClient;
}) => {
  const player = TextToSpeechAudioContext.from();
  client
    .query<FetchTextToSpeechQuery, FetchTextToSpeechQueryVariables>(FetchTextToSpeechDocument, {
      text: targetPhrase,
      language,
      playbackRate,
      voiceNumber,
    })
    .then(({ data, errors }) => {
      if (errors && errors.length > 0) {
        throw errors[0];
      }
      // loading audio buffer into audio context
      player.loadBuffer(base64ToUint8(data.fetchTextToSpeech));
    });

  return player;
};

/**
 * useTextToSpeech is a hook that allows us to make an abstraction over browser compatibility
 * for text to speech. This hook will return a promise that resolves a playable audio element or audio context.
 *
 * **Safari lt 15.5** fetches audio in the content script because of the interaction time is fast and back in forth with the background
 * breaks playback.
 *
 * **All other browsers & Safari gte 15.5** fetch audio in the background to avoid in page CSP issues.
 */
export const useTextToSpeech = () => {
  const { client } = useFinchClient();

  const fetchTextToSpeech = ({
    targetPhrase,
    language,
    playbackRate,
    voiceNumber,
  }: {
    targetPhrase: string;
    language: Language;
    playbackRate: number;
    voiceNumber: VoiceNumber;
  }) => {
    if (
      (browserVendor === BrowserVendors.SafariiOS || browserVendor === BrowserVendors.Safari) &&
      !isGreaterThanOrEqual(browserVersion, '15.5')
    ) {
      return getInScriptTextToSpeech({ targetPhrase, language, playbackRate, voiceNumber });
    }
    return getBackgroundTextToSpeech({ targetPhrase, language, playbackRate, voiceNumber, client });
  };

  return { fetchTextToSpeech };
};
