import React, { useState, useRef, useEffect, useCallback } from 'react';

import { getLuminance } from '@mui/material';

import { RootProviders } from '~/components/RootProviders';
import { Language, LanguageInfo } from '~/constants/language';
import { fadeInNode, fadeOutNode } from '~/helpers/domHelpers';
import { PendingTranslation } from '~/hooks/useDictionary/types';
import { usePrevious } from '~/hooks/usePrevious';
import useWindowDimensions from '~/hooks/useWindowDimensions';
import { createShadowPortal } from '~/libs/reactUtils';
import contentMessengers from '~/messengers/contentMessengers';

import { Highlight } from './Highlight';
import { SkeletonLoader } from './SkeletonLoader';

interface TranslationsProps {
  node: HTMLSpanElement;
  translation: string;
  word: string;
  isActive?: boolean;
  onFocus: () => void;
  onBlur: () => void;
  isHighlightToTranslate: boolean;
  language: Language | null;
  isQuizActive: boolean;
  isSaved: boolean;
  isSparkleShown: boolean;
  learningShortcutEmoji?: string;
  isActiveLearningShortcutPhrase: boolean;
}

export const Translation: React.FC<TranslationsProps> = React.memo(
  ({
    translation,
    node,
    word,
    isActive,
    isHighlightToTranslate,
    language,
    onFocus,
    onBlur,
    isQuizActive,
    isSaved,
    isSparkleShown,
    learningShortcutEmoji,
    isActiveLearningShortcutPhrase,
  }) => {
    const [translationIsActive, setTranslationIsActive] = useState(isActive);
    const translationElement = useRef<HTMLSpanElement | null>(null);
    const isMounted = useRef<boolean>(true);
    const [isLightText, setIsLightText] = useState(false);
    const wasActive = usePrevious(isActive);
    const display = isHighlightToTranslate ? 'inline' : 'inline-block';
    const languageInfo = language ? LanguageInfo[language] : null;
    const { windowHeight } = useWindowDimensions();

    const animateToggleActive = async () => {
      // don't fade in/out if location of node is outside of viewport
      const { y: nodeYLocation } = node.getBoundingClientRect();
      if (nodeYLocation > windowHeight) {
        setTranslationIsActive(isActive);
        return;
      }

      await fadeOutNode(node);
      if (isMounted.current) {
        setTranslationIsActive(isActive);
      }
      await fadeInNode(node);
    };

    useEffect(() => {
      if (translationIsActive && languageInfo) {
        node.setAttribute('lang', languageInfo.languageId);
      } else {
        node.removeAttribute('lang');
      }
    }, [languageInfo, translationIsActive]);

    useEffect(() => {
      // Animated node
      if (wasActive === isActive || typeof wasActive === 'undefined') {
        return;
      }
      animateToggleActive();
    }, [wasActive, isActive]);

    useEffect(() => {
      isMounted.current = true;
      return () => {
        isMounted.current = false;
      };
    }, []);

    useEffect(() => {
      if (isHighlightToTranslate && translation !== PendingTranslation) {
        contentMessengers.trackEvent({ eventName: 'Highlight to Translate seen', properties: {} });
      }
    }, [isHighlightToTranslate, translation]);

    const onSetRef = useCallback((span: HTMLSpanElement | null) => {
      translationElement.current = span;
      if (span && span instanceof HTMLElement) {
        const styles = window.getComputedStyle(span);
        // NOTE: same as emphasize
        // https://github.com/mui-org/material-ui/blob/b3cd80d651db897fc336b13624be1237a0e31d80/packages/material-ui/src/styles/colorManipulator.js#L213
        setIsLightText(getLuminance(styles.color || '#000') > 0.5);
      }
    }, []);

    return createShadowPortal(
      <RootProviders withCSSBaseline={false}>
        {translation === PendingTranslation && isHighlightToTranslate ? (
          <Highlight display="inline" active underline={isSaved}>
            <SkeletonLoader>{word}</SkeletonLoader>
          </Highlight>
        ) : (
          <Highlight
            ref={onSetRef}
            onClick={onFocus}
            onFocus={onFocus}
            onBlur={onBlur}
            isLightText={isLightText}
            active={translationIsActive ?? false}
            display={display}
            underline={isSaved}
            hideText={isQuizActive}
            isSparkleShown={isSparkleShown}
            learningShortcutEmoji={learningShortcutEmoji}
            isActiveLearningShortcutPhrase={isActiveLearningShortcutPhrase}
          >
            {translationIsActive ? translation : word}
          </Highlight>
        )}
      </RootProviders>,
      node,
      { tagName: 'span' },
    );
  },
);
