import { TranslatedNodeSource } from '~/hooks/useTranslatedNodes/types';

/**
 * hasUserTranslation is method that determines if a translation has a dictionary entry.
 * This is important because the dictionary entry is not always present, and some features
 * like "mark word as known" and "save word" are highly dependent on it.
 *
 * @param {TranslatedNodeSource} source The source of the translation.
 * @returns {boolean} True if the translation has a dictionary entry, false otherwise.
 */
export const hasUserTranslation = (source: TranslatedNodeSource) =>
  source === TranslatedNodeSource.Crawler || source === TranslatedNodeSource.OnboardingDictionary;

export const shouldSendPostMessage = (source: TranslatedNodeSource) =>
  source === TranslatedNodeSource.Onboarding ||
  source === TranslatedNodeSource.OnboardingDictionary ||
  source === TranslatedNodeSource.ExpandTranslation;
