/** @jsx jsx */
import { FC, memo } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box } from '@mui/material';

import { useTranslation } from '~/libs/i18n';

const { ToucanIcon, Tooltip, useColorScheme } = ToucanComponents;

export const ViewWords: FC<{ iconWidth: string; onClick: () => void }> = memo(({ iconWidth, onClick }) => {
  const { t } = useTranslation();
  const { textLink } = useColorScheme();

  return (
    <button
      type="button"
      data-testid="saved-word-link"
      onClick={onClick}
      css={css`
        padding: 0;
        outline: 0;
        border: none;
        background-color: transparent;
        cursor: pointer;
      `}
    >
      <Tooltip title={t('viewSavedWords')} enterDelay={1000}>
        <Box display="flex" justifyContent="center">
          <ToucanIcon
            icon="book"
            css={css`
              color: ${textLink};
              width: ${iconWidth};
              height: ${iconWidth};
              margin-top: 2px;
              &:hover {
                color: ${ToucanColors.yellow[300]};
              }
              &:active {
                color: ${ToucanColors.yellow[400]};
              }
            `}
          />
        </Box>
      </Tooltip>
    </button>
  );
});
