/** @jsx jsx */
import { FC, useState } from 'react';

import { jsx, css } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import Configs from '~/configs/config';
import { StorageKey } from '~/constants/storage';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';

import { Chiclet } from './Chiclet';

const { Typography, useColorScheme } = ToucanComponents;

interface TranslateChicletProps {
  onLayout: (size: { width: number; height: number }) => void;
  onClick: () => void;
  onOpenSettings: () => void;
  overWordLimit: boolean;
}

const ChicletLink: FC<{ onClick?: () => void }> = ({ onClick, children }) => {
  const { isDarkMode } = useColorScheme();
  const Component = onClick ? 'button' : 'span';

  return (
    <Typography variant="xs" isBold>
      <Component
        type="button"
        onClick={onClick}
        css={css`
          cursor: pointer;
          background: transparent;
          border: 0;
          padding: 0;
          margin: 0;
          text-decoration: none;
          font-weight: 700;
          display: flex;
          align-items: center;
          outline: none;
          color: ${isDarkMode ? ToucanColors.teal[200] : ToucanColors.blue[300]};
          &:hover {
            color: ${isDarkMode ? ToucanColors.teal[100] : ToucanColors.blue[200]};
          }
          &:active {
            color: ${isDarkMode ? ToucanColors.teal[300] : ToucanColors.blue[400]};
          }
        `}
      >
        {children}
      </Component>
    </Typography>
  );
};

export const TranslateChiclet: FC<TranslateChicletProps> = ({ onLayout, onClick, overWordLimit, onOpenSettings }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const [hasSentLayout, setLayoutSent] = useState(false);
  const { t } = useTranslation();
  const color = isDarkMode ? ToucanColors.white : ToucanColors.gray[400];
  const [showOverlimitMessage, changeVisibility] = useStorage<boolean>(
    StorageKey.HighlightToTranslateOverlimitMessage,
    true,
  );

  if (!showOverlimitMessage && overWordLimit) {
    return null;
  }

  return (
    <Chiclet
      css={css`
        cursor: ${overWordLimit ? 'initial' : 'pointer'};
        display: inline-flex;
        flex-direction: column;
        justify-content: center;
        max-width: 216px;
      `}
      ref={el => {
        if (el && !hasSentLayout) {
          const styles = window.getComputedStyle(el);
          onLayout({
            width: parseInt(styles.width, 10),
            height: parseInt(styles.height, 10),
          });
          setLayoutSent(true);
        }
      }}
      onClick={overWordLimit ? undefined : onClick}
      onOpenSettings={overWordLimit ? undefined : onOpenSettings}
      afterContent={
        overWordLimit ? (
          <Box pt={1}>
            <Typography
              variant="2xs"
              css={css`
                color: ${color};
                padding-bottom: ${theme.spacing(1)};
              `}
            >
              {t('weAreWorkingToIncreaseLimits')}
            </Typography>
            <ChicletLink
              onClick={() => {
                changeVisibility(false);
              }}
            >
              {t('dontShowAgain')}
            </ChicletLink>
          </Box>
        ) : null
      }
    >
      <Box display="flex" flexDirection="row" alignItems="center">
        <Image
          css={css`
            width: 16px;
            height: 16px;
            margin-right: ${theme.spacing(1)};
          `}
          src={`/images/icons/${Configs.env}/icon48.png`}
          alt="Toucan"
        />
        {overWordLimit ? (
          <Typography
            variant="xs"
            isBold
            css={css`
              color: ${color};
            `}
          >
            {t('overOneHundredWordLimit')}
          </Typography>
        ) : (
          <ChicletLink>{t('translate')}</ChicletLink>
        )}
      </Box>
    </Chiclet>
  );
};
