import { useCallback, useMemo } from 'react';

import { useFinchClient } from '@finch-graphql/react';
import { useAtom } from 'jotai';
import { isEqual } from 'lodash';
import { useDeepCompareMemoize } from 'use-deep-compare-effect';

import { Events } from '~/constants/events';
import { StorageKey } from '~/constants/storage';
import { isError } from '~/helpers/errors';
import { useDailyPracticeQuizUses } from '~/hooks/useDailyPracticeQuizUses';
import { useStorage } from '~/hooks/useStorage';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  QuizType,
  QuizQuery,
  QuizQueryVariables,
  QuizDocument,
  useRecordQuizProgressionMutation,
} from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import {
  quizAtom,
  startQuizAtom,
  nextQuestionAtom,
  submitQuizAtom,
  finishedQuizAtom,
  quizErrorAtom,
  retryQuizAtom,
  resetQuizAtom,
} from './atom';
import { QuestionsType } from './types';

const StartQuizEventMap: Record<QuizType, Events> = {
  [QuizType.Practice]: Events.PracticeQuizStarted,
  [QuizType.MultiPractice]: Events.MultiPracticeQuizStarted,
};

const QuizSubmissionEventMap: Record<QuizType, Events> = {
  [QuizType.Practice]: Events.PracticeQuizCompleted,
  [QuizType.MultiPractice]: Events.MultiPracticeQuizCompleted,
};

export const useQuiz = () => {
  const currentQuiz = useAtom(quizAtom)[0];
  const hydrateQuiz = useAtom(startQuizAtom)[1];
  const nextQuestion = useAtom(nextQuestionAtom)[1];
  const submitQuiz = useAtom(submitQuizAtom)[1];
  const finishQuiz = useAtom(finishedQuizAtom)[1];
  const quizError = useAtom(quizErrorAtom)[1];
  const resetQuiz = useAtom(resetQuizAtom)[1];
  const retry = useAtom(retryQuizAtom)[1];
  const { incrementPracticeQuizUse } = useDailyPracticeQuizUses();
  const [recordQuizProgression] = useRecordQuizProgressionMutation();
  const [, setHasSeenPracticeQuizCallout] = useStorage<boolean>(StorageKey.HasSeenPracticeQuizCallout, false);
  const { client } = useFinchClient();

  /**
   * start quiz by adding translation id, all questions, and setting the first question
   * @param quizType
   * @param questions
   * @param translationId
   */
  const startQuiz = useCallback(
    ({
      questions,
      translationIds,
      quizType,
    }: {
      questions: QuestionsType[];
      translationIds: Array<string>;
      quizType: QuizType;
    }) => {
      hydrateQuiz({ questions, translationIds, quizType });

      ContentMessengers.trackEvent({
        eventName: StartQuizEventMap[quizType],
        properties: {},
      });
    },
    [hydrateQuiz],
  );

  /**
   * Update currentQuiz state after every question submission based on passing or failing quiz by
   * setting the next question. if end of fluency quiz and has passed all, mutation made to markWordAsFluent.
   * If did not pass, user will have an option to retake the quiz.
   * Setting status to finished will trigger quiz close. an error status will trigger error notification.
   * In addition, we will determine if the user should be shown the premium upsell message.
   * @param result QuizStatus.Pass | QuizStatus.Fail
   */
  const submitQuestion = useCallback(async (result: QuizStatus.Pass | QuizStatus.Fail) => {
    // If not the last question go to the next question.
    if (!isEqual(currentQuiz.questions[currentQuiz.questions.length - 1], currentQuiz.question)) {
      nextQuestion(result);
      return;
    }

    const isPassed =
      currentQuiz?.results.every(questionResult => questionResult === QuizStatus.Pass) && result === QuizStatus.Pass;
    const passOrFail = isPassed ? QuizStatus.Pass : QuizStatus.Fail;

    ContentMessengers.trackEvent({
      eventName: QuizSubmissionEventMap[currentQuiz.quizType],
      properties: {
        result: passOrFail,
      },
    });

    // Before tracking state changes
    if (currentQuiz.quizType === QuizType.Practice) {
      incrementPracticeQuizUse();
      setHasSeenPracticeQuizCallout(true);
    }

    try {
      submitQuiz({
        status: currentQuiz.quizType !== QuizType.MultiPractice ? passOrFail : QuizStatus.Pass,
        result,
      });

      // Sync with server, and background
      await recordQuizProgression({
        input: {
          quizType: currentQuiz.quizType,
          translationIds: [...currentQuiz?.translationIds],
          quizStatus: passOrFail,
          results: [...currentQuiz?.results, result],
        },
      });
      // If the quiz is passed or multi practice finish the quiz
      if (isPassed || currentQuiz.quizType === QuizType.MultiPractice) {
        finishQuiz();
      }
    } catch (e) {
      quizError(isError(e) ? e.message : 'Unknown error');
    }
  }, useDeepCompareMemoize([finishQuiz, quizError, currentQuiz]));

  const retryQuiz = useCallback(async () => {
    const { data } = await client.query<QuizQuery, QuizQueryVariables>(QuizDocument, {
      translationIds: currentQuiz?.translationIds ?? [],
      quizType: currentQuiz.quizType,
    });
    retry(data.quiz?.questions ?? currentQuiz.questions);

    ContentMessengers.trackEvent({
      eventName:
        currentQuiz.quizType === QuizType.MultiPractice ? Events.MultiPracticeQuizStarted : Events.PracticeQuizStarted,
      properties: {},
    });
  }, [retry, currentQuiz]);

  return useMemo(
    () => ({
      currentQuiz,
      startQuiz,
      submitQuestion,
      stopQuiz: finishQuiz,
      onError: quizError,
      retryQuiz,
      onReset: resetQuiz,
      questionNumber: currentQuiz.questions.indexOf(currentQuiz.question as QuestionsType) + 1,
    }),
    [resetQuiz, retryQuiz, currentQuiz, startQuiz, submitQuestion, quizError],
  );
};
