import React, { useContext } from 'react';

interface ShadowContext {
  root: ShadowRoot;
  head: HTMLElement;
  body: HTMLElement;
  modals: HTMLElement;
  jssElement: HTMLElement;
}

const mockEl = document.createElement('span');
const shadowMock = mockEl.attachShadow({ mode: 'open' });

export const shadowContext = React.createContext<ShadowContext>({
  root: shadowMock,
  head: mockEl,
  body: mockEl,
  modals: mockEl,
  jssElement: mockEl,
});

export const useShadow = () => useContext(shadowContext);

export const ShadowProvider: React.FC<ShadowContext> = ({ root, head, body, children, modals, jssElement }) => (
  <shadowContext.Provider value={{ root, body, head, modals, jssElement }}>{children}</shadowContext.Provider>
);
