import React, { FC, useState, useEffect, useCallback, useRef, memo } from 'react';

import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';
import { AnimatePresence } from 'framer-motion';

import { Box, MotionBox } from '~/components/Box';
import { useContextlessLearningMoment } from '~/components/ContextlessLearningMoment/useContextlessLearningMoment';
import { RootProviders } from '~/components/RootProviders';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { FeatureName } from '~/constants/features';
import { Pinchpoints } from '~/constants/pinchpoints';
import { isDarkColor } from '~/helpers/colors';
import {
  filterDomain,
  getExtraPracticeContainerWidth,
  useIsExtraPracticeEnabledOnDomain,
} from '~/helpers/extraPractice';
import { useDomainEnabledEffect } from '~/hooks/useDomainEnabledEffect';
import { useHistoryChange } from '~/hooks/useHistoryChange';
import { useMediaQuery } from '~/hooks/useMediaQuery';
import { useReferral } from '~/hooks/useReferral';
import { useSubscription } from '~/hooks/useSubscription';
import { useToast } from '~/hooks/useToast';
import { useUserInfo } from '~/hooks/useUserInfo';
import { Feature, useFeatureEnabled } from '~/libs/features/client';
import { createShadowPortal } from '~/libs/reactUtils';
import ContentMessengers from '~/messengers/contentMessengers';
import {
  useContextlessLearningMomentQuery,
  ContextlessLearningType,
  useToggleContextlessLearningMomentMutation,
  useRemoveContextlessLearningMomentCacheMutation,
} from '~/schema/background';
import { QuizStatus } from '~/types/quiz';
import { PanelName, SlideName } from '~/types/sidebar';

import { Celebration } from './Celebration';
import { ContextlessLearningQuiz } from './ContextlessLearningQuiz';
import { Error } from './Error';
import { Launcher } from './Launcher';
import { TurnOffToastBody } from './Launcher/TurnOffToastBody';
import { Placeholder } from './Placeholder';
import { ContainerSize } from './types';
import { urlAdapter as contextlessLearningUrlAdapter } from './urlAdapter';

const { ColorScheme } = ToucanComponents;

interface ContextlessLearningMomentProps {
  currentDomain: string;
}

export const ContextlessLearningMoment: FC<ContextlessLearningMomentProps> = memo(({ currentDomain }) => {
  const theme = useTheme();
  const [contextlessLearningMomentContainer, setContextlessLearningMomentContainer] = useState<HTMLElement | null>(
    null,
  );
  const [isToucanEnabledOnDomain, setIsToucanEnabledOnDomain] = useState(true);
  const [isBeginClicked, setIsBeginClicked] = useState(false);
  const [historyChangeAmount, setHistoryChangeAmount] = useState(0);
  const numChecksForPageContainer = useRef(0);
  const isViewportBelowSm = useMediaQuery(theme.breakpoints.down('sm'));
  const {
    startQuiz,
    submitQuestion,
    onReset,
    currentQuiz,
    questionNumber,
    quizTranslationIds,
    adhocEventTracking,
  } = useContextlessLearningMoment({ currentDomain });
  const { isContextlessLearningMomentEnabled, isPremiumSubscribed } = useUserInfo();
  const { showToast, removeToast } = useToast();
  const [toggleContextlessLearningMoment] = useToggleContextlessLearningMomentMutation();
  const { openSidebar } = useSidebar();
  const { openSubscribePage } = useSubscription();
  const isReferralEnabled = useFeatureEnabled(FeatureName.ReferralsV2);
  const [isReferralShown, setIsReferralShown] = useState<boolean>(false);
  const { referralCTAText } = useReferral();
  const [removeContextlessLearningMomentCache] = useRemoveContextlessLearningMomentCacheMutation();
  const isCLMIntroScreenDisabled = useFeatureEnabled(FeatureName.ContextlessLearningMomentABTest) ?? false;
  const [currentUrl, setCurrentUrl] = useState<string>('');
  const containerRef = useRef<HTMLDivElement>(null);
  const [containerSize, setContainerSize] = useState<ContainerSize>(ContainerSize.Small);

  const possiblyFilteredDomain = filterDomain(currentDomain);

  // enable clm on various domains
  const isExtraPracticeEnabledOnDomain = useIsExtraPracticeEnabledOnDomain(currentDomain);

  const { data, loading, error, refetch } = useContextlessLearningMomentQuery({
    skip: !isBeginClicked && !isCLMIntroScreenDisabled,
    timeout: 100000, // This can take a long time if user have lots of user translations
  });

  useEffect(() => {
    if (data?.contextlessLearningMoment.learnings.length) {
      startQuiz({
        questions: data.contextlessLearningMoment.learnings,
        translationIds: data.contextlessLearningMoment.translationIds,
        quizType: data.contextlessLearningMoment.learnings[0].learningType,
        isCLMIntroScreenDisabled,
      });
    }
  }, [JSON.stringify(data?.contextlessLearningMoment.learnings)]);

  /**
   * Attempt to embed the contextless learning moment for 10 seconds (10 tries) if the container isn't present on page load
   * */
  const attemptToEmbedContextlessLearningMoment = ({
    forcefullyAddContainer = false,
  }: {
    forcefullyAddContainer?: boolean;
  }) => {
    const learningMomentResetInterval = setInterval(() => {
      if (!isExtraPracticeEnabledOnDomain) {
        return;
      }
      const containerOnPage = contextlessLearningUrlAdapter[possiblyFilteredDomain].getParentElement();
      if (!containerOnPage) {
        // If the page container hasn't been found in 10 tries, set an error message on Sentry so we can be aware of it
        if (numChecksForPageContainer.current >= 10) {
          ContentMessengers.captureContentError({
            message: 'Contextless learning moment site container not found',
            info: { domain: currentDomain },
          });
          clearInterval(learningMomentResetInterval);
          numChecksForPageContainer.current = 0;
          return;
        }

        numChecksForPageContainer.current += 1;
        return;
      }

      // Add contextless learning moment container to specific area on page
      if (!contextlessLearningMomentContainer || forcefullyAddContainer) {
        const embeddedElement = document.createElement('div');
        embeddedElement.setAttribute('id', 'toucan-contextless-learning-moment');

        contextlessLearningUrlAdapter[possiblyFilteredDomain].appendElement(containerOnPage, embeddedElement);
        setContextlessLearningMomentContainer(embeddedElement);
      }
      numChecksForPageContainer.current = 0;
      clearInterval(learningMomentResetInterval);
    }, 1000);

    return () => {
      clearInterval(learningMomentResetInterval);
    };
  };

  const resetQuiz = async () => {
    onReset();
    setIsBeginClicked(false);
    // if a non-premium user has exited the quiz, show the referral
    if (!isPremiumSubscribed) {
      setIsReferralShown(true);
    }
    await removeContextlessLearningMomentCache({});
    if (isCLMIntroScreenDisabled) {
      refetch();
    }
  };

  useHistoryChange(
    useCallback(() => {
      setContextlessLearningMomentContainer(null);
      if (!isCLMIntroScreenDisabled) {
        resetQuiz();
      }
      setHistoryChangeAmount(currentAmount => currentAmount + 1);
    }, [isCLMIntroScreenDisabled]),
  );

  useEffect(() => attemptToEmbedContextlessLearningMoment({}), [
    historyChangeAmount,
    possiblyFilteredDomain,
    isExtraPracticeEnabledOnDomain,
  ]);

  // Hide contextless learning moment learning if user disables extension or pauses domain with contextless learning moment
  // Display contextless learning moment if user (enables extension or unpauses current domain) and was not dismissed by user previously
  useDomainEnabledEffect(
    isDomainEnabled => {
      if (!isDomainEnabled) {
        setIsToucanEnabledOnDomain(false);
      } else if (isDomainEnabled && !isToucanEnabledOnDomain) {
        setIsToucanEnabledOnDomain(true);
      }
    },
    [isToucanEnabledOnDomain],
  );

  const openSettingsFromTurnOffToast = () => {
    removeToast('contextlessLearningRefusalToast');
    openSidebar({
      panelName: PanelName.Settings,
      slideName: SlideName.MainSettings,
    });
  };

  const enableFromTurnOffToast = () => {
    removeToast('contextlessLearningRefusalToast');
    toggleContextlessLearningMoment({ enabled: true });
  };

  const toggleOffAndShowToast = () => {
    toggleContextlessLearningMoment({ enabled: false });
    showToast({
      id: 'contextlessLearningRefusalToast',
      body: (
        <TurnOffToastBody
          onClickOpenSettings={openSettingsFromTurnOffToast}
          onClickTurnOnContextlessLearningMoment={enableFromTurnOffToast}
        />
      ),
      image: '/images/toucan-circle.svg',
      onClick: () => {},
      timeout: 10000,
    });
  };

  const trackHelpfulSurveyQuestion = (isHelpful: boolean) => {
    ContentMessengers.trackEvent({
      eventName: 'Contextless learning moment micro-feedback recorded',
      properties: {
        type:
          data.contextlessLearningMoment.learnings[0].learningType === ContextlessLearningType.MultipleChoice
            ? 'multiple choice'
            : 'flash cards',
        placement: 'sidebar',
        website: currentDomain,
        question: 'Was this helpful?',
        response: isHelpful ? 'yes' : 'no',
      },
    });
  };

  useEffect(() => {
    const containerRefRect = containerRef.current?.getBoundingClientRect();
    if (containerRefRect?.width && containerRefRect.width > 600) {
      setContainerSize(ContainerSize.Large);
    } else if (containerRefRect?.width && containerRefRect.width > 400) {
      setContainerSize(ContainerSize.Medium);
    } else {
      setContainerSize(ContainerSize.Small);
    }
  }, [containerRef.current]);

  if (!contextlessLearningMomentContainer) {
    return null;
  }

  if (isCLMIntroScreenDisabled && currentDomain === 'twitter.com') {
    // when going back to the main feed
    onpopstate = e => {
      // @ts-ignore - location type does not recognize it is part of PopStateEvent
      const { href } = e.target?.location;
      if (href !== currentUrl) {
        attemptToEmbedContextlessLearningMoment({ forcefullyAddContainer: true });
        setCurrentUrl(prevUrl => (!prevUrl || prevUrl !== href ? href : prevUrl));
      }
    };
  }

  const isSiteBackgroundDark = isDarkColor(contextlessLearningUrlAdapter[possiblyFilteredDomain].getBackgroundColor());
  const isContextlessLearningMomentVisible =
    isToucanEnabledOnDomain && !isViewportBelowSm && isContextlessLearningMomentEnabled;

  return createShadowPortal(
    <RootProviders>
      {isContextlessLearningMomentVisible && (
        <Feature name={FeatureName.ContextlessLearningMoment}>
          <ColorScheme isDarkMode={isSiteBackgroundDark}>
            <AnimatePresence>
              <MotionBox
                key="contextless-learning-moment-container"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 1, ease: 'easeIn', duration: 0.3 }}
                exit={{ opacity: 0, transition: { ease: 'easeOut', duration: 0.6 } }}
                display="flex"
                justifyContent="center"
                width="100%"
                ref={containerRef}
              >
                <Box
                  width={getExtraPracticeContainerWidth({ currentDomain })}
                  bgcolor={isSiteBackgroundDark ? ToucanColors.gray[600] : ToucanColors.white}
                  borderRadius="8px"
                  border={`1px solid ${isSiteBackgroundDark ? ToucanColors.gray[500] : ToucanColors.gray[200]}`}
                  p={2}
                  m={contextlessLearningUrlAdapter[possiblyFilteredDomain].containerMargin}
                >
                  {(() => {
                    if (!isBeginClicked && !isCLMIntroScreenDisabled) {
                      return (
                        <Launcher
                          onClickBegin={() => setIsBeginClicked(true)}
                          onClickDontShowMe={toggleOffAndShowToast}
                          isReferralShown={isReferralShown && !!isReferralEnabled}
                        />
                      );
                    }

                    if (error) {
                      return <Error error={error} />;
                    }

                    if (currentQuiz.status === QuizStatus.Finished) {
                      return (
                        <Celebration
                          onClickHelpfulButton={trackHelpfulSurveyQuestion}
                          resetQuiz={resetQuiz}
                          isReferralEnabled={!!isReferralEnabled}
                          referralCTAText={referralCTAText}
                        />
                      );
                    }

                    if (loading || !currentQuiz?.question) {
                      return <Placeholder />;
                    }
                    return (
                      <ContextlessLearningQuiz
                        question={currentQuiz?.question}
                        onExitQuiz={resetQuiz}
                        onClickUnlockPremium={() =>
                          openSubscribePage({ pinchpoint: Pinchpoints.ContextlessLearningMomentQuiz })
                        }
                        onCompleteQuestion={(quizStatus: QuizStatus.Pass | QuizStatus.Fail) => {
                          submitQuestion(quizStatus);
                        }}
                        questionNumber={questionNumber}
                        totalNumQuestions={currentQuiz.questions.length}
                        quizTranslationIds={quizTranslationIds}
                        referralCTAText={referralCTAText}
                        isCLMIntroScreenDisabled={isCLMIntroScreenDisabled}
                        adhocEventTracking={adhocEventTracking}
                        containerSize={containerSize}
                      />
                    );
                  })()}
                </Box>
              </MotionBox>
            </AnimatePresence>
          </ColorScheme>
        </Feature>
      )}
    </RootProviders>,
    contextlessLearningMomentContainer,
  );
});
