import {
  LanguageInfo,
  Language,
  LanguageInformation,
  InDevLanguageInformation,
  LanguageISOCode,
  COMING_SOON_LANGUAGE_INFO,
} from '~/constants/language';
import { i18n } from '~/libs/i18n';
import { ComingSoonLanguageId } from '~/schema/background';
import { LanguageId } from '~/schema/server';

export const getLanguageByLanguageId = (languageId: LanguageId) => {
  let language;
  Object.values(LanguageInfo).forEach(info => {
    if (info.languageId === languageId) {
      language = info.key;
    }
    if (info.variants) {
      Object.values(info.variants).forEach(variantInfo => {
        if (variantInfo.languageId === languageId) {
          language = info.key;
        }
      });
    }
  });
  return language ?? Language.Spanish;
};

export const hasPronunciation = (language: Language) => {
  const languageInfo = LanguageInfo[language];
  return !!languageInfo.variants;
};

export const getLanguageIdFromLanguage = (language: Language, languageVariant: string | undefined | null) => {
  const languageInfo: LanguageInformation | undefined = LanguageInfo[language];
  if (!languageVariant || !languageInfo?.variants) {
    return languageInfo?.languageId ?? LanguageId.Es;
  }
  const languageVariantInfo = Object.values(languageInfo.variants).find(variant => variant.key === languageVariant);
  return languageVariantInfo?.languageId ?? LanguageId.Es;
};

