import { atom } from 'jotai';
import omit from 'lodash/omit';

import { Translation } from '~/types/dictionary';

export const dictionaryAtom = atom<Record<string, Translation>>({});

export const addDictionaryEntriesAtom = atom<unknown, Record<string, Translation>>(null, (get, set, newEntries) => {
  const existingEntries = get(dictionaryAtom);
  set(dictionaryAtom, {
    ...existingEntries,
    ...newEntries,
  });
});

export const removeTranslationsAtom = atom<unknown, string[]>(null, (get, set, ids) => {
  const existingEntries = get(dictionaryAtom);
  set(dictionaryAtom, omit(existingEntries, ids));
});
