import React, { FC } from 'react';

import Configs from '~/configs/config';
import { useTranslation } from '~/libs/i18n';

import { ErrorProps } from '../BirdCage';
import { Box } from '../Box';
import { ErrorMessage } from '../shared/ErrorMessage';

export const SidebarError: FC<ErrorProps> = ({ error, errorId }) => {
  const { t } = useTranslation();
  let message = error?.message ?? t('sorryMessyNestRefresh');
  if (Configs.env === 'production') {
    message = t('sorryMessyNestRefresh');
  }
  return (
    <Box p={2}>
      <ErrorMessage message={message} errorId={errorId} />
    </Box>
  );
};
