export type TextToSpeechAudio = TextToSpeechAudioContext | TextToSpeechAudioElement;

const TTS_URI = `https://texttospeech.googleapis.com/v1/text:synthesize?key=${Config.GOOGLE_TEXT_TO_SPEECH_KEY}`;
const SAMPLE_RATES = 16000;

enum TTSService {
  Google = 'google',
  Azure = 'azure',
}

export type VoiceNumber = 0 | 1;

type VoiceType = {
  language: Language;
  languageCode: string;
  name: [string, string];
  service: TTSService;
};

