const getVoice = (language: Language) => voices.find(languageVoice => languageVoice.language === language);

const unsupportedDisplayAlphabets = [LanguageId.JaRoman, LanguageId.KoRoman, LanguageId.ZhRoman];

export const isDisplayAlphabetSupported = (languageId: LanguageId) => !unsupportedDisplayAlphabets.includes(languageId);

const createRequestOptions = (
  text: string,
  voice: VoiceType | undefined,
  playbackRate: number,
  voiceNumber: VoiceNumber,
) => {
  if (!voice) {
    return null;
  }

  return {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      input: {
        text,
      },
      voice: {
        languageCode: voice.languageCode,
        name: voice.name[voiceNumber],
        ssmlGender: 'FEMALE',
      },
      audioConfig: {
        audioEncoding: 'MP3',
        sampleRateHertz: SAMPLE_RATES,
        speakingRate: playbackRate,
      },
    }),
  };
};

/**
 * gets the auth token from Azure
 * @returns auth token
 */
const azureAccessToken = async () => {
  const issueTokenEndpoint = 'https://westus2.api.cognitive.microsoft.com/sts/v1.0/issuetoken';

  const options = {
    method: 'POST',
    headers: {
      'Ocp-Apim-Subscription-Key': Config.AZURE_TEXT_TO_SPEECH_KEY,
      'Content-Length': '0',
      'Content-type': 'application/x-www-form-urlencoded',
    },
  };

  const response = await fetch(issueTokenEndpoint, options);
  return response.text();
};

/**
 * requests TTS from Azure
 *
 * https://docs.microsoft.com/en-us/azure/cognitive-services/speech-service/rest-text-to-speech
 * @param accessToken jwt token
 * @param text in-language copy
 * @param language determines what voice to use
 * @returns audio base64 content
 */
const getAzureTextToSpeech = async (
  accessToken: string,
  text: string | Array<string>,
  voice: VoiceType,
  voiceNumber: VoiceNumber = 0,
  playbackRate: number = 1,
) => {
  // Create the SSML request.
  const body = `<speak version="1.0" xml:lang="${voice.languageCode}"><voice xml:lang="${voice.languageCode}" name="${
    voice.name[voiceNumber]
  }"><prosody rate="${playbackRate}" pitch="0%">${Array.isArray(text) ? text[0] : text}</prosody></voice></speak>`;

  const audioOutputFormat = 'audio-16khz-32kbitrate-mono-mp3';
  const applicationName = 'Text-To-Speech-Toucan';
  const voiceEndpoint = 'https://westus2.tts.speech.microsoft.com/cognitiveservices/v1';

  const options = {
    method: 'POST',
    headers: {
      Authorization: `Bearer ${accessToken}`,
      'Content-Type': 'application/ssml+xml',
      'X-Microsoft-OutputFormat': audioOutputFormat,
      'User-Agent': applicationName,
    },
    body,
  };

  const response = await fetch(voiceEndpoint, options);
  const audioBlob = await response.blob();
  return new Promise((resolve: (value: Uint8Array) => void, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(audioBlob);
    reader.onload = () => {
      const base64data = reader.result;
      // @ts-ignore - split does not like that base64data could be a string or ArrayBuffer
      // per MDN to retrieve only the Base64 encoded string, first remove data:*/*;base64, from the result https://developer.mozilla.org/en-US/docs/Web/API/FileReader/readAsDataURL
      const audioContent = base64data?.split(/(?=data:)(.*)(?:;base64,)/)[2];
      resolve(audioContent);
    };
    reader.onerror = () => {
      reject(reader.error);
      reader.abort();
    };
  });
};

/**
 * textToSpeech creates a HTMLAudioElement like interface to be consumed by the design libraries player
 * @param text the text to convert to speech
 * @param language the language of the text to be converted
 * @returns audio base64 content
 */
export const textToSpeech = async (
  text: string,
  language: Language,
  playbackRate: number = 1,
  voiceNumber: VoiceNumber = 0,
) => {
  const voice = getVoice(language);

  if (voice?.service === TTSService.Azure) {
    const accessToken = await azureAccessToken();
    const azureTTS = await getAzureTextToSpeech(accessToken, text, voice, voiceNumber, playbackRate);
    return azureTTS;
  }

  const options = createRequestOptions(text, voice, playbackRate, voiceNumber);
  if (!options) {
    throw new Error(`Unable to fetch audio file for "${text}" in "${language}"`);
  }
  const resp = await fetch(TTS_URI, options);

  if (!resp.ok) {
    throw new Error(`${resp.status} = ${resp.statusText}`);
  }

  const json = await resp.json();
  const { audioContent } = json;

  return audioContent;
};
