import { isBoolean } from 'lodash';
import moment from 'moment';
import browser, { StorageObject, TypedStorageObject } from 'webextension-polyfill';

import { StorageKey } from '~/constants/storage';
import { StorageQueryCache } from '~/types/storage';

interface GetItemArgs {
  storageKey: string;
}

// Helper functions to handle getting variables out of local storage
const getItem = async <T extends StorageObject>({ storageKey }: GetItemArgs) => {
  const result = (await browser.storage.local.get(`${storageKey}`)) as T;
  return (result?.[storageKey] ?? null) as T[string] | null;
};

interface GetCacheOptions {
  staleAfter?: [number, 'minute' | 'day' | 'hour' | 'second'];
}

const getCache = async <T extends {}>(storageKey: StorageKey, options: GetCacheOptions = {}) => {
  const result = (await browser.storage.local.get(`${storageKey}`)) as StorageQueryCache<T>;
  if (!result || !result[storageKey]) {
    return null;
  }
  const storage = result[storageKey];
  const { createdAt } = storage;
  const { data } = storage;
  if (
    options.staleAfter &&
    moment(createdAt)
      .add(...options.staleAfter)
      .isBefore(moment())
  ) {
    return null;
  }
  return data;
};

async function getInlineChallenges() {
  const result = await browser.storage.local.get('inlineTranslationChallenges');
  return result.inlineTranslationChallenges;
}

async function getLastFetchedPackageDate() {
  const result = await browser.storage.local.get('lastFetchedPackageDate');
  const date = result.lastFetchedPackageDate;
  return date;
}

async function getIsEnabled() {
  try {
    const result = await browser.storage.local.get('isEnabled');
    return result.isEnabled;
  } catch (err) {
    return false;
  }
}

async function getIsFreshInstall() {
  try {
    const result = await browser.storage.local.get('isFreshInstall');
    return result.isFreshInstall == null;
  } catch (err) {
    return true;
  }
}

async function getDefaultBlacklistedDomains() {
  try {
    const result = (await browser.storage.local.get(StorageKey.DefaultBanlistedDomains)) as TypedStorageObject<
      string[]
    >;
    return result?.defaultBlacklistedDomains ?? [];
  } catch (err) {
    return [];
  }
}

async function getBlacklistedDomains() {
  try {
    const result = (await browser.storage.local.get(StorageKey.BanlistedDomains)) as TypedStorageObject<string[]>;
    return result?.blacklistedDomains ?? [];
  } catch (err) {
    return [];
  }
}

const getGlobalEnableState = async () => {
  try {
    const result = await browser.storage.local.get(StorageKey.GlobalEnableState);
    // not a boolean most likely a new user
    if (!isBoolean(result.globalEnableState)) {
      return true;
    }
    return result.globalEnableState;
  } catch (err) {
    return false;
  }
};

