  getCache,
  getInlineChallenges,
  getLastFetchedPackageDate,
  getIsEnabled,
  getIsFreshInstall,
  getDefaultBlacklistedDomains,
  getBlacklistedDomains,
  getGlobalEnableState,
  getHasIKnowThisMessageDisplayed,
  getAuthToken,
  getRefreshToken,
  getHoverForMoreChallenges,
  getIsDarkModeEnabled,
  getOnboardingEvents,
  getHasSeenHighlightsOnboarding,
  getPermissionsOnboardingLastSeen,
  getTurnExtensionOnDate,
