/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';
import { DisplayType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';
import { PanelName } from '~/types/sidebar';

const { Typography, useColorScheme, Button } = ToucanComponents;

interface PrimaryButtonProps {
  errorId?: string;
  isShortcutSelected: boolean;
  startExtraPractice: () => void;
  buttonCTA: string;
  status: QuizStatus;
  submitQuestion: (result: QuizStatus.Pass | QuizStatus.Fail) => void;
  displayType: DisplayType;
  currentDomain: string;
  title: string;
}

export const PrimaryButton = ({
  errorId,
  isShortcutSelected,
  startExtraPractice,
  buttonCTA,
  status,
  submitQuestion,
  displayType,
  currentDomain,
  title,
}: PrimaryButtonProps) => {
  const { textLevel3 } = useColorScheme();
  const { t } = useTranslation();
  const { openSidebar } = useSidebar();
  const isButtonHidden =
    displayType === DisplayType.Question || [QuizStatus.Finished, QuizStatus.Fail].includes(status);

  const onButtonClick = () => {
    if (isShortcutSelected) {
      if (status === QuizStatus.Pending || !status) {
        startExtraPractice();

        ContentMessengers.trackEvent({
          eventName: 'Extra Practice Session Started',
          properties: {
            domain: currentDomain,
            activeShortcut: title,
          },
        });
      } else if (status === QuizStatus.InProgress) {
        submitQuestion(QuizStatus.Pass);

        ContentMessengers.trackEvent({
          eventName: 'Extra Practice Step Completed',
          properties: {
            domain: currentDomain,
            activeShortcut: title,
            displayType,
          },
        });
      }
    } else {
      openSidebar({
        panelName: PanelName.Progress,
      });
    }
  };

  return (
    <Box width="100%" p={2}>
      {(() => {
        if (errorId) {
          return (
            <Typography
              variant="2xs"
              css={css`
                color: ${textLevel3};
              `}
            >
              {t('errorId', { id: errorId ?? '' })}
            </Typography>
          );
        }
        if (isButtonHidden) {
          return null;
        }
        return (
          <Button type="utility" size="small" onClick={onButtonClick} fullWidth>
            {buttonCTA}
          </Button>
        );
      })()}
    </Box>
  );
};
