/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { GraphQLFormattedError } from 'graphql';

import Config from '~/configs/config';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';

import { LearningBlurb } from './LearningBlurb';

const { Typography, Link } = ToucanComponents;
interface ErrorProps {
  error: GraphQLFormattedError | Error;
}

export const Error: FC<ErrorProps> = ({ error }) => {
  const { t } = useTranslation();

  const errorId = ('extensions' in error && error.extensions?.errorId) ?? error.message;

  return (
    <React.Fragment>
      <LearningBlurb blurbColor={ToucanColors.red[100]}>
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${ToucanColors.red[500]};
          `}
        >
          <Trans i18nKey="somethingWentWrong" components={[<Link href={`${Config.siteURL}/contact`}>a</Link>]} />
        </Typography>
        <Typography
          variant="xs"
          css={css`
            color: ${ToucanColors.red[500]};
            word-break: break-all;
          `}
        >
          {t('quizErrorId', { id: errorId })}
        </Typography>
      </LearningBlurb>
    </React.Fragment>
  );
};
