import { GraphQLFormattedError } from 'graphql';
import { atom } from 'jotai';

import { TranslateSelectionMutation } from '~/schema/background';

interface HTTSidebarState {
  preText?: string;
  postText?: string;
  sourcePhrase?: string;
  pendingTranslateSelectionResp?: Promise<{
    data?: TranslateSelectionMutation | null;
    errors?: GraphQLFormattedError[];
  }>;
}

export const HighlightToTranslateSidebarAtom = atom<HTTSidebarState>({});
export const SetHighlightToTranslateSidebarAtom = atom<unknown, Partial<HTTSidebarState>>(null, (get, set, state) => {
  const existingState = get(HighlightToTranslateSidebarAtom);
  set(HighlightToTranslateSidebarAtom, {
    ...existingState,
    ...state,
  });
});
