/** @jsx jsx */
import React, { useState, useRef, useEffect, FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Events } from '~/constants/events';
import { Language } from '~/constants/language';
import { useCurrentDomain } from '~/hooks/useCurrentDomain';
import { useSupportsAudio } from '~/hooks/useSupportAudio';
import { useTextToSpeech } from '~/hooks/useTextToSpeech';
import { useUserInfo } from '~/hooks/useUserInfo';
import { TextToSpeechAudio, VoiceNumber } from '~/libs/TextToSpeech';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

const { Button, ToucanIcon, Tooltip, useColorScheme } = ToucanComponents;

interface AudioButtonProps {
  targetPhrase: string;
  language: Language;
  isStacked?: boolean;
  playbackRate?: number;
  voiceNumber?: VoiceNumber;
  marginLeft?: number;
  marginRight?: number;
  onPlay?: () => void;
  showPause?: boolean;
  onTapHold?: () => void;
  isDisabled?: boolean;
  disabledMessage?: string;
}

export const AudioPlayer: FC<AudioButtonProps> = ({
  targetPhrase,
  children,
  language,
  isStacked,
  playbackRate: passedPlaybackRate,
  voiceNumber: passedVoiceNumber,
  marginLeft = 0,
  marginRight = 0,
  onPlay,
  showPause = false,
  onTapHold,
  isDisabled = false,
  disabledMessage,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const supportsAudio = useSupportsAudio();
  const { fetchTextToSpeech } = useTextToSpeech();
  const { playbackRate: userPlaybackRate, voiceNumber: userVoiceNumber } = useUserInfo();
  const mounted = useRef(true);
  const [audioFile, setAudioFile] = useState<TextToSpeechAudio>();
  const [isPlaying, setIsPlaying] = useState(false);
  const playbackRate = passedPlaybackRate ?? userPlaybackRate;
  const voiceNumber = passedVoiceNumber ?? userVoiceNumber;
  const timer = useRef<number>(0);
  const { textLevel4 } = useColorScheme();
  const { currentDomain } = useCurrentDomain();

  const isFullyFunctional = supportsAudio && !isDisabled;

  // If the component is unmounted, stop playing the audio file
  useEffect(
    () => () => {
      if (audioFile) {
        audioFile.pause();
      }
    },
    [audioFile],
  );

  const playAudioFile = (audioFileToPlay: TextToSpeechAudio) => {
    if (audioFileToPlay) {
      audioFileToPlay.play();

      if (!mounted.current) {
        return;
      }

      setIsPlaying(true);

      ContentMessengers.trackEvent({
        eventName: Events.LearningExperienceStarted,
        properties: {
          exercise: Events.AudioPronunciationClicked,
          url: currentDomain,
        },
        userOptions: { includePremiumSubscriptionStatus: true },
      });

      // eslint-disable-next-line no-param-reassign
      audioFileToPlay.onended = () => {
        if (!mounted.current) {
          return;
        }

        setIsPlaying(false);

        ContentMessengers.trackEvent({
          eventName: Events.LearningExperienceCompleted,
          properties: {
            exercise: Events.AudioPronunciationFinished,
            url: currentDomain,
          },
          userOptions: { includePremiumSubscriptionStatus: true },
        });
      };
    }
  };

  const playSound = async () => {
    if (!isPlaying && !audioFile && mounted.current) {
      const resp = await fetchTextToSpeech({
        targetPhrase,
        language,
        playbackRate,
        voiceNumber,
      });
      if (!mounted.current || !resp) {
        return;
      }
      setAudioFile(resp);
      playAudioFile(resp);
    } else if (!isPlaying && audioFile) {
      playAudioFile(audioFile);
    }
  };

  const iconContent = isFullyFunctional ? (
    <ToucanIcon
      css={css`
        color: inherit;
        margin-left: ${theme.spacing(marginLeft)};
        margin-right: ${theme.spacing(marginRight)};
      `}
      icon={isPlaying && showPause ? 'pause' : 'volume-up'}
    />
  ) : (
    <ToucanIcon
      css={css`
        color: inherit;
        cursor: default;
        margin-left: ${theme.spacing(marginLeft)};
        margin-right: ${theme.spacing(marginRight)};
      `}
      icon="volume-slash"
    />
  );

  return (
    <Button
      type="text"
      onClick={e => {
        e.stopPropagation();
        if (isDisabled) {
          return;
        }
        window.clearTimeout(timer.current);
        if (supportsAudio && !isPlaying && !audioFile) {
          playSound();
          onPlay?.();
        } else if (audioFile && !isPlaying) {
          audioFile.play();
          setIsPlaying(true);
        } else if (audioFile && isPlaying) {
          audioFile.pause();
          setIsPlaying(false);
        }
      }}
      onMouseDown={e => {
        e.stopPropagation();
        if (isDisabled) {
          return;
        }
        window.clearTimeout(timer.current);
        timer.current = window.setTimeout(() => {
          onTapHold?.();
        }, 500);
      }}
      onMouseUp={e => {
        e.stopPropagation();
        if (isDisabled) {
          return;
        }
        window.clearTimeout(timer.current);
      }}
      css={css`
        display: inline-flex;
        & > span {
          display: inline-flex;
        }
        &:hover {
          cursor: ${isFullyFunctional ? 'pointer' : 'default'};
          text-decoration: ${isFullyFunctional ? 'underline' : 'none'};
        }
        flex-direction: ${isStacked ? 'column' : 'row'};
        ${!isFullyFunctional &&
        `
            color: ${textLevel4};
            &:hover {
              color: ${textLevel4};
            }
          `}
      `}
    >
      {!supportsAudio || (isDisabled && disabledMessage) ? (
        <Tooltip title={(isDisabled && disabledMessage) || t('toucanIsUnableToPlayAudio')}>{iconContent}</Tooltip>
      ) : (
        iconContent
      )}
      {children}
    </Button>
  );
};
