import React, { FC, useMemo, useEffect, useState } from 'react';

import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { AnimatePresence, motion } from 'framer-motion';
import { capitalize } from 'lodash';
import { useDeepCompareMemoize } from 'use-deep-compare-effect';

import { Box, BoxProps } from '~/components/Box';
import { Listen } from '~/components/InlineTranslations/InlineQuiz/Listen';
import { Swap } from '~/components/InlineTranslations/InlineQuiz/Swap';
import { QuestionBaseProps } from '~/components/InlineTranslations/InlineQuiz/types';
import { QuizSize, QuizSizeProvider } from '~/components/InlineTranslations/InlineQuiz/useQuizSize';
import { HorizontalLine } from '~/components/shared/HorizontalLine';
import { Slide } from '~/components/shared/Slide';
import { SlideDirection, Slides } from '~/components/shared/Slides';
import { QuizContext } from '~/constants/quiz';
import { filterNull } from '~/helpers/utilityHelpers';
import { CurrentQuiz, QuestionsType } from '~/hooks/useQuiz/types';
import { useTranslation } from '~/libs/i18n';
import { PracticeType, Practice, DisplayType, QuizType, Homonym } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { Badge } from './Badge';
import { Confirmation } from './Confirmation';
import { Error } from './Error';
import { Example } from './Example';
import { Finish } from './Finish';
import { Footer } from './Footer';
import { Header } from './Header';
import { IntroShortcutNotSelected } from './IntroShortcutNotSelected';
import { IntroShortcutSelected } from './IntroShortcutSelected';
import { PrimaryButton } from './PrimaryButton';

const { useColorScheme } = ToucanComponents;

interface ExtraPracticeProps extends BoxProps {
  title: string;
  topicName: string;
  badgeImage: { badgeImageUrl: string; badgeImageAltText: string };
  timeEstimation: number;
  errorId: string | undefined;
  isShortcutSelected: boolean;
  currentQuiz: CurrentQuiz;
  practice: Practice[];
  retryQuiz: () => void;
  submitQuestion: (result: QuizStatus.Pass | QuizStatus.Fail) => void;
  homonym: Homonym;
  showInlineQuizPremiumUpsell?: boolean;
  startQuiz: ({
    questions,
    translationIds,
    quizType,
  }: {
    questions: QuestionsType[];
    translationIds: Array<string>;
    quizType: QuizType;
  }) => void;
  questionNumber: number;
  onResetExtraPractice: () => void;
  currentDomain: string;
  setIsConfettiShown: (value: boolean) => void;
  isConfettiShown: boolean;
}

export const QuestionComponentMap: Record<PracticeType, FC<QuestionBaseProps>> = {
  [PracticeType.SwapPracticeQuestion]: Swap,
  [PracticeType.ListenPracticeQuestion]: Listen,
  [PracticeType.SourcePhraseSwapPracticeQuestion]: Swap,
  [PracticeType.NonLatinSpellingPracticeQuestion]: () => null,
  [PracticeType.LatinSpellingPracticeQuestion]: () => null,
  [PracticeType.MatchPracticeQuestion]: () => null,
};

interface MakeQuestionKeyProps {
  question: QuestionsType;
  id: number;
}

const makeQuestionKey = ({ question, id }: MakeQuestionKeyProps) => `${id}:${question.displayType}`;

export const ExtraPractice = ({
  title,
  topicName,
  badgeImage,
  timeEstimation,
  errorId,
  isShortcutSelected,
  currentQuiz,
  practice,
  retryQuiz,
  submitQuestion,
  homonym,
  showInlineQuizPremiumUpsell = false,
  startQuiz,
  questionNumber,
  onResetExtraPractice,
  currentDomain,
  setIsConfettiShown,
  isConfettiShown,
  ...boxProps
}: ExtraPracticeProps) => {
  const { t } = useTranslation();
  const { isDarkMode, uiSecondary, textError, backgroundBase, backgroundAccent } = useColorScheme();
  const { status, question, results } = currentQuiz;
  const { targetPhrase: homonymTargetPhrase, homonymId } = homonym;
  const [buttonCTA, setButtonCTA] = useState(t('practice'));
  const [currentTitle, setCurrentTitle] = useState(title);
  const [currentTopicName, setCurrentTopicName] = useState<string | undefined>(topicName);
  const examplePlusQuestion: QuestionsType[] = practice.map(item => ({
    questionType: item.questionType ?? PracticeType.SwapPracticeQuestion,
    displayPhrase: item.displayPhrase ?? '',
    answer: item.answer ?? '',
    options: item.options ?? [],
    translationId: '',
    displayType: item.displayType,
    sentence: item.sentence ?? null,
  }));

  const memoizeQuestions = useMemo(
    () => (examplePlusQuestion ?? []).filter(filterNull),
    useDeepCompareMemoize([examplePlusQuestion ?? []]),
  );

  const startExtraPractice = () => {
    startQuiz({
      questions: memoizeQuestions,
      translationIds: [homonymId],
      quizType: QuizType.Practice,
    });
  };

  useEffect(() => {
    if (status === QuizStatus.Pending) {
      if (isShortcutSelected) {
        setButtonCTA(t('practice'));
      } else {
        setButtonCTA(t('takeShortcut'));
      }
    } else if (status === QuizStatus.InProgress) {
      setButtonCTA(t('keepGoingNoPunctuation'));
    } else if (status === QuizStatus.Finished) {
      setButtonCTA(t('keepPracticing'));
    }
  }, [question, status, isShortcutSelected]);

  useEffect(() => {
    if (status === QuizStatus.Pending || status === QuizStatus.Finished) {
      setCurrentTitle(title);
      setCurrentTopicName(topicName);
    } else if (status === QuizStatus.InProgress) {
      if (question?.displayType === DisplayType.Example) {
        setCurrentTitle(t('readThisSentence'));
        setCurrentTopicName(undefined);
      } else if (question?.displayType === DisplayType.Question) {
        setCurrentTitle(t('chooseTheBestOption'));
        setCurrentTopicName(undefined);
      } else if (question?.displayType === DisplayType.Confirm) {
        setCurrentTitle(t('didYouGetItRight'));
        setCurrentTopicName(undefined);
      }
    }
  }, [status, question]);

  return (
    <Box display="flex" flexDirection="column" alignItems="center" data-testid="extra-practice-intro">
      <Badge badgeImage={badgeImage} status={status} />
      <Box
        width="100%"
        border={`1px solid ${errorId ? ToucanColors.red[200] : ToucanColors.gray[isDarkMode ? 500 : 200]}`}
        mt="-24px"
        borderRadius="8px"
        overflow="hidden"
        bgcolor={isDarkMode ? backgroundAccent : backgroundBase}
      >
        <Box height="6px" width="100%" bgcolor={errorId ? textError : uiSecondary} />
        {isShortcutSelected && (
          <>
            <Box mt={3} mx={4} mb={2}>
              <Header title={currentTitle} topicName={currentTopicName} />
            </Box>
            <HorizontalLine color={isDarkMode ? ToucanColors.gray[500] : ToucanColors.gray[200]} />
          </>
        )}
        <Box
          data-testid="extra-practice-body"
          px={4}
          py={isShortcutSelected ? 2 : 5}
          sx={{
            backgroundImage: isConfettiShown
              ? `url(${browser.runtime.getURL(`/images/learningShortcuts/extraPractice/funfetti.svg`)})`
              : undefined,
          }}
        >
          <Box display="flex" flexDirection="column" alignItems="center">
            {(() => {
              if (errorId) {
                return <Error />;
              }
              if (isShortcutSelected) {
                if (status === QuizStatus.Pending) {
                  // ensure the phrase is capitalized on the introductory slide
                  return (
                    <IntroShortcutSelected
                      homonymTargetPhrase={homonymTargetPhrase ? capitalize(homonymTargetPhrase) : ''}
                      timeEstimation={timeEstimation}
                    />
                  );
                }
                return (
                  <QuizSizeProvider value={QuizSize.Medium}>
                    <Box
                      display="flex"
                      justifyContent="space-between"
                      height={showInlineQuizPremiumUpsell ? 240 : 232}
                      flexDirection="column"
                      width="100%"
                      {...boxProps}
                    >
                      <Slides
                        activeSlideName={
                          question && status === QuizStatus.InProgress
                            ? makeQuestionKey({
                                question,
                                id: practice.map(item => item.sentence).indexOf(question?.sentence),
                              })
                            : status
                        }
                        activeSlideDirection={SlideDirection.Forward}
                      >
                        {status === QuizStatus.InProgress &&
                          examplePlusQuestion.map(thisQuestion => {
                            const questionKey = makeQuestionKey({
                              question: thisQuestion,
                              id: practice.map(item => item.sentence).indexOf(thisQuestion?.sentence),
                            });
                            const QuestionComponent =
                              QuestionComponentMap[thisQuestion.questionType ?? PracticeType.SwapPracticeQuestion];
                            return (
                              <Slide slideName={questionKey} key={questionKey}>
                                {thisQuestion.displayType === DisplayType.Question && (
                                  <QuestionComponent
                                    question={thisQuestion}
                                    retryQuiz={retryQuiz}
                                    submitQuestion={submitQuestion}
                                    quizType={QuizType.Practice}
                                    quizContext={QuizContext.ExtraPractice}
                                    onResetExtraPractice={onResetExtraPractice}
                                  />
                                )}
                                {thisQuestion.displayType === DisplayType.Example && (
                                  <Example answer={question?.answer ?? ''} sentence={question?.sentence ?? []} />
                                )}
                                {thisQuestion.displayType === DisplayType.Confirm && (
                                  <Confirmation
                                    answer={question?.answer ?? ''}
                                    displayPhrase={question?.displayPhrase ?? ''}
                                  />
                                )}
                              </Slide>
                            );
                          })}
                        {(status === QuizStatus.Finished || status === QuizStatus.Fail) && (
                          <Slide slideName={status}>
                            <Finish
                              progressionScore={homonym.progressionScore}
                              results={results}
                              homonymId={homonymId}
                              setIsConfettiShown={setIsConfettiShown}
                              currentDomain={currentDomain}
                              title={title}
                            />
                          </Slide>
                        )}
                      </Slides>
                    </Box>
                  </QuizSizeProvider>
                );
              }
              return <IntroShortcutNotSelected />;
            })()}
          </Box>
        </Box>
        {status !== QuizStatus.Finished && (
          <PrimaryButton
            errorId={errorId}
            isShortcutSelected={isShortcutSelected}
            startExtraPractice={startExtraPractice}
            buttonCTA={buttonCTA}
            status={status}
            submitQuestion={submitQuestion}
            displayType={question?.displayType ?? DisplayType.Example}
            currentDomain={currentDomain}
            title={title}
          />
        )}
        <AnimatePresence>
          {[QuizStatus.InProgress, QuizStatus.Finished, QuizStatus.Fail].includes(status) && (
            <motion.div initial={{ scaleY: 0 }} animate={{ scaleY: 1 }} exit={{ scaleY: 0 }}>
              <Footer
                buttonCTA={buttonCTA}
                status={status}
                onResetExtraPractice={onResetExtraPractice}
                totalSteps={practice.length ?? 0}
                questionNumber={questionNumber}
                setIsConfettiShown={setIsConfettiShown}
              />
            </motion.div>
          )}
        </AnimatePresence>
      </Box>
    </Box>
  );
};
