export const delay = (milliseconds: number) => new Promise(resolve => setTimeout(resolve, milliseconds));

export const uniqueKeyComparisonIgnoreCase = (subject: string, comparator: string) =>
  subject.toLowerCase() === comparator.toLowerCase();

export const filterNull = <T>(v: T | null | undefined): v is T => !!v;

export const replaceKeyboardCharacters = (str: string) => {
  const keyMap = {
    '': 'Shift',
    '': 'Cmd',
    '': 'Ctrl',
    '': 'Alt', // Opt (Mac)
  };

  type KeyMapType = keyof typeof keyMap;

  const regex = new RegExp(Object.keys(keyMap).join('|'), 'gi');

  return str.replace(regex, matched => `${keyMap[matched as KeyMapType]} + `);
};

/**
 * This is to fallback from requestIdleCallback to requestAnimationFrame
 * This is because requestIdleCallback is not supported in Safari
 */
const requestIdleHelper =
  'requestIdleCallback' in window && typeof window.requestIdleCallback === 'function'
    ? window.requestIdleCallback.bind(window)
    : window.requestAnimationFrame.bind(window);

/**
 * This is to fallback from requestIdleCallback to requestAnimationFrame
 * This is because requestIdleCallback is not supported in Safari
 */
const cancelIdleHelper =
  'cancelIdleCallback' in window && typeof window.cancelIdleCallback === 'function'
    ? window.cancelIdleCallback.bind(window)
    : window.cancelAnimationFrame.bind(window);

/**
 * requestIdle is a little helper that makes it easier to use requestIdle of the page, and be able to easily cancel it too.
 * @param callback the callback to run when the page is idle
 * @returns a function that will cancel the idle callback
 */
export const requestIdle = (callback: IdleRequestCallback & FrameRequestCallback) => {
  const frame = requestIdleHelper(callback);
  return () => {
    cancelIdleHelper(frame);
  };
};

/**
 * idleAsyncSeries is a helper that runs a series of async functions in order
 * but will request idle time before running the next function.
 *
 * A reject will stop the series.
 *
 * Only for client script since requestAnimation frame never fires in the background.
 *
 * @param {Array<() => Promise<any>>} tasks  an array of async functions to process.
 * @returns {object} an object that should allow you to await the result of the series, or cancel the series.
 */
export const idleAsyncSeries = <T>(
  tasks: Array<() => Promise<T>>,
): { results: () => Promise<Array<T>>; cancel: () => void } =>
  asyncSeries(
    tasks.map(task => () =>
      new Promise(resolve => {
        requestIdle(async () => {
          resolve(await task());
        });
      }),
    ),
  );

/**
 * asyncSeries is a helper that runs a series of async functions in order
 * passed.
 *
 * A reject will stop the series.
 *
 * @param {Array<() => Promise<any>>} tasks  an array of async functions to process.
 * @returns {object} an object that should allow you to await the result of the series, or cancel the series.
 */
export const asyncSeries = <T>(
  tasks: Array<() => Promise<T>>,
): { results: () => Promise<Array<T>>; cancel: () => void } => {
  let cancelled = false;
  const results: Array<T> = [];
  const isFinished = tasks.reduce<Promise<void>>(
    (promise, task) =>
      promise.then(
        () =>
          new Promise((resolve, reject) => {
            if (cancelled) {
              reject(new Error('Cancelled by user'));
              return;
            }
            task().then(result => {
              results.push(result);
              resolve();
            });
          }),
      ),
    Promise.resolve(),
  );
  return {
    results: async () => {
      await isFinished;
      return results;
    },
    cancel: () => {
      cancelled = true;
    },
  };
};

/**
 * hasProp is a helper that help check for properties on an object of an unknown type
 */
export const hasProp = <Key extends PropertyKey>(data: object, prop: Key): data is Record<Key, unknown> => prop in data;
