import React, { useEffect } from 'react';

import { useActiveTranslation } from '~/hooks/useActiveTranslation';
import { useTranslatedNodeTransitions } from '~/hooks/useTranslatedNodes';

import { useShadow } from '../ShadowProvider';

export const MobileTouchEventListeners: React.FC = () => {
  const { setIdle } = useTranslatedNodeTransitions();
  const currentActiveTranslation = useActiveTranslation();
  const { root } = useShadow();

  useEffect(() => {
    if (root) {
      const stopPropogatingOutsideContainer = (event: Event) => event.stopPropagation();
      root.addEventListener('touchstart', stopPropogatingOutsideContainer);
      return () => root.removeEventListener('touchstart', stopPropogatingOutsideContainer);
    }
    return () => {};
  }, [root]);

  useEffect(() => {
    const closeCardWhenTouchInDocument = () => {
      if (currentActiveTranslation) {
        setIdle(currentActiveTranslation);
      }
    };
    document.addEventListener('touchstart', closeCardWhenTouchInDocument);
    return () => document.removeEventListener('touchstart', closeCardWhenTouchInDocument);
  }, [currentActiveTranslation]);

  return null;
};
