import React, { ReactNode, useMemo, useState } from 'react';

import { ToucanColors } from '@jointoucan/toucan-design';
import { alpha } from '@mui/material';
import { AnimatePresence, motion } from 'framer-motion';

import { LottieAnimation } from '~/components/shared/LottieAnimation';

interface HighlightProps {
  onFocus?: () => void;
  onBlur?: () => void;
  onClick?: () => void;
  isLightText?: boolean;
  active: boolean;
  highlightColor?: string;
  underlineColor?: string;
  outerContent?: ReactNode;
  display?: string;
  children?: ReactNode;
  underline?: boolean;
  hideText?: boolean;
  isSparkleShown?: boolean;
  learningShortcutEmoji?: string;
  isActiveLearningShortcutPhrase?: boolean;
}

/**
 * Highlight component is the component that controls the styling of the highlight that is injected into the page.
 * couple of things to note:
 *
 * Styling is hit or miss if emotion or MUI styles are used. This is largely due to the fact we
 * mount the component before adding it to the page. This means that fast mechanisms to append styles in these
 * systems fail because the appended styles are not available until on the DOM.
 * That is the main reason why you see inline styles in this component.
 */
export const Highlight = React.forwardRef<HTMLSpanElement, HighlightProps>(
  (
    {
      children,
      onFocus,
      onBlur,
      onClick,
      highlightColor = ToucanColors.blue[100],
      underlineColor = ToucanColors.blue[200],
      isLightText,
      active,
      outerContent,
      display,
      underline = false,
      hideText = false,
      isSparkleShown,
      learningShortcutEmoji,
      isActiveLearningShortcutPhrase = false,
    },
    ref,
  ) => {
    const [hovered, setHovered] = useState(false);
    const onMouseEnter = () => {
      if (onFocus) {
        onFocus();
      }
      setHovered(true);
    };
    const onMouseLeave = () => {
      if (onBlur) {
        onBlur();
      }
      setHovered(false);
    };

    const colors = useMemo(
      () => ({
        bgcolor: alpha(highlightColor, isLightText ? 0.15 : 0.5),
        underlineColor: alpha(underlineColor, isLightText ? 0.3 : 0.7),
        hoverBgcolor: alpha(highlightColor, isLightText ? 0.3 : 1),
      }),
      [highlightColor, isLightText],
    );

    const bgColors = hovered
      ? {
          boxShadow: `2px 0 0 0 ${colors.hoverBgcolor}, -2px 0 0 0 ${colors.hoverBgcolor}`,
          backgroundColor: `${colors.hoverBgcolor}`,
        }
      : {
          boxShadow: `2px 0 0 0 ${colors.bgcolor}, -2px 0 0 0 ${colors.bgcolor}`,
          backgroundColor: `${colors.bgcolor}`,
        };

    return (
      <span
        style={
          active
            ? {
                display: display || 'inline-block',
                borderRadius: underline ? '2px 2px 0 0' : '2px',
                lineHeight: 'normal',
                position: 'relative',
                width: 'max-content',
                height: 'max-content',
                transition: 'all 250ms linear 0ms',
                ...(isActiveLearningShortcutPhrase && { top: '-1px' }),
                ...bgColors,
              }
            : {}
        }
      >
        {isSparkleShown && (
          <span
            style={{
              position: 'absolute',
              top: 0,
              right: 0,
              pointerEvents: 'none',
            }}
          >
            <LottieAnimation
              filePath="/animations/inlineTranslation/inlineSparkle.json"
              loop
              rendererSettings={{ preserveAspectRatio: 'xMidYMin slice' }}
              isStopped={false}
            />
          </span>
        )}
        {active ? (
          <span
            role="button"
            ref={ref}
            tabIndex={-1}
            onMouseDown={event => {
              event.preventDefault();
              onFocus?.();
            }}
            onClick={event => {
              event.stopPropagation();
              onClick?.();
            }}
            onFocus={onFocus}
            onKeyDown={onFocus}
            onMouseEnter={onMouseEnter}
            onMouseLeave={onMouseLeave}
            onBlur={onBlur}
            style={{
              cursor: 'pointer',
              zIndex: 2,
              position: 'relative',
              opacity: hideText ? '0' : '1',
              transition: 'all 250ms linear 0ms',
              pointerEvents: hideText ? 'none' : 'auto',
              display: 'inline-flex',
              alignItems: 'center',
            }}
          >
            {learningShortcutEmoji && (
              <>
                <span
                  aria-hidden="false"
                  aria-label="shortcut-emoji"
                  style={{
                    fontSize: '0.75em',
                  }}
                >
                  {learningShortcutEmoji}
                </span>
                &nbsp;
              </>
            )}
            {children}
          </span>
        ) : (
          children
        )}
        <AnimatePresence>
          {underline && active && (
            <motion.span
              initial={{ scaleX: 0 }}
              animate={{ scaleX: 1 }}
              exit={{ scaleX: 0 }}
              transition={{ duration: 0.25 }}
              style={{
                transformOrigin: 'left',
                position: 'absolute',
                bottom: '-2px',
                left: '-2px',
                right: '-2px',
                height: '2px',
                backgroundColor: `${colors.underlineColor}`,
              }}
            />
          )}
        </AnimatePresence>
        {outerContent}
      </span>
    );
  },
);
