import React, { createContext, useContext, useState, useCallback, FC, useRef, useEffect } from 'react';

import { v4 } from 'uuid';

import { isToucanSite } from '~/helpers/currentDomainHelpers';

import { Toast } from './Toast';
import { ToastBox } from './ToastBox';
import { ToastContext, ToastProps } from './types';

/*
  TODO: add the toast to the place where we increment the word count.
  if we add it there what we can do is store the value that the word is special ;)
  and that then fire the toast from there.
*/

const toastContext = createContext<ToastContext>({ toasts: [], showToast: () => {}, removeToast: () => {} });
const { Provider } = toastContext;
export const useToast = () => useContext(toastContext);

export const ToastProvider: FC = ({ children }) => {
  const isMounted = useRef<boolean>(true);
  const [toasts, setToasts] = useState<ToastContext['toasts']>([]);

  const removeToast = useCallback(
    (id: string) => {
      setToasts(currentToasts => {
        const index = currentToasts.findIndex(toast => toast.id === id);
        if (index !== -1) {
          return [...currentToasts.slice(0, index), ...currentToasts.slice(index + 1)];
        }
        return currentToasts;
      });
    },
    [toasts],
  );

  const showToast = useCallback(
    (incomingToast: ToastProps) => {
      // Do not show toast on Toucan sites
      if (isToucanSite()) {
        return;
      }

      const id = incomingToast.id ?? v4();

      setToasts(currentToasts => {
        const index = currentToasts.findIndex(toast => toast.id === id);
        if (index !== -1) {
          return currentToasts;
        }
        return [...currentToasts, { ...incomingToast, id }];
      });
      // Cleanup toasts in timeout
      setTimeout(() => {
        if (!isMounted) {
          return;
        }
        removeToast(id);
      }, incomingToast.timeout ?? 5000);
    },
    [toasts, removeToast],
  );

  useEffect(() => {
    isMounted.current = true;
    return () => {
      isMounted.current = false;
    };
  }, []);

  return (
    <Provider value={{ toasts, showToast, removeToast }}>
      {children}
      <ToastBox>
        {toasts.map(
          (
            { title, subtitle, body, image, onClick, onClose, id, className, subtitleColor, titleColor, isAnimation },
            i,
          ) => (
            <Toast
              yPosition={-(i * 96)} // 96 = 80 toast height + 16 bottom margin
              id={id}
              className={className}
              title={title}
              subtitle={subtitle}
              body={body}
              image={image}
              onClick={onClick}
              subtitleColor={subtitleColor}
              titleColor={titleColor}
              key={id}
              onAttemptClose={() => {
                removeToast(id);
                if (onClose) {
                  onClose();
                }
              }}
              isAnimation={isAnimation}
            />
          ),
        )}
      </ToastBox>
    </Provider>
  );
};
