/** @jsx jsx */
import React, { FC, useState, useEffect, useRef } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';
import uniqueId from 'lodash/uniqueId';

import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import { QuizStatus } from '~/types/quiz';

import { isSingleWordQuestion } from '../helpers';
import { QuestionBaseProps } from '../types';
import { useQuizSize } from '../useQuizSize';

import { AccentButton } from './AccentButton';
import { Form } from './Form';

const { useColorScheme, Typography, Button: ToucanButton, ToucanIcon } = ToucanComponents;

export const Spelling: FC<QuestionBaseProps> = ({ question, submitQuestion }) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();
  const [hasInputText, setHasInputText] = useState(false); // for enabling continue button
  const [isContinueClicked, setIsContinueClicked] = useState(false); // for the continue button
  const [entry, setEntry] = useState('');
  const [isSpelledCorrectly, setIsSpelledCorrectly] = useState(false);
  const [isNotKnow, setIsNotKnown] = useState(false);
  const maxCharacterLimit = 30;
  const { size, fontVariant, inputPadding, subFontSize } = useQuizSize();
  const ref = useRef<HTMLInputElement>(null);

  const surrenderColorMap = isDarkMode ? ToucanColors.white : ToucanColors.gray[500];
  const letterMatchColorMap = isDarkMode ? ToucanColors.white : ToucanColors.gray[500];
  const letterNoMatchColorMap = isDarkMode ? ToucanColors.red[200] : ToucanColors.red[400];

  useEffect(() => {
    if (entry.length > 0) {
      setHasInputText(true);
    }
  }, [entry]);

  if (!question || !targetLanguageInfo || !isSingleWordQuestion(question)) {
    return null;
  }
  const { answer, options, displayPhrase } = question;

  const onSubmit = (result: boolean) => {
    setTimeout(() => {
      submitQuestion(result ? QuizStatus.Pass : QuizStatus.Fail);
    }, 1000);
  };

  const onContinueClick = () => {
    setIsContinueClicked(true);
    const isSpeltCorrectly = entry.trim() === answer.trim();
    setIsSpelledCorrectly(isSpeltCorrectly);
    onSubmit(isSpeltCorrectly);
  };

  const onNotKnown = () => {
    setIsContinueClicked(true);
    setIsNotKnown(true);
    onSubmit(false);
  };

  return (
    <Box data-testid="spelling-question">
      <Typography
        variant={fontVariant}
        css={css`
          color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
          margin-bottom: ${theme.spacing(2)};
        `}
      >
        <Trans i18nKey="quizSpellTheWord" values={{ translation: displayPhrase }} components={[<strong>a</strong>]} />
      </Typography>
      {!isContinueClicked && (
        <React.Fragment>
          <Form
            setEntry={setEntry}
            language={targetLanguageInfo.language}
            entry={entry}
            onSubmit={onContinueClick}
            ref={ref}
          />
          <Box display="flex" justifyContent="space-between" mb={2} overflow="hidden">
            {options.map((option: string, idx: number) => (
              <AccentButton
                key={option}
                onClick={() => {
                  if (entry.split('').length < maxCharacterLimit) {
                    setEntry(prevEntry => prevEntry + option);
                    ref.current?.focus();
                  }
                }}
                index={idx}
                option={option}
              />
            ))}
          </Box>
          <Box display="flex" flexDirection="column" justifyContent="center">
            <ToucanButton
              data-testid="continue-button"
              size={size}
              type="utility"
              isDisabled={!hasInputText}
              onClick={onContinueClick}
              css={css`
                width: 100%;
                margin-bottom: ${theme.spacing(inputPadding + 0.5)};
              `}
            >
              {t('quizContinue')}
            </ToucanButton>
            <ToucanButton
              size={size}
              type="text"
              onClick={onNotKnown}
              css={css`
                font-size: ${subFontSize};
                line-height: 1em;
              `}
            >
              {t('quizIDontKnow')}
            </ToucanButton>
          </Box>
        </React.Fragment>
      )}
      {isContinueClicked && (
        <React.Fragment>
          {!isSpelledCorrectly && !isNotKnow && (
            <Box
              width="100%"
              p={inputPadding}
              display="flex"
              alignItems="center"
              mb={1}
              overflow="hidden"
              css={css`
                border-radius: 4px;
                background-color: ${ToucanColors.gray[isDarkMode ? 400 : 200]};
              `}
            >
              {entry.split('').map((letter, idx) => {
                const splitAnswer = answer.split('');
                const isMatch = splitAnswer[idx] === letter;
                return (
                  <span key={uniqueId(`${letter}_`)}>
                    <Typography
                      variant={fontVariant}
                      css={css`
                        color: ${isMatch ? letterMatchColorMap : letterNoMatchColorMap};
                      `}
                    >
                      {letter}
                    </Typography>
                  </span>
                );
              })}
            </Box>
          )}
          <Box
            width="100%"
            p={inputPadding}
            display="flex"
            alignItems="center"
            justifyContent="space-between"
            css={css`
              border-radius: 4px;
              background-color: ${isNotKnow ? ToucanColors.gray[isDarkMode ? 400 : 200] : ToucanColors.green[300]};
            `}
          >
            <Typography
              variant={fontVariant}
              css={css`
                color: ${isNotKnow ? surrenderColorMap : ToucanColors.white};
              `}
            >
              {answer}
            </Typography>
            {isSpelledCorrectly && (
              <ToucanIcon
                data-testid="checkmark"
                css={css`
                  color: ${ToucanColors.white};
                  margin-right: ${theme.spacing(0.5)};
                  width: 16px;
                  height: 16px;
                `}
                icon="check"
              />
            )}
          </Box>
        </React.Fragment>
      )}
    </Box>
  );
};
