import { useMemo } from 'react';

import { useAtom } from 'jotai';

import { useBuiltTranslatedNodes } from '../useTranslatedNodes';

import { activeTranslationAtom, setActiveTranslationAtom } from './atoms';

export const useActiveTranslationId = () => useAtom(activeTranslationAtom)[0];
export const useSetActiveTranslation = () => useAtom(setActiveTranslationAtom)[1];

export const useActiveTranslation = () => {
  const translatedNodes = useBuiltTranslatedNodes();
  const activeTranslationId = useActiveTranslationId();
  return useMemo(() => {
    if (!activeTranslationId) {
      return null;
    }
    return translatedNodes.find(node => node.id === activeTranslationId) ?? null;
  }, [translatedNodes, activeTranslationId]);
};
