/** @jsx jsx */
import React, { FC, isValidElement, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, BoxProps, useTheme } from '@mui/material';
import { AnimatePresence, motion } from 'framer-motion';
import { omit } from 'lodash';

import { maximumZIndex } from '~/constants/styling';

const { Card } = ToucanComponents;

enum ModalSegment {
  Header,
  Content,
}

interface ModalProps {
  isOpen: boolean;
  className?: string;
  onClose?: () => void;
  onRequestClose: () => void;
  leftOverride?: string;
  shouldSkipAnimation?: boolean;
}

interface ModalSegmentProps extends BoxProps {
  // eslint-disable-next-line react/no-unused-prop-types
  name?: ModalSegment;
  children?: React.ReactNode;
}

export const ModalHeader: FC<ModalSegmentProps> = ({ children }) => <React.Fragment>{children}</React.Fragment>;
export const ModalContent: FC<ModalSegmentProps> = ({ children }) => <React.Fragment>{children}</React.Fragment>;

ModalHeader.defaultProps = { name: ModalSegment.Header };
ModalContent.defaultProps = { name: ModalSegment.Content };

const isValidReactElement = (child: any): child is React.ReactElement<ModalSegmentProps> => isValidElement(child);

export const Modal: FC<ModalProps> = ({
  children,
  className,
  isOpen,
  onClose,
  onRequestClose,
  leftOverride,
  shouldSkipAnimation,
}) => {
  const theme = useTheme();
  const childArray = React.Children.toArray(children).filter(isValidReactElement);
  const header = childArray.find(child => child.props.name === ModalHeader.defaultProps?.name);
  const headerBoxProps = omit(header?.props ?? {}, 'name', 'children');
  const content = childArray.find(child => child.props.name === ModalContent.defaultProps?.name);
  const contentBoxProps = omit(content?.props ?? {}, 'name', 'children');

  const stopPropagationProps = {
    onClick: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      // Prevent outer handler taking events;
      event.stopPropagation();
    },
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.removeProperty('overflow');
    }
  }, [isOpen]);

  const cardContainer = (
    <Box
      tabIndex={-1}
      onClick={onRequestClose}
      onKeyDown={event => {
        if (event.nativeEvent.key === 'Escape') {
          onRequestClose();
        }
      }}
      role="button"
      ref={(element: HTMLElement | null) => {
        if (element) {
          element.focus();
        }
      }}
      position="fixed"
      top={0}
      left={leftOverride ?? 0}
      width="100vw"
      height="100vh"
      display="flex"
      alignItems="center"
      justifyContent="center"
      bgcolor="rgba(0, 0, 0, 0.5)"
      zIndex={maximumZIndex}
    >
      <Card
        aria-hidden="false"
        role="dialog"
        autoFocus
        css={css`
          max-width: 446px;
          padding: 0;
          ${theme.breakpoints.down('sm')} {
            height: 100%;
            border-radius: 0;
            width: 100%;
            max-width: 100%;
          }
        `}
        className={className}
      >
        {header?.props.children ? (
          <Box width="100%" {...stopPropagationProps} {...headerBoxProps}>
            {header?.props.children}
          </Box>
        ) : null}
        {content?.props.children ? (
          <Box pb={5} pl={4} pr={4} {...stopPropagationProps} {...contentBoxProps}>
            {content?.props.children}
          </Box>
        ) : null}
      </Card>
    </Box>
  );

  if (shouldSkipAnimation) {
    return isOpen ? cardContainer : null;
  }

  return (
    <AnimatePresence onExitComplete={onClose}>
      {isOpen ? (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0, ease: 'easeIn', duration: 0.3 }}
          exit={{ opacity: 0, transition: { ease: 'easeOut', duration: 0.3 } }}
        >
          {cardContainer}
        </motion.div>
      ) : null}
    </AnimatePresence>
  );
};
