/** @jsx jsx */
import React from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { useSidebar } from '~/components/Sidebar/useSidebar';
import { Image } from '~/components/shared/Image';
import { Events } from '~/constants/events';
import { useTranslation } from '~/libs/i18n';
import contentMessengers from '~/messengers/contentMessengers';
import { PanelName, SlideName } from '~/types/sidebar';

const { Button, TransparentButton, Typography, useColorScheme } = ToucanComponents;

export const RegistrationPrompt = () => {
  const overlapInPx = 88;
  const { openSidebar } = useSidebar();
  const { isDarkMode, textLevel1 } = useColorScheme();
  const { t } = useTranslation();

  return (
    <Box
      border={`1px solid ${ToucanColors.gray[isDarkMode ? 500 : 200]}`}
      borderRadius="8px"
      css={css`
        background: ${isDarkMode
          ? `linear-gradient(0deg, ${ToucanColors.gray[600]} 46.17%, ${ToucanColors.teal[400]}B8 150.3%, ${ToucanColors.gray[600]}B8 150.3%)`
          : `linear-gradient(180deg, ${ToucanColors.blue[100]}80 -29.69%, ${ToucanColors.yellow[100]}4D 211.99%), ${ToucanColors.white}`};
      `}
    >
      <TransparentButton
        css={css`
          width: 100%;
          display: block;
        `}
        onClick={() => {
          contentMessengers.trackEvent({ eventName: Events.SettingsOpened, properties: { source: 'signed-out ep' } });
          openSidebar({
            panelName: PanelName.Settings,
            slideName: SlideName.Authenticate,
          });
        }}
      >
        <Box component="span" display="flex" justifyContent="center">
          {/* Use margin-left to "ignore" the shadow when centering the image. */}
          <Image
            src={`/images/learningShortcuts/extraPractice/registration-background-${isDarkMode ? 'dark' : 'light'}.svg`}
            alt=""
            css={css`
              margin-top: 20px;
              margin-left: -10px;
            `}
            width={265}
            height={227}
          />
        </Box>
        <Box component="span" display="block" position="relative" mt={`-${overlapInPx}px`}>
          <Box
            component="span"
            display="block"
            position="absolute"
            height={32}
            width={1}
            top={-30}
            css={css`
              background: ${isDarkMode
                ? `linear-gradient(0deg, ${ToucanColors.gray[600]} 0%, ${ToucanColors.gray[600]}00 94.12%)`
                : `linear-gradient(0deg, ${ToucanColors.white} 0%, ${ToucanColors.white}00 94.12%)`};
            `}
          />
          <Box
            component="span"
            bgcolor={isDarkMode ? ToucanColors.gray[600] : ToucanColors.white}
            display="flex"
            flexDirection="column"
            alignItems="center"
            px={4}
            py={2}
            gap="10px"
            minHeight={overlapInPx}
            borderRadius="0 0 8px 8px"
          >
            <Typography component="span" color={textLevel1} isBold variant="xs">
              {t('learnFasterWithExtraPractice')}
            </Typography>
            <Button
              component="span"
              size="small"
              type="primary"
              css={css`
                width: 100%;
              `}
            >
              {t('createAccount')}
            </Button>
          </Box>
        </Box>
      </TransparentButton>
    </Box>
  );
};
