/** @jsx jsx */
import { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme, Box } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

import Config from '../../configs/config';

const { ToucanIcon, Typography } = ToucanComponents;

interface HeaderProps {
  onRequestClose: () => void;
  isBackgroundWhite?: boolean;
  isDailyGoalsModalEnabled?: boolean;
  isOnOffButtonVisible?: boolean;
}

export const Header: FC<HeaderProps> = ({
  onRequestClose,
  isBackgroundWhite = false,
  isDailyGoalsModalEnabled,
  isOnOffButtonVisible = false,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();

  const onToggleDailyReportClick = () => {
    window.open(`${Config.siteURL}/dashboard/personalize`, '_blank');
    onRequestClose();
  };

  return (
    <div
      css={css`
        padding: ${theme.spacing(2)};
        width: 100%;
        display: flex;
        flex: 1;
        justify-content: space-between;
        align-items: center;
        background-color: ${isBackgroundWhite && ToucanColors.white};
      `}
    >
      <Image
        src={`images/logo-${isBackgroundWhite ? 'color' : 'white'}-horizontal.svg`}
        alt="Toucan logo"
        css={css`
          height: 22px;
        `}
      />
      <Box flex="1" />
      {isOnOffButtonVisible && (
        <Box
          onClick={onToggleDailyReportClick}
          onKeyDown={onToggleDailyReportClick}
          role="button"
          tabIndex={0}
          display="flex"
          justifyContent="center"
          alignItems="center"
          css={css`
            background: rgba(255, 255, 255, 0.1);
            border-radius: 16px;
            padding: ${theme.spacing(0.5)} ${theme.spacing(0.75)};
            margin-right: ${theme.spacing(2)};
            cursor: pointer;
          `}
        >
          <Typography
            variant="2xs"
            isBold
            css={css`
              padding-right: ${theme.spacing(0.75)};
              color: ${ToucanColors.white};
            `}
          >
            {isDailyGoalsModalEnabled ? t('on') : t('off')}
          </Typography>
          <div
            css={css`
              background-color: ${isDailyGoalsModalEnabled ? ToucanColors.green[200] : ToucanColors.gray[300]};
              width: 10px;
              height: 10px;
              border-radius: 50%;
            `}
          />
        </Box>
      )}
      <div
        onClick={() => onRequestClose()}
        onKeyDown={() => onRequestClose()}
        role="button"
        tabIndex={0}
        css={css`
          cursor: pointer;
        `}
      >
        <ToucanIcon
          icon="times"
          css={css`
            color: ${ToucanColors.gray[200]};
            width: 16px;
            height: 16px;
            display: flex;
            justify-content: flex-start;
          `}
        />
      </div>
    </div>
  );
};
