import React, { useEffect, useState } from 'react';

import browser from 'webextension-polyfill';

import { MessageKey } from '~/constants/messages';

export const Features = {
  // NOTE: cache is meant to avoid extra round trips
  cache: new Map<string, boolean>(),
  isFeatureEnabled: async (feature: string) => {
    const result: boolean = await browser.runtime.sendMessage({
      msg: MessageKey.IsFeatureEnabled,
      feature,
    });
    Features.cache.set(feature, result);
    return result;
  },
};

export const useFeatureEnabled = (name: string) => {
  const [isEnabled, setIsEnabled] = useState<boolean | null>(() => Features.cache.get(name) ?? null);

  useEffect(() => {
    let isMounted = true;
    if (isEnabled === null) {
      Features.isFeatureEnabled(name).then(result => {
        if (!isMounted) {
          return;
        }
        setIsEnabled(result);
      });
    }
    return () => {
      isMounted = false;
    };
  }, [isEnabled]);

  useEffect(() => {
    setIsEnabled(() => Features.cache.get(name) ?? null);
  }, [name]);

  return isEnabled;
};

export const Feature: React.FC<{ name: string }> = ({ name, children }) => {
  const isEnabled = useFeatureEnabled(name);

  if (!isEnabled) {
    return null;
  }

  return <>{children}</>;
};
