import { intersectionWith, isEmpty, isEqual, startsWith } from 'lodash';

import { excludedTags, excludedRoles, excludedClassNames, toucanTargetAttribute } from '~/constants/html';
import { isToucanSpan } from '~/helpers/htmlHelpers';

import { DomTarget } from '../useDomTargets/types';

export const isChildOfExcludedNodeTypeFilter = (targetNode: DomTarget, customExcludedTags: Array<string> = []) =>
  targetNode.parentNode ? !isChildOfExcludedNodeType(targetNode.parentNode, customExcludedTags) : false;

export const isParentOfExcludedNodeTypeFilter = (targetNode: DomTarget, customExcludedTags: Array<string> = []) =>
  targetNode.parentNode ? !isParentOfExcludedNodeType(targetNode.parentNode, customExcludedTags) : false;

export const isNotToucanSpan = (targetNode: DomTarget) => !isToucanSpan(targetNode.parentNode);

export const isNotToucanTarget = (targetNode: DomTarget) =>
  !targetNode.parentNode?.attributes?.getNamedItem(toucanTargetAttribute);

export const hasNotBeenCrawled = (targetNode: Pick<DomTarget, 'parentNode'>) =>
  !targetNode.parentNode?.dataset.toucanCrawled;

export const hasSufficientContent = (targetNode: DomTarget) =>
  (targetNode.parentNode?.textContent?.trim().length ?? 0) > 3;

/**
 * Checks if the span node within Facebook is properly structured to accept translations. Without this
 * check the node disappears from the DOM when "See more" is clicked because the text is updated
 * and doesn't match the previous version. If the sentence ends with a period before the "See more"
 * link then the node is properly structured. We limit the check to strings with more than 100 characters
 * to avoid unnecessary checks.
 * Properly structured example (period included): house. See more
 * Inproperly structured example (period excluded): withou See more
 * @param targetNode
 * @returns boolean - false if the targetNode is not properly structured.
 */
export const isFacebookProperlyStructured = (targetNode: DomTarget) => {
  const { baseURI, nodeName, textContent } = targetNode.parentNode;
  if (baseURI.includes('facebook.com') && nodeName === 'SPAN' && textContent && textContent.length > 100) {
    const regex = /[a-zA-Z0-9] See more/g;
    const incorrectMatch = textContent.match(regex);
    return !incorrectMatch;
  }
  return true;
};

export const filterValidTargets = (target: DomTarget, customExcludedTags: Array<string> = []) =>
  isNotToucanSpan(target) &&
  isNotToucanTarget(target) &&
  isParentOfExcludedNodeTypeFilter(target, customExcludedTags) &&
  isChildOfExcludedNodeTypeFilter(target, customExcludedTags) &&
  hasSufficientContent(target) &&
  hasNotBeenCrawled(target) &&
  isFacebookProperlyStructured(target);

/*
 * isChildOfExcludedNodeType() Recursively checks that a node is not a child of an excluded node type
 */

export const isChildOfExcludedNodeType = (node: HTMLElement, customExcludedTags: Array<string> = []): boolean => {
  const parentNode = node.parentNode as HTMLElement | null;

  if (parentNode === null || !parentNode.tagName || parentNode.tagName === 'BODY') {
    return false;
  }
  // Use Accessible Rich Internet Applications role
  const role = parentNode.attributes.getNamedItem('role');
  if (role) {
    if (excludedRoles.includes(role.nodeValue ?? '')) {
      return true;
    }
  }
  if ([...excludedTags, ...customExcludedTags].includes(parentNode.tagName)) {
    return true;
  }
  if (parentNode.contentEditable === 'true') {
    return true;
  }
  const classListContainsExcludedClass = intersectionWith(
    Array.from(parentNode.classList),
    excludedClassNames,
    isEqual,
  );
  const classListStartsWithExcludedClass = intersectionWith(
    Array.from(parentNode.classList),
    excludedClassNames,
    startsWith,
  );
  if (!isEmpty(classListContainsExcludedClass) || !isEmpty(classListStartsWithExcludedClass)) {
    return true;
  }
  const childrenNodes = Array.from(parentNode.childNodes);
  if (childrenNodes.length === 1) {
    const isChildNodeAnExcludedTag = excludedTags.includes(childrenNodes[0].nodeName);
    if (isChildNodeAnExcludedTag) {
      return true;
    }
  }

  return isChildOfExcludedNodeType(parentNode, customExcludedTags);
};

/*
 * isParentOfExcludedNodeType() checks that the parent node of the incoming node is not of an excluded node type
 */
export const isParentOfExcludedNodeType = (node: Node, customExcludedTags: Array<string> = []) => {
  const isParentAnExcludedTag = [...excludedTags, ...customExcludedTags].includes(
    node?.parentNode?.parentElement?.nodeName ?? '',
  );
  if (isParentAnExcludedTag) {
    return true;
  }
  return false;
};
