const trackEvent = async ({
  eventName,
  properties,
  userOptions = {},
  destinationOptions = {},
}: {
  eventName: string;
  properties?: Record<string, unknown> | null;
  userOptions?: {
    includePremiumSubscriptionStatus?: boolean;
  };
  destinationOptions?: {
    datalake?: boolean;
    skipSegment?: boolean;
  };
}) => {
  const newMessage = {
    msg: 'track_event',
    data: {
      eventName,
      properties,
      userOptions,
      destinationOptions,
    },
  };

  return browser.runtime.sendMessage(newMessage);
};

const fetchRegistrationStatus = async () => {
  const newMessage = {
    msg: 'fetch_registration_status',
  };

  return browser.runtime.sendMessage(newMessage);
};

// error: string, stack: string, info = Record<string, unknown>
const captureContentError = async (error: Error | any) =>
  browser.runtime.sendMessage({ msg: 'capture_content_error', contentError: { message: error.message } });

const serverDrivenTranslationsSeenCount = async (translationsSeenCount: number) => {
  const newMessage = {
    msg: MessageKey.ServerDrivenTranslationsSeenCount,
    data: {
      translationsSeenCount,
    },
  };

  try {
    const message = await browser.runtime.sendMessage(newMessage);
    return message;
  } catch (err) {
    return null;
  }
};

const serverDrivenTranslationsHoverCount = async () => {
  const newMessage = {
    msg: MessageKey.ServerDrivenTranslationsHoverCount,
  };

  try {
    const message = await browser.runtime.sendMessage(newMessage);
    return message;
  } catch (err) {
    return null;
  }
};

export default {
  captureContentError,
  fetchRegistrationStatus,
  pageLoadEvent,
  reloadCurrentPage,
  serverDrivenTranslationsSeenCount,
  serverDrivenTranslationsHoverCount,
  trackInlineTranslationHoverInHeartbeat,
  trackAudioPronunciationClicked,
  trackErrorReportFlagClicked,
  trackEvent,
};
