/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';
import { LanguageId } from '~/schema/server';

const { Typography, useColorScheme, Link, ToucanIcon } = ToucanComponents;

export const PostSurvey: FC = () => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const { sourceLanguage } = useUserInfo();

  const enExtraSurveyUrl = 'https://info014470.typeform.com/to/A1Z7UiMV';

  if (sourceLanguage === LanguageId.En) {
    return (
      <Box display="flex" flexDirection="column">
        <Typography
          variant="xs"
          css={css`
            color: ${isDarkMode ? ToucanColors.gray[100] : ToucanColors.blue[500]};
          `}
        >
          {t('mindTellingUsMoreShortSurvey')}
        </Typography>
        <Typography
          variant="xs"
          css={css`
            flex: 1;
          `}
        >
          <Link
            href={enExtraSurveyUrl}
            css={css`
              display: flex;
              align-items: center;
              gap: ${theme.spacing(0.5)};
            `}
          >
            {t('shareFeedback')}
            <ToucanIcon
              icon="link-out"
              css={css`
                width: 10px;
                color: inherit;
              `}
            />
          </Link>
        </Typography>
      </Box>
    );
  }

  return (
    <Typography
      variant="xs"
      css={css`
        color: ${isDarkMode ? ToucanColors.gray[100] : ToucanColors.blue[500]};
      `}
    >
      {t('thanksForYourFeedback')}
    </Typography>
  );
};
