/** @jsx jsx */
import React, { FC, ReactNode } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { Image } from '~/components/shared/Image';

const { useColorScheme, Typography, ToucanIcon, TransparentButton } = ToucanComponents;

interface ProgressBarCallToActionProps {
  isVisible: boolean;
  onClick: () => void;
  imgSrc: string;
  imgAlt: string;
  children: ReactNode | string;
  extraText?: string;
}

export const ProgressBarCallToAction: FC<ProgressBarCallToActionProps> = ({
  isVisible,
  onClick,
  imgSrc,
  imgAlt,
  children,
  extraText,
}) => {
  const theme = useTheme();
  const { textLink } = useColorScheme();

  if (!isVisible) {
    return null;
  }

  return (
    <Box mt={1}>
      <TransparentButton
        css={css`
          display: flex;
          align-items: center;
          width: 100%;
          height: 100%;
        `}
        onClick={onClick}
      >
        <Image
          src={imgSrc}
          alt={imgAlt}
          css={css`
            height: 14px;
            width: 14px;
            margin-right: ${theme.spacing(1)};
          `}
        />
        <Typography
          variant="2xs"
          isBold
          css={css`
            color: ${textLink};
          `}
        >
          {children}
        </Typography>
        <div
          css={css`
            display: flex;
            align-items: center;
            margin-left: auto;
          `}
        >
          {extraText && (
            <Typography
              variant="2xs"
              css={css`
                color: ${textLink};
                margin-right: ${theme.spacing(1)};
                text-align: right;
                padding-left: ${theme.spacing(1)};
              `}
            >
              {extraText}
            </Typography>
          )}
          <ToucanIcon
            css={css`
              color: ${textLink};
              width: 8px;
              height: 8px;
            `}
            icon="chevron-right"
          />
        </div>
      </TransparentButton>
    </Box>
  );
};
