/** @jsx jsx */
import React, { useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { QuizContext } from '~/constants/quiz';
import { useLanguageInfo } from '~/hooks/useLanguageInfo';
import { useTranslation } from '~/libs/i18n';
import { Trans } from '~/libs/i18n/Trans';
import { QuizType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

import { isSingleWordQuestion } from '../helpers';
import { QuestionBaseProps } from '../types';
import { useQuizSize } from '../useQuizSize';

import { Button } from './Button';

const { useColorScheme, Typography, Button: ToucanButton } = ToucanComponents;

export const Listen = ({
  question,
  retryQuiz,
  submitQuestion,
  quizType,
  quizContext,
  onResetExtraPractice,
}: QuestionBaseProps) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const [hasBeenClicked, setHasBeenClicked] = useState(false);
  const [userSelectedChoice, setUserSelectedChoice] = useState<string | null>(null);
  const { t } = useTranslation();
  const { targetLanguageInfo } = useLanguageInfo();
  const { size, fontVariant, subFontSize } = useQuizSize();

  if (!question || !targetLanguageInfo || !isSingleWordQuestion(question)) {
    return null;
  }

  const { answer, options, displayPhrase } = question;

  /**
   * submission of user selection.
   * selecting `I can't listen right now` to advance to the next question does not result in a fail.
   * @param option user selection.
   * @returns prevent multiple submissions if answer previously submitted.
   */
  const onChoiceClick = (option: string) => {
    if (hasBeenClicked) {
      return;
    }
    setUserSelectedChoice(option);
    setHasBeenClicked(true);
    setTimeout(() => {
      submitQuestion(option === answer ? QuizStatus.Pass : QuizStatus.Fail);
    }, 1000);
  };

  /**
   * Retry a new quiz if its a practice quiz.
   * Otherwise, advance to the next question and record as a pass.
   */
  const onCantListenNowClick = () => {
    if (quizContext === QuizContext.ExtraPractice) {
      onResetExtraPractice?.();
      return;
    }

    setTimeout(() => {
      if (quizType === QuizType.Practice) {
        retryQuiz();
        return;
      }

      submitQuestion(QuizStatus.Pass);
    }, 1000);
  };

  return (
    <Box data-testid="listen-question">
      <Typography
        variant={fontVariant}
        css={css`
          color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
          margin-bottom: ${theme.spacing(2)};
        `}
      >
        <Trans i18nKey="quizHowDoYouSay" values={{ translation: displayPhrase }} components={[<strong>a</strong>]} />
      </Typography>
      <Box display="flex" justifyContent="space-between" mb={2}>
        {options.map((option: string, idx: number) => {
          const isCorrectAnswer = option === answer;
          return (
            <Button
              key={option}
              hasBeenClicked={hasBeenClicked}
              userSelection={{
                isCorrectAnswer,
                isUserSelected: option === userSelectedChoice,
              }}
              index={idx}
              option={option}
              language={targetLanguageInfo.key}
              setUserSelectedChoice={setUserSelectedChoice}
            />
          );
        })}
      </Box>
      {!hasBeenClicked && (
        <Box display="flex" flexDirection="column" justifyContent="center">
          <ToucanButton
            data-testid="continue-button"
            size={size}
            type="utility"
            isDisabled={!userSelectedChoice}
            onClick={() => onChoiceClick(userSelectedChoice ?? '')}
            css={css`
              width: 100%;
              margin-bottom: ${theme.spacing(1.5)};
            `}
          >
            {t('quizContinue')}
          </ToucanButton>
          <ToucanButton
            size={size}
            type="text"
            onClick={onCantListenNowClick}
            css={css`
              font-size: ${subFontSize};
              line-height: 1em;
            `}
          >
            {t('quizCantListenNow')}
          </ToucanButton>
        </Box>
      )}
    </Box>
  );
};
