/** @jsx jsx */
import React, { FC, forwardRef, ReactElement } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme, ClickAwayListener } from '@mui/material';

import { maximumZIndex } from '~/constants/styling';

import { Box } from '../Box';

const MODAL_WIDTH = '380px';

interface ModalCardProps {
  isInPopup?: boolean;
  isInSidebar?: boolean;
  onRequestClose: () => void;
  className?: string;
  disableClickAway?: boolean;
  modalWidth?: string;
  children: ReactElement | false | null;
  isUserRegistered?: boolean;
  shouldPromptUserToRegister?: Boolean | null;
}

const { useColorScheme, Card } = ToucanComponents;

const MaybeClickAwayListener: FC<Omit<ModalCardProps, 'modalWidth'>> = ({
  disableClickAway,
  children,
  onRequestClose,
}) => {
  if (disableClickAway) {
    return <React.Fragment>{children}</React.Fragment>;
  }
  return (
    <ClickAwayListener mouseEvent="onMouseUp" onClickAway={onRequestClose}>
      {/* @ts-ignore - avoid wrapping children within an empty element */}
      {children}
    </ClickAwayListener>
  );
};

export const ModalCard: FC<ModalCardProps> = forwardRef(
  (
    {
      isInPopup = false,
      isInSidebar = false,
      children,
      onRequestClose,
      className,
      disableClickAway,
      modalWidth = MODAL_WIDTH,
      isUserRegistered,
      shouldPromptUserToRegister,
    },
    ref,
  ) => {
    const { isDarkMode } = useColorScheme();
    const theme = useTheme();
    const getCardHeight = () => {
      if (!isInSidebar) {
        return 'auto';
      }
      if (isInPopup) {
        return '600px';
      }
      return '100vh';
    };

    return (
      <MaybeClickAwayListener disableClickAway={disableClickAway} onRequestClose={onRequestClose}>
        <Card
          ref={ref}
          hasPadding={false}
          css={css`
            position: ${isInPopup ? 'static' : 'fixed'};
            z-index: ${maximumZIndex};
            right: ${isInSidebar ? 0 : theme.spacing(2)};
            top: ${isInSidebar ? 0 : theme.spacing(2)};
            width: ${modalWidth};
            max-width: ${modalWidth};
            height: ${getCardHeight()};
            border: 0;
            overflow-x: hidden !important;
            border-radius: ${isInSidebar ? 0 : 8}px;
          `}
        >
          <Box
            className={className}
            css={css`
              position: relative;
              height: 100%;
              width: 100%;
              max-width: inherit;
              flex: 1;
              display: flex;
              flex-direction: column;
              align-items: flex-start;
              background-color: ${isDarkMode ? ToucanColors.gray[500] : ToucanColors.white};
              border: 0;
              ${shouldPromptUserToRegister &&
              !isUserRegistered && {
                background: `linear-gradient(180deg, ${
                  isDarkMode
                    ? '#052430 -7.33%, rgba(5, 36, 48, 0) 29.13%'
                    : '#85c3d8 -14.73%, rgba(133, 195, 216, 0) 25.54%'
                })`,
                mixBlendMode: 'normal',
                height: isInPopup ? '75vh' : '50vh',
                paddingTop: theme.spacing(0.5),
              }}
            `}
          >
            {children}
          </Box>
        </Card>
      </MaybeClickAwayListener>
    );
  },
);
