/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';

const { Typography, Button, useColorScheme } = ToucanComponents;

interface TurnOffToastBodyProps {
  onClickOpenSettings: () => void;
  onClickTurnOnContextlessLearningMoment: () => void;
}

export const TurnOffToastBody: FC<TurnOffToastBodyProps> = ({
  onClickOpenSettings,
  onClickTurnOnContextlessLearningMoment,
}) => {
  const { isDarkMode } = useColorScheme();
  const { t } = useTranslation();
  const theme = useTheme();

  return (
    <React.Fragment>
      <Typography
        variant="xs"
        isBold
        css={css`
          color: ${isDarkMode ? ToucanColors.gray[100] : ToucanColors.gray[500]};
        `}
      >
        {t('wontShowTheseAnymore')}
      </Typography>
      <Typography
        variant="xs"
        css={css`
          margin-top: ${theme.spacing(0.5)};
          color: ${isDarkMode ? ToucanColors.gray[100] : ToucanColors.gray[500]};
        `}
      >
        {t('turnThemOnAnyTimeInSettings')}
      </Typography>
      <Box display="flex" gap={theme.spacing(2)} mt={0.5}>
        <Typography variant="xs">
          <Button type="text" onClick={onClickOpenSettings}>
            {t('openSettings')}
          </Button>
        </Typography>
        <Typography variant="xs">
          <Button type="text" onClick={onClickTurnOnContextlessLearningMoment}>
            {t('turnOnNow')}
          </Button>
        </Typography>
      </Box>
    </React.Fragment>
  );
};
