import moment from 'moment';

import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { StorageKey } from '~/constants/storage';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useStorage } from '~/hooks/useStorage';
import { DailyPracticeQuizInfo, PRACTICE_INLINE_DAILY_LIMIT } from '~/types/quiz';

const initialValues = {
  date: moment().format('MM/DD/YY'),
  uses: 0,
};

export const useDailyPracticeQuizUses = () => {
  const [dailyPracticeQuizInfo, setDailyPracticeQuizInfo] = useStorage<DailyPracticeQuizInfo>(
    StorageKey.DailyPracticeQuizInfo,
    initialValues,
  );
  const { config, getFeaturePaywallStatus } = usePremiumExperiment();
  const featurePaywallStatus = getFeaturePaywallStatus(PremiumFeatureName.InlinePractice);
  const inlinePracticeConfig = config?.premiumFeatures?.inlinePractice;
  const isLimitedPremiumFeature = featurePaywallStatus === PremiumFeaturePaywallStatus.FreeUses;
  const numFreeUses = typeof inlinePracticeConfig === 'boolean' ? 0 : inlinePracticeConfig?.numFreeUses ?? 0;

  const maxDailyFreeUses = isLimitedPremiumFeature ? numFreeUses : PRACTICE_INLINE_DAILY_LIMIT;

  const isSameDay = moment(dailyPracticeQuizInfo.date, 'MM/DD/YY').isSame(moment(), 'day');
  const uses = isSameDay ? dailyPracticeQuizInfo.uses : 0;

  const incrementPracticeQuizUse = () =>
    setDailyPracticeQuizInfo({
      date: moment().format('MM/DD/YY'),
      uses: uses + 1,
    });

  return {
    practiceQuizUses: uses,
    incrementPracticeQuizUse,
    arePracticeQuizUsesRemaining: uses < maxDailyFreeUses,
    numPracticeQuizUsesRemaining: Math.max(maxDailyFreeUses - uses, 0),
  };
};
