/**
 * TextToSpeechAudioElement is a player that plays an audio buffer
 * using the HTML audio element. This is used when audio context
 * is not available in a browser.
 */
export class TextToSpeechAudioElement {
  private audioElement: HTMLAudioElement;

  public onended: () => void;

  static from = (buffer: Uint8Array) => new TextToSpeechAudioElement(buffer);

  constructor(buffer: Uint8Array) {
    const blob = new Blob([buffer], { type: 'audio/mp3' });
    const audioSrc = window.URL.createObjectURL(blob);
    this.audioElement = new Audio(audioSrc);

    this.onended = () => {};
    return this;
  }

  async pause() {
    this.audioElement.pause();
    return this;
  }

  // TODO: handle errors from CSP
  async play() {
    await this.audioElement.play();
    this.audioElement.onended = this.onended;
    return this;
  }

  async setPlaybackRate(rate: number) {
    this.audioElement.playbackRate = rate;
    return this;
  }
}
