import { useEffect, useState } from 'react';

import { useThemeProps } from '@mui/material';

/**
 * useMediaQuery implements useMediaQuery from Material-UI, but uses window.matchMedia instead of
 * matchMedia without window. Not sure why but it throws when its not on the accessor of the window.
 */
export const useMediaQuery = (queryInput: string, options = {}) => {
  const props = useThemeProps({
    name: 'MuiUseMediaQuery',
    props: {},
  });

  let query = queryInput;
  query = query.replace(/^@media( ?)/m, '');

  // Wait for jsdom to support the match media feature.
  // All the browsers Material-UI support have this built-in.
  // This defensive check is here for simplicity.
  // Most of the time, the match media logic isn't central to people tests.
  const supportMatchMedia = typeof window !== 'undefined' && typeof window.matchMedia !== 'undefined';
  const { matchMedia = supportMatchMedia ? window.matchMedia : null } = {
    ...props,
    ...options,
  };

  const [match, setMatch] = useState(() => (supportMatchMedia ? window.matchMedia(query).matches : false));

  useEffect(() => {
    let active = true;

    if (!supportMatchMedia) {
      return undefined;
    }

    const queryList = window.matchMedia(query);
    const updateMatch = () => {
      // Workaround Safari wrong implementation of matchMedia
      // TODO can we remove it?
      // https://github.com/mui-org/material-ui/pull/17315#issuecomment-528286677
      if (active) {
        setMatch(queryList.matches);
      }
    };
    updateMatch();
    queryList.addListener(updateMatch);
    return () => {
      active = false;
      queryList.removeListener(updateMatch);
    };
  }, [query, matchMedia, supportMatchMedia]);

  return match;
};
