import React from 'react';

import { css } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';

import { Box } from '~/components/Box';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { useUserInfo } from '~/hooks/useUserInfo';
import { Sentence, SentenceType } from '~/schema/background';

const { useColorScheme, Typography } = ToucanComponents;

interface ExampleProps {
  answer: string;
  sentence: Sentence[];
}

export const Example = ({ answer, sentence }: ExampleProps) => {
  const { textLevel1 } = useColorScheme();
  const { targetLanguage } = useUserInfo();
  return (
    <Box height="100%" width="100%" display="inline-flex" alignItems="center" justifyContent="center">
      <Typography
        data-testid="example-sentence"
        variant="sm"
        css={css`
          color: ${textLevel1};
          text-align: center;
        `}
      >
        {sentence.map(option => {
          if (option.type === SentenceType.Text) {
            return <span key={option.text}>{option.text}</span>;
          }
          return (
            <Box
              component="span"
              sx={{
                verticalAlign: 'bottom',
                display: 'inline',
              }}
              key={answer}
            >
              <AudioPlayer language={targetLanguage} targetPhrase={answer} marginRight={0.5}>
                {answer}
              </AudioPlayer>
            </Box>
          );
        })}
      </Typography>
    </Box>
  );
};
