/** @jsx jsx */
import React, { FC, useEffect, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { useTranslation } from '~/libs/i18n';
import { useRemoveContextlessLearningMomentCacheMutation } from '~/schema/background';

import { LearningBlurb } from '../LearningBlurb';

import { PostSurvey } from './PostSurvey';

const { Typography, useColorScheme, ToucanIcon, Button } = ToucanComponents;

interface CelebrationProps {
  onClickHelpfulButton: (isHelpful: boolean) => void;
  resetQuiz: () => void;
  isReferralEnabled?: boolean;
  referralCTAText?: string;
}

export const Celebration: FC<CelebrationProps> = ({
  onClickHelpfulButton,
  resetQuiz,
  isReferralEnabled,
  referralCTAText,
}) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const [isMicroFeedbackSurveyCompleted, setIsMicroFeedbackSurveyCompleted] = useState(false);
  const [removeContextlessLearningMomentCache] = useRemoveContextlessLearningMomentCacheMutation();

  const possibleAffirmations = [
    t('niceJobYoureGettingGood'),
    t('lookAtYouGoWellDone'),
    t('youreReallyMakingProgress'),
    t('thatWasImpressive'),
    t('nicelyDoneYoureDoingGreat'),
    t('youNailedItWayToGo'),
  ];

  const [randomAffirmation] = useState(
    () => possibleAffirmations[Math.floor(Math.random() * possibleAffirmations.length)],
  );

  useEffect(() => {
    const clearContextlessLearningMomentCache = async () => {
      await removeContextlessLearningMomentCache({});
    };
    clearContextlessLearningMomentCache();
  }, []);

  return (
    <React.Fragment>
      <LearningBlurb
        imagePath="/images/toucan-party.svg"
        isReferralShown={isReferralEnabled}
        referralCTAText={referralCTAText}
      >
        <Typography
          variant="sm"
          isBold
          css={css`
            color: ${isDarkMode ? ToucanColors.white : ToucanColors.blue[500]};
          `}
        >
          {randomAffirmation}
        </Typography>
        <Box display="flex" alignItems="center" gap={theme.spacing(2)} mt={0.25}>
          {isMicroFeedbackSurveyCompleted ? (
            <PostSurvey />
          ) : (
            <Box display="flex">
              <Typography
                variant="xs"
                css={css`
                  color: ${isDarkMode ? ToucanColors.gray[100] : ToucanColors.blue[500]};
                  margin-right: ${theme.spacing(3)};
                `}
              >
                {t('wasThisHelpful')}
              </Typography>
              <Box display="flex">
                <Typography variant="xs">
                  <Button
                    type="text"
                    onClick={() => {
                      onClickHelpfulButton(false);
                      setIsMicroFeedbackSurveyCompleted(true);
                    }}
                    css={css`
                      display: inline-flex;
                      align-items: center;
                    `}
                  >
                    <ToucanIcon
                      icon="thumbs-down"
                      css={css`
                        width: 10px;
                        color: inherit;
                        margin-right: ${theme.spacing(1)};
                      `}
                    />
                    {t('no')}
                  </Button>
                </Typography>
                <Typography
                  variant="xs"
                  css={css`
                    margin-left: ${theme.spacing(2)};
                  `}
                >
                  <Button
                    type="text"
                    onClick={() => {
                      onClickHelpfulButton(true);
                      setIsMicroFeedbackSurveyCompleted(true);
                    }}
                    css={css`
                      display: inline-flex;
                      align-items: center;
                    `}
                  >
                    <ToucanIcon
                      icon="thumbs-up"
                      css={css`
                        width: 10px;
                        color: inherit;
                        margin-right: ${theme.spacing(1)};
                      `}
                    />
                    {t('yes')}
                  </Button>
                </Typography>
              </Box>
            </Box>
          )}
        </Box>
        <Button
          type="utility"
          onClick={resetQuiz}
          css={css`
            margin: ${theme.spacing(2)} -${theme.spacing(2)} -${theme.spacing(2)} -${theme.spacing(2)};
            border-radius: 0 0 8px 8px;
          `}
        >
          {t('keepPracticing')}
        </Button>
      </LearningBlurb>
    </React.Fragment>
  );
};
