/** @jsx jsx */
import React, { FC, useEffect, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { Button as QuizButton } from '~/components/InlineTranslations/InlineQuiz/Swap/Button';
import { QuizSize, QuizSizeProvider } from '~/components/InlineTranslations/InlineQuiz/useQuizSize';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { Language } from '~/constants/language';
import { Trans } from '~/libs/i18n/Trans';
import { ContextlessLearningType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

const { Typography, useColorScheme } = ToucanComponents;

interface MultipleChoiceContentProps {
  onCompleteQuestion: (quizStatus: QuizStatus.Pass | QuizStatus.Fail) => void;
  questionNumber: number;
  questionTargetPhrase: string;
  questionSourcePhraseAnswer: string;
  questionSourcePhraseOptions: Array<string>;
  languageKey: Language | undefined;
  isCLMIntroScreenDisabled: boolean;
  adhocEventTracking: ({ quizType }: { quizType: ContextlessLearningType }) => void;
}

export const MultipleChoiceContent: FC<MultipleChoiceContentProps> = ({
  onCompleteQuestion,
  questionNumber,
  questionTargetPhrase,
  questionSourcePhraseAnswer,
  questionSourcePhraseOptions,
  languageKey,
  isCLMIntroScreenDisabled,
  adhocEventTracking,
}) => {
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const [hasBeenClicked, setHasBeenClicked] = useState(false);
  const [userSelectedChoice, setUserSelectedChoice] = useState<string | null>(null);

  useEffect(() => {
    setUserSelectedChoice(null);
    setHasBeenClicked(false);
  }, [questionNumber]);

  const onChoiceClick = (option: string) => {
    if (hasBeenClicked) {
      return;
    }
    setUserSelectedChoice(option);
    setHasBeenClicked(true);
    setTimeout(() => {
      onCompleteQuestion(option === questionSourcePhraseAnswer ? QuizStatus.Pass : QuizStatus.Fail);
    }, 1000);
    if (questionNumber === 1 && isCLMIntroScreenDisabled) {
      adhocEventTracking({ quizType: ContextlessLearningType.MultipleChoice });
    }
  };

  return (
    <QuizSizeProvider value={QuizSize.Medium}>
      <Typography
        variant="sm"
        css={css`
          margin-top: ${theme.spacing(2)};
          color: ${isDarkMode ? ToucanColors.gray[100] : ToucanColors.gray[500]};
          display: inline-block;
        `}
      >
        {languageKey && (
          <Trans
            i18nKey="quizMeans"
            key={questionTargetPhrase}
            values={{ translation: questionTargetPhrase }}
            components={[
              <AudioPlayer language={languageKey} targetPhrase={questionTargetPhrase} marginRight={0.5}>
                a
              </AudioPlayer>,
            ]}
          />
        )}
      </Typography>
      <Box mt={1} mb={3}>
        {questionSourcePhraseOptions.map((option: string, idx: number) => {
          const isCorrectAnswer = option === questionSourcePhraseAnswer;
          return (
            <QuizButton
              key={option}
              onClick={() => {
                onChoiceClick(option);
              }}
              hasBeenClicked={hasBeenClicked}
              userSelection={{
                isCorrectAnswer,
                isUserSelected: option === userSelectedChoice,
              }}
              index={idx}
              option={option}
            />
          );
        })}
      </Box>
    </QuizSizeProvider>
  );
};
