import React, { useState, useEffect } from 'react';

import { Pinchpoints } from '~/constants/pinchpoints';
import { PremiumFeatureName, PremiumFeaturePaywallStatus } from '~/constants/premiumFeatureConfig';
import { StorageKey } from '~/constants/storage';
import { usePremiumExperiment } from '~/hooks/usePremiumExperiment';
import { useStorage } from '~/hooks/useStorage';
import { useSubscription } from '~/hooks/useSubscription';
import { useUserInfo } from '~/hooks/useUserInfo';
import { useTranslation } from '~/libs/i18n';

import { PremiumUpsell } from './PremiumUpsell';

export type PremiumFeaturesWithCTA =
  | PremiumFeatureName.DensitySettings
  | PremiumFeatureName.DifficultySettings
  | PremiumFeatureName.SaveWord
  | PremiumFeatureName.RemoveWord
  | PremiumFeatureName.IKnowThisWord;

export const PremiumUpsellContainer = () => {
  const { t } = useTranslation();
  const { openSubscribePage } = useSubscription();
  const { isPremiumSubscribed } = useUserInfo();
  const { getFeaturePaywallStatus } = usePremiumExperiment();
  const [premiumUpsellCtaViewCounts, setPremiumUpsellCtaViewCounts] = useStorage(
    StorageKey.PremiumUpsellCtaViewCounts,
    {
      [PremiumFeatureName.DensitySettings]: 0,
      [PremiumFeatureName.DifficultySettings]: 0,
      [PremiumFeatureName.SaveWord]: 0,
      [PremiumFeatureName.RemoveWord]: 0,
      [PremiumFeatureName.IKnowThisWord]: 0,
    },
  );
  const [activePremiumUpsell, setActivePremiumUpsell] = useState<PremiumFeaturesWithCTA | null>(null);

  const premiumUpsellPriorityOrder: PremiumFeaturesWithCTA[] = [
    PremiumFeatureName.DensitySettings,
    PremiumFeatureName.DifficultySettings,
    PremiumFeatureName.SaveWord,
    PremiumFeatureName.RemoveWord,
    PremiumFeatureName.IKnowThisWord,
  ];

  const premiumUpsellInfo = {
    [PremiumFeatureName.DensitySettings]: {
      message: t('wishYouCouldChooseHowManyTranslationsYouSee'),
      linkText: t('startPremiumFreeTrial'),
      imageUrl: '/images/sparkle.svg',
      imageAlt: t('sparkle'),
      pinchpoint: Pinchpoints.InlineTranslationDensitySettingsCta,
    },
    [PremiumFeatureName.DifficultySettings]: {
      message: t('speedUpLanguageMoreDifficultWords'),
      linkText: t('startPremiumFreeTrial'),
      imageUrl: '/images/bars.svg',
      imageAlt: t('barChart'),
      pinchpoint: Pinchpoints.InlineTranslationDifficultySettingsCta,
    },
    [PremiumFeatureName.SaveWord]: {
      message: t('saveSpecificWordsReviewLaterToucanPremium'),
      linkText: t('startFreeTrialFirstLetterCapitalized'),
      imageUrl: '/images/paper.svg',
      imageAlt: t('notepad'),
      pinchpoint: Pinchpoints.InlineTranslationSaveWordCta,
    },
    [PremiumFeatureName.RemoveWord]: {
      message: t('hideWordsDontWantSeePremium'),
      linkText: t('startFreeTrialFirstLetterCapitalized'),
      imageUrl: '/images/toucan-hiding.svg',
      imageAlt: t('toucanBehindLeaf'),
      pinchpoint: Pinchpoints.InlineTranslationRemoveWordCta,
    },
    [PremiumFeatureName.IKnowThisWord]: {
      message: t('alreadyKnowThisWordPersonalizeYourTranslations'),
      linkText: t('startPremiumFreeTrial'),
      imageUrl: '/images/sparkle.svg',
      imageAlt: t('sparkle'),
      pinchpoint: Pinchpoints.InlineTranslationIKnowThisWordCta,
    },
  };

  const getOrderedPaidFeaturesList = () =>
    premiumUpsellPriorityOrder.filter(
      featureName => getFeaturePaywallStatus(featureName) === PremiumFeaturePaywallStatus.Paid,
    );

  // This is a string unique string that is a representation of the paid status of each premium feature
  // If any feature changes from paid to unpaid or vice versa, this string will change and it will trigger a re-render of the premium upsell
  const premiumUpsellFeaturesPremiumStatusString = premiumUpsellPriorityOrder.reduce((string, feature) => {
    const featureStatus = getFeaturePaywallStatus(feature);
    return `${string}${featureStatus === PremiumFeaturePaywallStatus.Paid ? '1' : '0'}`;
  }, '');

  const resetPaidFeaturesViewCount = (ctaViewCounts: Record<PremiumFeaturesWithCTA, number>) => {
    const newCtaViewCounts = { ...ctaViewCounts };
    getOrderedPaidFeaturesList().forEach(featureName => {
      newCtaViewCounts[featureName] = 0;
    });
    setPremiumUpsellCtaViewCounts(newCtaViewCounts);
    return newCtaViewCounts;
  };

  const assignActivePremiumUpsell = (ctaViewCounts: Record<PremiumFeaturesWithCTA, number>) => {
    const paidFeatures = getOrderedPaidFeaturesList();
    let newActivePremiumUpsell = null;
    for (let i = 0; i < paidFeatures.length; i++) {
      const featureName = paidFeatures[i];
      if (ctaViewCounts[featureName] < 3) {
        setPremiumUpsellCtaViewCounts({
          ...ctaViewCounts,
          [featureName]: ctaViewCounts[featureName] + 1,
        });

        newActivePremiumUpsell = featureName;
        break;
      }
    }

    setActivePremiumUpsell(newActivePremiumUpsell);
  };

  const haveAllCtasBeenViewed = (ctaViewCounts: Record<PremiumFeaturesWithCTA, number>) =>
    getOrderedPaidFeaturesList().every(featureName => ctaViewCounts[featureName] >= 3);

  useEffect(() => {
    if (!isPremiumSubscribed) {
      let ctaViewCounts = premiumUpsellCtaViewCounts;

      if (haveAllCtasBeenViewed(ctaViewCounts)) {
        ctaViewCounts = resetPaidFeaturesViewCount(ctaViewCounts);
      }

      assignActivePremiumUpsell(ctaViewCounts);
    }
  }, [isPremiumSubscribed, premiumUpsellFeaturesPremiumStatusString]);

  if (!activePremiumUpsell) {
    return null;
  }

  const { message, linkText, imageUrl, imageAlt, pinchpoint } = premiumUpsellInfo[activePremiumUpsell];

  return (
    <PremiumUpsell
      imageUrl={imageUrl}
      imageAlt={imageAlt}
      linkText={linkText}
      onClickTextButton={() => openSubscribePage({ pinchpoint })}
    >
      {message}
    </PremiumUpsell>
  );
};
