import { createContext, useContext } from 'react';

export enum QuizSize {
  Small = 'small',
  Medium = 'medium',
}

const context = createContext<QuizSize>(QuizSize.Small);

export const QuizSizeProvider = context.Provider;

const quizSizeTheme: Record<QuizSize, { inputPadding: number; fontVariant: 'sm' | 'xs'; subFontSize: string }> = {
  [QuizSize.Small]: {
    inputPadding: 1,
    fontVariant: 'xs',
    subFontSize: '12px',
  },
  [QuizSize.Medium]: {
    inputPadding: 2,
    fontVariant: 'sm',
    subFontSize: '14px',
  },
};

export const useQuizSize = () => {
  const size = useContext(context);
  const theme = quizSizeTheme[size];
  return {
    size,
    ...theme,
  };
};
