import { useActiveTranslation, useSetActiveTranslation } from '~/hooks/useActiveTranslation';
import { useTranslatedNodesOnPage, useTranslatedNodeTransitions } from '~/hooks/useTranslatedNodes';
import { useMarkWordAsRemovedMutation } from '~/schema/background';

export const useRemoveWord = () => {
  const currentActiveTranslation = useActiveTranslation();
  const { setInactive } = useTranslatedNodeTransitions();
  const setActiveTranslation = useSetActiveTranslation();
  const [markWordAsRemoved] = useMarkWordAsRemovedMutation();
  const nodes = useTranslatedNodesOnPage();

  const removeTranslatedNodeOnPage = (translationId: string) => {
    const selectedNodes = nodes.filter(node => node.translationId === translationId);
    selectedNodes.forEach(node => setInactive(node));
  };

  const removeWord = async (translationId: string) => {
    // TODO: Refactor to allow setting of active translation without hack
    if (currentActiveTranslation) {
      setActiveTranslation({ id: 'fake' });
    }

    removeTranslatedNodeOnPage(translationId);
    await markWordAsRemoved({ id: translationId });
  };

  return {
    removeWord,
  };
};
