import React, { createContext, useEffect, useState, useContext, FC, memo } from 'react';

import { StorageValue, TypedStorageObject } from 'webextension-polyfill';

import { StorageConnectionKeys } from '~/constants/messages';
import { StorageKey } from '~/constants/storage';
import StorageGetters from '~/storage/storageGetters';
import { StorageConnectionMessage } from '~/types/messages';

import { useMountedRef } from './useMountedRef';
import { usePort } from './usePort';

interface StorageContext {
  cache: { [key: string]: unknown };
  setItem: (key: StorageKey, value: unknown) => void;
  getItem: <T extends unknown>(key: StorageKey) => Promise<T | null>;
}

export const context = createContext<StorageContext>({
  cache: {},
  setItem: () => {},
  getItem: () => Promise.resolve(null),
});

export const StorageProvider: FC<{ cache?: StorageContext['cache'] }> = memo(({ children, cache: passedCache }) => {
  const [cache, setCache] = useState<StorageContext['cache']>(passedCache ?? {});
  const port = usePort();
  const isMounted = useMountedRef();

  useEffect(() => {
    const onStorageChanged = (message: StorageConnectionMessage | {}) => {
      if ('msg' in message && message.msg === StorageConnectionKeys.StorageChanged) {
        setCache(currentCache => ({ ...currentCache, [message.key]: message.value }));
      }
    };
    if (port) {
      port.onMessage.addListener(onStorageChanged);
    }
    return () => {
      if (port) {
        port.onMessage.removeListener(onStorageChanged);
      }
    };
  }, [port]);

  const getItem = async <T extends unknown>(key: string) => {
    if (cache[key]) {
      return cache[key] as T;
    }
    try {
      const value = await StorageGetters.getItem<TypedStorageObject<T>>({ storageKey: key });
      if (!(await isMounted())) {
        return null;
      }

      if (value !== null) {
        setCache(existingCache => ({ ...existingCache, [key]: value }));
        return value;
      }
    } catch (error) {
      // ignore errors
    }
    return null;
  };

  const setItem = (key: StorageKey, value: unknown) => {
    try {
      if (port) {
        port.postMessage({ msg: StorageConnectionKeys.StorageSet, key, value });
      }
      setCache(currentCache => ({ ...currentCache, [key]: value }));
    } catch (e) {
      // refresh with value from store.
      getItem(key);
    }
  };

  return (
    <context.Provider
      value={{
        cache,
        getItem,
        setItem,
      }}
    >
      {children}
    </context.Provider>
  );
});

export const useStorage = <T extends StorageValue>(
  key: StorageKey,
  defaultValue: T,
): [T, (arg: T) => void, boolean] => {
  const { cache, getItem, setItem } = useContext(context);
  const [loading, setLoading] = useState<boolean>(true);
  const isMounted = useMountedRef();
  const state = (cache[key] as T) ?? defaultValue;

  useEffect(() => {
    setLoading(true);
    // Forces pull or value if not in storage cache
    getItem<T>(key)
      .then(() => {
        if (isMounted()) {
          setLoading(false);
        }
      })
      .catch(() => {
        if (isMounted()) {
          setLoading(false);
        }
      });
  }, [key]);

  const setStorage = (value: T) => {
    try {
      setItem(key, value);
    } catch (e) {
      // refresh with value from store.
      getItem(key);
    }
  };

  return [state, setStorage, loading];
};
