/**
 * isGreaterThanOrEqual is a helper that compares two semver versions and returns true if the first is greater than or equal to the second.
 */
export const isGreaterThanOrEqual = (version: string, compareTo: string) => {
  const [major, minor, patch] = version.split('.').map(Number);
  const [majorCompareTo, minorCompareTo, patchCompareTo] = compareTo.split('.').map(Number);

  return (
    major >= majorCompareTo ||
    (major === majorCompareTo && minor >= minorCompareTo) ||
    (major === majorCompareTo && minor === minorCompareTo && patch >= patchCompareTo)
  );
};
