import { ColorPalette, ToucanColors } from '@jointoucan/toucan-design';
import { Aracari } from 'aracari';
import intersectionWith from 'lodash/intersectionWith';
import isEmpty from 'lodash/isEmpty';
import isEqual from 'lodash/isEqual';
import startsWith from 'lodash/startsWith';
import uniqWith from 'lodash/uniqWith';

import { crawlerDebug } from '~/configs/config';
import { excludedRoles, excludedTags, excludedClassNames, toucanPrefix } from '~/constants/html';
import { createElementId } from '~/hooks/useTranslatedNodes/helpers';

/**
 * getNodesAddress will walk in inverse out to the root of the DOM tree or by default to the body element.
 * this gives us a clear path to nodes without having to know selectors. This means that if this page does not change
 * dynamically we are able to target the node, just by knowing the address and selecting the body element.
 * This is very similar to how React manages the virtual DOM. By knowing the address of the virtual node you have a good chance of
 * knowing the position of the node in the actual DOM.
 *
 * This is used right now for de-duping targets that are in the same address scheme.
 * Eg A target with "0.25.1.0" will be filtered out if there is another target with "0.25.1"
 *
 * @param node HTMLElement to get the address for.
 * @param path path is the un-serialized address.
 * @param root the root element to address to.
 */
export const getNodesAddress = (
  node: HTMLElement | undefined,
  path: number[] = [],
  root: HTMLElement = document.body,
): string => {
  const { parentNode } = node ?? {};
  const index = Array.prototype.indexOf.call(parentNode?.childNodes ?? [], node);
  path.push(index);
  if (parentNode === root || index === -1) {
    return path.reverse().join('.');
  }
  return getNodesAddress(parentNode as HTMLElement, path);
};

// Type guard for HTMLElement without using "instanceof HTMLElement" since it does not working in JSDOM.
export const isHTMLElement = (node: Node): node is HTMLElement => node.nodeType === 1;
export const isTextNode = (node: Node): node is Text => node.nodeType === 3;

// This does the opposite of what the address does. It will essentially take the parse address and walk to the node at that address.
export const getNodeFromAddress = (address: number[], node: HTMLElement = document.body): HTMLElement | undefined => {
  if (!address.length || !node) {
    return node as HTMLElement | undefined;
  }
  const nextNode = node.childNodes[address[0]];
  return getNodeFromAddress(address.slice(1), nextNode as HTMLElement);
};

export const isExcludedNode = (node: HTMLElement, customExcludedTags: Array<string> = []): boolean => {
  if (node === null || !node.tagName || node.tagName === 'BODY') {
    return false;
  }
  // Use Accessible Rich Internet Applications role
  const role = node.attributes.getNamedItem('role');
  if (role) {
    if (excludedRoles.includes(role.nodeValue ?? '')) {
      return true;
    }
  }

  const allExcludedTags = [...excludedTags, ...customExcludedTags];

  if (allExcludedTags.includes(node.tagName)) {
    return true;
  }

  if (node.contentEditable === 'true') {
    return true;
  }
  const classListContainsExcludedClass = intersectionWith(Array.from(node.classList), excludedClassNames, isEqual);
  const classListStartsWithExcludedClass = intersectionWith(Array.from(node.classList), excludedClassNames, startsWith);
  if (!isEmpty(classListContainsExcludedClass) || !isEmpty(classListStartsWithExcludedClass)) {
    return true;
  }
  return false;
};

export const getExcludedSubNodes = (
  textMappings: Aracari,
  parentNode: HTMLElement,
  customExcludedTags: Array<string>,
) =>
  uniqWith(
    // Grab all addresses of the sub nodes within a parentNode
    textMappings.mapping.flatMap(([, address]) =>
      address
        .split('.')
        .map(n => parseInt(n, 10))
        .slice(0, -1)
        .map((_x, i, siblingAddresses) => siblingAddresses.slice(0, i + 1)),
    ),
    // Remove dupes of those addresses
    (prev, next) => prev.every((x, i) => x === next[i]) && prev.length === next.length,
  )
    // Filter out only the subNodes that are to be excluded.
    .filter(address => {
      const sibling = getNodeFromAddress(address, parentNode);
      return sibling ? isExcludedNode(sibling, customExcludedTags) : false;
    })
    // Join the address again.
    .map(address => address.join('.'));

export const getElementIdentifier = (node: HTMLElement) => {
  let selector = `${(node.tagName ?? 'none').toLowerCase()} `;
  if (node.id) {
    selector += `#${node.id}`;
  }

  if (node.classList.value) {
    const classes: string[] = [];
    node.classList.forEach(value => {
      classes.push(value);
    });
    selector += `.${classes.join('.')}`;
  }
  return selector;
};

export const isToucanSpan = (node: HTMLElement) => {
  const identifier = getElementIdentifier(node);
  return !!identifier.match(new RegExp(toucanPrefix, 'gi'));
};

export const createToucanSpan = ({ address, id }: { address?: string; id: string }) => {
  const span = document.createElement('span');
  span.classList.add(`${toucanPrefix}-node`);
  span.id = createElementId({ address, id });
  return span;
};

// Lang tag docs:
// https://developer.mozilla.org/en-US/docs/Web/HTML/Global_attributes/lang
export const getPageLanguages = () => {
  const tags = document.querySelectorAll('[lang]');
  const html = document.getElementsByTagName('html')[0];
  const languages = Array.from(tags)
    .filter(isHTMLElement)
    .map(tag => ({
      language: tag.getAttribute('lang'),
      address: getNodesAddress(tag),
      root: tag === html,
      node: tag,
    }));

  return languages;
};

/**
 * isLanguagePage returns true if the page is the users source language, or true if we are unable to
 * determine the language of the page.
 * @param {string } language
 * @returns {boolean}
 */
export const isLanguagePage = (language: string, languageTag?: { language?: string | null }) => {
  // NOTE: We default unknown pages to English.
  // We also support any type of english right now.
  if (
    !languageTag ||
    languageTag.language === 'unknown' ||
    languageTag.language === 'mul' ||
    languageTag.language?.startsWith(language)
  ) {
    return true;
  }

  return false;
};

/**
 * getHTMLElement finds a html give a selector or a HTMLElement.
 * @param el selector or HTMLElement
 * @returns HTMLElement or null if none is found
 */
export const getHTMLElement = (el: string | HTMLElement): HTMLElement | null => {
  let element: HTMLElement | null = typeof el !== 'string' ? el : null;
  if (typeof el === 'string') {
    const maybeElement = document.querySelector(el);
    if (maybeElement && isHTMLElement(maybeElement)) {
      element = maybeElement;
    }
  }
  return element && isHTMLElement(element) ? element : null;
};

/**
 * highlightDebuggingNode highlights a node with a give color if debugging is on.
 * @param node HTML element to highlight
 * @param option.color a color name from ToucanColors
 * @param option.shade a color shade in ToucanColors pallet
 */
export const highlightDebuggingNode = (
  node: HTMLElement,
  { color = 'blue', shade = 200 }: { color?: keyof typeof ToucanColors; shade?: keyof ColorPalette } = {
    color: 'blue',
    shade: 200,
  },
) => {
  const highlightColor = ToucanColors[color][shade];
  if (crawlerDebug) {
    // eslint-disable-next-line
    node.style.boxShadow = `0 0 1px 1px ${highlightColor}, inset 0 0 1px 1px ${highlightColor}`;
  }
};

/**
 * getRootAddresses returns the root address of the root nodes. Root means that the node is the
 * closest selected node to the root document.
 * @param {string[]} addresses this is an array of addresses like ['0.1', '0.1.1', '0.1.2', '2.0']
 * @returns the root addresses like ['0.1', '2.0']
 */
export const getRootAddresses = (addresses: string[]) => {
  const rootAddresses: string[] = [];
  addresses.forEach((address, _index, allAddresses) => {
    const isRootAddress = allAddresses.every(nextAddress => {
      const isChild = address.startsWith(nextAddress) && nextAddress !== address;
      return !isChild;
    });
    if (isRootAddress && !rootAddresses.includes(address)) {
      rootAddresses.push(address);
    }
  });
  return rootAddresses;
};
/**
 * getParentElements given an array of element will find just the parents of those element to
 * dedupe any children element that might be reprocessed.
 * @param elements array of HTMLElements
 */
export const getParentElements = (elements: HTMLElement[]) => {
  const elementsWithAddress = elements.map(el => ({ htmlElement: el, address: getNodesAddress(el) }));
  const parentAddresses = getRootAddresses(elementsWithAddress.map(el => el.address));
  return elementsWithAddress.filter(el => parentAddresses.includes(el.address));
};
