/** @jsx jsx */
import React, { FC } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { useTheme } from '@mui/material';

import { Box } from '~/components/Box';
import { AudioPlayer } from '~/components/shared/AudioPlayer';
import { IconButton } from '~/components/shared/IconButton';
import { FeatureName } from '~/constants/features';
import { Language } from '~/constants/language';
import { Pinchpoints } from '~/constants/pinchpoints';
import { useFeatureEnabled } from '~/libs/features/client';
import { useTranslation } from '~/libs/i18n';
import { ContextlessLearningType } from '~/schema/background';
import { QuizStatus } from '~/types/quiz';

const { Typography, Button, useColorScheme, ProgressBar } = ToucanComponents;

interface FlashCardBackProps {
  onCompleteQuestion: (quizStatus: QuizStatus.Pass | QuizStatus.Fail) => void;
  questionTargetPhrase: string;
  onClickToFlip: () => void;
  isNonLatin: boolean;
  questionSourcePhrase: string;
  pronunciation: string | null;
  onClickCheckmark: () => Promise<void>;
  onClickSaveWord: () => Promise<void>;
  isSaved: boolean;
  isKnown: boolean | undefined;
  isFluent: boolean | undefined;
  progressionScore: number | undefined;
  languageKey: Language | undefined;
  onAudioPlayRequested: () => void;
  questionNumber: number;
  isCLMIntroScreenDisabled: boolean;
  adhocEventTracking: ({ quizType }: { quizType: ContextlessLearningType }) => void;
  openSubscribePage: (options: { pinchpoint: Pinchpoints }) => void;
  isSaveWordDisabled?: boolean;
  isIKnowThisWordDisabled?: boolean;
}

export const FlashCardBack: FC<FlashCardBackProps> = ({
  onCompleteQuestion,
  questionTargetPhrase,
  onClickToFlip,
  isNonLatin,
  questionSourcePhrase,
  pronunciation,
  onClickCheckmark,
  onClickSaveWord,
  isSaved,
  isFluent,
  isKnown,
  progressionScore,
  languageKey,
  onAudioPlayRequested,
  questionNumber,
  isCLMIntroScreenDisabled,
  adhocEventTracking,
  openSubscribePage,
  isSaveWordDisabled,
  isIKnowThisWordDisabled,
}) => {
  const { t } = useTranslation();
  const theme = useTheme();
  const { isDarkMode } = useColorScheme();
  const saveIKnowThisContextlessFlashcardEnabled = useFeatureEnabled(FeatureName.SaveIKnowThisContextlessFlashcard);
  const isLearned = progressionScore === 100;

  let barCompleteTransitionColor: string | undefined;

  const onClickNextWord = () => {
    onCompleteQuestion(QuizStatus.Pass);
    onClickToFlip();
    if (questionNumber === 1 && isCLMIntroScreenDisabled) {
      adhocEventTracking({ quizType: ContextlessLearningType.FlashCard });
    }
  };

  return (
    <Box
      width="100%"
      mt={2}
      mb={2}
      p={2}
      alignItems="center"
      height="232px"
      display="flex"
      flexDirection="column"
      css={css`
        transition: background-color 0.15s ease-in-out;
        border-radius: 8px;
        border: 1px solid ${ToucanColors.gray[isDarkMode ? 600 : 200]};
        background-color: ${isDarkMode ? ToucanColors.teal[600] : 'transparent'};
      `}
    >
      <Box display="flex" justifyContent="space-between" width="100%" flex="1">
        <Box pt={1} display="flex" alignItems="flex-start" mr={1}>
          {languageKey && (
            <Box display="flex" alignItems="flex-start">
              <AudioPlayer targetPhrase={questionTargetPhrase} language={languageKey} onPlay={onAudioPlayRequested} />
            </Box>
          )}
        </Box>
        <Box flex="1">
          <Typography variant="md" isBold color={isDarkMode ? ToucanColors.white : ToucanColors.gray[600]}>
            {questionTargetPhrase}
          </Typography>
          {isNonLatin && (
            <Typography variant="xs" isBold color={isDarkMode ? ToucanColors.gray[200] : ToucanColors.gray[500]}>
              {pronunciation}
            </Typography>
          )}
          <Typography variant="xs" color={isDarkMode ? ToucanColors.gray[300] : ToucanColors.gray[400]}>
            {questionSourcePhrase}
          </Typography>
        </Box>
        {saveIKnowThisContextlessFlashcardEnabled && (
          <Box flex="0" display="flex" flexDirection="row" justifyContent="flex-end">
            <Box pl={1} data-testid={`${isSaved ? 'remove' : 'add'}-save-word`}>
              <IconButton
                active={isSaved}
                disabled={isSaveWordDisabled}
                icon="bookmark-outline"
                activeIcon="bookmark-solid"
                alt={t('save')}
                onClick={
                  isSaveWordDisabled
                    ? () => {
                        openSubscribePage({
                          pinchpoint: Pinchpoints.FlashCardSaveWordCta,
                        });
                      }
                    : onClickSaveWord
                }
              />
            </Box>
            <Box position="relative" display="flex" justifyContent="flex-end">
              <Box position="relative" pl={1} data-testid={`${isKnown ? 'remove' : 'add'}-known-word`}>
                <IconButton
                  active={isKnown || isFluent || isLearned}
                  disabled={isFluent || isIKnowThisWordDisabled}
                  icon="check"
                  activeIcon={isFluent ? 'double-check' : 'check'}
                  alt={t('iKnowThis')}
                  tooltipProps={{ width: 100 }}
                  onClick={
                    isIKnowThisWordDisabled
                      ? () => {
                          openSubscribePage({
                            pinchpoint: Pinchpoints.FlashCardIKnowThisWordCta,
                          });
                        }
                      : onClickCheckmark
                  }
                  colors={{
                    ...(isFluent
                      ? {
                          light: {
                            background: {
                              idle: ToucanColors.green[300],
                              hover: ToucanColors.green[300],
                              active: ToucanColors.green[300],
                            },
                            color: {
                              idle: ToucanColors.white,
                              hover: ToucanColors.white,
                              active: ToucanColors.white,
                            },
                          },
                          dark: {
                            background: {
                              idle: ToucanColors.green[300],
                              hover: ToucanColors.green[300],
                              active: ToucanColors.green[300],
                            },
                            color: {
                              idle: ToucanColors.white,
                              hover: ToucanColors.white,
                              active: ToucanColors.white,
                            },
                          },
                        }
                      : {}),
                  }}
                />
              </Box>
            </Box>
          </Box>
        )}
      </Box>
      <Box mt={2} flex="0" width="100%">
        <ProgressBar
          barCompleteTransitionOptions={{ duration: 0 }}
          beginPercentage={progressionScore}
          endPercentage={progressionScore}
          barColor={isFluent ? ToucanColors.green[300] : ToucanColors.blue[200]}
          barCompleteTransitionColor={barCompleteTransitionColor}
          onAnimationComplete={async () => {}}
        />
        <Box display="flex" alignItems="flex-start" mt={2}>
          <Button
            type="secondary"
            size="small"
            onClick={onClickToFlip}
            css={css`
              flex: 1 1 0;
              margin-right: ${theme.spacing(1)};
              padding: ${theme.spacing(1)} ${theme.spacing(2)};
            `}
          >
            {t('back')}
          </Button>
          <Button
            type="utility"
            size="small"
            onClick={onClickNextWord}
            css={css`
              flex: 1 1 0;
              padding: 8px 16px;
            `}
          >
            {t('clickNext')}
          </Button>
        </Box>
      </Box>
    </Box>
  );
};
