import { atom, useAtom } from 'jotai';
import { useHydrateAtoms } from 'jotai/utils';

const supportsAudioAtom = atom(true);

export const useSupportsAudio = () => useAtom(supportsAudioAtom)[0];
export const useHydrateSupportsAudio = (supportsAudio: boolean) =>
  useHydrateAtoms([[supportsAudioAtom, supportsAudio]]);
