/** @jsx jsx */
import { css, jsx } from '@emotion/react';
import { ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { Image } from '~/components/shared/Image';
import { useTranslation } from '~/libs/i18n';

const { ToucanIcon, Typography, Button, useColorScheme } = ToucanComponents;

interface PremiumUpsellProps {
  children: string;
  onClickTextButton: () => void;
  linkText: string;
  imageUrl: string;
  imageAlt: string;
}

export const PremiumUpsell: React.FC<PremiumUpsellProps> = ({
  children,
  onClickTextButton,
  linkText,
  imageUrl,
  imageAlt,
}) => {
  const { t } = useTranslation();
  const { uiPromotion, textPromotion } = useColorScheme();
  const theme = useTheme();

  return (
    <Box display="flex" width="224px" bgcolor={uiPromotion} borderRadius="4px" mt={1} ml={-1} mb={-1} p={1}>
      {imageUrl && <Image src={imageUrl} width={32} height={32} alt={imageAlt || t('premiumUpsellIcon')} mr={1} />}
      <Box>
        <Typography variant="2xs" color={textPromotion}>
          {children}
        </Typography>

        <Typography variant="2xs">
          <Button
            type="text"
            onClick={onClickTextButton}
            css={css`
              display: flex;
              align-items: center;
              gap: ${theme.spacing(0.5)};
            `}
          >
            {linkText}
            <ToucanIcon
              icon="chevron-right"
              css={css`
                width: 8px;
                color: inherit;
              `}
            />
          </Button>
        </Typography>
      </Box>
    </Box>
  );
};
