export enum ModalType {
  Sidebar = 'sidebar',
  MicroFeedback = 'micro-feedback',
  DomainPermissionUpsell = 'domain-permision-upsell',
  LanguageNowLive = 'language-now-live',
  RemoveWord = 'remove-word',
  ReportError = 'report-error',
  ExpandTranslation = 'expand-translation',
  ShortcutsCelebration = 'shortcuts-celebration',
}

export interface ModalInfoProps {
  priority: 0 | 1 | 2;
  canOverride: boolean; // can close other modals
}

export interface ModalBaseProps {
  selectedModalType: ModalType | null;
  onRequestClose: (modalType: ModalType) => boolean;
  onRequestOpen: (modalType: ModalType, cleanup?: () => void) => boolean;
}
