/** @jsx jsx */
import { FC, useEffect } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanColors, ToucanComponents } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';

import { ModalBaseProps, ModalType } from '~/components/ModalContainer/types';
import Header from '~/components/shared/Header';
import { Image } from '~/components/shared/Image';
import { Modal, ModalContent, ModalHeader } from '~/components/shared/Modal';
import { StorageKey } from '~/constants/storage';
import { useModalStorage } from '~/hooks/useModalStorage';
import { useRemoveWord } from '~/hooks/useRemoveWord';
import { useStorage } from '~/hooks/useStorage';
import { useTranslation } from '~/libs/i18n';
import ContentMessengers from '~/messengers/contentMessengers';

const { Typography, Button } = ToucanComponents;

interface RemoveWordModalProps extends ModalBaseProps {}

export const RemoveWordModal: FC<RemoveWordModalProps> = ({ onRequestClose, onRequestOpen, selectedModalType }) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { removeWord } = useRemoveWord();

  const [, setSeenRemovedWordMessage] = useStorage<boolean>(StorageKey.HasRemoveWordMessageDisplayed, false);

  const [translationIdToBeRemoved, setTranslationIdToBeRemoved] = useStorage<string | null>(
    StorageKey.TranslationIdToBeRemoved,
    null,
  );

  const [isRemoveWordModalOpenRequested, setIsRemoveWordModalOpenRequested] = useModalStorage(
    StorageKey.IsRemoveWordModalOpenRequested,
    false,
  );

  // NOTE: Since the source of truth for this modal is based off of storage
  // we just respond to the changing of that state, this modal has a priority of 0
  // so it always overrides
  useEffect(() => {
    if (selectedModalType !== ModalType.RemoveWord && isRemoveWordModalOpenRequested) {
      onRequestOpen(ModalType.RemoveWord);
      setIsRemoveWordModalOpenRequested(false);
    }
  }, [selectedModalType, isRemoveWordModalOpenRequested]);

  useEffect(() => {
    if (selectedModalType === ModalType.RemoveWord && translationIdToBeRemoved) {
      ContentMessengers.trackEvent({
        eventName: 'Remove Word Modal Viewed',
        properties: {},
      });

      setSeenRemovedWordMessage(true);
    }
  }, [selectedModalType, translationIdToBeRemoved]);

  const closeModal = () => {
    onRequestClose(ModalType.RemoveWord);
    setTranslationIdToBeRemoved(null);
    ContentMessengers.trackEvent({
      eventName: 'Remove Word Modal Closed',
      properties: {},
    });
  };

  const removeWordAndCloseModal = async () => {
    if (translationIdToBeRemoved) {
      await removeWord(translationIdToBeRemoved);
    }

    closeModal();
  };

  return (
    <Modal
      css={css`
        text-align: center;
        align-items: center;
        width: 418px;
      `}
      onRequestClose={closeModal}
      isOpen={!!(selectedModalType === ModalType.RemoveWord && translationIdToBeRemoved)}
    >
      <ModalHeader>
        <Header
          onRequestClose={closeModal}
          css={css`
            background-color: transparent;
          `}
          horizontalLineProps={{
            color: ToucanColors.gray[200],
          }}
          isMobileReferralShown={false}
          onMobileReferralClick={() => {}}
        />
      </ModalHeader>
      <ModalContent p={4}>
        <Box pb={4}>
          <Image src="/images/browser-window.svg" width={96} height={96} alt={t('browserWindow')} />
        </Box>
        <Box pb={1}>
          <Typography
            variant="md"
            font="secondary"
            css={css`
              color: ${ToucanColors.gray[600]};
            `}
          >
            {t('areYouSure')}
          </Typography>
        </Box>
        <Box pb={4}>
          <Typography
            variant="sm"
            css={css`
              color: ${ToucanColors.gray[500]};
            `}
          >
            {t('weWontTranslateWordAnymore')}
          </Typography>
        </Box>
        <Box pb={3}>
          <Button
            type="utility"
            onClick={removeWordAndCloseModal}
            css={css`
              ${theme.breakpoints.down('sm')} {
                width: 100%;
              }
            `}
          >
            {t('removeWord')}
          </Button>
        </Box>
        <Typography variant="xs">
          <Button
            type="text"
            onClick={closeModal}
            css={css`
              display: inline-block;
            `}
          >
            {t('nevermind')}
          </Button>
        </Typography>
      </ModalContent>
    </Modal>
  );
};
