import { v4 } from 'uuid';

import { getNodesAddress } from '~/helpers/htmlHelpers';
import { PartialBy } from '~/types/utils';

import { DomTarget } from './types';

/**
 * stampDomTarget stamps a version of the domTarget that is filled out this often will add
 * an identifier to the domTarget that is used to identify it in the reducer
 * @param {PartialBy<DomTarget, 'id' | 'address'>} domTarget partial domTarget
 * @returns {DomTarget} a full domTarget
 */
export const stampDomTarget = (node: PartialBy<DomTarget, 'id' | 'address'>): DomTarget => ({
  intersectionRatio: 0, // intersection  observer ratio
  ...node,
  id: node.id || v4(),
  address: node.address ?? getNodesAddress(node.parentNode),
});
