import { useAtom } from 'jotai';

import {
  HighlightToTranslateSidebarAtom,
  SetHighlightToTranslateSidebarAtom,
} from './HighlightToTranslateSidebar/atom';

export const useHTTSidebar = () => useAtom(HighlightToTranslateSidebarAtom)[0];
export const useSetHTTSidebar = () => useAtom(SetHighlightToTranslateSidebarAtom)[1];
