/** @jsx jsx */
import React, { FC, useMemo, useState } from 'react';

import { css, jsx } from '@emotion/react';
import { ToucanComponents, ToucanColors } from '@jointoucan/toucan-design';
import { Box, useTheme } from '@mui/material';
import { shuffle } from 'lodash';
import { useDeepCompareMemoize } from 'use-deep-compare-effect';
import { v4 } from 'uuid';

import { useTranslation } from '~/libs/i18n';
import { QuizStatus } from '~/types/quiz';

import { isMultipleWordQuestion } from '../helpers';
import { QuestionBaseProps } from '../types';
import { useQuizSize } from '../useQuizSize';

import { Button } from './Button';

const { useColorScheme, Typography } = ToucanComponents;

export const Match: FC<QuestionBaseProps> = ({ displayAnswerTimeout = 1200, question, submitQuestion }) => {
  const theme = useTheme();
  const { t } = useTranslation();
  const { isDarkMode } = useColorScheme();
  const { fontVariant } = useQuizSize();

  // Map in ids just in case there is multiples of the same word
  const matches = useMemo(
    () =>
      question && isMultipleWordQuestion(question)
        ? question.matches.map(({ sourcePhrase, targetPhrase }) => ({
            sourcePhrase: { id: v4(), phrase: sourcePhrase },
            targetPhrase: { id: v4(), phrase: targetPhrase },
          }))
        : [],
    useDeepCompareMemoize([question]),
  );

  const mixedMatches = useMemo(
    () => shuffle(matches.flatMap(({ targetPhrase, sourcePhrase }) => [targetPhrase, sourcePhrase])),
    useDeepCompareMemoize([matches]),
  );

  const [firstSelectedButton, setFirstSelectedButton] = useState<string | null>(null);
  const [secondSelectedButton, setSecondSelectedButton] = useState<string | null>(null);
  const [answered, setAnswered] = useState<Array<string>>([]);

  const onChoiceClick = (id: string) => {
    if (!firstSelectedButton) {
      setFirstSelectedButton(id);
      return;
    }

    // This is currently showing the answer lets just wait
    if (secondSelectedButton) {
      return;
    }

    const currentQuestion = matches.find(
      match => match.sourcePhrase.id === firstSelectedButton || match.targetPhrase.id === firstSelectedButton,
    );

    // No question found should not be possible, but ts wants it.
    if (!currentQuestion) {
      setFirstSelectedButton(null);
      return;
    }

    const isCorrect = currentQuestion.targetPhrase.id === id || currentQuestion.sourcePhrase.id === id;
    const allAnswered = [...answered, ...(isCorrect ? [id, firstSelectedButton] : [])];

    setSecondSelectedButton(id);
    setAnswered(allAnswered);
    // Reset state
    setTimeout(() => {
      setFirstSelectedButton(null);
      setSecondSelectedButton(null);
      if (allAnswered.length === mixedMatches.length) {
        submitQuestion(QuizStatus.Pass);
      }
    }, displayAnswerTimeout);
  };

  return (
    <Box>
      <Typography
        variant={fontVariant}
        css={css`
          color: ${ToucanColors.gray[isDarkMode ? 200 : 500]};
          margin-bottom: ${theme.spacing(2)};
        `}
      >
        {t('matchTheFollowingWords')}
      </Typography>
      <Box display="flex" flexDirection="row" flexWrap="wrap" gap={`${theme.spacing(1)}`}>
        {mixedMatches.map(({ phrase, id }, index) => {
          const isActive = firstSelectedButton === id || secondSelectedButton === id;
          const bothAnswered = !!firstSelectedButton && !!secondSelectedButton;
          let status: QuizStatus.Pass | QuizStatus.Fail | undefined;
          if (bothAnswered && isActive) {
            status = answered.includes(id) ? QuizStatus.Pass : QuizStatus.Fail;
          }
          const disabled = !isActive && answered.includes(id);
          return (
            <Button
              // eslint-disable-next-line react/no-array-index-key
              key={`${phrase}-${index}`}
              onClick={() => onChoiceClick(id)}
              status={status}
              disabled={disabled}
              active={isActive}
              option={phrase}
            />
          );
        })}
      </Box>
    </Box>
  );
};
