import { useState } from 'react';

/**
 * useMutationObserver will create a mutation observer that can be used in a react component
 * @todo onMutation hooks can not update, we should eventually support that.
 * @param options.onMutation a function that will be called when a mutation happens
 */
export const useMutationObserver = ({ onMutation }: { onMutation: MutationCallback }) => {
  const [observer] = useState(() => new MutationObserver(onMutation));
  return observer;
};
