import { useEffect, useState } from 'react';

import { ContentMessageProducerArgs } from '~/contentMessageProducer';

export interface ContentMessageProducerHookResult {
  data: any;
  loading: boolean;
  error: Error | undefined;
}

/**
 * useContentMessageProducer is a hook that takes a Content Message Producer promise
 * and provides a loading state, error, and data when resolved.
 */
export const useContentMessageProducer = (
  contentMessageProducer: (args: any) => Promise<any>,
  args?: ContentMessageProducerArgs,
) => {
  const [loading, setIsLoading] = useState(true);
  const [data, setData] = useState<any>();
  const [error, setError] = useState<Error>();
  useEffect(() => {
    const resolveContentMessageProducer = async () => {
      try {
        const resolvedData = await contentMessageProducer(args);
        setData(resolvedData);
        setIsLoading(false);
      } catch (err) {
        if (err instanceof Error) {
          setError(err);
          setIsLoading(false);
        }
        throw err;
      }
    };
    resolveContentMessageProducer();
  }, []);

  return {
    data,
    error,
    loading,
  };
};
